/// <reference path="./Interfaces.ts" />
/// <reference path="./DataFilter.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    export abstract class GraphNode {
        public static bindings: { [index: string]: string } = {};
        public static dataToIPC: (data: any) => Uint8Array;
        public static viewModel: QuickBase.DocumentViewModel;
        public static VSRx = /^<%[^<>%]*%>$/i;
        public data: Data | string | undefined;

        id: string;
        group: "data" | "stream" | "filter" | "function" | "groupBy" | "join" | "update" | "result";
        meta?;
        subscription?;

        constructor(data: any) {
            this.id = data.id;
            this.group = data.group;
            if (data.meta) {
                this.meta = data.meta;
            }
            if (this.group !== "result") {
                this.data = data.data || {};
            }
        }

        public static getBindingKeysFromArray(
            existingKeys: Array<string>,
            arr: string[],
        ): Array<string> {
            return existingKeys.concat(
                _.filter(arr, k => _.isString(k) && GraphNode.VSRx.exec(k)).map((k: string) =>
                    k.substring(2, k.length - 2),
                ),
            );
        }

        public static getActiveBindingKeys(data: any): Array<string> {
            return [];
        }

        public static replaceAllBindings(obj: any, value = "value"): void {
            _.each(obj, item => {
                if (!item[value] && item["children"]) {
                    GraphNode.replaceAllBindings(item["children"]);
                } else {
                    item[value] =
                        typeof item[value] !== "string"
                            ? item[value]
                            : GraphNode.VSRx.test(item[value])
                            ? GraphNode.replaceBindings(item[value])
                            : item[value];
                }
            });
        }

        public static replaceBindings(val: string): string {
            const key = val.substring(2, val.length - 2);
            if (GraphNode.viewModel) {
                const vs = GraphNode.viewModel.getByPath(key);
                return vs && vs.get("value");
            } else {
                return GraphNode.bindings[key];
            }
        }

        public static replaceKeys(obj: SelectData | AggData): void {
            let replaceKeys = false;

            _.each(obj, (val, index, obj) => {
                let newIndex;

                if (GraphNode.VSRx.exec(index)) {
                    newIndex = index.replace(GraphNode.VSRx, GraphNode.replaceBindings.bind(this));
                    // replace this key and all that come after to preserve insertion order
                    replaceKeys = true;
                }

                if (replaceKeys) {
                    delete obj[index];
                    obj[newIndex || index] = val;
                }
            });
        }

        public toIPC(): Uint8Array {
            return QuickBase.ipc.Dict.toIPC(
                ["type", "data"],
                QuickBase.ipc.List.toIPC([
                    QuickBase.ipc.Symbol.toIPC(this.group),
                    QueryBuilder.App.groupToClass[this.group].dataToIPC(this.data),
                ]),
            );
        }
    }
}
