/// <reference path="I32.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Month extends I32 {
        static readonly TYPE_NUM = 13;

        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Month, dataView);
        }

        public static fromMoment(m: moment.Moment): rMonthClass {
            // i is the number of months since 2000-01
            return m === null
                ? null
                : new rMonthClass(Math.round(m.diff(moment([2000]), "months", true)));
        }

        public static fromScalar(value: number): rMonthClass {
            return value === null ? null : new rMonthClass(value);
        }

        public static fromString(str: string): rMonthClass {
            if (str === null) return null;
            str = str.replace(/\./g, "-").replace(/m$/, "");
            const m = moment.utc(str);
            return Month.fromMoment(m);
        }

        public static listToIPC(values: Array<rMonthClass>): Uint8Array {
            const size = values.length * 4 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(Month.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Month.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        public static toIPC(value: rMonthClass): Uint8Array {
            const buffer = Util.createBuffer(5);

            buffer.wb(256 - Month.TYPE_NUM); // type
            Month.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        public static toMoment(value: rMonthClass): moment.Moment {
            if (value === null) return null;
            const m = moment.tz("2000-01-01", "UTC");
            m.add(value.i, "month");
            return m;
        }

        public static toScalar(value: rMonthClass): number {
            return value === null ? null : value.i;
        }

        public static toString(value: rMonthClass, format = "YYYY-MM"): string {
            return value === null ? "" : Month.toMoment(value).format(format);
        }

        public static writeValue(value: rMonthClass, wb: (b: number) => void): void {
            Int.writeValue(Month.toScalar(value), wb);
        }

        getValue(i: number): number | rMonthClass {
            const s = super.getScalar(i);
            return s === I32.NULL ? null : new rMonthClass(s);
        }
    }
}
