/*globals Backbone, _, $, console*/
/// <reference path="../templates/combined.ts" />
/// <reference path="../lib/delta-client-lib.ts" />
/// <reference path="../classes/log.ts"/>
/// <reference path="../lib/i18n.d.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ChangePasswordDialog extends Backbone.View<Backbone.Model> {
        MESSAGE_TYPES = ["error", "success", "warning"];

        $dialog: JQuery;
        $messageBox: JQuery;
        $okBtn: JQuery;

        clicked = false;
        form: any;

        boundToggleCaps;

        onChangeSuccess: () => void;

        constructor(options) {
            super(options);
            const username = null;

            const User = Backbone.Model.extend({
                schema: {
                    username: {
                        title: t("Username"),
                    },
                    oldpassword: {
                        title: t("Current Password"),
                        type: "Password",
                        validators: ["required"],
                    },
                    password1: {
                        title: t("New Password"),
                        type: "Password",
                        validators: ["required"],
                    },
                    password2: {
                        title: t("Re-Enter"),
                        type: "Password",
                        validators: [
                            { type: "match", field: "password1", message: "Passwords must match!" },
                            "required",
                        ],
                    },
                },
            });

            this.boundToggleCaps = this.toggleCapsLockMsg.bind(this);

            this.model = new User({
                username: username || undefined,
            });

            if (!username) {
                delete (this.model as any).schema.username;
            }

            this.form = new Backbone["Form"]({
                model: this.model,
            }).render();

            this.$el.append(this.form.el);
            this.$el.append('<div class="message-box">');

            // handle form submit
            $(this.form.el).submit($.proxy(this.onSubmit, this));

            this.$messageBox = this.$el.find(".message-box");

            if (options.message) {
                this.showMessage(options.message);
            } else {
                this.showMessage();
            }

            this.onChangeSuccess = options.onChangeSuccess;

            this.$dialog = this.$el["dialog"](
                _.extend(
                    {
                        appendTo: options.container,
                        dialogClass: "quick-base change-password-dialog",
                        title: t("Change Password"),
                        resizable: false,
                        height: 262,
                        width: 320,
                        modal: true,
                        autoOpen: true,
                        draggable: true,
                        buttons: [
                            {
                                text: t("OK"),
                                click: $.proxy(this.onSubmit, this),
                                icons: {
                                    primary: "fa fa-check",
                                },
                            },
                            {
                                text: t("Cancel"),
                                click: $.proxy(this.close, this),
                                icons: {
                                    primary: "fa fa-times",
                                },
                            },
                        ],
                        open: () => {
                            // add a dialog icon
                            $('<i class="fa fa-check" />').insertBefore(
                                this.$el.parent().find(".ui-dialog-title"),
                            );

                            // add blur
                            $(".quick-base").addClass("disturbed");

                            this.$okBtn = $(this)
                                .parent()
                                .find(".ui-dialog-buttonpane button")
                                .first();

                            this.$el.find(".old-password-input").focus();
                        },
                        close: () => {
                            $(".quick-base").removeClass("disturbed");
                            ErrorTooltip.clearAll();

                            if (_.isFunction(options.onClose)) {
                                options.onClose();
                            }

                            this.onRemove();

                            if (_.isFunction(options.onDialogClose)) {
                                options.onDialogClose();
                            }
                        },
                    },
                    options,
                ),
            );

            $(this.form.el)
                .find("input")
                .on("keydown", e => {
                    if (e.which === 13) {
                        this.onSubmit();
                    }
                });
            // Key Press has different event state then keydown
            $(this.form.el)
                .find("input")
                .on("keypress", (e: JQuery.Event) => {
                    if (LoginDialog.checkCapslockOn(e.originalEvent)) {
                        this.showMessage(
                            '<i class="fa fa-warning"/>' + t("Caps Lock is on"),
                            "warning",
                        );
                    } else {
                        this.showMessage(); // hide
                    }
                });

            window.addEventListener("keydown", this.boundToggleCaps, true);
        }

        close() {
            this.$dialog["dialog"]("close");
        }

        onSubmit(e?: any) {
            let $okBtn,
                $cancelBtn,
                $icon,
                undo,
                self = this;

            if (e) {
                e.stopPropagation();
            }

            if (!this.clicked) {
                const validation = this.form.commit();
                if (!_.isEmpty(validation)) {
                    return;
                }

                this.clicked = true;
                this.$messageBox.hide();

                undo = function() {
                    $icon.removeClass("fa-spinner fa-pulse");
                    $icon.css({
                        width: "",
                        height: "",
                    });
                    $icon.addClass("fa-check");
                    $okBtn.find(".ui-button-text").text(t("OK"));
                    $cancelBtn["button"]("enable");
                    self.clicked = false;
                    DeltaClientLib.changingPassword = false;
                };

                // show changing in the button

                // show wait info in the button
                $okBtn = this.$okBtn; // $(e.currentTarget);
                $icon = $okBtn.find(".ui-button-icon-primary.fa-check");
                $icon.removeClass("fa-check");
                $icon.css({
                    width: "auto",
                    height: "auto",
                });
                $icon.addClass("fa-spinner fa-pulse");
                $okBtn.find(".ui-button-text").text(t("wait" + "..."));
                // disable cancel button
                $cancelBtn = $okBtn.next();
                $cancelBtn["button"]("disable");

                // validate new password
                DeltaClientLib.changeUserPassword(
                    self.model.get("oldpassword"),
                    self.model.get("password1"),
                    function() {
                        Log.Info("Password changed successfully");
                        self.showMessage("Password changed successfully", "success");
                        if (_.isFunction(self.onChangeSuccess)) {
                            self.onChangeSuccess();
                        }
                        _.delay(function() {
                            self.close();
                        }, 1000);
                    },
                    function(e) {
                        Log.Error("Password change failed");
                        // display error
                        self.showMessage(e);
                        undo();
                    },
                );
            }
        }

        onRemove() {
            $(this.form.el)
                .find("input")
                .off();

            window.removeEventListener("keydown", this.boundToggleCaps, true);

            this.remove();
        }

        showMessage(message?: string, type?: string) {
            if (message) {
                this.$messageBox.html(message);
                this.$messageBox
                    .removeClass(this.MESSAGE_TYPES.join(" "))
                    .addClass(type || "error");
                this.$messageBox.show();
            } else {
                this.$messageBox.html();
                this.$messageBox.removeClass(this.MESSAGE_TYPES.join(" "));
                this.$messageBox.hide();
            }
        }

        toggleCapsLockMsg(e: Event) {
            if (e["which"] === 20) {
                if (e["getModifierState"]("CapsLock")) {
                    this.showMessage(
                        '<i class="fa fa-warning"/>' + t("Caps Lock is on"),
                        "warning",
                    );
                } else {
                    this.showMessage(); // hide
                }
            }
        }
    }
}
