/*global _,$,Backbone,console, xlsx*/
/// <reference path="FileExportDataGenerator.ts" />
/// <reference path="ExportTools.ts" />
/// <reference path="../views/SimpleInputDialog.ts" />
/// <reference path="../lib/i18n.d.ts" />

/*This class uses sheetJs.js library exported as a module XLSX.
 *The library is used to convert data grid form data from array or arrays
 * into Excel, csv, ect. file.
 *Detailed documentation is available at:
 *https://github.com/SheetJS/js-xlsx/blob/master/README.md
 */

/**
 * appView parameters are passed as callback funcitons to avoid stale data
 * @param {object} options - object with key setting and models
 * @param {array} [options.dataSet] - returns appView.model.dataSet
 * @param {array}  options.metaData {obj} - alternative data collection to facilitate
 *       different data set for non pivot datasets
 *   options.workSheetTitle {string} - widget component name
 *   options.fileFormat {string} - what file export to be generated
 *   options.fileName {string} - file name from the file name prop
 *   options.csvExportDelimiter {string} - csv delimiter to replace comma
 */ // eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class FileExport {
        bookType: XLSX.BookType;
        csvExportDelimiter: string;
        dataSet: any;
        metaData: any;
        configSets: any;
        workSheetTitle: string;
        fileFormat: string;
        fileName: string;

        constructor(options) {
            this.csvExportDelimiter = options.csvExportDelimiter;
            this.dataSet = options.dataSet;
            this.metaData = options.metaData;
            this.configSets = options.configSets;
            this.workSheetTitle = options.workSheetTitle; //used as name for work_sheet
            this.fileFormat = options.fileFormat;
            this.fileName = options.fileName;

            // Map required for Excel conversion by XLSX class
            this.bookType = {
                ".xlsx": "xlsx",
                ".xls": "xlml",
                ".csv": "csv",
            }[options.fileFormat];
        }

        createExportOfCurrentData(filename: string) {
            let self = this,
                //exporter variables
                configSets = self.configSets,
                dataSet = self.dataSet,
                metadata = self.metaData,
                tableDataArr = [],
                workbook = { SheetNames: [], Sheets: {} },
                workbookOutput,
                delimiter = ",",
                arrStream,
                bookType = self.bookType;

            //Generate single table from multiple datasources
            if (configSets && _.keys(configSets).length) {
                _.each(configSets, function(configSet) {
                    tableDataArr.push(
                        FileExportDataGenerator.getFormData(
                            configSet.dataSet,
                            configSet.metaData,
                            bookType,
                        ),
                    );
                });
            } else if (dataSet && dataSet.length && metadata && metadata.length) {
                tableDataArr[0] = FileExportDataGenerator.getFormData(dataSet, metadata, bookType);
            }

            //Check that data is present
            if (tableDataArr.length && tableDataArr[0].data.length) {
                _.each(tableDataArr, function(tableData, ix) {
                    let ws_name, worksheet;

                    //get datasource name here
                    ws_name = self.workSheetTitle || "Work_sheet";
                    ws_name += tableDataArr.length > 1 ? "_" + ix : "";

                    //convert table data JSsheet compatible format
                    worksheet = self.convertDataToWorksheet(
                        tableData.data,
                        tableData.columnHeaders,
                    );

                    // add new sheet name to workbook
                    workbook.SheetNames.push(ws_name);
                    // add worksheet with data to workbook
                    workbook.Sheets[ws_name] = worksheet;
                    // process workbook and create export output
                });

                if (bookType !== "csv") {
                    workbookOutput = XLSX.write(workbook, {
                        bookType: bookType,
                        bookSST: false,
                        type: "binary",
                    });

                    arrStream = FileExport.s2ab(workbookOutput);
                    self.downloadExportFile(filename, arrStream);
                } else {
                    let index = 1;
                    if (self.csvExportDelimiter && self.csvExportDelimiter.length) {
                        delimiter = self.csvExportDelimiter;
                    }

                    _.each(workbook.Sheets, function(worksheet) {
                        // If multiple files append part string to export name
                        const newPartName =
                            tableDataArr.length > 1
                                ? filename.slice(0, -4) + "part" + index + ".csv"
                                : filename;

                        try {
                            workbookOutput = XLSX.utils.sheet_to_csv(worksheet, { FS: delimiter });
                        } catch (err) {
                            console.log("Error", err);
                            return;
                        }

                        /* CSV Export is string of comma separated values devided in to 
                            rows by new line character */
                        //fix:KXAX-2956 "ID" column caused excel to show warning when opening csv
                        //get index of ID string in header substring
                        if (workbookOutput.indexOf("ID") === 0) {
                            // Escape special characters if used as delimiter
                            if (/[-\+\{\}\[\]\?\^\*\.\|\$]/.test(delimiter)) {
                                delimiter = "\\" + delimiter;
                            }
                            const regex = new RegExp("(ID[^" + delimiter + "\n]*)");
                            workbookOutput = workbookOutput.replace(regex, '"$1"');
                        }

                        self.downloadExportFile(newPartName, workbookOutput);

                        index += 1;
                    });
                }
            } else {
                console.error("Data formatter function returned invalid parameter or no data");
            }
        }

        downloadExportFile(filename, data) {
            let blob,
                link,
                //required for download
                contentType = "application/octet-stream";

            // Download Exported file
            link = document.createElement("a");

            if (window.Blob) {
                blob = new Blob([data], {
                    type: contentType,
                });

                if (window.navigator.msSaveOrOpenBlob) {
                    //IE fix
                    link.onclick = function() {
                        window.navigator.msSaveOrOpenBlob(blob, filename);
                    };
                } else {
                    if (link.download !== undefined) {
                        link.setAttribute("href", URL.createObjectURL(blob));
                    }
                }
            } else {
                link.setAttribute("href", encodeURI(contentType + "," + data));
            }

            if (link.download !== undefined) {
                link.setAttribute("download", filename);
            }

            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        }

        /*
         * Call this function to start file export process
         */
        showDialog() {
            let self = this,
                defaultName,
                fileFormat = self.fileFormat,
                namePrefix = {
                    ".csv": "Csv",
                    ".xlsx": "Excel",
                }[fileFormat];

            //generate default file name
            defaultName = self.fileName + fileFormat;

            SimpleInputDialog.ShowInput(
                function() {
                    let filename = this.inputValue;

                    filename = "" + filename || ExportTools.getDefaultExportFileName(namePrefix);
                    // Sanitize file name
                    filename = filename.replace(/:/g, "-").replace(/[^\w \.\-]/g, "");
                    //validate file extention name at the end of the string
                    if (!new RegExp(fileFormat + "$").test(filename)) {
                        filename += fileFormat;
                    }
                    self.createExportOfCurrentData(filename);
                },
                t("Export file"),
                t("Please enter the filename"),
                "fa fa-download",
                defaultName,
                "",
                true,
            );
        }

        /**Set of functions for Excel/csv converter convert  -
         * function example from http://sheetjs.com/demos/writexlsx.html
         * @param data {obj} - expected values
         * @param resetIndexes {obj} - where key is a index of columns to be set to string
         */
        convertDataToWorksheet(data, columnHeaders) {
            //as many as column you have in data, columnInfo-array has the same length

            const ws = {},
                range = { s: { c: 10000000, r: 10000000 }, e: { c: 0, r: 0 } },
                datenum = function datenum(v, date1904) {
                    if (date1904) v += 1462;
                    const epoch = Date.parse(v);
                    return (
                        (epoch - new Date(Date.UTC(1899, 11, 30)).getTime()) / (24 * 60 * 60 * 1000)
                    );
                };
            //parse row index
            for (let R = 0; R != data.length; ++R) {
                //parse cell index
                for (let C = 0; C != data[R].length; ++C) {
                    if (range.s.r > R) range.s.r = R;
                    if (range.s.c > C) range.s.c = C;
                    if (range.e.r < R) range.e.r = R;
                    if (range.e.c < C) range.e.c = C;

                    let cellVals = data[R][C],
                        //check if cell has type override
                        type =
                            cellVals.type !== undefined && cellVals.type !== ""
                                ? cellVals.type
                                : columnHeaders[C]["type"],
                        format = cellVals.format ? cellVals.format : columnHeaders[C]["format"],
                        cell: XLSX.CellObject = {} as XLSX.CellObject;

                    if (type === null) {
                        continue;
                    }

                    //facility to reset all cell in the column to the
                    // same type used if number digits length is > 16
                    if (columnHeaders[C]["isStringType"]) {
                        type = "String";
                        //use unformatted value that
                        cellVals.value = cellVals.formattedVal || cellVals.value;
                    }

                    //map to excel table values
                    const cell_ref = XLSX.utils.encode_cell({ c: C, r: R });

                    //use XLSX.SSF.get_table() to ccheck all formats and their indexes
                    //XLSX.SSF.format(formatindex, value) in some cases show output val
                    switch (type) {
                        case "Number":
                            if (format) {
                                cell.t = "n";
                                cell.z = format;
                            } else {
                                cell.t = "s";
                            }
                            break;
                        case "Boolean":
                            cell.t = "b"; //must be Boolean and not string
                            break;
                        case "Date":
                            cell.t = "d";
                            cell.z = format;
                            break;
                        case "DateTime":
                            cell.t = "d";
                            cell.z = format;
                            break;
                        //String and Time handled here
                        default:
                            cell.t = "s";
                            break;
                    }

                    cell.s = { alignment: { horizontal: "top" } };
                    cell.v = cellVals.value;
                    ws[cell_ref] = cell;
                }
            }
            if (range.s.c < 10000000) ws["!ref"] = XLSX.utils.encode_range(range);
            return ws;
        }

        // Create array buffer for download
        private static s2ab(s) {
            const buf = new ArrayBuffer(s.length);
            const view = new Uint8Array(buf);
            for (let i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
            return buf;
        }
    }
}
