/// <reference path="Util.ts" />
/// <reference path="Symbol.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class QString {
        static TYPE_NUM = 10;

        static u8u16(u16: string): Array<number> {
            const u8 = [];
            for (let i = 0; i < u16.length; i++) {
                let c = u16.charCodeAt(i);
                if (c < 0x80) u8.push(c);
                else if (c < 0x800) u8.push(0xc0 | (c >> 6), 0x80 | (c & 0x3f));
                else if (c < 0xd800 || c >= 0xe000)
                    u8.push(0xe0 | (c >> 12), 0x80 | ((c >> 6) & 0x3f), 0x80 | (c & 0x3f));
                else {
                    c = 0x10000 + (((c & 0x3ff) << 10) | (u16.charCodeAt(++i) & 0x3ff));
                    u8.push(
                        0xf0 | (c >> 18),
                        0x80 | ((c >> 12) & 0x3f),
                        0x80 | ((c >> 6) & 0x3f),
                        0x80 | (c & 0x3f),
                    );
                }
            }
            return u8;
        }

        static listToIPC(values: Array<string>): Uint8Array {
            return List.toIPC(values.map(v => QString.toIPC(v)));
        }

        static toIPC(value: string): Uint8Array {
            const valEn = value ? QString.u8u16(value) : [];
            const size = valEn.length + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(QString.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(valEn.length); // vector size
            _.each(valEn, v => buffer.wb(v)); // values

            return buffer.data;
        }
    }
}
