/*global $, T, Backbone, FormSchemas, BBForm, DialogBase*/
/// <reference path="../templates/combined.ts" />
/// <reference path="DialogBase.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    interface AreYouSureDialogOptions extends DialogBaseOptions {
        noSaveCb: Function;
        saveCb: Function;
        components?: Array<any>;
        message: string;
        dashChanged: boolean;
    }

    export class AreYouSureDialog extends DialogBase {
        $errorInfo: JQuery;
        $errorList: JQuery;
        $firstBtn: JQuery;
        closeArgs: any;

        protected options: AreYouSureDialogOptions;

        constructor(options) {
            super(
                _.extend(
                    {
                        iconClass: "fa-asterisk",
                        dialogTitle: "Save Changes",
                        minHeight: 110,
                        width: 300,
                    },
                    options,
                    {
                        userClass:
                            "areyousure-dialog" +
                            (options.userClass ? " " + options.userClass : ""),
                    },
                ),
            );
        }

        dialogButtons() {
            return [
                {
                    text: "Save",
                    click: _.bind(this.onClickSave, this),
                    icons: {
                        primary: "fa fa-floppy-o",
                    },
                },
                {
                    text: "Don't Save",
                    click: _.bind(this.onClickDontSave, this),
                    icons: {
                        primary: "fa fa-ban",
                    },
                },
                {
                    text: "Cancel",
                    click: _.bind(this.onClickCancel, this),
                    icons: {
                        primary: "fa fa-times",
                    },
                },
            ];
        }

        doSave() {
            const deferred = $.Deferred();

            // TODO: gk don't like this here
            (this.model as any).saveNow(deferred.reject, deferred.resolve);

            return deferred.promise();
        }

        onKeyDown(e) {
            e.stopPropagation();

            switch (e["keyCode"]) {
                case $.ui.keyCode.ESCAPE:
                    this.onClickCancel();
                    break;
                case 78: // n
                    this.onClickDontSave();
                    break;
                case 83: // s
                case 89: // y
                    this.onClickSave();
                    break;
            }
        }

        onClickCancel() {
            // don't leave this dashboard
            this.isCancel = true;
            this.closeArgs = "cancel";
            this.dialogClose();
        }

        onClickDontSave() {
            if (this.options && this.options.noSaveCb) {
                this.options.noSaveCb();
            }
            this.dialogClose();
        }

        onClickSave() {
            // Use callback if present else use promiss based doSave()
            if (this.options && this.options.saveCb) {
                this.options.saveCb();

                this.dialogClose();
            } else {
                // block ui
                this.$firstBtn.button("disable");
                this.$firstBtn.next().button("disable");
                this.$firstBtn
                    .next()
                    .next()
                    .button("disable");
                this.doSave()
                    .done(_.bind(this.onSaveDone, this))
                    .fail(_.bind(this.onSaveFail, this));
            }
        }

        onSaveDone() {
            const errors = [];

            if (_.isEmpty(this.options.components)) {
                // close
                this.dialogClose();
            } else {
                // navigate to the dash only after each widget has completed its callback
                const afterSave = _.after(this.options.components.length, () => {
                    if (_.isEmpty(errors)) {
                        this.dialogClose();
                    } else {
                        // show errors
                        this.$errorInfo.show();
                        this.$errorList.empty();

                        _.each(errors, error => {
                            const $li = $("<li />"),
                                $span = $("<span />");

                            $span.attr("title", error.widget);
                            $span.text(error.component);
                            $li.text(": " + error.message);

                            $li.prepend($span);
                            this.$errorList.append($li);
                        });

                        // unlock ui
                        this.$firstBtn.button("enable");
                        this.$firstBtn.next().button("enable");
                        this.$firstBtn
                            .next()
                            .next()
                            .button("enable");
                    }
                });

                _.each(this.options.components, callbackInfo => {
                    if (_.isFunction(callbackInfo.callback)) {
                        callbackInfo.callback(error => {
                            if (error) {
                                errors.push(
                                    _.extend(
                                        {
                                            message: error,
                                        },
                                        _.pick(callbackInfo, ["component", "widget"]),
                                    ),
                                );
                            }

                            afterSave();
                        });
                    }
                });
            }
        }

        onSaveFail() {
            // show error
            this.$errorInfo.show();
            // unlock ui
            this.$firstBtn.button("enable");
            this.$firstBtn.next().button("enable");
            this.$firstBtn
                .next()
                .next()
                .button("enable");
        }

        getPromptMessage() {
            const message = this.options.message || "There are unsaved changes in this dashboard.";
            return message;
        }

        postRender() {
            $(window).on("keydown.AreYouSureDialog", _.bind(this.onKeyDown, this));
            // Error info elements
            this.$errorInfo = this.$(".error-info");
            this.$errorList = this.$(".error-list");
            this.$firstBtn = this.$dialog
                .next()
                .find("button")
                .first();
            this.$errorInfo.hide();
        }

        renderTemplate() {
            return T.AreYouSureDialog({
                dashChanged: this.options.dashChanged, // if changed display changes
                components: this.options.components, //each component that changed
                message: this.getPromptMessage(),
            });
        }

        remove() {
            $(window).off(".AreYouSureDialog");
            this.trigger("close", this.closeArgs);
            return DialogBase.prototype.remove.apply(this);
        }
    }
}
