/*global _,Backbone */

var removeObsoleteWidgets = function (appKey, objectWithWidgets) {
    //fix: https://issuetracker.firstderivatives.com/browse/KXAX-11462
    // remove obsolete widgets (previously undeleted during copy/paste or drag&drop)
    if ((appKey === "Accordion" || appKey === "Popup") &&
        objectWithWidgets && objectWithWidgets.widgets && objectWithWidgets.widgets.length > 1 &&
        !(objectWithWidgets.widgets instanceof Backbone.Collection)
    ) {
        //change array in place
        if (appKey === "Popup") {
            //use last valid widget (component is not null)
            var lastValidWidget = _.findLast(objectWithWidgets.widgets, function (w) {
                return w.component;
            });

            //clear/empty array (faster than array.splice(0))
            objectWithWidgets.widgets.length = 0;
            if (lastValidWidget) {
                objectWithWidgets.widgets.push(lastValidWidget);
            }

        } else {
            //accordion: remove duplicated sections
            var usedSectionIds = {},
                sectionId;

            for (var i = objectWithWidgets.widgets.length - 1; i >= 0; i--) {
                sectionId = objectWithWidgets.widgets[i].sectionId;
                if (usedSectionIds[sectionId]) {
                    objectWithWidgets.widgets.splice(i, 1);
                } else {
                    usedSectionIds[sectionId] = true;
                }
            }
        }
    }
};