/*global $,_,Backbone,console,require,T,DocumentDataModel*/
/// <reference path="../lib/backbone-deep-model.ts" />
/// <reference path="../classes/commonDefinitions.ts" />
/// <reference path="../classes/ComponentApi.ts"/>
/// <reference path="../classes/log.ts"/>
/// <reference path="../models/documentDataModel.ts" />
/// <reference path="../templates/combined.ts" />
/// <reference path="../views/AppView.ts" />
/// <reference path="../lib/i18n.d.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ComponentView extends AppView {
        private static _instances: Array<ComponentView> = [];

        COMPONENT_SAVE_TIMEOUT = 2000;
        QUERY_STATUS_DELAY = 200;
        QUERY_STATUS_FADE = 3000;

        $appDiv: JQuery;
        $borderDiv: JQuery;
        $customCss: JQuery;
        $errorMessage: JQuery;
        $progressBarDiv: JQuery;
        $queryStatus: JQuery;
        $titleDiv: JQuery;

        app;
        cssClasses;
        customCss;
        debouncedOnAlignmentChanged;
        fadeTimer: number;
        queryStatusTimer;
        loadingTimer;
        showLoadingIndicators;
        showNoResultsIndicators = false;
        queryStatusRemoveTimer: number;
        customErrorClass = "";

        constructor(options) {
            super(options);
            this.model.set("upgrading", true, { silent: true });
            ComponentView._instances.push(this);
        }

        static getInstances(): ComponentView[] {
            return ComponentView._instances;
        }

        initalizeEvents() {
            const doc = this.getContainerDocument();

            AppView.prototype.initalizeEvents.apply(this, arguments);

            this.listenTo(this.model, "beforeRemove", this.beforeRemove);

            this.listenTo(this.dashboardViewModel, "change:buildMode", this.onBuildModeChanged);

            // api listeners
            this.listenTo(this.componentApi, "hideErrorMessage", this.hideErrorMessage);
            this.listenTo(this.componentApi, "hideQueryStatus", this.hideQueryStatus);
            this.listenTo(this.componentApi, "showErrorMessage", this.showErrorMessage);
            this.listenTo(this.componentApi, "showQueryStatus", this.showQueryStatus);

            // border and inner spacing changes
            this.listenTo(doc, "change:borderBackground", (m, v) =>
                this.setOptionsValue("format", { tileBackgroundColor: "#" + v }),
            );

            this.listenTo(doc, "change:transparentBackground", (m, v) =>
                this.setOptionsValue("format", { tileTransparentBackground: v }),
            );

            this.listenTo(doc, "change:borderColor", (m, v) =>
                this.setOptionsValue("format", { tileBorderColor: "#" + v }),
            );

            this.listenTo(doc, "change:borderRounding", (m, v) =>
                this.setOptionsValue("format", { tileBorderRounding: v }),
            );

            this.listenTo(doc, "change:borderSpacing", (m, v) =>
                this.setOptionsValue("Alignment", {
                    innerPaddingLeft: v,
                    innerPaddingRight: v,
                    innerPaddingTop: v,
                    innerPaddingBottom: v,
                    titlePaddingLeft: v,
                    titlePaddingRight: v,
                    titlePaddingTop: v,
                }),
            );

            this.listenTo(doc, "change:borderWidth", (m, v) =>
                this.setOptionsValue("format", { tileBorderWidth: v }),
            );

            this.listenTo(doc, "change:widgetsSpacing", (m, v) =>
                this.setOptionsValue("Alignment", {
                    paddingLeft: v,
                    paddingRight: v,
                    paddingTop: v,
                    paddingBottom: v,
                }),
            );

            this.listenTo(doc, "change:borderShadow", (m, v) =>
                this.setOptionsValue("format", { tileShadow: v }),
            );
            this.listenTo(doc, "change:showLoadingIndicators", this.onShowLoadingIndicatorsChanged);

            // on format / alignment changes (widget styling)
            this.listenTo(this.model.get("options"), "change:format", this.onFormatChanged);

            this.debouncedOnAlignmentChanged = _.debounce(this.onAlignmentChanged, 240);
            this.listenTo(
                this.model.get("options"),
                "change:Alignment",
                this.debouncedOnAlignmentChanged,
            );
        }

        applyCustomCss(newCustomCss) {
            if (newCustomCss) {
                if (!this.$customCss || !this.$customCss.length) {
                    this.$customCss = $("<style />", {
                        "data-widgetid": this.widgetId,
                        html: newCustomCss,
                        type: "text/css",
                    }).appendTo(document.head);
                } else {
                    this.$customCss.html(newCustomCss);
                }
                this.customCss = newCustomCss;
            } else {
                if (this.$customCss) {
                    this.$customCss.remove();
                    this.$customCss = null;
                    this.customCss = null;
                }
            }
        }

        applyCssClasses(newCssClasses) {
            this.$appDiv.removeClass(this.cssClasses);

            if (newCssClasses) {
                if (_.isString(newCssClasses)) {
                    this.cssClasses = newCssClasses;
                } else {
                    this.cssClasses = _.reduce(newCssClasses, function(classList, value) {
                        return classList + " " + value;
                    });
                }
                this.$appDiv.addClass(this.cssClasses);
            } else {
                this.cssClasses = null;
            }
        }

        applyDashboardStyleSettings(): void {
            const selectedDocumentId = this.dashboardViewModel.get("selectedDocumentId");
            const doc = this.dashboardAppModel.get("documents").get(selectedDocumentId);
            const format = this.model.get("options").get("format");
            const align = this.model.get("options").get("Alignment");

            // update format
            if (format["tileBackgroundColor"] === null) {
                format["tileBackgroundColor"] = "#" + doc.get("borderBackground");
            }
            if (format["tileBorderColor"] === null) {
                format["tileBorderColor"] = "#" + doc.get("borderColor");
            }
            if (format["tileBorderRounding"] === null) {
                format["tileBorderRounding"] = doc.get("borderRounding");
            }
            if (format["tileBorderWidth"] === null) {
                format["tileBorderWidth"] = doc.get("borderWidth");
            }
            if (format["tileTransparentBackground"] === null) {
                format["tileTransparentBackground"] = doc.get("transparentBackground");
            }
            if (format["tileShadow"] === null) {
                format["tileShadow"] = doc.get("borderShadow");
            }
            // set format
            this.model.get("options").set("format", format);

            // update align
            if (align["innerPaddingLeft"] === null) {
                align["innerPaddingLeft"] = doc.get("borderSpacing");
            }
            if (align["innerPaddingRight"] === null) {
                align["innerPaddingRight"] = doc.get("borderSpacing");
            }
            if (align["innerPaddingTop"] === null) {
                align["innerPaddingTop"] = doc.get("borderSpacing");
            }
            if (align["innerPaddingBottom"] === null) {
                align["innerPaddingBottom"] = doc.get("borderSpacing");
            }
            if (align["titlePaddingLeft"] === null) {
                align["titlePaddingLeft"] = doc.get("borderSpacing");
            }
            if (align["titlePaddingRight"] === null) {
                align["titlePaddingRight"] = doc.get("borderSpacing");
            }
            if (align["titlePaddingTop"] === null) {
                align["titlePaddingTop"] = doc.get("borderSpacing");
            }
            if (align["titlePaddingBottom"] === null) {
                align["titlePaddingBottom"] = 0;
            }
            if (align["paddingLeft"] === null) {
                align["paddingLeft"] = doc.get("widgetsSpacing");
            }
            if (align["paddingRight"] === null) {
                align["paddingRight"] = doc.get("widgetsSpacing");
            }
            if (align["paddingTop"] === null) {
                align["paddingTop"] = doc.get("widgetsSpacing");
            }
            if (align["paddingBottom"] === null) {
                align["paddingBottom"] = doc.get("widgetsSpacing");
            }
            // set align
            this.model.get("options").set("Alignment", align);
        }

        beforeRemove() {
            let self = this,
                appSaveCallback,
                appSaveMessage,
                beforeRemove;

            if (this.app && _.isFunction(this.app.beforeRemove)) {
                // if a component requires a check before being removed it should implement beforeRemove.
                // returning a function/object from beforeRemove means that changes are pending and an AreYouSureDialog will be shown.
                // the function will be called if the user decides to save and should then call the callback passed to it when complete.
                // returning a falsey value means that no save is pending and the remove can continue as normal.
                beforeRemove = this.app.beforeRemove();

                // beforeRemove could be a simple function or an object containing the function and a message to display in AreYouSureDialog, e.g.
                // {
                //   callback(callback) {
                //      doSave().then(function () {
                //          // if error string is passed to callback it will be displayed and the save dialog will remain open
                //          callback(error);
                //      });
                //   }
                //   message: 'Changes need to be saved'
                // }

                if (beforeRemove) {
                    if (_.isFunction(beforeRemove)) {
                        appSaveCallback = beforeRemove;
                    } else if (_.isFunction(beforeRemove.callback)) {
                        appSaveCallback = beforeRemove.callback;
                        appSaveMessage = beforeRemove.message;
                    }
                }

                if (appSaveCallback) {
                    return {
                        // this wrapper callback will be be fired by AreYouSureDialog
                        callback: saveCompleteCallback => {
                            // ensure we only call callback to indicate component is finished once
                            const complete = _.once(saveCompleteCallback),
                                timer = _.delay(function() {
                                    // put a time limit on the callback's execution
                                    complete("Timed out");
                                }, self.COMPONENT_SAVE_TIMEOUT);

                            try {
                                // call the save function provided by the component
                                appSaveCallback(function(error) {
                                    // this is called by the component when it has finished its save
                                    clearTimeout(timer);
                                    complete(error);
                                });
                            } catch (error) {
                                // return error to callback
                                Log.Error("Save error", error);
                                clearTimeout(timer);
                                complete("Save error");
                            }
                        },
                        component: self.model.get("key"), // component name
                        message: appSaveMessage, // message to show in AreYouSureDialog
                        widget: self.model.id, // widget id
                    };
                }
            }
        }

        getComponentName() {
            const componentDefinition =
                this.dashboardAppModel.get("components").get(this.model.get("definitionId")) ||
                this.dashboardAppModel.get("components").find({
                    appKey: this.model.get("key"),
                });

            if (componentDefinition) {
                return componentDefinition.get("componentName");
            }

            return "";
        }

        getContainerDocument() {
            const selectedDocumentId = this.dashboardViewModel.get("selectedDocumentId");
            return this.dashboardAppModel.get("documents").get(selectedDocumentId);
        }

        hideErrorMessage() {
            this.$errorMessage.hide();
        }

        hideQueryStatus() {
            clearTimeout(this.queryStatusTimer);
            clearInterval(this.loadingTimer);
            this.queryStatusRemoveTimer = _.delay(() => {
                this.$queryStatus.hide();
            }, 200);
        }

        onAlignmentChanged(dontResize?) {
            let self = this,
                alignment = this.model.get("options").get("Alignment"),
                appDivCss,
                borderCss,
                getAlignmentValue,
                titleCss,
                titleOuterHeight;

            getAlignmentValue = function(key) {
                return Number(
                    self.componentApi.getPropertyValue(alignment[key], "Alignment." + key),
                );
            };

            borderCss = {
                "margin-left": getAlignmentValue("paddingLeft"),
                "margin-right": getAlignmentValue("paddingRight"),
                "margin-top": getAlignmentValue("paddingTop"),
                "margin-bottom": getAlignmentValue("paddingBottom"),
            };

            this.$borderDiv.css(borderCss);

            // first set title
            titleCss = {
                "margin-left": getAlignmentValue("titlePaddingLeft"),
                "margin-right": getAlignmentValue("titlePaddingRight"),
                "padding-top": getAlignmentValue("titlePaddingTop"),
                "padding-bottom": getAlignmentValue("titlePaddingBottom"),
            };

            this.$titleDiv.css(titleCss);

            // then set app div
            if (self.$titleDiv.is(":visible")) {
                titleOuterHeight =
                    self.$titleDiv.outerHeight(true) + getAlignmentValue("innerPaddingTop");
            } else {
                titleOuterHeight = getAlignmentValue("innerPaddingTop");
            }

            appDivCss = {
                left: getAlignmentValue("innerPaddingLeft"),
                right: getAlignmentValue("innerPaddingRight"),
                top: titleOuterHeight,
                bottom: getAlignmentValue("innerPaddingBottom"),
            };

            self.$appDiv.css(appDivCss);

            if (!dontResize) {
                self.onResize();
            }
        }

        onBuildModeChanged(model, value, options) {
            if (this.dashboardViewModel.get("buildMode")) {
                this.$el.removeClass("build-off");
            } else {
                this.$el.addClass("build-off");
            }
        }

        onFormatChanged(dontResize?) {
            let align,
                borderCss,
                format = this.model.get("options").get("format"),
                getFormatValue,
                titleCss,
                widgetTitle,
                self = this;

            //console.log("on format changed", model, value);

            getFormatValue = function(key) {
                return self.componentApi.getPropertyValue(format[key], "Format." + key);
            };

            borderCss = {
                "border-width": getFormatValue("tileBorderWidth"),
                "border-radius": getFormatValue("tileBorderRounding"),
                "border-color": getFormatValue("tileBorderColor"),
                "background-color": getFormatValue("tileTransparentBackground")
                    ? "transparent"
                    : getFormatValue("tileBackgroundColor"),
                "box-shadow": getFormatValue("tileShadow")
                    ? "rgba(0, 0, 0, 0.78) 2px 2px 5px"
                    : "none",
            };

            // add css border
            this.$borderDiv.css(borderCss);

            titleCss = {
                "font-size": getFormatValue("titleFontSize"),
                "font-weight": getFormatValue("titleFontBold") ? "bold" : "normal",
                "text-align": getFormatValue("titleHorizontal"),
                color: getFormatValue("titleColor"),
                "background-color": getFormatValue("titleTransparentBackground")
                    ? "transparent"
                    : getFormatValue("titleBackgroundColor"),
                "border-bottom-width": getFormatValue("titleBottomBorderWidth"),
                "text-shadow": getFormatValue("titleShadow")
                    ? "rgba(0, 0, 0, 0.5) 2px 2px 3px"
                    : "none",
            };

            //set titleDiv to have a border radius to prevent background overflow
            let titleRadius = 0;
            if (getFormatValue("tileBorderRounding") > 0) {
                titleRadius =
                    getFormatValue("tileBorderRounding") * 0.85 -
                    getFormatValue("tileBorderWidth") / 4;
            }
            this.$titleDiv.css("border-top-left-radius", titleRadius);
            this.$titleDiv.css("border-top-right-radius", titleRadius);

            align = self.model.get("options").get("Alignment");
            widgetTitle = getFormatValue("widgetTitle");

            if (widgetTitle) {
                // title
                this.$titleDiv.css(titleCss);
                // set the text
                this.$titleDiv.text(widgetTitle);
                // show
                this.$titleDiv.show();
                // change top
                self.$appDiv.css(
                    "top",
                    align["innerPaddingTop"] + self.$titleDiv.outerHeight(true),
                );
            } else {
                this.$titleDiv.hide();
                // change top
                self.$appDiv.css("top", align["innerPaddingTop"]);
            }

            if (!dontResize) {
                this.onResize();
            }
        }

        onShowLoadingIndicatorsChanged(model, value) {
            this.showLoadingIndicators = value;
        }

        onShowNoResultsIndicatorsChanged(model, value) {
            this.showNoResultsIndicators = value;
        }

        onStyleChanged() {
            const style = this.model.get("options").get("Style");
            const getStyleValue = (key: string): any =>
                this.componentApi.getPropertyValue(style[key], "Style." + key);

            const advancedCss = getStyleValue("advanced");
            if (advancedCss !== this.customCss) {
                this.applyCustomCss(advancedCss);
            }

            const cssClasses = getStyleValue("cssClasses");
            if (cssClasses !== this.cssClasses) {
                this.applyCssClasses(cssClasses);
            }
        }

        postComponentLoad(componentDefinition, customElement?): void {
            // on processed setting change
            this.appOnSettingsChange = (settings): void => {
                const keys = Object.keys(settings);
                this.quickStart(componentDefinition.appArgs.quickStart);

                // process Style changed if keys present
                if (_.find(keys, k => k.indexOf("Style") === 0)) {
                    this.onStyleChanged();
                }

                // process format changed if keys present
                if (_.find(keys, k => k.indexOf("format") === 0)) {
                    this.onFormatChanged();
                }

                // process Alignment changed if keys present
                if (_.find(keys, k => k.indexOf("Alignment") === 0)) {
                    this.onAlignmentChanged();
                }

                if (customElement) customElement.settings = settings;
                else if (this.app && _.isFunction(this.app.onSettingsChange)) {
                    this.app.onSettingsChange(settings);
                }
            };

            // set component initial state
            if (this.appOnSettingsChange && this.model.get("options").toFlat) {
                const settings = this.componentApi.processSettings(
                    this.model.get("options").toFlat(),
                    this.appOnSettingsChange,
                );
                if (customElement) {
                    customElement.settings = settings;
                    this.quickStart(componentDefinition.appArgs.quickStart);
                } else {
                    // invoke component setting change on load
                    if (this.app && _.isFunction(this.app.onSettingsChange)) {
                        this.app.onSettingsChange(settings);
                        this.quickStart(componentDefinition.appArgs.quickStart);
                    }

                    // Set flag if component implements onSettingsChange (used by PropertiesPanel)
                    if (_.isFunction(this.app.onSettingsChange)) {
                        if (this.model.get("key") === "BasicComponents") {
                            // temporary extra check until all BasicComponents implement onSettingsChange
                            if (_.isFunction(this.app.$view.onSettingsChange)) {
                                this.model.set("hasOnSettingsChange", true, {
                                    silent: true,
                                });
                            } else {
                                this.model.unset("hasOnSettingsChange", { silent: true });
                            }
                        } else {
                            this.model.set("hasOnSettingsChange", true, { silent: true });
                        }
                    } else {
                        this.model.unset("hasOnSettingsChange", { silent: true });
                    }
                }
            }

            this.model.trigger("componentLoaded");
        }

        processComponentDefinition(existingComponentDefiniton, componentDefinition): void {
            if (existingComponentDefiniton) {
                existingComponentDefiniton.set(
                    _.omit(
                        componentDefinition,
                        "id",
                        "componentName",
                        "componentDescription",
                        "appKey",
                        "buildModeOnly",
                        "listViewThumb",
                    ),
                );
            }

            // mark components that should only show in build mode
            if (componentDefinition["buildModeOnly"]) {
                this.$el.addClass("build-mode-only");
            }

            // add class .build-off if buildMode is false
            if (!this.dashboardViewModel.get("buildMode")) {
                this.$el.addClass("build-off");
            }

            // new - injecting common definitions (styling)
            this.updateComponentDefinition(componentDefinition, this.model);

            // Set default options if none exist
            if ($.isEmptyObject(this.model.get("options").attributes) && componentDefinition) {
                //self.model.set(componentDefinition);
                this.model.get("options").set(componentDefinition["appArgs"].json);
            } else {
                // not empty -- need to update options
                // update model options
                this.model
                    .get("options")
                    .set(
                        "Alignment",
                        _.extend(
                            {},
                            componentDefinition["appArgs"]["json"]["Alignment"],
                            this.model.get("options").get("Alignment"),
                        ),
                        { silent: true },
                    );

                this.model
                    .get("options")
                    .set(
                        "Style",
                        _.extend(
                            {},
                            componentDefinition["appArgs"]["json"]["Style"],
                            this.model.get("options").get("Style"),
                        ),
                        { silent: true },
                    );

                // update model options
                const formatOptions = _.extend(
                    {},
                    componentDefinition["appArgs"]["json"]["format"],
                    this.model.get("options").get("format"),
                );

                // remove old props
                delete formatOptions["titlePaddingLeft"];
                delete formatOptions["titlePaddingRight"];
                delete formatOptions["titlePaddingTop"];
                delete formatOptions["titlePaddingBottom"];

                // set as options
                this.model.get("options").set({ format: null }, { silent: true });
                this.model.get("options").set({ format: formatOptions }, { silent: true });
            }

            this.componentApi.convertPathsToModels(
                "data",
                componentDefinition["appArgs"].schema,
                this.model.get("options"),
            );
            this.componentApi.convertPathsToModels(
                "viewstate",
                componentDefinition["appArgs"].schema,
                this.model.get("options"),
            );

            Component.convertDashboardTypesToModels(
                this.model.get("options"),
                (path: string) => this.docDataModel.getByPath(path),
                (path: string) => this.docViewModel.getByPath(path),
            );

            // if null then set dashboard setting
            this.applyDashboardStyleSettings();

            this.model.unset("upgrading", { silent: true });

            this.onStyleChanged();
            this.onFormatChanged();
            this.onAlignmentChanged();
        }

        quickStart(config) {
            _.each(config, obj => {
                if (this.settingsModel.get(obj.path) || this.quickView) {
                    this.hideErrorMessage();
                } else {
                    const message = obj.message || "populate widget";

                    this.showErrorMessage({
                        error: t("Click to ") + " <b> " + message + "</b>",
                        type: "QuickStart",
                        onClick: () => {
                            if (this.dashboardViewModel.get("selectedWidgetId") !== this.widgetId) {
                                this.$el.trigger("click");
                            }

                            this.model.get("options").trigger("quickStart", obj.path);
                        },
                    });

                    return false;
                }
            });
        }

        remove() {
            // Remove custom css
            if (this.$customCss) {
                this.$customCss.remove();
            }

            ComponentView._instances = _.without(ComponentView._instances, this);

            AppView.prototype.remove.apply(this, arguments);
        }

        render() {
            const showLoadingIndicators = this.getContainerDocument().get("showLoadingIndicators");

            this.$el.html(T.ComponentView(this.model.toJSON()));
            this.$errorMessage = this.$el.find(".errorMessage");
            this.$queryStatus = this.$el.find(".queryStatus");
            this.$titleDiv = this.$el.find(".title-div");
            this.$borderDiv = this.$el.find(".border-div");

            this.$appDiv = this.$el.find(".app-div");
            if (this.widgetId) {
                this.$customCss = $("head style[data-widgetid='" + this.widgetId + "']");
            }
            this.$progressBarDiv = this.$el.find(".progress-bar-div");

            // show loading if needed
            this.onShowLoadingIndicatorsChanged(null, showLoadingIndicators);

            this.model.set("upgrading", true, { silent: true });

            return this;
        }

        requireApp() {
            const self = this,
                rKey = self.model.get("key");

            let existingComponentDefiniton = self.dashboardAppModel
                .get("components")
                .get(self.model.get("definitionId"));

            if (!existingComponentDefiniton) {
                existingComponentDefiniton = self.dashboardAppModel.get("components").find({
                    appKey: self.model.get("key"),
                });
            }

            const customElementRef = existingComponentDefiniton.get("customElement");
            if (customElementRef && customElements.get(customElementRef)) {
                // create & append custom element
                const customElement = document.createElement(customElementRef);
                self.$appDiv.append(customElement);

                customElement.addEventListener("componentDefinition", val => {
                    const componentDefinition = val.detail;

                    // update api ref
                    customElement.componentApi = this.componentApi.interface;

                    // load component
                    this.processComponentDefinition(
                        existingComponentDefiniton,
                        componentDefinition,
                    );

                    this.initalizeEvents();

                    // initially set theme
                    customElement.theme = this.dashboardViewModel.get("DashboardTheme");

                    // add listener for theme changes
                    this.listenTo(self.dashboardViewModel, "change:DashboardTheme", () => {
                        customElement.theme = this.dashboardViewModel.get("DashboardTheme");
                    });

                    // add resize listeners for build mode change
                    this.listenTo(
                        self.dashboardViewModel,
                        "change:buildMode",
                        () => (customElement.resize = ""),
                    );

                    // settings change setup
                    this.postComponentLoad(componentDefinition, customElement);
                });

                _.delay(() => {
                    customElement.options = {
                        api: self.componentApi.interface,
                        componentId: parseInt(self.model.get("definitionId"), 10),
                        componentModel: self.model,
                        dashboardAppModel: self.dashboardAppModel,
                        dashboardViewModel: self.dashboardViewModel,
                        dataModel: self.docDataModel,
                        settingsModel: self.model.get("options"),
                        viewModel: self.docViewModel,
                        websiteUrl: self.dashboardAppModel.get("websiteUrl"),
                    };

                    // hide loading
                    this.$progressBarDiv.fadeOut(0, function() {
                        $(this).remove();
                    });
                }, 1000);
            } else {
                requirejs(
                    [rKey],
                    function(AppModule) {
                        try {
                            // hide loading
                            self.$progressBarDiv.fadeOut(0, function() {
                                $(this).remove();
                            });

                            //take the saved value and merge with default component settings
                            if (AppModule.mergeWithDefault) {
                                self.model.set(
                                    "options",
                                    AppModule.mergeWithDefault(self.model.get("options")),
                                    { silent: true },
                                );
                            }
                            if (AppModule.getDefaultJSONTemplates) {
                                self.model.get(
                                    "options",
                                )._templates = AppModule.getDefaultJSONTemplates();
                                //merge Global JSON Props
                                self.model.get("options")._templates.Defaults = _.extend(
                                    {},
                                    self.model.get("options")._templates.Defaults,
                                );
                            }

                            // Get component definition from AppView
                            const componentDefinition = _.isFunction(
                                AppModule.getComponentDefinition,
                            )
                                ? AppModule.getComponentDefinition({
                                      api: self.componentApi.interface,
                                      componentId: parseInt(self.model.get("definitionId"), 10),
                                      componentModel: self.model,
                                      dashboardAppModel: self.dashboardAppModel,
                                      dashboardViewModel: self.dashboardViewModel,
                                      dataModel: self.docDataModel,
                                      settingsModel: self.model.get("options"),
                                      viewModel: self.docViewModel,
                                      websiteUrl: self.dashboardAppModel.get("websiteUrl"),
                                  })
                                : _.cloneDeep({
                                      appArgs: {
                                          json: {},
                                          schema: {
                                              properties: {},
                                              type: "object",
                                          },
                                      },
                                  });

                            // load component
                            self.processComponentDefinition(
                                existingComponentDefiniton,
                                componentDefinition,
                            );

                            self.app = new AppModule({
                                el: self.$appDiv[0],
                                viewModel: self.docViewModel,
                                dataModel: self.docDataModel,
                                dashboardAppModel: self.dashboardAppModel,
                                dashboardViewModel: self.dashboardViewModel,
                                settingsModel: self.model.get("options"),
                                websiteUrl: self.dashboardAppModel.get("websiteUrl"),
                                componentModel: self.model,
                                DeltaClientLib: self.deltaClient,
                                api: self.componentApi.interface,
                                widgetId: self.widgetId,
                                quickView: self.quickView,
                            });

                            self.initalizeEvents();

                            // settings change setup
                            self.postComponentLoad(componentDefinition);

                            if (_.isFunction(self.callback)) {
                                self.callback(self.app);
                            }
                        } catch (error) {
                            Log.Error(
                                "loading app error, require key: " + self.model.get("key"),
                                error,
                            );
                            if (_.isFunction(self.callback)) {
                                self.callback(error);
                            }
                        }
                    },
                    function(err) {
                        Log.Error("ComponentView:loadApp require error", err);
                        if (_.isFunction(self.callback)) {
                            self.callback(err);
                        }
                    },
                );
            }
        }

        showErrorMessage(errorObj) {
            const errorType = errorObj.type || "Error";
            let errorText = errorObj.error;
            const $errorText = this.$errorMessage.find(".errorText");

            //Remove flattened array indices if present in the string.
            if (errorText.indexOf("0.0") === 0) {
                errorText = errorText.substring(4);
            }

            this.$errorMessage.removeClass("Error Warning Info QuickStart").addClass(errorType);

            this.$errorMessage.find(".errorTitle span").text(t(errorType));
            $errorText.html(errorText);
            this.$errorMessage.show();

            // clear any previously set faders
            if (this.fadeTimer) {
                clearTimeout(this.fadeTimer);
            }

            this.$errorMessage.off("click");

            if (errorObj.onClick) {
                this.$errorMessage.click(e => {
                    // restrict to clicks within the panel
                    const $target = $(e.target);
                    if ($target.hasClass("errorPanel") || $target.parents(".errorPanel").length) {
                        e.stopPropagation();
                        errorObj.onClick(e);
                    }
                });
            }

            // set a fader?
            if (errorType === "Info") {
                this.fadeTimer = _.delay(() => this.$errorMessage.fadeOut("slow"), 3000);
            }
        }

        showQueryStatus(statusObj) {
            let self = this,
                statusText,
                statusType,
                statusTypeDisplay;

            statusText = statusObj.message || statusObj.error || "";
            statusType = statusObj.type || "Error";

            // remove previously applied error class
            if (self.customErrorClass) {
                self.$queryStatus.removeClass(self.customErrorClass);
                self.customErrorClass = "";
            }

            clearInterval(self.loadingTimer);
            if (statusType === "Loading") {
                let startTime = moment(),
                    duration;

                if (self.showLoadingIndicators) {
                    statusText = "0s";
                    self.loadingTimer = setInterval(function() {
                        duration = moment.duration(moment().diff(startTime));
                        statusText = duration.format("m[m] s[s]");
                        self.$queryStatus.find(".statusText").html(statusText);
                    }, 1000);
                } else return;
            }

            switch (statusType) {
                case "NoResults":
                    statusTypeDisplay = "No Results";
                    break;
                default:
                    statusTypeDisplay = statusType;
            }

            //Remove flattened array indices if present in the string.
            if (statusText.indexOf("0.0") === 0) {
                statusText = statusText.substring(4);
            }

            self.$queryStatus.css("margin", self.$borderDiv.css("border-width"));
            self.$queryStatus.css("border-radius", self.$borderDiv.css("border-radius"));

            self.$queryStatus
                .removeClass("Error Warning Info Loading NoResults")
                .addClass(statusType);

            self.$queryStatus
                .find(".statusTitle span")
                .text(t(statusTypeDisplay) + (statusText && statusType !== "Loading" ? ": " : ""));

            let rgxMatch;
            // first matched group is empty in standalone mode
            // with Delta, the error message starts with "0: Database exception: "
            if (
                statusType === "Error" &&
                (rgxMatch = /(.*)cssClass:"(.*)":(.*)/m.exec(statusText))
            ) {
                self.customErrorClass = rgxMatch[2];
                self.$queryStatus.addClass(self.customErrorClass);
                statusText = rgxMatch[3];
            }

            self.$queryStatus.find(".statusText").html(statusText);

            clearTimeout(self.queryStatusTimer);
            clearTimeout(self.queryStatusRemoveTimer);
            self.queryStatusTimer = _.delay(function() {
                self.$queryStatus.show();
            }, self.QUERY_STATUS_DELAY);

            // clear any previously set faders
            if (self.fadeTimer) {
                clearTimeout(self.fadeTimer);
            }

            // set a fader?
            if (statusType === "Info") {
                self.fadeTimer = _.delay(function() {
                    self.$queryStatus.fadeOut("slow");
                }, self.QUERY_STATUS_FADE);
            }
        }

        updateComponentDefinition(componentDefinition, model) {
            let commonDefinition = getCommonDefinition(),
                alignment = componentDefinition["appArgs"]["schema"]["properties"]["Alignment"],
                format = componentDefinition["appArgs"]["schema"]["properties"]["format"],
                style = componentDefinition["appArgs"]["schema"]["properties"]["Style"],
                json = componentDefinition["appArgs"]["json"]["Style"],
                formatOptions;

            // 1. Style
            // #schema
            if (style) {
                // fix
                _.each(
                    commonDefinition["appArgs"]["schema"]["properties"]["Style"]["properties"],
                    function(value, key) {
                        // fix or inject schema property
                        style["properties"][key] = value;
                    },
                );
            } else {
                // inject whole object
                componentDefinition["appArgs"]["schema"]["properties"]["Style"] =
                    commonDefinition["appArgs"]["schema"]["properties"]["Style"];
            }

            if (json) {
                componentDefinition["appArgs"]["json"]["Style"] = _.extend(
                    {},
                    commonDefinition["appArgs"]["json"]["Style"],
                    json,
                );
            } else {
                // inject whole object
                componentDefinition["appArgs"]["json"]["Style"] = _.clone(
                    commonDefinition["appArgs"]["json"]["Style"],
                );
            }

            // 2. Alignment
            // #schema - no need to clone
            if (alignment) {
                // fix
                _.each(
                    commonDefinition["appArgs"]["schema"]["properties"]["Alignment"]["properties"],
                    function(value, key) {
                        // fix or inject schema property
                        alignment["properties"][key] = value;
                    },
                );
            } else {
                // inject whole object
                componentDefinition["appArgs"]["schema"]["properties"]["Alignment"] =
                    commonDefinition["appArgs"]["schema"]["properties"]["Alignment"];
            }

            json = componentDefinition["appArgs"]["json"]["Alignment"];
            // #json - clone
            if (json) {
                componentDefinition["appArgs"]["json"]["Alignment"] = _.extend(
                    {},
                    commonDefinition["appArgs"]["json"]["Alignment"],
                    json,
                );
            } else {
                // inject whole object
                componentDefinition["appArgs"]["json"]["Alignment"] = _.clone(
                    commonDefinition["appArgs"]["json"]["Alignment"],
                );
            }

            // 3. Format
            // #schema
            if (format) {
                // fix
                _.each(
                    commonDefinition["appArgs"]["schema"]["properties"]["format"]["properties"],
                    function(value, key) {
                        // fix or inject schema property
                        format["properties"][key] = value;
                    },
                );

                // remove 4 schema properites
                delete format["properties"]["titlePaddingLeft"];
                delete format["properties"]["titlePaddingRight"];
                delete format["properties"]["titlePaddingTop"];
                delete format["properties"]["titlePaddingBottom"];
            } else {
                // inject whole object
                componentDefinition["appArgs"]["schema"]["properties"]["format"] =
                    commonDefinition["appArgs"]["schema"]["properties"]["format"];
            }

            // #json
            json = componentDefinition["appArgs"]["json"]["format"];
            if (json) {
                componentDefinition["appArgs"]["json"]["format"] = _.extend(
                    {},
                    commonDefinition["appArgs"]["json"]["format"],
                    json,
                );

                // remove 4 schema properites
                delete componentDefinition["appArgs"]["json"]["format"]["titlePaddingLeft"];
                delete componentDefinition["appArgs"]["json"]["format"]["titlePaddingRight"];
                delete componentDefinition["appArgs"]["json"]["format"]["titlePaddingTop"];
                delete componentDefinition["appArgs"]["json"]["format"]["titlePaddingBottom"];
            } else {
                // inject whole object
                componentDefinition["appArgs"]["json"]["format"] = _.clone(
                    commonDefinition["appArgs"]["json"]["format"],
                );
            }
        }

        private setOptionsValue(prop: string, value: any): void {
            // make change to component settings ('options')
            const existingVal = this.model.get("options").get(prop);

            // set new
            this.model.get("options").set(prop, _.extend({}, existingVal, value));
        }
    }
}
