Backbone.emulateHTTP = true;

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ConnectionGroup extends Backbone.Model {
        public get idAttribute(): string {
            return "name";
        }

        defaults(): object {
            return {
                name: "default",
                type: "kdb",
                connections: [],
                connectionsType: [],
            };
        }
    }

    export class ConnectionGroupCollection extends Backbone.Collection<ConnectionGroup> {
        model = ConnectionGroup;
    }
}
