/// <reference path="./DataNode.ts" />
/// <reference path="./FilterNode.ts" />
/// <reference path="./GraphNode.ts" />
/// <reference path="./GroupByNode.ts" />
/// <reference path="./JoinNode.ts" />
/// <reference path="./ResultNode.ts" />
/// <reference path="./UpdateNode.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    export class App extends Backbone.View<Backbone.Model> {
        public static groupToClass = {
            data: DataNode,
            function: DataNode,
            stream: DataNode,
            filter: FilterNode,
            update: UpdateNode,
            groupBy: GroupByNode,
            join: JoinNode,
            result: ResultNode,
        };
        public static VSRx = /^<%[^<>%]*%>$/i;
        public docDataModel: QuickBase.DocumentDataModel | null = null;
        private bindingParams = [];

        public static calculateReverseEdges(edges: any): any {
            // todo could be shorter
            const edgesR = _.reduce(
                edges,
                (s, v, k) => {
                    _.each(v, vi => {
                        if (s[vi] === undefined) {
                            s[vi] = [k];
                        } else s[vi].push(k);
                    });

                    return s;
                },
                {} as any,
            );

            // sort edgesR values for consistent order
            return _.fromPairs(_.map(edgesR, (v, k) => [k, _.sortBy(v)]));
        }

        public static createNode(node: GraphNode): GraphNode {
            switch (node.group) {
                case "data":
                case "function":
                case "stream":
                    return new DataNode(node);
                case "filter":
                    return new FilterNode(node);
                case "groupBy":
                    return new GroupByNode(node);
                case "join":
                    return new JoinNode(node);
                case "update":
                    return new UpdateNode(node);
                case "result":
                    return new ResultNode(node);
                default:
                    throw "Invalid node " + node;
            }
        }

        public static graphToIPC(nodes: any, edges: any, selected: string): string {
            const filteredNodes = {},
                filteredEdges = {},
                edgesR = this.calculateReverseEdges(edges);

            const getConnectedNodes = (id: string): void => {
                _.extend(filteredNodes, _.pick(nodes, id));
                if (id !== selected) {
                    _.extend(filteredEdges, _.pick(edges, id));
                }
                _.each(edgesR[id], nodeId => {
                    getConnectedNodes(nodeId);
                });
            };
            getConnectedNodes(selected || "result");

            nodes = filteredNodes;
            edges = filteredEdges;

            const qIPC = QuickBase.ipc.Dict.toIPC(
                ["nodes", "edges"],
                QuickBase.ipc.List.toIPC([
                    // nodes
                    QuickBase.ipc.Dict.toIPC(
                        _.keys(nodes),
                        QuickBase.ipc.List.toIPC(
                            _.values(nodes).map(n => this.createNode(n).toIPC()),
                        ),
                    ),
                    // edges
                    QuickBase.ipc.Dict.toIPC(
                        _.keys(edgesR),
                        QuickBase.ipc.List.toIPC(
                            _.values(edgesR).map(v =>
                                QuickBase.ipc.List.toIPC(
                                    _.sortBy(v).map(vi => QuickBase.ipc.Symbol.toIPC(vi)),
                                ),
                            ),
                        ),
                    ),
                ]),
            );

            return QuickBase.ipc.Util.toHexString(QuickBase.ipc.Util.toMessage(qIPC));
        }

        public getIPC(
            graphJSON: string,
            selectedNode: string,
            model: QuickBase.DocumentDataModel,
        ): string {
            let graph = JSON.parse(graphJSON);
            this.docDataModel = model;

            // replace viewstates/bindings
            graph = this.replaceBindingsInGraph(graph);

            return App.graphToIPC(graph.nodes, graph.edges, selectedNode);
        }

        private replaceBindings(val: string): any {
            const key = val.substring(2, val.length - 2);
            const vs = this.docDataModel.getViewState().getByPath(key);
            this.bindingParams.push({ value: val });
            return vs && vs.get("value");
        }

        private replaceBindingsInGraph(graph: any): any {
            this.bindingParams = [];

            _.values(graph.nodes).forEach(n => {
                // update binding values
                [
                    { type: "params", value: "value", children: null },
                    { type: "filter", value: "value", children: "children" },
                    { type: "where", value: "value", children: "children" },
                    { type: "update", value: "val", children: null },
                ].forEach(
                    prop =>
                        n.data &&
                        n.data[prop.type] &&
                        this.replaceAllBindings(n.data[prop.type], prop.value, prop.children),
                );
                // update key values
                ["select", "agg", "groupBy"].forEach(
                    prop => n.data && n.data[prop] && this.replaceKeys(n.data[prop]),
                );
            });

            this.docDataModel.set("_builderParams", this.bindingParams);
            return graph;
        }

        private replaceAllBindings(obj: any, value = "value", children = ""): void {
            if (!_.isEmpty(children)) obj = obj[children];

            _.each(obj, item => {
                if (!item[value] && item["children"]) {
                    this.replaceAllBindings(item["children"]);
                } else if (item[value] !== undefined) {
                    item[value] =
                        typeof item[value] !== "string"
                            ? item[value]
                            : App.VSRx.test(item[value])
                            ? this.replaceBindings(item[value])
                            : item[value];
                }
            });
        }

        private replaceKeys(obj): void {
            let replaceKeys = false;
            _.each(obj, (val, index, obj) => {
                let newIndex;
                if (App.VSRx.exec(index)) {
                    newIndex = index.replace(App.VSRx, this.replaceBindings.bind(this));
                    // replace this key and all that come after to preserve insertion order
                    replaceKeys = true;
                }
                if (replaceKeys) {
                    delete obj[index];
                    obj[newIndex || index] = val;
                }
            });
        }
    }
}
