// 2016.03.18 char vectors and symbols now [de]serialize [from]to utf8
// 2014.03.18 Serialize date now adjusts for timezone.
// 2013.04.29 Dict decodes to map, except for keyed tables.
// 2013.02.13 Keyed tables were not being decoded correctly.
// 2012.06.20 Fix up browser compatibility. Strings starting with ` encode as symbol type.
// 2012.05.15 Provisional test release, subject to change
// for use with websockets and kdb+v3.0, (de)serializing kdb+ ipc formatted data within javascript within a browser.
// e.g. on kdb+ process, set .z.ws:{neg[.z.w] -8!value -9!x;}
// and then within javascript websocket.send(serialize("10+20"));
// ws.onmessage=function(e){var arrayBuffer=e.data;if(arrayBuffer){var v=deserialize(arrayBuffer);...
// note ws.binaryType = 'arraybuffer';

/// <reference path="tools.ts" />
/// <reference path="moment.d.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    var JS_DATE_MAX = 8640000000000000;

    export class rTimestampClass {
        i: number;
        n: number;

        constructor(i: number, n: number) {
            this.i = i;
            this.n = n;
        }

        toDate(): moment.Moment {
            return QuickBase.Tools.convertKDBTemporalToMoment(this) as moment.Moment;
        }

        toString(): string {
            return this.toDate().formatNano("YYYY-MM-DD HH:mm:ss.SSSSSSSSS");
        }
    }

    export class rMonthClass {
        i: number;

        constructor(i: number) {
            this.i = i;
        }

        toDate(): moment.Moment {
            return Tools.convertKDBTemporalToMoment(this) as moment.Moment;
        }

        toString(): string {
            return typeof this.i === "number" && !isNaN(this.i)
                ? this.i === Number.POSITIVE_INFINITY
                    ? "Infinity"
                    : this.i === Number.NEGATIVE_INFINITY
                    ? "-Infinity"
                    : this.toDate().format("YYYY-MM")
                : "";
        }
    }

    export class rDateClass {
        i: number;
        n: number;

        constructor(i: number, n?: number) {
            this.i = i;
            this.n = n;
        }

        toDate(): moment.Moment {
            return Tools.convertKDBTemporalToMoment(this) as moment.Moment;
        }

        toString(): string {
            return typeof this.i === "number" && !isNaN(this.i)
                ? this.i === Number.POSITIVE_INFINITY
                    ? "Infinity"
                    : this.i === Number.NEGATIVE_INFINITY
                    ? "-Infinity"
                    : moment(new Date(this.i))
                          .utcOffset(0)
                          .format("YYYY-MM-DD")
                : "";
        }
    }

    export class rDateTimeClass {
        i: number;

        constructor(i: number) {
            this.i = i;
        }

        toDate(): moment.Moment {
            return Tools.convertKDBTemporalToMoment(this) as moment.Moment;
        }

        toString(): string {
            return this.toDate().format("YYYY-MM-DD HH:mm:ss.SSS");
        }
    }

    export class rTimespanClass {
        i: number;

        constructor(i: number) {
            this.i = i;
        }

        toDate(): moment.Duration {
            return Tools.convertKDBTemporalToMoment(this) as moment.Duration;
        }

        toString(): string {
            return this.toDate().formatNano("HH:mm:ss.SSSSSSSSS", { trim: false });
        }
    }

    export class rMinuteClass {
        i: number;
        n: number;

        constructor(i: number) {
            this.i = i;
            this.n = 0;
        }

        toDate(): moment.Duration {
            return Tools.convertKDBTemporalToMoment(this) as moment.Duration;
        }

        toString(): string {
            return this.toDate().format("HH:mm", { trim: false });
        }
    }

    export class rSecondClass {
        i: number;

        constructor(i: number) {
            this.i = i;
        }

        toDate(): moment.Duration {
            return Tools.convertKDBTemporalToMoment(this) as moment.Duration;
        }

        toString(): string {
            return this.toDate().format("HH:mm:ss", { trim: false });
        }
    }

    export class rTimeClass {
        i: number;

        constructor(i: number) {
            this.i = i;
        }

        toDate(): moment.Duration {
            return Tools.convertKDBTemporalToMoment(this) as moment.Duration;
        }

        toString(): string {
            return this.toDate().format("HH:mm:ss.SSS", { trim: false });
        }
    }

    // annotate clases
    rTimestampClass.prototype["class"] = "1212";
    rMonthClass.prototype["class"] = "1213";
    rDateClass.prototype["class"] = "1214";
    rDateTimeClass.prototype["class"] = "1215";
    rTimespanClass.prototype["class"] = "1216";
    rMinuteClass.prototype["class"] = "1217";
    rSecondClass.prototype["class"] = "1218";
    rTimeClass.prototype["class"] = "1219";
}
