/// <reference path="../cClasses.ts" />
/// <reference path="../long.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export function fromString(qType, val): any {
        return _.isString(val) && qType.fromString
            ? // fromString used for some types (e.g., date, time, etc.)
              qType.fromString(val)
            : val;
    }

    export enum TypeNum {
        List = 0,
        Bool = 1,
        Guid = 2,
        Byte = 4,
        Short = 5,
        Int = 6,
        Long = 7,
        Float = 8,
        Double = 9,
        Char = 10,
        Symbol = 11,
        Timestamp = 12,
        Month = 13,
        Date = 14,
        DateTime = 15,
        Timespan = 16,
        Minute = 17,
        Second = 18,
        Time = 19,
        Table = 98,
        Dict = 99,
        Unary = 101,
    }

    export abstract class TypeBase {
        public length: number;
        public readonly qtype: TypeNum;
        protected offset: number;
        protected dataView: DataView;

        constructor(length: number, offset: number, qtype: number, dataView: DataView) {
            this.length = length;
            this.offset = offset;
            this.qtype = qtype;
            this.dataView = dataView;
        }

        public isAligned(): boolean {
            return true;
        }

        public isTemporal(): boolean {
            return 12 >= this.qtype && this.qtype <= 19;
        }

        public toLegacy(i: number): any {
            return this.getValue(i);
        }

        public toViewState(i): any {
            return {
                _type: Parse.TYPE_NAMES[Math.abs(this.qtype)],
                value: Tools.convertKDBToViewstateValue(this.toLegacy(i)),
            };
        }

        public abstract getValue(i: number): any;

        public abstract mergeIndexed(arg: TypeBase);

        public abstract mergeKeyedPrimary(
            arg: TypeBase,
            maxRows: number,
            insertIndices: Array<number>,
        ): number;

        public abstract mergeKeyed(
            arg: TypeBase,
            indices: Array<number>,
            indexOffset: number,
            maxRows: number,
        );
    }
}
