/*
 * RawDataFormatter suppose to accept raw data, format string, and data type and
 * output formatted string. Also if format is not specified a default format is
 * applied based on provided colKdbType
 * @param rawValue {any} - value can be number, string or kdb temporal object
 * @param format {String} - VALID format string, validated in FileExportInterface
 * @param colKdbType {Number} - kdb type
 * @param isApplyDefaultFormat {Boolean} - should format string to be ignored
 * @param isTypeDate {Boolean} - if value of the date or/and time type
 * @return formattedValue {String} - formatted value
 */ // eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class RawDataFormatter {
        public static applyFormatToValue(value, format, dataType, colKdbType, hideTrailingZeroes) {
            let formattedValue,
                kdbDataTypeClass, //ethier number or date
                mappedKdbType;
            //apply formatter
            mappedKdbType = {
                "1": "Boolean", //boolean
                "2": "String", //guid
                "4": "Number", //byte
                "5": "Number", //short
                "6": "Number", //int
                "7": "Number", //long
                "8": "Number", //real
                "9": "Number", //float
                "10": "String", //char
                "11": "String", //symbol
                "12": "Timestamp", //timestamp ""1990.01.01D20:59:59.000111123""
                "13": "Month", //month  "1990.01"
                "14": "Date", //date "1990.01.01" Excel convertable?
                "15": "Datetime", //datetime "2003.03.23T08:31:53.001"
                "16": "Timespan", //timespan "0D00:00:00.001786123"
                "17": "Minute", //minute    23:59
                "18": "Second", //second    23:59:59
                "19": "Time", //time  09:01:02:042
            }[colKdbType];
            //use kdb type of the value more acurate than field type
            if (colKdbType > 3 && colKdbType < 10) {
                kdbDataTypeClass = "Number";
            } else if (colKdbType > 11 && colKdbType < 20) {
                kdbDataTypeClass = "DateTime";
            } else {
                kdbDataTypeClass = "String";
            }

            // If we pass through TO_STRING then return value.toString
            if (format == "TO_STRING") {
                return value.toString();
            }

            //default format options to be used
            if (!format) {
                formattedValue = RawDataFormatter._applyDefaultFormat(
                    value,
                    dataType,
                    mappedKdbType,
                    hideTrailingZeroes,
                    kdbDataTypeClass,
                );
            } else {
                formattedValue = RawDataFormatter._applyFormat(
                    value,
                    format,
                    dataType,
                    mappedKdbType,
                    hideTrailingZeroes,
                    kdbDataTypeClass,
                );
            }

            return formattedValue;
        }

        /* Apply format and traling zeroes to value
         * @param value {any} - cell raw value
         * @param format {String} - format to apply
         * @param dataType {String} - column type as selected
         * @param mappedKdbType {String} - value kdb type name
         * @param hideTrailingZeroes {Boolen}
         * @param kdbDataTypeClass {String} - classify values based on their kdb types
         * @return formattedValue {String}
         */
        //TODO adopt to allow Temporal conversion in to number if
        private static _applyFormat(
            value,
            format,
            dataType,
            mappedKdbType,
            hideTrailingZeroes,
            kdbDataTypeClass,
        ) {
            let formattedValue = value.toString(),
                decimalPlaces,
                m;

            //In case of format is not resolved properly
            if (format.indexOf("SmartNumber") === 0) {
                mappedKdbType = "Smart Number";
            }
            // apply formatter if not forced to string
            switch (mappedKdbType) {
                case "Smart Number":
                    //case when trying to force kdb Temporals as number
                    if (kdbDataTypeClass === "DateTime") {
                        m = Tools.convertKDBTemporalToMoment(value);
                        value = _.escape(m);
                    }

                    decimalPlaces = format.indexOf(".") !== -1 ? format.split(".")[1].length : 0;

                    formattedValue = Tools.smartFormatNumber(
                        value,
                        decimalPlaces,
                        !hideTrailingZeroes, //showTrailingZeroes
                    );
                    break;
                case "Number":
                    //do not format Infinity values
                    if (!_.isNaN(Number(value)) && _.isFinite(Number(value))) {
                        formattedValue = XLSX.SSF.format(format, Number(value));
                        //hide trailig zeroes
                        if (hideTrailingZeroes) {
                            formattedValue = RawDataFormatter._hideTrailingZeroes(formattedValue);
                        }
                    }
                    break;
                //invalid dates to be converted to empty strings
                /* XSLX converter for .csv exports all values as string
                    so moment formatter is not suitable */
                case "Date":
                case "Month":
                case "Timestamp":
                case "Datetime":
                case "Timespan":
                case "Minute":
                case "Second":
                case "Time":
                    //use user selected type to understand what formatting is expected to apply
                    //allow to convert date in to number
                    const isTime = dataType === "Time",
                        isDateTime = dataType === "DateTime";

                    m = Tools.convertKDBTemporalToMoment(value);
                    //addressed situation in chartJs where date converted to number
                    //
                    if (dataType === "Number") {
                        formattedValue = _.escape(m);

                        if (
                            !_.isNaN(Number(formattedValue)) &&
                            _.isFinite(Number(formattedValue))
                        ) {
                            formattedValue = XLSX.SSF.format(format, Number(formattedValue));
                            //hide trailig zeroes
                            if (hideTrailingZeroes) {
                                formattedValue = RawDataFormatter._hideTrailingZeroes(
                                    formattedValue,
                                );
                            }
                        }
                    } else {
                        if (_.isFunction(m.isValid) && !m.isValid()) {
                            formattedValue = _.escape(m);
                        } else {
                            //todo format using "kk" is not recognised
                            //if (moment.isDuration(m)) {
                            //} else {
                            //time & datetime have 3 decimal places
                            formattedValue = isTime
                                ? m.formatNano(format, { trim: false, trimNano: false })
                                : isDateTime
                                ? m.formatNano(format, { trim: false, trimNano: false })
                                : m.formatNano(format);
                            //}
                        }
                    }
                    break;
                case "String":
                default:
                    formattedValue = value.toString();
                    break;
            }

            return formattedValue;
        }
        /* Apply format and traling zeroes to value
         * @param value {any} - cell raw value
         * @param dataType {String} - column type as selected
         * @param mappedKdbType {String} - value kdb type name
         * @param hideTrailingZeroes {Boolen}
         * @param kdbDataTypeClass {String} - classify values based on their kdb types
         * @return formattedValue {String}
         */
        private static _applyDefaultFormat(
            value,
            dataType,
            mappedKdbType,
            hideTrailingZeroes,
            kdbDataTypeClass,
        ) {
            let formattedValue = value.toString();
            let m = null;

            //if value is Temporal
            if (Tools.isKDBTemporal(value)) {
                m = Tools.convertKDBTemporalToMoment(value);
            }

            //export date as string and ignore timezone/daylight offsets
            if (kdbDataTypeClass === "DateTime") {
                //ChartJs dates can be given type of number to allow conversion of durations
                if (dataType === "Number") {
                    formattedValue = _.escape(m);
                } else {
                    if (m && _.isFunction(m.format)) {
                        formattedValue = m.format();
                    } else if (_.isFunction(value.toString)) {
                        formattedValue = value.toString();
                    }
                }
            } else if (kdbDataTypeClass === "Number") {
                //TODO add dates processing
                if (hideTrailingZeroes) {
                    //Hide trailing zeroes
                    formattedValue = RawDataFormatter._hideTrailingZeroes(formattedValue);
                }
            }

            return formattedValue;
        }

        /*@param valueStr {string} - number that may have traling zeros
         Smart numbers are hendled in Tools*/
        private static _hideTrailingZeroes(valueStr) {
            let toReturn = valueStr,
                tempSuffix,
                lastChar;
            //remove trailing zeroes
            if (toReturn.indexOf(".") >= 0) {
                lastChar = toReturn[toReturn.length - 1];
                tempSuffix = "";

                while (toReturn[toReturn.length - 1] === "0") {
                    toReturn = toReturn.slice(0, -1);
                }

                //after removing zeroes remove decimal separator if it's the last character
                if (toReturn[toReturn.length - 1] === ".") {
                    toReturn = toReturn.slice(0, -1);
                }

                //append suffix
                toReturn += tempSuffix;
            }
            return toReturn;
        }
    }
}
