/*global $, T, Backbone, FormSchemas, BBForm, DialogBase*/
/// <reference path="../templates/combined.ts" />
/// <reference path="../lib/i18n.d.ts" />
/// <reference path="DialogBase.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    interface SimpleInputDialogOptions extends DialogBaseOptions {
        inputValue?: any;
        showLoading: boolean;
    }

    export class SimpleInputDialog extends DialogBase {
        public inputValue: any;
        $dialogButtonpane: JQuery;
        protected options: SimpleInputDialogOptions;

        constructor(options: SimpleInputDialogOptions) {
            super(
                _.extend(
                    {
                        iconClass: "fa-keyboard-o",
                        dialogTitle: "Simple Input",
                        userClass: "simpleinput-dialog",
                        width: 275,
                        height: 140,
                    },
                    options,
                    {
                        userClass:
                            "simpleinput-dialog" +
                            (options.userClass ? " " + options.userClass : ""),
                    },
                ),
            );
        }

        dialogButtons() {
            const self = this;
            return [
                {
                    text: "OK",
                    click() {
                        if (self.options) {
                            if (self.options.showLoading) {
                                self.showLoadingSpinner();
                                _.defer(function() {
                                    self.dialogClose();
                                });
                            } else {
                                self.dialogClose();
                            }
                        }
                    },
                    icons: {
                        primary: "fa fa-check",
                    },
                },
                {
                    text: "Cancel",
                    click() {
                        self.isCancel = true;
                        self.dialogClose();
                    },
                    icons: {
                        primary: "fa fa-times",
                    },
                },
            ];
        }

        postRender() {
            this.$("input").val(this.options.inputValue);
            this.$("input").on("input", e => {
                // self.options is returned in the callback
                this.options.inputValue = this.inputValue = $(e.target).val();
            });
        }

        renderTemplate() {
            return t("<span>what?</span><input />");
        }

        showLoadingSpinner() {
            const $buttonOK = this.$dialogButtonpane.find("span.fa.fa-check");
            $buttonOK.removeClass("fa-check");
            $buttonOK.addClass("fa-spinner fa-pulse");
        }

        static ShowInput(
            callbackFn: any,
            title: string,
            label: string,
            iconClass: string,
            inputValue: string,
            userClass: string,
            showLoading?: boolean,
        ): SimpleInputDialog {
            return new SimpleInputDialog(
                _.extend(
                    {
                        iconClass: iconClass,
                        dialogTitle: title,
                        callback: callbackFn,
                        inputValue: inputValue,
                        userClass: userClass,
                        showLoading: showLoading,
                    },
                    label
                        ? {
                              renderTemplate: function() {
                                  return t(
                                      "<div>" +
                                          label +
                                          "</div><input style='width: 100%; box-sizing: border-box;' />",
                                  );
                              },
                          }
                        : {},
                ),
            );
        }
    }
}
