/// <reference path="../classes/Agent.ts"/>

interface ErrorInterface {
    /**
     * Error message
     */
    error: string;
    /**
     * Options
     */
    options: object;
    /**
     * Type of error
     */
    type: string;
}

interface PagingInfoInterface {
    /**
     * Lazy loading
     */
    _lazyLoading: boolean;
    /**
     * Force reset
     */
    _forceReset: boolean;
    /**
     * Number of rows per page
     */
    _pageSize: number;
    /**
     * Columns to sort by
     */
    _pageSort: object;
    /**
     * Page or offset number
     */
    _pagingNum: number;
    /**
     * Type of paging - "OFFSET", "OFFSET_NOCACHE", "PAGENO", "PAGENO_NOCACHE"
     */
    _pagingType: string;
    /**
     * Perform paging on the server
     */
    _serverPaging: boolean;
}

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export interface DataModelInterface {
        /*
         * toJSON override is necessary to prevent all of the data
         * contained within the data type models from being serialized.
         * - only the definition properties prefixed with '_' are
         *   saved
         */
        toJSON(): Record<string, any>;

        /**
         * Method to apply data, runs through each case: pivot, query, analytic, copy, virtual
         */
        apply(): void;

        /**
         * Get Meta
         * @param callback
         */
        getMeta(callback): void;

        /**
         * Get viewstate
         */
        getViewState(): any;

        /**
         * Get datasource parameters
         */
        getParameters(): Record<string, any>;

        /**
         * Subscribe to data
         * @param {String} key
         * @param {Subscriber} listener
         * @param {boolean} raw
         * @param {any} pagingInfo
         */
        subscribe(
            key: string,
            listener: Subscriber,
            raw?: boolean,
            pagingInfo?: PagingInfoInterface,
        ): void;

        /**
         * Unsubscribe to data
         * @param {String} key
         */
        unsubscribe(key): void;

        /**
         * on Successful datasource execution
         * @param {DCDS} data
         * @param {boolean} forceReset
         * @param {ArrayBuffer} buffer
         * @param { [index: string]: number } pivotMeta
         */
        onExecuteSuccess(
            data: DCDS,
            forceReset: boolean,
            buffer?: ArrayBuffer,
            pivotMeta?: { [index: string]: number },
        );

        /**
         * Begin datasource execution
         */
        onExecuteBegin(): void;

        /**
         *
         * @param {String} path
         * @returns {DocumentDataModel}
         */
        getByPath(path: string): DataModelInterface;
    }
}
