// 2016.09.15 performance enhancement for temporal constructors and type identification
// 2016.03.18 char vectors and symbols now [de]serialize [from]to utf8
// 2014.03.18 Serialize date now adjusts for timezone.
// 2013.04.29 Dict decodes to map, except for keyed tables.
// 2013.02.13 Keyed tables were not being decoded correctly.
// 2012.06.20 Fix up browser compatibility. Strings starting with ` encode as symbol type.
// 2012.05.15 Provisional test release, subject to change
// for use with websockets and kdb+v3.0, (de)serializing kdb+ ipc formatted data within javascript within a browser.
// e.g. on kdb+ process, set .z.ws:{neg[.z.w] -8!value -9!x;}
// and then within javascript websocket.send(serialize("10+20"));
// ws.onmessage=function(e){var arrayBuffer=e.data;if(arrayBuffer){var v=deserialize(arrayBuffer);...
// note ws.binaryType = 'arraybuffer';
/// <reference path="liteOp.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export interface qTyped {
        qtype: number;
    }

    export class qAtom implements qTyped {
        "offset": number;
        "qtype": number;

        constructor(offset: number, qtype: number) {
            this["offset"] = offset;
            this["qtype"] = qtype;
        }
    }

    export class qVector extends qAtom {
        "length": number;
        "isSorted": boolean;
        constructor(qtype: number, isSorted: boolean, length: number, offset: number) {
            super(offset, qtype);
            this["isSorted"] = isSorted;
            this["length"] = length;
        }
    }

    export class qGenericList implements qTyped {
        "qtype" = 0;
        "values": Array<qTyped>;
        "end": number;
        "isSorted": boolean;
        constructor(isSorted: boolean, values: Array<qTyped>, end: number) {
            this["isSorted"] = isSorted;
            this["values"] = values;
            this["end"] = end;
        }
    }

    export class qSymList implements qTyped {
        "qtype" = 11;
        "offsets": Array<number>;
        "isSorted": boolean;
        constructor(isSorted: boolean, offsets: Array<number>) {
            this["isSorted"] = isSorted;
            this["offsets"] = offsets;
        }
    }

    export class qDict implements qTyped {
        "qtype": number;
        "keys": qVector | qSymList | qGenericList | qDict;
        "values": qVector | qSymList | qGenericList | qDict;
        "isSorted": boolean;
        constructor(
            qtype: number,
            isSorted: boolean,
            keys: qVector | qSymList | qGenericList | qDict,
            values: qVector | qSymList | qGenericList | qDict,
        ) {
            this["keys"] = keys;
            this["values"] = values;
            this["qtype"] = qtype;
            this["isSorted"] = isSorted;
        }
    }

    export function getTypeSize(qType: number) {
        switch (qType) {
            case 1: // boolean
            case 4: // byte
            case 10: // char
                return 1;
            case 5: // short
                return 2;
            case 6: // int
            case 8: // real (single)
            case 13: // month
            case 14: // date
            case 17: // minute
            case 18: // second
            case 19: // time
                return 4;
            case 7: // long
            case 9: // float (double)
            case 12: // timestamp
            case 15: // datetime
            case 16: // timespan
                return 8;
            case 2: // guid
                return 16;
            default:
                return undefined;
        }
    }

    export function u8u16(u16: string): Array<number> {
        const u8 = [];
        for (let i = 0; i < u16.length; i++) {
            let c = u16.charCodeAt(i);
            if (c < 0x80) u8.push(c);
            else if (c < 0x800) u8.push(0xc0 | (c >> 6), 0x80 | (c & 0x3f));
            else if (c < 0xd800 || c >= 0xe000)
                u8.push(0xe0 | (c >> 12), 0x80 | ((c >> 6) & 0x3f), 0x80 | (c & 0x3f));
            else {
                c = 0x10000 + (((c & 0x3ff) << 10) | (u16.charCodeAt(++i) & 0x3ff));
                u8.push(
                    0xf0 | (c >> 18),
                    0x80 | ((c >> 12) & 0x3f),
                    0x80 | ((c >> 6) & 0x3f),
                    0x80 | (c & 0x3f),
                );
            }
        }
        return u8;
    }

    export function u16u8(u8: ArrayLike<number>): string {
        let u16 = "",
            c,
            c1,
            c2;
        for (let i = 0; i < u8.length; i++) {
            switch ((c = u8[i]) >> 4) {
                case 0:
                case 1:
                case 2:
                case 3:
                case 4:
                case 5:
                case 6:
                case 7:
                    u16 += String.fromCharCode(c);
                    break;
                case 12:
                case 13:
                    c1 = u8[++i];
                    u16 += String.fromCharCode(((c & 0x1f) << 6) | (c1 & 0x3f));
                    break;
                case 14:
                    c1 = u8[++i];
                    c2 = u8[++i];
                    u16 += String.fromCharCode(
                        ((c & 0x0f) << 12) | ((c1 & 0x3f) << 6) | ((c2 & 0x3f) << 0),
                    );
                    break;
            }
        }

        return u16;
    }

    // deserialize with less delta specific hacks
    export function deserialize(x: ArrayBuffer, offset?: number, isLite?: boolean) {
        let pos: number = offset === undefined ? 8 : offset;
        const dataView = new DataView(x);

        function rInt8(): number {
            return dataView.getInt8(pos++);
        }

        function rUInt8(): number {
            return dataView.getUint8(pos++);
        }

        function rInt32(): number {
            const ret = dataView.getInt32(pos, true);
            pos += 4;
            return ret;
        }

        function rSymbol() {
            let c,
                s = [];
            while ((c = dataView.getUint8(pos++)) !== 0) s.push(c);
            return u16u8(s);
        }

        function parseAtom(t: number): qAtom {
            const column = new qAtom(pos, t);
            const sz = getTypeSize(-t);
            if (sz) {
                // if size then is a fixed row advance
                pos += sz;
            } else if (t === -11) {
                // if symbol, need to index by scanning 0 terminated strings
                let c;
                while ((c = rUInt8()) !== 0) { }
            } else {
                throw "Unsupported type";
            }

            return column;
        }

        function parseList(t: number): qVector | qGenericList | qSymList {
            const isSorted = rUInt8() === 1;
            const n = rInt32();
            const sz = getTypeSize(t);

            if (sz) {
                // if size then is a fixed row advance
                const column = new qVector(t, isSorted, n, pos);
                pos += n * sz;
                return column;
            } else if (t === 11) {
                // if symbol, need to index by scanning 0 terminated strings
                const offsets = new Array(n);
                for (let i = 0; i < n; i++) {
                    offsets[i] = pos;
                    let c: number;
                    while ((c = rUInt8()) !== 0) { }
                }
                return new qSymList(isSorted, offsets);
            } else if (t === 0) {
                // mixed list is more complicated
                const values = new Array(n);
                for (let i = 0; i < n; i++) {
                    values[i] = r();
                }
                return new qGenericList(isSorted, values, pos);
            }

            throw "Unsupported type";
        }

        function parseTable(): qDict {
            const isSorted = rUInt8() === 1;
            const checkT = rInt8(); // check type is 99 here
            if (checkT !== 99) {
                console.error("c.js Assertion type=99 failed.");
            }

            return new qDict(98, isSorted, parseList(rInt8()), parseList(rInt8()));
        }

        function parseDict(): qDict {
            const flip = 98 === dataView.getUint8(pos);

            if (!flip) {
                const keys = parseList(rInt8());
                const t = rUInt8();
                const vals = t === 98 ? parseTable() : parseList(t);
                return new qDict(99, false, keys, vals);
            } else {
                // sorted keyed table
                // delta client table -8!([a:enlist 2]b:enlist 3)
                const isSorted = rUInt8() === 1;
                const a = parseTable();
                pos++;
                const b = parseTable();

                return new qDict(99, isSorted, a, b);
            }
        }

        function r(): qTyped {
            const t: number = rInt8();

            // parse SINGLE
            if (-20 < t && t < 0) {
                return parseAtom(t);
            }

            // skip EXOTIC shit
            if (t > 99) {
                const ret = new qAtom(pos, t);
                if (t === 100) {
                    rSymbol();
                    return r();
                }
                if (t < 104) return rInt8() === 0 && t === 101 ? new qAtom(pos - 1, t) : ret;
                if (t > 105) r();
                else for (let i = 0, n = rInt32() || 0; i < n; i++) r();

                return ret;
            }

            // DICT - note t=127 never appears on the other side of the network!!!
            if (99 === t) {
                return parseDict();
            }

            // TABLE
            if (98 === t) {
                return parseTable();
            }

            // LIST
            return parseList(t);
        }

        if (isLite) {
            // check message format
            if (rInt8() === 0) {
                const msgLength = rInt32();
                if (rInt8() === 0 && rInt8() === -6) {
                    const requestId = rInt32();
                    const qtype = rInt8();
                    const mtype = rUInt8();
                    const tmp = r();
                    return [requestId, mtype, tmp];
                }
            }

            return [0, Op.Error, "Message parse error"];
        } else return r();
    }
}
