/// <reference types="backbone" />

Backbone.emulateHTTP = true;

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Connection extends Backbone.Model {
        public get idAttribute(): string {
            return "name";
        }

        defaults(): object {
            return {
                name: "default",
                type: "q",
                host: "localhost",
                port: "80",
                tls: "false",
                user: "",
                password: "",
            };
        }
    }

    export class ConnectionCollection extends Backbone.Collection<Connection> {
        model = Connection;
    }
}
