/// <reference path="I64.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Char extends U8 {
        static readonly TYPE_NUM = 10;

        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Char, dataView);
        }

        static listToIPC(values: Array<string>): Uint8Array {
            const charData = values.map(v => u8u16(v && v.length ? v[0] : " "));

            // eslint-disable-next-line prefer-spread
            const merged = [].concat.apply([], charData);
            const size = charData.length + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(Char.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(merged.length); // vector size
            _.each(merged, v => buffer.wb(v)); // values

            return buffer.data;
        }

        static toIPC(value: string): Uint8Array {
            const charData = u8u16(value && value.length ? value[0] : " ");
            const buffer = Util.createBuffer(1 + charData.length);
            buffer.wb(256 - Char.TYPE_NUM); // type
            charData.forEach(c => buffer.wb(c));
            return buffer.data;
        }

        static u16u8(u8: ArrayLike<number>): string {
            let u16 = "",
                c,
                c1,
                c2;
            for (let i = 0; i < u8.length; i++)
                switch ((c = u8[i]) >> 4) {
                    case 0:
                    case 1:
                    case 2:
                    case 3:
                    case 4:
                    case 5:
                    case 6:
                    case 7:
                        u16 += String.fromCharCode(c);
                        break;
                    case 12:
                    case 13:
                        c1 = u8[++i];
                        u16 += String.fromCharCode(((c & 0x1f) << 6) | (c1 & 0x3f));
                        break;
                    case 14:
                        c1 = u8[++i];
                        c2 = u8[++i];
                        u16 += String.fromCharCode(
                            ((c & 0x0f) << 12) | ((c1 & 0x3f) << 6) | ((c2 & 0x3f) << 0),
                        );
                        break;
                }
            return u16;
        }

        getValue(i: number): string {
            const c = this.getScalar(i);
            return c === 0 ? "" : u16u8([c]);
        }
    }
}
