/// <reference path="./GroupModel.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.DataFilter {
    export class App extends Backbone.View<Backbone.Model> {
        public static generateIpcHexMessage(filtersJson): string {
            const gm = new GroupModel(
                _.extend(
                    {
                        //dummy model, to avoid exception thrown in RuleModel > setSchema
                        appModel: new Backbone.DeepModel({
                            "data.items": new Backbone.Collection(),
                        }),
                    },
                    filtersJson,
                ),
            );

            return QuickBase.ipc.Util.toHexString(QuickBase.ipc.Util.toMessage(gm.toIPC()));
        }
    }
}
