/*globals Backbone,_,$,console,DataModelPropertiesView,TreeView,T,t */
/// <reference path="../views/ChangePasswordDialog.ts" />
/// <reference path="../lib/i18n.d.ts" />
/// <reference path="../templates/combined.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class LoginDialog extends Backbone.View<Backbone.Model> {
        tagName: "form";

        $dialog: JQuery;
        $errorDialog: JQuery;

        $loginBtn: JQuery;
        $backBtn: JQuery;
        $resetBtn: JQuery;

        $buttonPanel: JQuery;
        $buttonText: JQuery;
        $userLoginForm: JQuery;
        $forgotPasswordForm: JQuery;
        $forgotPasswordBtn: JQuery;
        $passwordResetInfo: JQuery;
        $recoveryResponse: JQuery;
        $resetUserName: JQuery;
        $restPasswordError: JQuery;

        appModel;
        clicked;
        boundCapsLockFn;
        errorMessage: string;
        container;
        doLogin: () => void;
        changePasswordDialog: ChangePasswordDialog;

        constructor(options) {
            super(options);

            this.errorMessage = options.errorMessage;
            this.appModel = options.appModel;
            this.container = options.container;
            this.doLogin = options.doLogin;

            this.container.html(this.render().$el);

            this.boundCapsLockFn = $.proxy(this.onWindowCapsLockClick, this);

            this.initializeEvents();
        }
        /**
         * Function checks if CapsLock is on jquery key event
         */
        static checkCapslockOn(e: Event): boolean {
            const IS_MAC: boolean = /Mac/.test(navigator.platform),
                charCode: number = e["charCode"],
                shiftKey: boolean = e["shiftKey"];

            let capsLock = false;

            if (charCode >= 97 && charCode <= 122) {
                capsLock = shiftKey;
            } else if (charCode >= 65 && charCode <= 90 && !(shiftKey && IS_MAC)) {
                capsLock = !shiftKey;
            } // Other chars but ascii
            else if (charCode > 122 && e["getModifierState"]("CapsLock")) {
                capsLock = true;
            }

            return capsLock;
        }

        onWindowCapsLockClick(e: Event): void {
            if (e["which"] === 20) {
                if (e["getModifierState"]("CapsLock")) {
                    this.setErrorMessage("capsLock", "#ffc800");
                } else {
                    this.$el.find(".loginErrorPanel").css("visibility", "hidden");
                }
            }
        }

        render() {
            let self = this,
                errorMessage;

            this.$el.html(T.LoginDialog({}));

            // add blur
            $(".quick-base").addClass("disturbed");
            // Pause Loading Animation
            $(document)
                .find(".loadingScreen svg")
                .hide();
            $(document)
                .find(".loading-info svg")
                .hide();

            self.$userLoginForm = this.$el.find(".user-login-form");
            self.$forgotPasswordForm = this.$el.find(".forgot-password-form");
            self.$forgotPasswordBtn = this.$el.find(".forgot-info");
            self.$passwordResetInfo = this.$el.find(".password-reset-info");
            self.$recoveryResponse = this.$el.find(".recovery-response");
            self.$resetUserName = this.$el.find(".reset-username");

            this.$restPasswordError = this.$el.find(".reset-password-error");

            self.$dialog = this.$el.find(".login-form");

            self.$buttonPanel = this.$el.find(".login-button-panel");
            // get the login btn
            this.$el.find(".login-btn").button({
                label: t("Login"),
                icons: {
                    primary: "fa fa-check",
                },
            });

            this.$el.find(".back-btn").button({
                label: t("Back"),
                icons: {
                    primary: "fa fa-arrow-left",
                },
            });

            this.$el.find(".reset-btn").button({
                icons: {
                    primary: "fa fa-undo",
                },
                label: t("Reset"),
            });

            self.$loginBtn = this.$el.find(".login-btn");

            self.$backBtn = this.$el.find(".back-btn");
            self.$backBtn.hide();
            self.$resetBtn = this.$el.find(".reset-btn");
            self.$resetBtn.hide();

            if ((errorMessage = $.cookie("deltaLoginError"))) {
                $.removeCookie("deltaLoginError", { path: "/" });
            } else {
                errorMessage = this.errorMessage;
            }

            if (errorMessage) {
                this.setErrorMessage(errorMessage);
            }

            return this;
        }

        initializeEvents() {
            const self = this;
            //
            self.$el.find(".password, .username").on("keydown", (e: JQuery.Event) => {
                if (e.which === 13) {
                    self.onLoginButtonClick(e);
                }
            });

            window.addEventListener("keydown", this.boundCapsLockFn, true);

            // Key Press has different event state handle Caps Lock msg
            self.$el.find(".password").on("keypress", (e: JQuery.Event) => {
                if (LoginDialog.checkCapslockOn(e.originalEvent)) {
                    self.setErrorMessage("capsLock", "#ffc800");
                } else {
                    self.$el.find(".loginErrorPanel").css("visibility", "hidden");
                }
            });

            self.$forgotPasswordBtn.on("click", function() {
                self.showForgotPasswordForm();
            });

            self.$loginBtn.on("click", function(e) {
                e.preventDefault();
                self.onLoginButtonClick(e);
            });

            self.$backBtn.on("click", function(e) {
                e.preventDefault();
                self.hideForgotPasswordForm();
            });
            self.$resetBtn.on("click", function(e) {
                e.preventDefault();
                self.onResetButtonClick();
            });
            // process login success callback only once
            $(DeltaClientLib).one("DeltaClientLogin", $.proxy(self.onLoginSuccess, self));
        }

        clickLoginButton() {
            this.onLoginButtonClick({
                currentTarget: $(".login-button-panel .login-btn"),
            });
        }

        onLoginButtonClick(e) {
            let self = this,
                user,
                password,
                authJSON;

            if (!this.clicked) {
                user = this.$el.find(".username").val();
                password = this.$el.find(".password").val();
                this.$buttonText = $(e.currentTarget).find(".ui-button-text");

                if (!user || !password) {
                    this.setErrorMessage("Please enter your username and password.");
                    e.preventDefault();
                    return;
                }

                this.setErrorMessage("Connecting please wait...", "#fff");
                this.clicked = true;

                if (e) {
                    $(e.currentTarget)
                        .find(".ui-button-icon-primary")
                        .removeClass("fa-check");
                    $(e.currentTarget)
                        .find(".ui-button-icon-primary")
                        .css({
                            width: "auto",
                            height: "auto",
                        });
                    $(e.currentTarget)
                        .find(".ui-button-icon-primary")
                        .addClass("fa-spinner fa-pulse");
                    this.$buttonText.text(t("wait") + "...");
                }

                this.$el.find("input").attr("disabled", "disabled");

                //send user data to the backend
                _.isFunction(self.doLogin) &&
                    self.doLogin(user, password, function(e) {
                        self.setErrorMessage(e);
                    });
            }
        }

        onLoginSuccess() {
            const self = this;

            self.appModel.set("userName", DeltaClientLib.deltaClient.user());

            // set deltaToken
            $.cookie("deltaToken", DeltaClientLib.deltaClient.tokenKey(), {
                expires: 30,
                path: "/",
            });

            //$.cookie("dashauth", JSON.stringify(authJSON));
            $(".quick-base").removeClass("disturbed");

            self.onRemove();
        }

        onRemove() {
            this.$loginBtn.off();
            this.$backBtn.off();
            this.$resetBtn.off();
            this.$forgotPasswordBtn.off();
            // Restart Loading Screen Animation
            $(document)
                .find(".loadingScreen svg")
                .show();
            $(document)
                .find(".loading-info svg")
                .show();

            $(DeltaClientLib).off("DeltaClientLogin", this.onLoginSuccess);

            window.removeEventListener("keydown", this.boundCapsLockFn, true);

            this.remove();
        }

        onResetButtonClick() {
            let self = this,
                userName;

            this.$restPasswordError.css("visibility", "hidden");

            this.$userLoginForm.removeClass("open");
            userName = this.$resetUserName.val();

            if (userName && userName.length > 0) {
                this.showPasswordResetInfo();
                $.get("/kxuseradmin?operation=request&data=" + userName)
                    .done(function(data) {
                        self.$recoveryResponse.text(data);
                    })
                    .fail(function(data) {
                        let text = "Error";

                        if (_.isString(data)) {
                            text += ": " + data;
                        } else if (data && data.statusText) {
                            text += ": " + data.statusText;
                        }

                        self.$recoveryResponse.text(text);
                    });
            } else {
                this.$restPasswordError.css("visibility", "visible");
            }
        }

        setErrorMessage(errorText, colour?) {
            const self = this,
                //only login button is being modified
                $loginBtnIcon = self.$dialog
                    .parent()
                    .find(".ui-button-icon-primary.fa-spinner.fa-pulse"),
                cssJSON = {};

            this.$el.find(".loginErrorPanel").removeClass("warning");

            if (colour === "#fff") {
                this.$el.find(".loginErrorPanel").addClass("info");
            } else if (colour === "#ffc800") {
                // Caps Lock
                this.$el.find(".loginErrorPanel").addClass("warning");
            } else {
                this.$el.find(".loginErrorPanel").removeClass("info");
            }

            if (errorText === "locked") {
                errorText = t("Account is locked; please contact your administrator");
            } else if (errorText === "capsLock") {
                errorText = '<i class="fa fa-warning"/>' + t("Caps Lock is on");
            }

            this.$el.find(".loginErrorPanel").css(cssJSON);

            this.$el
                .find(".loginErrorPanel")
                .html(errorText)
                .css("visibility", "visible");

            self.clicked = false;

            if ($loginBtnIcon.length) {
                $loginBtnIcon.removeClass("fa-spinner fa-pulse");
                $loginBtnIcon.addClass("fa-check");
                this.$loginBtn.find(".ui-button-text").text(t("Login"));
            }

            self.$el.find("input").removeAttr("disabled");

            if (
                errorText === "Please reset password" ||
                errorText === "Your password has expired"
            ) {
                if (!self.changePasswordDialog) {
                    self.changePasswordDialog = new ChangePasswordDialog({
                        appModel: self.appModel,
                        container: self.container,
                        message: errorText,
                        onChangeSuccess: function() {
                            // user will be logged as part of deltaClient.resetPassword,
                            // so close login dialog
                            self.$el.find(".loginErrorPanel").css("visibility", "hidden");

                            if (self.$el.data("uiDialog")) {
                                self.$el.dialog("close");
                            }
                        },
                        onDialogClose: _.bind(function() {
                            self.changePasswordDialog = null;
                        }, self),
                    });
                }
            }
        }

        showForgotPasswordForm() {
            this.$userLoginForm.removeClass("open");
            this.$forgotPasswordForm.addClass("open");
            this.$passwordResetInfo.removeClass("open");
            this.$loginBtn.hide();
            this.$backBtn.show();
            this.$resetBtn.show();
        }

        showPasswordResetInfo() {
            this.$userLoginForm.removeClass("open");
            this.$forgotPasswordForm.removeClass("open");
            this.$passwordResetInfo.addClass("open");
            this.$loginBtn.hide();
            this.$resetBtn.hide();
            this.$backBtn.show();
        }

        hideForgotPasswordForm() {
            this.$forgotPasswordForm.removeClass("open");
            this.$passwordResetInfo.removeClass("open");
            this.$userLoginForm.addClass("open");
            this.$loginBtn.show();
            this.$backBtn.hide();
            this.$resetBtn.hide();
        }
    }
}
