/*global _,$,Backbone, window*/
/// <reference path="../models/component.ts" />
/// <reference path="../models/widget.ts" />
/// <reference path="../templates/combined.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class DocumentView extends Backbone.View<QuickBase.Document> {
        appModel: Backbone.Model;
        screenView: ComponentDropView;
        viewModel: DocumentViewModel;
        model: QuickBase.Document;
        quickView: boolean;

        cssClasses = null;
        customCss = null;
        $customCss = null;
        popups: Array<PopupView> = [];

        prevCssViewModel = null;

        constructor(options) {
            super(options);

            // assign props
            this.appModel = options.appModel;
            this.viewModel = options.viewModel;

            this.quickView = options.quickView;

            // upgrade document
            this.model.upgrade();

            // render
            this.el.className = "document-view";
            this.$el.html(T.DocumentView({}));

            // events
            this.initializeEvents();

            //add custom styling
            this.applyCustomCss();
            this.onCssClassesChanged();

            // select screen
            this.onSelectedScreenChanged();

            // if quickview, popups will be created after viewStatesParsed is triggered
            if (!this.quickView) {
                this.onResetPopups();
            }
        }

        initializeEvents() {
            this.listenTo(this.model.get("popups"), "reset viewStatesParsed", this.onResetPopups);
            this.listenTo(this.model.get("popups"), "add", this.onAddPopup);
            this.listenTo(this.model.get("popups"), "remove", this.onRemovePopup);

            this.listenTo(this.model, "change:advancedCss", this.applyCustomCss);
            this.listenTo(this.model, "change:cssClasses", this.onCssClassesChanged);

            this.listenTo(this.viewModel, "change:selectedScreenId", this.onSelectedScreenChanged);

            this.listenTo(this.viewModel, "change:DashboardTheme", function(e) {
                this.applyTheme(e.get("DashboardTheme"));
            });

            $(window).on("keydown", this.onKeyDown.bind(this));
        }

        applyCustomCss() {
            const newCustomCss = this.model.get("advancedCss");

            if (newCustomCss) {
                if (newCustomCss !== this.customCss) {
                    if (!this.$customCss || !this.$customCss.length) {
                        this.$customCss = $("<style />", {
                            documentid: this.model.id,
                            html: newCustomCss,
                            type: "text/css",
                        }).appendTo(document.head);
                    } else {
                        this.$customCss.html(newCustomCss);
                    }
                    this.customCss = newCustomCss;
                }
            } else {
                if (this.$customCss) {
                    this.$customCss.remove();
                    this.$customCss = null;
                    this.customCss = null;
                }
            }
        }

        applyCssClasses(newCssClasses) {
            if (newCssClasses) {
                if (_.isArray(newCssClasses)) {
                    newCssClasses = _.reduce(newCssClasses, function(classList, value) {
                        return classList + " " + value;
                    });
                }

                if (newCssClasses !== this.cssClasses) {
                    this.$el.removeClass(this.cssClasses);
                    this.$el.addClass(newCssClasses);
                    this.cssClasses = newCssClasses;
                }
            } else {
                this.$el.removeClass(this.cssClasses);
                this.cssClasses = null;
            }
        }

        applyDataSources() {
            _.each(this.model.get("data").flatten(), function(dataSource) {
                if (dataSource.isMapping() && dataSource.get(DocumentDataModel.AUTO_EXECUTE)) {
                    dataSource.apply();
                }
            });
        }

        //apply dashboard theme to existing widgets
        applyTheme(newTheme) {
            const self = this;
            if (this.screenView) {
                _.each(this.screenView.widgets, function(widget) {
                    self.applyThemeToWidget(widget, newTheme);
                });
            }
        }

        applyThemeToWidget(widget, newTheme) {
            if (widget.view.app && widget.view.app.setTheme) {
                widget.view.app.setTheme(newTheme);
            }
        }

        changeScreen(model) {
            if (this.screenView) {
                this.screenView.remove();
            }

            const $div = $(document.createElement("div"));
            $div.addClass("screen-view");
            $div.appendTo(this.$el.find(".screen-view-div"));

            // reset variable
            this.screenView = null;

            require(["Panel"], function(AppModule) {
                // ensure screen hasn't been added already
                if (!this.screenView) {
                    this.applyDataSources();
                    this.passScreenPropertiesToDocumentModel(model, this.model);

                    this.screenView = new AppModule({
                        el: $div,
                        model: model,
                        dashboardAppModel: this.appModel,
                        documentModel: this.model,
                        dashboardViewModel: this.viewModel,
                        //settingsModel: new Widget(),
                        websiteUrl: this.appModel.get("websiteUrl"),
                        //componentModel: new Component(),
                        DeltaClientLib: this.deltaClient,
                        api: {},
                        isBasePanel: true,
                        quickView: this.quickView,
                    });
                }
            }.bind(this));
        }

        createPopup(model): PopupView {
            return new PopupView({
                viewModel: this.viewModel,
                appModel: this.appModel,
                container: this.$el,
                quickView: this.quickView,
                model: model,
            });
        }

        onAddPopup() {
            this.popups.push(this.createPopup(arguments[0]));
        }

        onRemovePopup(popupModel) {
            const popupView = _.find(this.popups, popup => {
                return popup.model.get("id") === popupModel.get("id");
            });

            this.popups.splice(this.popups.indexOf(popupView), 1);
            popupView.remove();
        }

        onResetPopups() {
            const self = this;

            _.each(this.popups, function(popup) {
                popup.remove();
            });

            this.popups = [];

            this.model.get("popups").each(function(model) {
                self.popups.push(self.createPopup(model));
            });
        }

        onKeyDown(e: number): void {
            const keyCode = e["keyCode"];
            if (e["ctrlKey"] || e["metaKey"]) {
                const shortcuts = this.model.get("shortcuts");
                const keyString = String.fromCharCode(keyCode).toLowerCase();
                const shortcut = _.find(shortcuts, x => x.Key === keyString);

                if (shortcut && shortcut.Data) {
                    shortcut.Data.forceExecute();
                }
            }
        }

        onCssClassesChanged() {
            let newCssClasses = this.model.get("cssClasses");

            // save model triggers change with server values {_dashboardsType & value}
            if (newCssClasses && newCssClasses._dashboardsType) {
                return;
            }

            if (this.prevCssViewModel) {
                this.stopListening(this.prevCssViewModel);
                this.prevCssViewModel = null;
            }

            if (newCssClasses instanceof Backbone.Model) {
                this.listenTo(newCssClasses, "change:value", function(model, value) {
                    this.applyCssClasses(value);
                });
                this.prevCssViewModel = newCssClasses;
                newCssClasses = newCssClasses.get("value");
            }
            this.applyCssClasses(newCssClasses);
        }

        onResize() {
            this.screenView && this.screenView.onResize(true);
        }

        onSelectedScreenChanged() {
            let selectedScreenId,
                screen,
                ok = false;

            // get selected screen id
            selectedScreenId = this.viewModel.get("selectedScreenId");

            if (selectedScreenId) {
                // get screen
                screen = this.model.get("screens").get(selectedScreenId);

                if (screen) {
                    // set screen
                    this.changeScreen(screen);
                    ok = true;
                }
            }

            if (!ok) {
                // try to set default screen
                screen = this.model.get("screens").findWhere({ isDefault: true });
                if (screen) {
                    // select this one
                    this.viewModel.set("selectedScreenId", screen.id);
                } else {
                    // try first
                    screen = this.model.get("screens").first();
                    if (screen) {
                        screen.set("isDefault", true);
                        // select this one
                        this.viewModel.set("selectedScreenId", screen.id);
                    }
                }
            }
        }

        passScreenPropertiesToDocumentModel(screenModel, documentModel) {
            let relativeHeight = screenModel.get("relativeHeight"),
                rowCount = screenModel.get("rowCount") || 24,
                rowHeight = screenModel.get("rowHeight") || 25,
                updatedConfig;

            // use document's relative height, if screen doesn't have it
            if (relativeHeight === undefined) {
                relativeHeight = documentModel.get("relativeHeight");
                if (relativeHeight === undefined) {
                    //brand new dashboard: fill height is on
                    relativeHeight = true;
                }
            }

            updatedConfig = {
                relativeHeight: relativeHeight,
                rowCount: rowCount,
                rowHeight: rowHeight,
            };

            //update models with correct values
            screenModel.set(updatedConfig);
            documentModel.set(
                _.extend(updatedConfig, {
                    colCount: screenModel.get("colCount"),
                    floatable: screenModel.get("floatable"),
                }),
            );

            //let property inspector know to refresh layout
            documentModel.trigger("basePanelLayoutChanged");
        }

        pasteWidget(widgetModel) {
            if (this.screenView && !this.quickView) {
                // TODO screenview, panel etc should use a common interface
                (this.screenView as any).pasteWidget(widgetModel);
            }
        }

        remove() {
            let data;

            this.stopListening();

            if (this.screenView) {
                this.screenView.remove();
            }

            _.each(this.popups, function(popup) {
                popup.remove();
            });

            if (this.$customCss) {
                this.$customCss.remove();
            }

            // data model
            data = this.model.get("data");
            if (data) {
                if (data.stopPolledQueries) {
                    data.stopPolledQueries();
                }

                if (data.removeSubscriptions) {
                    data.removeSubscriptions();
                }
            }

            $(window).off("keydown");

            return Backbone.View.prototype.remove.call(this);
        }
    }
}
