/*global _, $, Backbone, console, Conv, T, Tools, Component, Widget */
/// <reference path="../templates/combined.ts" />
/// <reference path="../lib/delta-client-lib.ts" />
/// <reference path="../models/component.ts" />
/// <reference path="../views/ComponentView.ts" />
/// <reference path="../views/ConfirmDialog.ts" />
/// <reference path="../lib/i18n.d.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ComponentDropView extends Backbone.View<Backbone.Model> {
        protected dashboardAppModel: Backbone.Model;
        protected dashboardViewModel: Backbone.Model;
        DeltaClientLib: any;
        protected quickView: boolean;
        protected websiteUrl: string;
        widgetClass = "component-drop-widget";
        widgetsCollection: WidgetCollection;
        widgets: Array<any>;
        ignoreWidgetSelect: boolean;
        selected: any;
        debouncedResizeWidgets: (x: any) => void;

        constructor(options) {
            super(options);
        }

        initialize(options) {
            this.$el.addClass("component-drop");

            this.dashboardAppModel = options["dashboardAppModel"];
            this.dashboardViewModel = options["dashboardViewModel"];
            this.DeltaClientLib = options["DeltaClientLib"];
            this.quickView = options["quickView"];
            this.websiteUrl = options["websiteUrl"];
            this.widgetsCollection = this.widgetsCollection || options["widgets"];
            this.widgets = this.widgets || [];

            this.ignoreWidgetSelect = false;
            this.widgetClass = "component-drop-widget";

            this.initializeEvents();
            this.initializeEvents_widgetCollection();

            // init ui
            if (this.quickView) {
                this.switchOffBuildMode();
            } else {
                this.switchBuildMode(this.dashboardViewModel.get("buildMode"));
            }

            this.applyCurrentWidgets();
        }

        initializeEvents() {
            this.listenTo(this.dashboardViewModel, "change:buildMode", this.onBuildModeChanged);
            this.listenTo(
                this.dashboardViewModel,
                "change:selectedWidget",
                this.onSelectedWidgetChanged,
            );
            this.listenTo(
                this.dashboardAppModel,
                "change:pendingSelectionWidgetId",
                this.onSelectingWidget,
            );
        }

        initializeEvents_widgetCollection() {
            this.listenTo(this.widgetsCollection, "add", this.onWidgetCollectionAdd);
            this.listenTo(this.widgetsCollection, "remove", this.onWidgetCollectionRemove);
        }

        events(): any {
            return {
                dragenter: "onDragEnter",
                dragleave: "onDragLeave",
                dragover: "onDragOver",
                drop: "onDrop",
            };
        }

        addComponentToWidget(widgetModel: Widget, componentId: string, command: boolean) {
            const componentDefinition = this.dashboardAppModel.get("components").get(componentId);
            const appKey = this.getAppKey(componentDefinition);
            const widgetComponent = widgetModel.get("component");
            const widgetItem = this.findWidgetItem(widgetModel.id);
            const component = this.createNewComponent(componentId, appKey, widgetModel);

            if (widgetComponent) {
                const widgetKey = widgetComponent.get("key");

                if (widgetKey === "Tabs") {
                    // component responsible for loading the component
                    const parent = widgetComponent.getInnermostComponents(widgetKey);
                    parent.add(component);
                    component.setParent(parent);
                } else {
                    if (widgetItem && widgetItem.view) {
                        widgetItem.view.remove();
                        widgetItem.view = null;
                    }

                    if (command) {
                        widgetModel.commandSet({
                            component: component,
                        });
                    } else {
                        widgetModel.set("component", component);
                    }
                }
            } else {
                if (command) {
                    widgetModel.commandSet({
                        component: component,
                    });
                } else {
                    widgetModel.set("component", component);
                }
            }
        }

        addWidget(widgetModel: Widget, bulk: boolean, index: number | null) {
            let widgetId,
                widgetItem,
                $widget,
                $remove,
                self = this;

            // create widget id
            widgetId = widgetModel.id;

            // new widget
            $widget = this.renderWidget(widgetModel);

            // add remove btn
            $remove = $(document.createElement("div"));
            $remove.addClass("fa fa-times remove-btn");
            $remove.appendTo($widget);
            $remove.click(function(e) {
                e.preventDefault();
                e.stopPropagation();
                widgetModel.trigger("removeWidget");
            });

            // add widget class/id
            $widget.addClass(this.getWidgetClass(index));
            $widget.attr("data-widgetid", widgetId);

            // may not be in build mode
            if (!this.dashboardViewModel.get("buildMode")) {
                $widget.addClass("build-off");
            }

            // bind widget events
            this.addWidgetEvents($widget);

            widgetItem = {
                $remove: $remove,
                $widget: $widget,
                widgetId: widgetId,
                model: widgetModel,
                view: null,
            };

            // save widget
            this.widgets.push(widgetItem);

            // load app
            if (widgetModel.get("component")) {
                // select widget if not adding in bulk
                this.renderComponent(
                    widgetItem,
                    bulk ? () => {} : () => this.selectWidget(widgetItem),
                );
            }

            // listen for component
            self.stopListening(widgetModel, "change:component");
            self.listenTo(
                widgetModel,
                "change:component",
                _.bind(
                    function() {
                        if (widgetItem.view) {
                            widgetItem.view.remove();
                            widgetItem.view = null;
                        }

                        widgetItem.$widget.find(".grid-stack-item-content").remove();

                        self.renderComponent(widgetItem, () => self.selectWidget(widgetItem));
                    },
                    self,
                    widgetItem,
                ),
            );

            // listen for remove
            self.stopListening(widgetModel, "removeWidget");
            self.listenTo(widgetModel, "removeWidget", self.onRemoveWidget.bind(self, widgetModel));

            return widgetItem;
        }

        addNewWidget(widgetModel?: Widget, componentId?: string, pasted?: boolean) {
            if (!widgetModel) {
                widgetModel = new Widget({}, {}, this.widgetsCollection);
            }

            if (componentId && !pasted) {
                this.addComponentToWidget(widgetModel, componentId, false);
            }

            this.widgetsCollection.commandAdd(widgetModel, pasted ? "Paste" : null);

            return _.findLast(this.widgets, { widgetId: widgetModel.id });
        }

        addWidgetEvents($widgets) {
            let $widget,
                self = this;

            if ($widgets instanceof $) {
                _.each($widgets, function(widget) {
                    // get jquery obj
                    $widget = $(widget);
                    // hook up all events
                    $widget.off("click");
                    $widget.on("click", $.proxy(self.onWidgetClick, self));
                });
            } else {
                throw "Wrong parameter.";
            }
        }

        applyCurrentWidgets() {
            const self = this;

            // apply
            _.each(this.widgetsCollection.models, function(widgetModel) {
                self.addWidget(widgetModel, true, null);
            });
        }

        clearWidgets(callback?: () => void) {
            let self = this,
                temp;
            self.deselectCurrentWidget();
            // remove widgets
            // each remove widget is
            if (this.widgets.length > 0) {
                // remove widgets one by one
                _.each(this.widgets, function(widgetItem) {
                    // remove view
                    if (widgetItem.view && _.isFunction(widgetItem.view.remove)) {
                        widgetItem.view.remove();
                    }
                });
                this.widgets = [];

                // remove all widgets at once from gridstack
                if (callback) {
                    callback();
                }
            } else {
                if (callback) {
                    callback();
                }
            }
        }

        createComponentView(widgetItem, callback) {
            let componentModel = widgetItem.model.get("component"),
                view,
                selectedDocument;

            selectedDocument = this.getSelectedDocument();

            this.listenToOnce(componentModel, "componentLoaded", () => {
                widgetItem.model.trigger("componentLoaded");
            });

            view = new ComponentView({
                model: componentModel,
                callback: callback,
                dashboardAppModel: this.dashboardAppModel,
                dashboardViewModel: this.dashboardViewModel,
                deltaClient: this.dashboardAppModel.get("isLite") ? DashClient : DeltaClientLib,
                docViewModel: selectedDocument.get("viewState"),
                docDataModel: selectedDocument.get("data"),
                quickView: this.quickView,
                websiteUrl: this.websiteUrl,
                widgetId: widgetItem.widgetId,
            });

            widgetItem.view = view;

            if (view.model) {
                widgetItem.$widget.attr("data-componentid", view.model.get("key"));
                widgetItem.$remove.attr("title", view.getComponentName());
            }

            return view;
        }

        createNewComponent(componentId: string, appKey: string, widget: Widget) {
            const component = new Component({}, {}, widget);
            component.set({
                definitionId: componentId,
                key: appKey,
            });

            return component;
        }

        deselectCurrentWidget() {
            this.dashboardViewModel.set("selectedWidget", null);
            this.dashboardViewModel.set("selectedWidgetView", null, { silent: true });
        }

        disableWidgetSelect(e?: any) {
            //e, ui, $widget) {
            if (e && e.stopPropagation) {
                e.stopPropagation();
            }
            this.ignoreWidgetSelect = true;
        }

        enableWidgetSelect() {
            // (e, ui, $widget) {
            this.ignoreWidgetSelect = false;
        }

        findWidgetItem(widgetId) {
            if (!widgetId) return null;

            // find item in widgets
            return _.find(this.widgets, function(i) {
                return i.widgetId === widgetId;
            });
        }

        getAppKey(componentDefinition) {
            let appKey = componentDefinition.get("appKey");

            if (componentDefinition.get("user") && !appKey.match(/^(?:[a-z]+:)?\/\/|^\//)) {
                // user component with relative path - remove .js suffix so requirejs uses baseUrl settings
                appKey = appKey.replace(/\.js$/, "");
            }

            return appKey;
        }

        getSafeDragNDropHandler(fn) {
            return function(e) {
                // console.log("IK ComponentDropView.getSafeDragNDropHandler", _.now(), fn.name, arguments);
                return this.isDragValid() || this.isDraggingSupportedFile(e)
                    ? fn.apply(this, arguments)
                    : false;
            }.bind(this);
        }

        getSelectedDocument() {
            return this.dashboardAppModel
                .get("documents")
                .get(this.dashboardViewModel.get("selectedDocumentId"));
        }

        getWidgetClass(ix?: number | null) {
            return this.widgetClass;
        }

        isDragValid() {
            // using api.isBuildMode not enough!
            return this.dashboardAppModel.has("draggedComponentId") && !this.quickView;
        }

        isDraggingSupportedFile(e) {
            return (
                !!e.originalEvent.dataTransfer &&
                _.includes(e.originalEvent.dataTransfer.types, "Files") &&
                !this.quickView &&
                // IE doesn't expose filenames apart from drop event
                (e.originalEvent.dataTransfer.items === undefined ||
                    _.some(e.originalEvent.dataTransfer.items, function(file) {
                        return _.includes(["application/json", "text/xml"], file.type);
                    }))
            );
        }

        onBuildModeChanged(model, value, options: any) {
            this.switchBuildMode(this.dashboardViewModel.get("buildMode"), options.userInitiated);
        }

        onDragEnter(e) {
            // console.log("IK ComponentDropView.onDragEnter", _.now());
            if (this.isDragValid() && this.userCan("addNew")) {
                this.$el.addClass("drop-target");
            }

            // preventDefault + stopPropagation
            return false;
        }

        onDragLeave(e) {
            // console.log("IK ComponentDropView.onDragLeave", _.now());
            if (this.isDragValid()) {
                this.$el.removeClass("drop-target");
            }
        }

        onDragOver(e) {
            // console.log("IK ComponentDropView.onDragOver", _.now());
            if (this.isDragValid() && !this.$el.hasClass("drop-target")) {
                this.$el.addClass("drop-target");
            }

            // preventDefault + stopPropagation
            return false;
        }

        onDrop(e) {
            // console.log("IK ComponentDropView.onDrop", _.now());
            let componentDefinition, componentId, widgetId, widgetItem;

            if (this.isDragValid()) {
                this.$el.removeClass("drop-target");

                if (e.originalEvent.dataTransfer) {
                    componentId = e.originalEvent.dataTransfer.getData("text");
                }

                if (!componentId) return false;

                componentDefinition = this.dashboardAppModel.get("components").get(componentId);
                widgetId = $(e.currentTarget)
                    .find(".component-drop-widget")
                    .first()
                    .attr("data-widgetid");

                if (!componentDefinition) return false; // Invalid Drop Object

                if (widgetId) {
                    // use existing widget
                    widgetItem = this.findWidgetItem(widgetId);

                    // deselect current widget so we can reselect the new one
                    this.deselectCurrentWidget();
                }

                if (widgetItem) {
                    this.addComponentToWidget(widgetItem.model, componentId, true);
                } else {
                    this.addNewWidget(
                        new Widget({}, {}, this.widgetsCollection),
                        componentId,
                        false,
                    );
                }
            }

            // preventDefault + stopPropagation
            return false;
        }

        onRemoveWidget(widgetModel) {
            this.widgetsCollection.commandRemove(widgetModel);
        }

        onResizeStart() {
            _.each(this.widgets, function(widgetItem) {
                widgetItem.view.onResizeStart && widgetItem.view.onResizeStart(true);
            });
        }

        onResize(resizePanels?: boolean) {
            _.each(this.widgets, function(widgetItem) {
                widgetItem.view.onResize && widgetItem.view.onResize(true);
            });
        }

        onResizeStop() {
            _.each(this.widgets, function(widgetItem) {
                widgetItem.view.onResizeStop && widgetItem.view.onResizeStop(true);
            });

            !this.userCan()
                ? this.$el.find(".component-build-view").addClass("build-off")
                : this.$el.find(".component-build-view").removeClass("build-off");
        }

        onSelectedWidgetChanged(model, value) {
            // console.log("IK quickbase.componentdropview: onSelectedWidgetChanged", this.cid);
            let widgetItem;

            if (this.selected) {
                this.selected.$widget.removeClass("selected");
            }

            if (value) {
                widgetItem = this.findWidgetItem(value.id);

                if (widgetItem) {
                    this.selected = widgetItem;
                    this.selected.$widget.addClass("selected");
                }
            } else {
                this.selected = null;
            }
        }

        onSelectingWidget(model, id) {
            if (model) {
                this.onWidgetClick(null, id);
                this.dashboardAppModel.set("pendingSelectionWidgetId", null, { silent: true });
            }
        }

        onWidgetClick(e, widgetId) {
            // console.log("IK quickbase.componentdropview:onWidgetClick", e, widgetId);
            var widgetItem, widgetId;

            // get widget id
            if (e) {
                widgetId = $(e.currentTarget).attr("data-widgetid");
            } else if (widgetId) {
                widgetId = widgetId;
            }

            // find item
            widgetItem = this.findWidgetItem(widgetId);

            if (widgetItem && (widgetId || !$(e.target).is(widgetItem.$remove))) {
                if (!this.ignoreWidgetSelect) {
                    if (e && e.stopPropagation) {
                        e.stopPropagation();
                    }

                    if (
                        !(
                            e &&
                            $(e.target)
                                .parent()
                                .hasClass("QuickStart")
                        )
                    ) {
                        if (this.dashboardViewModel.get("selectedWidget") === widgetItem.model) {
                            this.dashboardViewModel.set("selectedWidget", null);
                            this.dashboardViewModel.set("selectedWidgetView", null, {
                                silent: true,
                            });
                        } else {
                            this.dashboardViewModel.set("selectedWidget", widgetItem.model);
                            this.dashboardViewModel.set("selectedWidgetView", widgetItem.view, {
                                silent: true,
                            });
                        }
                    }
                }
            }

            // beware of preventing default (using return false or e.preventDefault) ->
            //  form would not submit, chekboxes stop working !
            //  check this: https://issuetracker.firstderivatives.com/browse/KXAX-15933
        }

        onWidgetCollectionAdd(model: Widget, collection: WidgetCollection, options: any) {
            this.addWidget(model, false, null);
        }

        onWidgetCollectionRemove(model: Widget, collection: WidgetCollection, options: any) {
            this.removeWidget(model);
        }

        remove() {
            this.clearWidgets();

            return Backbone.View.prototype.remove.apply(this);
        }

        removeWidget(widgetModel) {
            //find widget item
            const widgetItem = _.find(this.widgets, { model: widgetModel });

            if (widgetItem) {
                if (this.selected && this.selected === widgetItem) {
                    this.deselectCurrentWidget();
                }

                // remove from widgets array
                this.widgets = _.without(this.widgets, widgetItem);

                // hide widget background before removing the app
                // has to be applied immediately
                widgetItem.$widget.css({
                    border: "none",
                    background: "none",
                    "box-shadow": "none",
                    "pointer-events": "none",
                });

                // remove the app
                if (widgetItem.view) {
                    if (_.isFunction(widgetItem.view.remove)) {
                        widgetItem.view.remove();
                    }
                }

                // detach all events from view
                widgetItem.$widget.off();
                widgetItem.$widget.remove();
            }
        }

        renderComponent(widgetItem, callback) {
            const view = this.createComponentView(widgetItem, callback);

            widgetItem.$widget.append(view.$el);
            this.$el.append(widgetItem.$widget);
        }

        renderWidget(widgetModel) {
            return $("<div/>");
        }

        selectWidget(widgetItem) {
            if (!this.ignoreWidgetSelect) {
                this.dashboardViewModel.set("selectedWidget", widgetItem.model);
                this.dashboardViewModel.set("selectedWidgetView", widgetItem.view, {
                    silent: true,
                });
            }
        }

        setTheme(newTheme) {
            _.each(this.widgets, function(widget) {
                if (widget.view && widget.view.app && _.isFunction(widget.view.app.setTheme)) {
                    widget.view.app.setTheme(newTheme);
                }
            });
        }

        showReplaceConfirmationDialog(agreedfn, widgetItem) {
            const componentName = _.get(
                this.dashboardAppModel
                    .get("components")
                    .get(
                        _.get(
                            widgetItem,
                            "model.attributes.component.attributes.definitionId",
                            null,
                        ),
                    ),
                "attributes.componentName",
                "",
            );

            ConfirmDialog.ShowConfirm({
                dialogTitle: "Confirm Replace",
                renderTemplate: function() {
                    return t(
                        `Are you sure you would like to replace this ${componentName} component?`,
                    );
                },
                callback: agreedfn,
            });
        }

        switchBuildMode(newValue, userInitiated?: boolean) {
            if (newValue) {
                this.switchOnBuildMode(userInitiated);
            } else {
                this.switchOffBuildMode(userInitiated);
            }
        }

        switchOffBuildMode(userInitiated?: boolean) {
            this.disableWidgetSelect();

            _.each(this.widgets, function(widgetItem) {
                widgetItem.$widget && widgetItem.$widget.addClass("build-off");
            });
        }

        switchOnBuildMode(userInitiated?: boolean) {
            this.enableWidgetSelect();

            _.each(this.widgets, function(widgetItem) {
                widgetItem.$widget && widgetItem.$widget.removeClass("build-off");
            });
        }

        userCan(action?: string) {
            return this.dashboardViewModel.get("buildMode") && !this.quickView;
        }
    }
}
