/// <reference types="backbone" />
/// <reference path="component.ts" />
/// <reference path="layout.ts" />

// Default options for `Collection#set`.
const setOptions = { add: true, remove: true, merge: true };
const addOptions = { add: true, remove: false };

// Splices `insert` into `array` at index `at`.
const splice = function(array, insert, at) {
    at = Math.min(Math.max(at, 0), array.length);
    const tail = Array(array.length - at);
    const length = insert.length;
    let i;
    for (i = 0; i < tail.length; i++) tail[i] = array[i + at];
    for (i = 0; i < length; i++) array[i + at] = insert[i];
    for (i = 0; i < tail.length; i++) array[i + length + at] = tail[i];
};

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Widget extends CommandoModel {
        readonly IMMATERIAL_ATTRIBUTES = [];

        constructor(attributes, options, parent: Commandable) {
            super(attributes, options, parent);

            if (this.get("layout") && !(this.get("layout") instanceof Layout)) {
                this.set({ layout: new Layout(this.get("layout"), {}, this) }, { silent: true });
            }

            if (this.get("component") && !(this.get("component") instanceof Component)) {
                this.set(
                    { component: new Component(this.get("component"), {}, this) },
                    { silent: true },
                );
            }

            if (this.get("component"))
                this.listenTo(
                    this.get("component"),
                    "materialChange",
                    this.onMaterialChange.bind(this),
                );
            if (this.get("layout"))
                this.listenTo(
                    this.get("layout"),
                    "materialChange",
                    this.onMaterialChange.bind(this),
                );

            this.listenTo(this, "change:component", this.onComponentChanged.bind(this));
            this.onComponentChanged(this, this.get("component"));
        }

        clone(widgetIdOverride?: string): Widget {
            const newWidget = new Widget({}, {}, this.parent);
            if (widgetIdOverride) {
                newWidget.set("id", widgetIdOverride);
            }

            // exclude previous id
            const attributes = _.omit(this.attributes, "id");

            // clone component and layout models
            attributes["component"] = this.get("component").clone({
                previousWidgetId: this.id,
                widgetId: newWidget.id,
            });
            attributes["layout"] = this.get("layout").clone();

            newWidget.set(attributes);

            return newWidget;
        }

        defaults(): object {
            return {
                id: Helpers.getGUID(),
                layout: new Layout({}, {}, this),
                component: null,
            };
        }

        commandSet(json): void {
            const oldJSON = this.clone(this.id).toJSON();

            this.postCommand(
                this,
                new Command(
                    "Set Widget",
                    [],
                    [new Todo(CommandOp.Set, "", json)],
                    [new Todo(CommandOp.Set, "", oldJSON)],
                ),
            );
        }

        doCommand(path: Array<string>, todos: Array<Todo>): void {
            if (path.length > 0) {
                const key = path.pop();
                if (["layout", "component"].indexOf(key) !== -1) {
                    return this.get(key).doCommand(path, todos);
                } else {
                    throw "Unsupported command key";
                }
            } else {
                todos.forEach(t => {
                    // setting a new component model is the only supported operation
                    if (t.payload.component) {
                        if (t.payload.component instanceof Backbone.Model) {
                            this.set("component", t.payload.component);
                        } else {
                            this.set("component", new Component(t.payload.component, {}, this));
                        }
                    }
                });
            }
        }

        getComponents(): Array<Component> {
            return this.get("component").getComponents();
        }

        postCommand(sender: object, command: Command): void {
            if (sender instanceof Layout) {
                command.path.push("layout");
            } else if (sender instanceof Component) {
                // remove component id
                command.path.pop();
                command.path.push("component");
            }

            command.path.push(this.id);
            return this.parent.postCommand(this, command);
        }

        onComponentChanged(model, value, options?): void {
            if (value) {
                this.listenTo(value, "change", function(m, v) {
                    this.trigger("change", m, v);
                });
            }
        }

        onMaterialChange(): void {
            this.trigger("materialChange");
        }

        setParent(parent): void {
            this.parent = parent;

            if (this.get("component")) {
                this.get("component").setParent(this);
            }

            if (this.get("layout")) {
                this.get("layout").setParent(this);
            }
        }
    }

    export class WidgetCollection extends ComandoCollection<Widget> {
        commandQueue = [];
        descriptor = "Widget";
        model = Widget;

        clone(): this {
            // need to clone each widget
            const models = _.map(this.models, function(widget) {
                return widget.clone();
            });

            return new WidgetCollection(
                models,
                {
                    model: Widget,
                    comparator: this.comparator,
                },
                this.parent,
            ) as this;
        }

        commandSet(models: Backbone.Model[]): void {
            const oldJSON = models.map((m, i) => {
                const model = this.get(m.id) || this.at(i);

                if (model) {
                    const changedAttributes = model.changedAttributes(m);

                    // create previous attributes taking care to CLONE layout object
                    const previousAttributes = _.mapValues(changedAttributes, (value, key) => {
                        const prop = model.get(key);
                        return prop instanceof Backbone.Model ? prop.toJSON() : prop;
                    });

                    if (model.id) {
                        previousAttributes.id = model.id;
                    }

                    return previousAttributes;
                }
            });

            const c = new Command(
                this.getCommandDescription(CommandOp.Set, models),
                [],
                [new Todo(CommandOp.Set, "", models)],
                [new Todo(CommandOp.Set, "", oldJSON)],
            );

            this.postCommand(this, c);
        }

        doCommandSet(payload): void {
            payload.forEach(w => {
                const widget = this.get(w.id);
                if (widget) {
                    w.component && widget.get("component").set(w.component);
                    w.layout && widget.get("layout").set(w.layout);
                }
            });
        }

        getCommandDescriptor(payload?): string {
            let descriptor = super.getCommandDescriptor(payload);
            let widget;

            if (_.isArray(payload) && payload.length === 1) {
                widget = payload[0];
            } else if (payload instanceof Widget) {
                widget = payload;
            }

            if (widget instanceof Backbone.Model && widget.get("component")) {
                descriptor = widget.get("component").get("key");
            }

            return descriptor || this.descriptor;
        }

        setParent(parent: Commandable): void {
            this.parent = parent;
            this.map(w => w.setParent(this));
        }
    }

    WidgetCollection.prototype.model = Widget;
}
