/// <reference path="DataModelInterface.ts"/>

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export interface ViewModelInterface {
        /**
         * @param {String} path
         * @returns {ViewModelInterface}
         */
        getByPath(path: string): ViewModelInterface;

        /*
         * toJSON override is necessary to prevent all of the data
         * contained within the data type models from being serialized.
         * - only the definition properties prefixed with '_' are
         *   saved
         */
        toJSON(withAllProperties): Record<string, any>;

        /**
         * Get viewstates
         * @param {String} root
         * @param {String} path
         * @param {boolean} includeSettings
         */
        getViewStates(root, path?: string, includeSettings?: boolean): void;

        /**
         * Get viewstate list
         * @param {boolean} includeSettings
         */
        getViewStateList(includeSettings?: boolean): void;

        /**
         * Change to viewstate
         */
        onChange(): void;

        /**
         * Parse data
         * @param data
         */
        parse(data): Record<string, any>;
    }
}
