/// <reference path="../lib/jquery.cookie.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Log {
        public static Debug: Function;
        public static Info: Function;
        public static Log: Function;
        public static Error: Function;
        public static Handled: Function;
        public static Warn: Function;
        public static ServerLogLevel = 4;
        private static clientLoggingEnabled: boolean;
        private static logUploadConnected: boolean;

        private static debounceRetryConnection = _.debounce(
            function() {
                Log.retryConnection(0);
            },
            300,
            { maxWait: 1000 },
        );

        Error: (message: string, argument?: any) => {};
        Handled: (message: string, argument?: any) => {};
        Warn: (message: string, argument?: any) => {};
        Debug: (message: string, argument?: any) => {};
        Log: (message: string, argument?: any) => {};
        Info: (message: string, argument?: any) => {};

        public static attemptUpload(logString: string): void {
            if (Log.logUploadConnected) {
                DeltaClientLib.createLog(
                    logString,
                    function() {
                        // do nothing
                    },
                    function() {
                        Log.logUploadConnected = false;
                        Log.storeLocal(logString);
                        Log.debounceRetryConnection();
                    },
                );
            } else {
                Log.storeLocal(logString);
            }
        }

        public static logPerformanceMetrics(
            modelPath: string,
            performanceMetrics: PerformanceMetrics,
        ): void {
            if (Log.clientLoggingEnabled) {
                const perf = performanceMetrics.performance;
                const totalTime = perf.onAgentNotify - perf.apply;
                let logString =
                    "Performance Metrics<-->" +
                    moment().format("H:mm:ss.SSS") +
                    "<-->" +
                    modelPath +
                    " (Roundtrip: " +
                    totalTime.toFixed(0) +
                    "ms)" +
                    "<-->";
                const executionTimes = [];

                const mapTimeValues = function(value) {
                    const percentage = (value / totalTime) * 100;
                    return value.toFixed(0) + " ms  " + percentage.toFixed(2) + "%";
                };

                executionTimes[0] = perf.serverStart ? perf.serverStart - perf.apply : 0;
                executionTimes[1] =
                    perf.serverStart && perf.serverEnd
                        ? perf.serverEnd - perf.serverStart
                        : totalTime;
                executionTimes[2] = perf.serverEnd ? perf.onAgentNotify - perf.serverEnd : 0;

                logString += "Dispatch: " + mapTimeValues(executionTimes[0]);
                logString += ", Server Duration: " + mapTimeValues(executionTimes[1]);
                logString += ", IPC & Deserialize: " + mapTimeValues(executionTimes[2]);

                Log.attemptUpload(logString);
            }
        }

        public static setClientLogging(): void {
            Log.logUploadConnected = true;
            Log.clientLoggingEnabled = true;
            Log.traceConsoleError();
            Log.trace(Tools.getCookie("dashboard-log-level"));
        }

        public static trace(logLevelStr: string): void {
            /* eslint-disable @typescript-eslint/unbound-method */
            const logLevel = Number(logLevelStr);
            if (Log.clientLoggingEnabled) {
                Log.turnOnUploads(logLevel);
            } else {
                Log.Error =
                    logLevel > 0
                        ? console.error.bind(console, "%s", Log.logPrefix(Log.Level.Error))
                        : _.noop;
                Log.Handled =
                    logLevel > 1
                        ? console.error.bind(console, "%s", Log.logPrefix(Log.Level.Handled))
                        : _.noop;
                Log.Warn =
                    logLevel > 1
                        ? console.warn.bind(console, "%s", Log.logPrefix(Log.Level.Warn))
                        : _.noop;
                Log.Debug =
                    logLevel > 2
                        ? console.debug.bind(console, "%s", Log.logPrefix(Log.Level.Debug))
                        : _.noop;
                Log.Log =
                    logLevel > 2
                        ? console.log.bind(console, "%s", Log.logPrefix(Log.Level.Info))
                        : _.noop;
                Log.Info =
                    logLevel > 3
                        ? console.info.bind(console, "%s", Log.logPrefix(Log.Level.Info))
                        : _.noop;
            }
            /* eslint-enable @typescript-eslint/unbound-method */
        }

        public static turnOnUploads(logLevel: number): void {
            Log.Error = function(message: string, argument?: any) {
                if (logLevel > 0) {
                    console.error(message, argument || "");
                }

                if (Log.ServerLogLevel > 0) {
                    Log.attemptUpload(Log.logUploadString(this.Level.Error, message, argument));
                }
            };

            Log.Handled = function(message: string, argument?: any) {
                if (logLevel > 1) {
                    console.error(message, argument || "");
                }

                if (Log.ServerLogLevel > 1) {
                    Log.attemptUpload(Log.logUploadString(this.Level.Handled, message, argument));
                }
            };

            Log.Warn = function(message: string, argument?: any) {
                if (logLevel > 1) {
                    console.warn(Log.logPrefix(this.Level.Warn) + message, argument || "");
                }

                if (Log.ServerLogLevel > 2) {
                    Log.attemptUpload(Log.logUploadString(this.Level.Warn, message, argument));
                }
            };

            Log.Debug = function(message: string, argument?: any) {
                if (logLevel > 2) {
                    console.debug(Log.logPrefix(this.Level.Debug) + message, argument || "");
                }

                if (Log.ServerLogLevel > 2) {
                    Log.attemptUpload(Log.logUploadString(this.Level.Debug, message, argument));
                }
            };

            Log.Log = function(message: string, argument?: any) {
                if (logLevel > 2) {
                    console.log(Log.logPrefix(this.Level.Log) + message, argument || "");
                }

                if (Log.ServerLogLevel > 2) {
                    Log.attemptUpload(Log.logUploadString(this.Level.Log, message, argument));
                }
            };

            Log.Info = function(message: string, argument?: any) {
                if (logLevel > 3) {
                    console.info(Log.logPrefix(this.Level.Info) + message, argument || "");
                }

                if (Log.ServerLogLevel > 3) {
                    Log.attemptUpload(Log.logUploadString(this.Level.Info, message, argument));
                }
            };
        }

        private static getStackTrace(): string {
            const stack = new Error("").stack || "";
            return (
                "\nStack Trace: " +
                stack
                    .split("\n")
                    .slice(3, 9)
                    .join("\n")
            );
        }

        private static logPrefix(level: Log.Level): Function {
            const timestamp = function() {};
            timestamp.toString = function() {
                return level + "\t" + moment().format("H:mm:ss.SSS") + "\t";
            };
            return timestamp;
        }

        private static logUploadString(
            level: Log.Level,
            message: string,
            argument: Record<string, any>,
        ): string {
            let logString;
            const timestamp = moment().format("H:mm:ss.SSS");

            logString = level + "<-->" + timestamp + "<-->" + message + "<-->";
            if (argument) {
                logString += JSON.stringify(argument);
            }
            if (level !== this.Level.Info) {
                logString += Log.getStackTrace();
            }
            return logString;
        }

        private static retryConnection(timeout: number) {
            const seconds = [1, 3, 15, 60, 180, 900];
            if (timeout === seconds.length) timeout -= 1;
            setTimeout(function() {
                Log.uploadStoredLogs(timeout);
            }, 1000 * seconds[timeout]);
        }

        // leading zeros added to ensure correct sequence of logs uploaded
        // assumes key doesnt go above 9999, if it does it will rewrite at 0000 repeatedly
        private static storeLocal(logString: string) {
            let key,
                newKey,
                n = localStorage.length - 1;

            // searches from end to find latest key added
            if (localStorage.getItem("0000")) {
                while (!newKey) {
                    key = localStorage.key(n);
                    if (key.match(/\d\d\d\d/)) {
                        newKey = ("000" + (parseInt(key) + 1)).slice(-4);
                    }
                    n -= 1;
                }
            } else {
                newKey = "0000";
            }
            localStorage.setItem(newKey, logString);
        }

        private static traceConsoleError() {
            const consoleError = console.error;
            console.error = function(message, argument) {
                const logString = Log.logPrefix(Log.Level.Error) + message;
                consoleError.apply(console, [logString, argument || ""]);
                Log.attemptUpload(Log.logUploadString(Log.Level.Error, message, argument));
            };
        }

        // recursive function that calls itself to upload next log in sequence
        // once callback received from deltaClient
        private static uploadStoredLogs(timeout: number): void {
            let key,
                logString,
                partialSuccess = false;

            const uploadItem = function(index: number): void {
                if (index < localStorage.length) {
                    key = localStorage.key(index);

                    if (key.match(/\d\d\d\d/)) {
                        logString = localStorage.getItem(key);

                        DeltaClientLib.createLog(
                            logString,
                            function() {
                                partialSuccess = true;
                                localStorage.removeItem(key);
                                uploadItem(index);
                            },
                            function() {
                                if (partialSuccess) {
                                    Log.retryConnection(0);
                                } else {
                                    Log.retryConnection(timeout + 1);
                                }
                            },
                        );
                    } else {
                        //moves onto next index if it does not match regex
                        uploadItem(index + 1);
                    }
                } else {
                    Log.logUploadConnected = true;
                }
            };
            uploadItem(0);
        }
    }

    // eslint-disable-next-line @typescript-eslint/no-namespace
    export namespace Log {
        export enum Level {
            Info = "INFO",
            Warn = "WARN",
            Error = "ERROR",
            Fatal = "FATAL",
            Critical = "CRITICAL",
            Off = "OFF",
            Handled = "HANDLED",
            Debug = "DEBUG",
        }
    }
    Log.trace($.cookie("dashboard-log-level"));

    window.onerror = function(
        message: string | Event,
        source: string,
        lineno: number,
        colno: number,
        error: Record<string, any>,
    ): void {
        /* eslint-disable @typescript-eslint/unbound-method */
        message && message.toString ? message.toString() : "" + message;
        error && error.toString ? error.toString() : "" + error;
        DeltaClientLib.createLog(
            message + "<-->" + moment().format("H:mm:ss.SSS") + "<-->" + error,
            _.noop,
            _.noop,
        );
        /* eslint-enable @typescript-eslint/unbound-method */
    };
}
