/*global _,$,console,Backbone */
/// <reference path="ColumnInfo.ts"/>
/// <reference path="log.ts"/>
/// <reference path="ChangeSet.ts"/>

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    var TEMPORAL_MAP = {
        1212: true,
        1213: true,
        1214: true,
        1215: true,
        1216: true,
        1217: true,
        1218: true,
        1219: true,
    };
    var DataSetModel = Backbone.Model;

    // A minimal version of set for better performance
    function _set(key, val, options) {
        if (key == null) return this;

        // Handle both `"key", value` and `{key: value}` -style arguments.
        let attrs;
        if (typeof key === "object") {
            attrs = key;
            options = val;
        } else {
            (attrs = {})[key] = val;
        }

        // Extract attributes and options.
        this.changed = {};

        // For each `set` attribute, update or delete the current value.
        for (const attr in attrs) {
            const current = this.attributes[attr];
            val = attrs[attr];

            if (
                !(current && val && typeof current === "object" && typeof val === "object"
                    ? //is temporal
                      TEMPORAL_MAP[current.class] && TEMPORAL_MAP[val.class]
                        ? current.i === val.i && current.n === val.n
                        : //array or non temporal objects
                          _.isEqual(current, val)
                    : // else simple comparison
                      current == val)
            ) {
                this.attributes[attr] = this.changed[attr] = val;
            }
        }

        if (this.idAttribute in attrs) this.id = this.get(this.idAttribute);

        return this;
    }

    function _changedAttributes() {
        return this.hasChanged() ? this.changed : false;
    }

    interface IPagingMeta {
        startRows: number;
    }

    export class DataSet {
        public static readonly DEFAULT_ROW_INDEX = "_rowIndex";
        breakdownColumns: Array<string> = [];
        columns = new Backbone.Collection();
        columnDict: { [index: string]: ColumnInfo } = {};
        agent: Agent;
        collection;

        // store collectionFunc so tempDataSet can use it
        collectionFunc;

        forceReset: boolean; // used by tempDataSet
        preferBackground = false;
        idAttribute: string;
        public lazyLoading: boolean;
        maxRows: number;
        pagingMeta: IPagingMeta;
        primaryKey: string;

        tempDataSet: DataSet;
        visibilityChangeHandler: EventListenerObject;

        noBackground: boolean;

        constructor(options) {
            this.breakdownColumns = [];

            this.agent = options.agent;
            this.collection = new options.collectionFunc();

            // store collectionFunc so tempDataSet can use it
            this.collectionFunc = options.collectionFunc;

            // gk: set prototype function on model - extend apporach doesn't work
            this.collection.model.prototype.set = _set;
            this.collection.model.prototype.changedAttributes = _changedAttributes;

            this.idAttribute = options.idAttribute;
            this.maxRows = options.maxRows || 100;
            this.primaryKey = options.primaryKey || DataSet.DEFAULT_ROW_INDEX;
            this.lazyLoading = options.lazyLoading || false;
        }

        static columnsToCollection(models: any[]): Backbone.Collection {
            const collection = new Backbone.Collection();
            collection.models = models.map(a => {
                const m = new Backbone.Model();
                m.id = a.id;
                m.attributes = a;
                return m;
            });
            collection.length = models.length;
            return collection;
        }

        static updateColumns(
            columns: { [index: string]: ColumnInfo },
            meta,
            changeSet,
            noChangeSet?: boolean,
        ): { [index: string]: ColumnInfo } {
            const adds = [],
                changes = [],
                columnInfos: ColumnInfo[] = meta.columns || [];
            let removes = [];

            if (noChangeSet || _.isEmpty(columns)) {
                /*jshint sub:true*/
                columns = {};
                columnInfos.forEach(c => (columns[c.id] = c));
                changeSet["reset"] = columnInfos;
            } else {
                columnInfos.forEach(c => {
                    const existing = columns[c.id];
                    if (existing) {
                        const diff = {};
                        let isDiff = false;
                        _.each(c, (v, k) => {
                            if (existing[k] !== v) {
                                isDiff = true;
                                diff[k] = v;
                            }
                        });

                        if (isDiff) {
                            changes.push(_.extend(diff, { id: c.id }));
                        }
                    } else {
                        adds.push(c);
                    }
                });

                // if existing + added <= new length then no removes
                const columnObjs = _.values(columns);
                if (columnObjs.length + adds.length > columnInfos.length) {
                    // this is costly ~On2 but it shouldn't happen often
                    removes = _.reject(columnObjs, function(m) {
                        return _.find(columnInfos, function(c) {
                            return c.id === m.id;
                        });
                    });
                }

                // remove those added
                removes.forEach(r => delete columns[r.id]);
                adds.forEach(a => (columns[a.id] = a));

                if (adds.length) {
                    changeSet["add"] = adds;
                }

                if (changes.length) {
                    changeSet["change"] = changes;
                }

                if (removes && removes.length) {
                    changeSet["remove"] = removes;
                }
            }

            return columns;
        }

        addOrExtendExisting(dict, key, data): boolean {
            //support for guid/datetime/timestamp/timespan
            key = _.isObject(key) ? key.toString() : key;
            const item = dict[key];

            if (item) {
                _.extend(item, data);
                return false;
            } else {
                dict[key] = data;
                return true;
            }
        }

        // TODO: gk - must be a better way to do pivot subscriptions without heirarchy
        addSubscribeFunctions(data: Array<any>) {
            data.forEach(d => {
                const row = this.collection.get(d[this.primaryKey]);
                if (row) {
                    if (row.subscribe) {
                        d.subscribe = (uid, handler) => {
                            row.subscribe(uid, handler);
                        };
                    }

                    if (row.unsubscribe) {
                        d.unsubscribe = uid => {
                            row.unsubscribe(uid);
                        };
                    }
                }
            });

            return data;
        }

        background(): void {
            this.preferBackground = true;
        }

        cleanUp() {
            this.columnDict = {};
            this.columns = DataSet.columnsToCollection([]);
            this.collection.reset();

            if (this.visibilityChangeHandler) {
                document.removeEventListener("visibilitychange", this.visibilityChangeHandler);
            }
        }

        flushUpdates() {
            // get meta data
            if (this.tempDataSet) {
                this.onDataInternal(
                    {
                        breakdownColumns: this.tempDataSet.breakdownColumns,
                        columns: _.values(this.tempDataSet.columnDict),
                        paging: this.tempDataSet.pagingMeta,
                    },
                    this.tempDataSet.collection.models.map(m => m.attributes),
                    this.tempDataSet.forceReset,
                );

                delete this.tempDataSet;
                this.preferBackground = false;
            }
        }

        indexArray(arrData) {
            let i, pos;

            if (this.lazyLoading) {
                pos = this.pagingMeta.startRows;
                for (i = 0; i < arrData.length; i += 1, pos += 1) {
                    arrData[i][DataSet.DEFAULT_ROW_INDEX] = pos;
                }
            } else {
                for (i = 0; i < arrData.length; i += 1) {
                    arrData[i][DataSet.DEFAULT_ROW_INDEX] = i;
                }
            }
        }

        isBackground() {
            return (
                !this.noBackground &&
                (this.preferBackground || (document.hidden && !("FSBL" in window)))
            );
        }

        isEmpty() {
            return (
                !(this.collection && this.collection.models && this.collection.models.length) &&
                (!this.tempDataSet || this.tempDataSet.isEmpty())
            );
        }

        // data  = data['rows']
        mergeData(
            data: Array<object>,
            forceReset: boolean,
            changeSet: ChangeSet,
            noChangeSet?: boolean,
        ) {
            let self = this,
                i,
                key,
                existingRow,
                totalCount,
                addedDict = {},
                changeDict = {},
                adds: Array<object> = [],
                changes: Array<object> = [],
                removeModels: Array<Backbone.Model>;

            if (self.primaryKey === DataSet.DEFAULT_ROW_INDEX) {
                self.indexArray(data);
            }

            if (self.collection.length === 0 && data.length > 0) {
                // use reset event if not appending data
                data.forEach(d => {
                    if (_.isNull(d[self.primaryKey])) {
                        d[self.primaryKey] = "";
                    }
                    adds.push(d);
                });

                changeSet.reset = adds;

                // TODO: the addition of pivot defaults - requires a clone
                if (self.collection.model && self.collection.model.prototype.defaults) {
                    self.collection.reset(adds);
                } else {
                    // faster path without clone
                    self.collection.reset(
                        adds.map(function(m) {
                            const model = new self.collection.model();
                            model.attributes = m;
                            if (model.idAttribute in m) model.id = m[model.idAttribute];
                            return model;
                        }),
                    );
                }

                self.addSubscribeFunctions(adds);
            } else {
                // build change and added dicts
                for (i = 0; i < data.length; i += 1) {
                    // stringify key
                    if (_.isNull(data[i][self.primaryKey])) {
                        data[i][self.primaryKey] = "";
                    }
                    key = data[i][self.primaryKey];
                    key = _.isObject(key) ? key.toString() : key;

                    // check for existing defined as:
                    // no force add AND no already added AND is existing
                    if (!addedDict[key] && self.collection.get(key)) {
                        self.addOrExtendExisting(changeDict, key, data[i]);
                    } else {
                        if (self.addOrExtendExisting(addedDict, key, data[i])) {
                            adds.push(data[i]);
                        }
                    }
                }

                // create added array
                //adds = _.values(addedDict);

                removeModels = [];
                //remove items that are no longer present
                if (forceReset) {
                    removeModels = self.collection.filter(function(mdl) {
                        key = mdl.get(self.primaryKey);
                        return !(_.has(changeDict, key) || _.has(addedDict, key));
                    });
                } else {
                    // remove reduntant data - create remove array

                    totalCount = self.collection.length + adds.length;
                    if (totalCount > self.maxRows) {
                        removeModels = self.collection.models.slice(0, totalCount - self.maxRows);
                    }
                    totalCount = totalCount - removeModels.length;
                    if (totalCount > self.maxRows) {
                        adds = adds.slice(totalCount - self.maxRows);
                    }
                }

                if (removeModels.length) {
                    self.collection.remove(removeModels);
                    if (!noChangeSet) {
                        changeSet["remove"] = _.map(removeModels, function(m) {
                            return m.attributes;
                        });
                    }
                }

                // apply changes and save changed array
                _.each(changeDict, function(row) {
                    let change;

                    key = row[self.primaryKey];
                    existingRow = self.collection.get(key);
                    if (existingRow) {
                        existingRow.set(row);
                        if (!noChangeSet) {
                            change = existingRow.changedAttributes();
                            if (change) {
                                change[self.primaryKey] = key;
                                changes.push(change);
                            }
                        }
                    } else {
                        // change got removed - do nothing
                    }
                });

                // apply adds
                self.collection.add(adds);
                self.addSubscribeFunctions(adds);

                if (!noChangeSet) {
                    if (adds.length) {
                        changeSet["add"] = adds;
                    }

                    if (changes.length) {
                        changeSet["change"] = changes;
                    }

                    if (
                        forceReset &&
                        data.length === 0 &&
                        !(changeSet["add"] || changeSet["change"] || changeSet["remove"])
                    ) {
                        changeSet["reset"] = [];
                    }
                }
            }
        }

        onData(
            meta,
            data: Array<object>,
            forceReset: boolean,
            error?: any,
            performanceMetrics?: PerformanceMetrics,
        ) {
            if (this.isBackground()) {
                if (!this.tempDataSet) {
                    this.tempDataSet = new DataSet({
                        idAttribute: this.idAttribute,
                        collectionFunc: this.collectionFunc,
                        agent: new Agent(),
                        maxRows: this.maxRows,
                        primaryKey: this.primaryKey,
                    });
                    this.tempDataSet.forceReset = false;
                }

                this.tempDataSet.onDataInternal(meta, data, forceReset, error, undefined, true);

                if (forceReset) {
                    this.tempDataSet.forceReset = true;
                }

                return;
            }

            this.onDataInternal(meta, data, forceReset, error, performanceMetrics);
        }

        onDataInternal(
            meta: any,
            data: Array<Record<string, any>>,
            forceReset: boolean,
            error?,
            performanceMetrics?: PerformanceMetrics,
            noChangeSet?: boolean,
        ): void {
            const dataChangeSet = new ChangeSet(this.collection);
            const columnsChangeSet = new ChangeSet(undefined);

            if (meta) {
                if (!_.isUndefined(meta.breakdownColumns)) {
                    this.breakdownColumns = meta.breakdownColumns;
                }

                this.columnDict = DataSet.updateColumns(
                    this.columnDict,
                    meta,
                    columnsChangeSet,
                    noChangeSet,
                );

                //Log.Info("Meta: " + this.updateSetCountToString(columnsChangeSet, this.columns.models));

                // columns removed - remove from collection
                if (columnsChangeSet.remove) {
                    const removes = columnsChangeSet.remove.map((r: any) => r.id);
                    this.collection.models.forEach(m => {
                        removes.forEach(r => {
                            delete m.attributes[r];
                            delete m.changed[r];
                        });
                    });
                }
            }

            this.pagingMeta = meta["paging"];

            // update data
            if (data) {
                this.mergeData(data, forceReset, dataChangeSet, noChangeSet);
                Log.Info(
                    "Data: " + this.updateSetCountToString(dataChangeSet, this.collection.models),
                );
            }

            // no point in calculating changeSets
            if (noChangeSet) return;

            const metaChangeSet = {
                primaryKey: this.primaryKey,
                columns: columnsChangeSet,
            };

            const errorObj = error || undefined;

            if (this.pagingMeta) {
                metaChangeSet["paging"] = this.pagingMeta;
            }

            if (_.isArray(this.breakdownColumns)) {
                metaChangeSet["breakdownColumns"] = this.breakdownColumns;

                if (metaChangeSet["breakdownColumns"] instanceof Backbone.Model) {
                    metaChangeSet["breakdownColumns"] = metaChangeSet["breakdownColumns"].get(
                        "value",
                    );
                }
            }

            // notify subscribers
            if (performanceMetrics) {
                performanceMetrics.trigger("onAgentNotify");
            }

            // gk: almost like a real collection
            metaChangeSet.columns.assign(
                (this.columns = DataSet.columnsToCollection(_.values(this.columnDict))),
            );
            this.agent.notifyAllLegacyChange(
                metaChangeSet,
                dataChangeSet,
                errorObj,
                performanceMetrics,
            );
        }

        onDataRefresh(subscriber: Subscriber) {
            let columnValues = _.values(this.columnDict),
                dataChangeSet = new ChangeSet(this.collection),
                columnsChangeSet = new ChangeSet(this.columns),
                metaChangeSet = {};

            // get meta data
            columnsChangeSet["reset"] = columnValues;

            metaChangeSet = {
                primaryKey: this.primaryKey,
                columns: columnsChangeSet,
            };

            if (_.isArray(this.breakdownColumns)) {
                metaChangeSet["breakdownColumns"] = this.breakdownColumns;

                if (metaChangeSet["breakdownColumns"] instanceof Backbone.Model) {
                    metaChangeSet["breakdownColumns"] = metaChangeSet["breakdownColumns"].get(
                        "value",
                    );
                }
            }

            if (this.pagingMeta) {
                metaChangeSet["paging"] = this.pagingMeta;
            }

            // get data
            dataChangeSet["reset"] = this.addSubscribeFunctions(
                this.collection.map(function(model) {
                    return model.attributes;
                }),
            );

            // if subscriber notify single or all
            if (subscriber) {
                this.agent.notifyChange(subscriber, metaChangeSet, dataChangeSet);
            } else {
                this.agent.notifyAllLegacyChange(metaChangeSet, dataChangeSet);
            }
        }

        updateSetCountToString(set, data): string {
            return (
                (set["add"] ? " +" + set["add"].length : "") +
                (set["remove"] ? " -" + set["remove"].length : "") +
                (set["change"] ? " ~" + set["change"].length : "") +
                (set["reset"] ? " =" + set["reset"].length : "") +
                " ==>" +
                data.length
            );
        }
    }
}
