/// <reference path="TypeBase.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export abstract class Vector extends TypeBase {
        public readonly SIZE;
        protected bufferLength;
        protected values: Array<number>;
        protected indexOffset = 0;

        private index: number[] = null;
        private rangeCache: Array<number | bigint | Long> | null = null;

        protected constructor(
            length: number,
            offset: number,
            qtype: number,
            dataView: DataView,
            size: number,
        ) {
            super(length, offset, qtype, dataView);
            this.SIZE = size;
            this.bufferLength = length;
        }

        public getRange(): Array<number | bigint | Long> {
            return !this.index || this.index.length === 0
                ? this.rangeCache || (this.rangeCache = this.calcRange())
                : [this.hash(this.index[0]), this.hash(this.index[this.index.length - 1])];
        }

        public mergeIndexed(arg: Vector): void {
            if (arg.length >= this.length) throw "Invalid Merge Size";

            this.resetRangeCache();

            // todo potentially transfer

            // copy flat data on top of existing buffer
            new Uint8Array(this.dataView.buffer, this.offset, arg.length * this.SIZE).set(
                new Uint8Array(arg.dataView.buffer, arg.offset, arg.length * this.SIZE),
            );
        }

        public checkIndex(): boolean {
            for (let i = 1; i < this.index.length; i++) {
                if (this.hash(this.index[i - 1]) > this.hash(this.index[i])) return true;
            }

            return false;
        }

        public hasIndex(): boolean {
            return !!this.index;
        }

        public getIndexFromSortedIndex(i: number): number {
            return (this.length + this.index[i] - this.indexOffset) % this.length;
        }

        public getScalarFromSortedIndex(i: number): number | bigint | Long {
            i = (this.indexOffset + i) % this.length;
            if (this.SIZE === 8 && Tools.isIE11 && [9, 15].indexOf(this.qtype) !== -1) {
                return ((this as unknown) as I64).getLegacyScalar(this.index[i]);
            } else return this.getScalar(this.index[i]);
        }

        public findIndex(value: number | bigint | Long): number {
            let compareFn;
            if (this.SIZE !== 8 || [9, 15].indexOf(this.qtype) !== -1) {
                compareFn = (x: number, y): number => x - (this.hash(y) as number);
            } else if (Tools.isIE11) {
                compareFn = (x: Long, y): number => x.compare(this.hash(y) as Long);
            } else {
                compareFn = (x: bigint, y): number => Number(x - (this.hash(y) as bigint));
            }

            return Tools.binarySearch2(value, this.index, compareFn);
        }

        public mergeKeyedPrimary(
            arg: Vector,
            maxRows: number,
            insertIndices: Array<number>,
        ): number {
            this.resetRangeCache();
            if (!this.hasIndex()) {
                this.generateIndex();
            }
            for (let i = 0; i < arg.length; i++) {
                // get arg value and existing index
                let indexIndex = this.findIndex(arg.hash(i));
                // if a new item
                if (indexIndex < 0) {
                    let targetIndex;
                    // if length exceeds max
                    if (this.length === maxRows) {
                        //debugger;
                        // loop around using target index
                        targetIndex = this.indexOffset % maxRows;
                        //console.log("Remove: " + this.getValue(targetIndex).toString())
                        //console.log("Prior", [0,1,2,3].map(i=>this.getValue(i).toString()))
                        // remove old target index
                        const targetIndexIndex = this.findIndex(this.hash(targetIndex));
                        this.index.splice(targetIndexIndex, 1);
                        // if(this.checkIndex()) {
                        //     debugger;
                        // }
                        // if indexIndex is after target index, go back one
                        if (-indexIndex - 1 > targetIndexIndex) {
                            //debugger;
                            indexIndex++;
                        }
                        // insert target in index
                        this.index.splice(-indexIndex - 1, 0, targetIndex);
                        // around we go
                        this.indexOffset = (this.indexOffset + 1) % maxRows;
                    } else {
                        // add on the end
                        targetIndex = this.length;
                        if (targetIndex >= this.bufferLength) {
                            this.reallocate(maxRows);
                        }
                        // update index
                        this.index.splice(-indexIndex - 1, 0, targetIndex);
                        this.length++;
                    }
                    // write value at targetIndex
                    new Uint8Array(
                        this.dataView.buffer,
                        this.offset + targetIndex * this.SIZE,
                        this.SIZE,
                    ).set(
                        new Uint8Array(arg.dataView.buffer, arg.offset + i * this.SIZE, this.SIZE),
                    );
                    //console.log("Add: " + arg.getValue(i).toString())
                    //console.log("After", [0,1,2,3].map(i=>this.getValue(i).toString()))
                    // if(this.checkIndex()) {
                    //     debugger;
                    // }
                    insertIndices.push(targetIndex);
                } else {
                    const valueIndex = this.index[indexIndex];
                    insertIndices.push(valueIndex);
                }
            }
            //console.log("+" + arg.length + ":" + JSON.stringify(_.times(arg.length, i=>arg.getValue(i))));
            //console.log("=" + this.length +"("+this.indexOffset+"):" + JSON.stringify(_.times(this.length, i=>this.getValue(i))));
            return this.indexOffset;
        }

        public mergeKeyed(
            arg: Vector,
            indices: Array<number>,
            indexOffset: number,
            maxRows: number,
        ): void {
            this.resetRangeCache();
            for (let i = 0; i < indices.length; i++) {
                // get arg value and existing index
                const targetIndex = indices[i];

                if (targetIndex === this.length) {
                    // re-allocate if necessary
                    if (targetIndex >= this.bufferLength) {
                        // TODO intelligently up buffer size not jump to max
                        this.reallocate(maxRows);
                    }

                    // update length
                    this.length++;
                }

                // write value at targetIndex
                new Uint8Array(
                    this.dataView.buffer,
                    this.offset + targetIndex * this.SIZE,
                    this.SIZE,
                ).set(new Uint8Array(arg.dataView.buffer, arg.offset + i * this.SIZE, this.SIZE));
            }

            this.indexOffset = indexOffset;

            //console.log("+" + arg.length + ":" + JSON.stringify(_.times(arg.length, i=>arg.getValue(i))));
            //console.log("=" + this.length +"():" + JSON.stringify(_.times(this.length, i=>this.getValue(i))));
        }

        public reallocate(extLength?: number): void {
            this.bufferLength = extLength || this.bufferLength;
            const buffer = new ArrayBuffer(this.bufferLength * this.SIZE);
            new Uint8Array(buffer, 0, this.length * this.SIZE).set(
                new Uint8Array(this.dataView.buffer, this.offset, this.length * this.SIZE),
            );

            this.dataView = new DataView(buffer);
            this.offset = 0;
        }

        public toTypedArray():
            | Uint8Array
            | Int16Array
            | Int32Array
            | Float32Array
            | Float64Array
            | BigInt64Array {
            this.align();

            // TODO: really what we want to do is to return 2 typedarrays
            let TA;
            if (this.SIZE === 1) {
                TA = Uint8Array;
            } else if (this.SIZE === 2) {
                TA = Int16Array;
            } else if (this.SIZE === 4) {
                TA = this.qtype === 8 ? Float32Array : Int32Array;
            } else if (this.SIZE === 8) {
                TA = [9, 15].indexOf(this.qtype) !== -1 ? Float64Array : BigInt64Array;
            }

            if (this.indexOffset === 0)
                return new TA(this.dataView.buffer, this.offset, this.length);

            const typedArray = new TA(this.length);

            // copy indexOffset+ to start
            typedArray.set(
                new TA(
                    this.dataView.buffer,
                    this.indexOffset * this.SIZE,
                    this.length - this.indexOffset,
                ),
            );

            // copy indexOffset- to start
            typedArray.set(
                new TA(this.dataView.buffer, 0, this.indexOffset),
                this.length - this.indexOffset,
            );

            return typedArray;
        }

        protected align(): void {
            if (this.offset % this.SIZE !== 0) {
                this.reallocate();
            }
        }

        protected getByteLocation(i: number): number {
            return this.offset + ((i + this.indexOffset) % this.length) * this.SIZE;
        }

        protected getHashLocation(i: number): number {
            return this.offset + i * this.SIZE;
        }

        private generateIndex(): void {
            this.index = new Array(this.length);
            for (let i = 0; i < this.length; i++) {
                this.index[i] = i;
            }
            // type appropriate sort
            if (this.SIZE !== 8 || [9, 15].indexOf(this.qtype) !== -1) {
                this.index.sort((a, b) => (this.hash(a) as number) - (this.hash(b) as number));
            } else if (Tools.isIE11) {
                this.index.sort((a, b) =>
                    ((this.hash(a) as unknown) as Long).compare(this.hash(b) as Long),
                );
            } else {
                this.index.sort((a, b) =>
                    Number((this.hash(a) as bigint) - (this.hash(b) as bigint)),
                );
            }
        }

        private resetRangeCache(): void {
            this.rangeCache = null;
        }

        public abstract calcRange(): Array<number | bigint | Long>;

        public abstract getScalar(i: number): number | bigint;

        public abstract hash(i: number): number | bigint | Long;
    }
}
