/*global _,$,console,Backbone */
/// <reference path="DataSet.ts"/>
/// <reference path="../lib/ipc/Parse.ts"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class DataSet2 {
        breakdownColumns: Array<string> = [];
        columns = new Backbone.Collection();
        columnDict: { [index: string]: ColumnInfo } = {};
        agent: Agent;
        collection;

        // store collectionFunc so tempDataSet can use it
        collectionFunc;

        idAttribute: string;
        maxRows: number;
        pagingMeta: object;
        primaryKey: string;
        cols: { [index: string]: ipc.TypeBase };

        constructor(options) {
            this.breakdownColumns = [];

            this.agent = options.agent;

            this.idAttribute = options.idAttribute;
            this.maxRows = Number(options.maxRows || 100);
            this.primaryKey = options.primaryKey || DataSet.DEFAULT_ROW_INDEX;
        }

        cleanUp() {
            this.columnDict = {};
            this.columns = DataSet.columnsToCollection([]);
            this.cols = undefined;
        }

        indexArray(arrData) {
            let i;
            for (i = 0; i < arrData.length; i += 1) {
                arrData[i][DataSet.DEFAULT_ROW_INDEX] = i;
            }
        }

        isEmpty() {
            const keys = Object.keys(this.columnDict);
            return (
                !this.cols ||
                keys.length == 0 ||
                !this.cols[keys[0]] ||
                this.cols[keys[0]].length == 0
            );
        }

        onData(
            meta: any,
            cols: { [index: string]: ipc.TypeBase },
            forceReset: boolean,
            error?: any,
            performanceMetrics?: PerformanceMetrics,
        ) {
            let dataChangeSet = new ChangeSet(this.collection),
                columnsChangeSet = new ChangeSet(undefined),
                metaChangeSet = {},
                errorObj = error || undefined;

            // update meta data
            if (meta) {
                if (!_.isUndefined(meta.breakdownColumns)) {
                    this.breakdownColumns = meta.breakdownColumns;
                }

                this.columnDict = DataSet.updateColumns(this.columnDict, meta, columnsChangeSet);
                //Log.Info("Meta: " + this.updateSetCountToString(columnsChangeSet, this.columns.models));
            }

            metaChangeSet = {
                primaryKey: this.primaryKey,
                columns: columnsChangeSet,
            };

            this.pagingMeta = meta["paging"];
            if (this.pagingMeta) {
                metaChangeSet["paging"] = this.pagingMeta;
            }

            if (_.isArray(this.breakdownColumns)) {
                metaChangeSet["breakdownColumns"] = this.breakdownColumns;

                if (metaChangeSet["breakdownColumns"] instanceof Backbone.Model) {
                    metaChangeSet["breakdownColumns"] = metaChangeSet["breakdownColumns"].get(
                        "value",
                    );
                }
            }

            // if force reset or have no data
            if (forceReset || !this.cols) {
                this.cols = cols;
            } else if (this.primaryKey == null || this.primaryKey === "_rowIndex") {
                // if indexed data
                _.each(cols, (c, key) => {
                    const existing = this.cols[key];
                    if (existing) {
                        if (c.length < existing.length) {
                            if (c.qtype !== existing.qtype) {
                                Log.Error(
                                    "Column '" +
                                        key +
                                        "': meta mis-match - cannot merge kdb type " +
                                        c.qtype +
                                        " -> " +
                                        existing.qtype,
                                );
                            } else existing.mergeIndexed(c);
                        } else {
                            // if update length exceeds existing - simply replace it
                            this.cols[key] = c;
                        }
                    } else {
                        // debugger is this a real use-case why
                        debugger;
                    }
                });
            } else {
                // keyed data
                const keyCol = this.cols[this.primaryKey];

                // no merge if length = max-rows
                if (cols[this.primaryKey] && cols[this.primaryKey].length >= this.maxRows) {
                    this.cols = cols;
                } else {
                    // get index from merge key data
                    const keyIndex: Array<number> = [];
                    const indexOffset = keyCol.mergeKeyedPrimary(
                        cols[this.primaryKey],
                        this.maxRows,
                        keyIndex,
                    );

                    _.each(cols, (c, key) => {
                        if (key === this.primaryKey) return;
                        const existing = this.cols[key];
                        if (existing) {
                            existing.mergeKeyed(c, keyIndex, indexOffset, this.maxRows);
                        } else {
                            // debugger is this a real use-case why
                            debugger;
                            this.cols[key] = c;
                        }
                    });
                }
            }

            dataChangeSet["cols"] = this.cols;

            // gk: almost like a real collection
            columnsChangeSet.assign(
                (this.columns = DataSet.columnsToCollection(_.values(this.columnDict))),
            );

            // notify subscribers
            this.agent.notifyAllRawChange(
                metaChangeSet,
                dataChangeSet,
                errorObj,
                performanceMetrics,
            );
        }

        onDataRefresh(subscriber: Subscriber): void {
            const columnValues = _.values(this.columnDict),
                dataChangeSet = new ChangeSet(this.collection),
                columnsChangeSet = new ChangeSet(this.columns);

            // get meta data
            columnsChangeSet["reset"] = columnValues;

            const metaChangeSet = {
                primaryKey: this.primaryKey,
                columns: columnsChangeSet,
            };

            if (_.isArray(this.breakdownColumns)) {
                metaChangeSet["breakdownColumns"] = this.breakdownColumns;

                if (metaChangeSet["breakdownColumns"] instanceof Backbone.Model) {
                    metaChangeSet["breakdownColumns"] = metaChangeSet["breakdownColumns"].get(
                        "value",
                    );
                }
            }

            if (this.pagingMeta) {
                metaChangeSet["paging"] = this.pagingMeta;
            }

            // get data
            dataChangeSet["cols"] = this.cols;

            // if subscriber notify single or all
            if (subscriber) {
                this.agent.notifyChange(subscriber, metaChangeSet, dataChangeSet);
            } else {
                this.agent.notifyAllRawChange(metaChangeSet, dataChangeSet);
            }
        }

        updateSetCountToString(set, data) {
            return (
                (set["add"] ? " +" + set["add"].length : "") +
                (set["remove"] ? " -" + set["remove"].length : "") +
                (set["change"] ? " ~" + set["change"].length : "") +
                (set["reset"] ? " =" + set["reset"].length : "") +
                " ==>" +
                data.length
            );
        }
    }
}
