/// <reference path="I32.ts" />
/// <reference path="Constants.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class QDate extends I32 {
        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Date, dataView);
        }

        public static fromMoment(m: moment.Moment): rDateClass {
            return m === null ? null : new rDateClass(m.valueOf());
        }

        public static fromScalar(value: number): rDateClass {
            return value === null
                ? null
                : new rDateClass(Constants.QEPOCH + value * Constants.MSDAY);
        }

        public static fromString(str: string): rDateClass {
            if (!str) return null;
            str = str.replace(/\./g, "-");
            const m = moment.utc(str);
            return QDate.fromMoment(m);
        }

        public static listToIPC(values: Array<rDateClass>): Uint8Array {
            const size = values.length * 4 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(TypeNum.Date); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => QDate.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        public static toIPC(value: rDateClass): Uint8Array {
            const buffer = Util.createBuffer(5);

            buffer.wb(256 - TypeNum.Date); // type
            QDate.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        public static writeValue(value: rDateClass, wb: (b: number) => void): void {
            Int.writeValue(value === null ? null : QDate.toScalar(value), wb);
        }

        public static toMoment(value: rDateClass): moment.Moment {
            return value === null ? null : moment.tz(value.toString(), "UTC");
        }

        public static toScalar(value: rDateClass): number {
            return value === null ? null : (value.i - Constants.QEPOCH) / Constants.MSDAY;
        }

        public static toString(value: rDateClass, format = "YYYY-MM-DD"): string {
            return value === null ? null : QDate.toMoment(value).format(format);
        }

        public getValue(i: number): number | rDateClass {
            const s = super.getScalar(i);
            return s === I32.NULL
                ? null
                : s === I32.INFINITY_NEGATIVE
                ? -Infinity
                : s === I32.INFINITY_POSITIVE
                ? Infinity
                : new rDateClass(Constants.QEPOCH + s * Constants.MSDAY);
        }
    }
}
