/// <reference path="I32.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Second extends I32 {
        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Second, dataView);
        }

        public static fromMoment(m: moment.Duration): rSecondClass {
            return m === null ? null : new rSecondClass(m.asSeconds());
        }

        public static fromScalar(value: number): rSecondClass {
            return value === null ? null : new rSecondClass(value);
        }

        public static fromString(str: string): rSecondClass {
            str = str.replace("1970-01-01T", "").replace("Z", "");
            const timeParts = str.match(Constants.TIME_PARTS_RX);
            const m = moment.duration(timeParts && timeParts[1] ? timeParts[1] : str);
            return Second.fromMoment(m);
        }

        public static listToIPC(values: Array<rSecondClass>): Uint8Array {
            const size = values.length * 4 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(TypeNum.Second); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Second.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        public static toIPC(value: rSecondClass): Uint8Array {
            const buffer = Util.createBuffer(5);

            buffer.wb(256 - TypeNum.Second); // type
            Second.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        public static toMoment(value: rSecondClass): moment.Duration {
            return value === null ? null : moment.duration(value.i * 1000);
        }

        public static toScalar(value: rSecondClass): number {
            return value === null ? null : value.i;
        }

        public static toString(
            value: rSecondClass,
            format = "HH:mm:ss.SSS",
            options = { trim: false },
        ): string {
            return value === null ? "" : Second.toMoment(value).format(format, options);
        }

        private static writeValue(value: rSecondClass, wb: (b: number) => void): void {
            Int.writeValue(Second.toScalar(value), wb);
        }

        public getValue(i: number): rSecondClass {
            const s = super.getScalar(i);
            return s === I32.NULL ? null : new rSecondClass(s);
        }
    }
}
