/// <reference path="I32.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Minute extends I32 {
        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Minute, dataView);
        }

        public static fromMoment(m: moment.Duration): rMinuteClass {
            return Minute.fromScalar(m.asMinutes());
        }

        public static fromScalar(s: number): rMinuteClass {
            return s === null ? null : new rMinuteClass(s);
        }

        public static fromString(str: string): rMinuteClass {
            str = str.replace("1970-01-01T", "").replace("Z", "");
            const timeParts = str.match(Constants.TIME_PARTS_RX);
            const m = moment.duration(timeParts && timeParts[1] ? timeParts[1] : str);
            return Minute.fromMoment(m);
        }

        public static listToIPC(values: Array<rMinuteClass>): Uint8Array {
            const size = values.length * 4 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(TypeNum.Minute); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Minute.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        public static toMoment(value: rMinuteClass): moment.Duration {
            return value === null ? null : moment.duration(value.i * 60 * 1000);
        }

        public static toScalar(value: rMinuteClass): number {
            return value === null ? null : value.i;
        }

        public static toString(
            value: rMinuteClass,
            format = "HH:mm:ss.SSS",
            options = { trim: false },
        ): string {
            return value === null ? "" : Minute.toMoment(value).format(format, options);
        }

        public static toIPC(value: rMinuteClass): Uint8Array {
            const buffer = Util.createBuffer(5);

            buffer.wb(256 - TypeNum.Minute); // type
            Minute.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        private static writeValue(value: rMinuteClass, wb: (b: number) => void): void {
            Int.writeValue(Minute.toScalar(value), wb);
        }

        public getValue(i: number): rMinuteClass {
            const s = super.getScalar(i);
            return s === I32.NULL ? null : new rMinuteClass(s);
        }
    }
}
