// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Parse {
        public static readonly TYPE_NAMES = [
            "list",
            "boolean",
            "guid",
            undefined,
            "byte",
            "short",
            "int",
            "long",
            "real",
            "float",
            "char",
            "symbol",
            "timestamp",
            "month",
            "date",
            "datetime",
            "timespan",
            "minute",
            "second",
            "time",
        ];

        private static readonly VECTOR_COLUMN_CLASSES = [
            null,
            Boolean,
            Guid,
            null,
            Byte,
            Short,
            Int,
            QLong,
            Float,
            Double,
            Char,
            null,
            Timestamp,
            Month,
            QDate,
            DateTime,
            Timespan,
            Minute,
            Second,
            Time,
        ];

        public static col(
            qStruct: qVector | qSymList | qGenericList | qDict,
            dataView: DataView,
        ): TypeBase | Table | Dict {
            // parse symbols, mixed and unary
            if (qStruct.qtype === -11) {
                const qa = qStruct as qAtom;
                return new Symbol(1, [qa.offset], dataView);
            } else if (qStruct.qtype === 11) {
                // is a symbol
                const sl = qStruct as qSymList;
                return new Symbol(sl.offsets.length, sl.offsets, dataView);
            } else if (qStruct.qtype === 0) {
                // is a generic list
                const l = qStruct as qGenericList;
                return new List(l.values, l.end, dataView);
            } else if (qStruct.qtype === 101) {
                const qa = qStruct as qAtom;
                return new Unary(1, qa.offset, dataView);
            } else if (qStruct.qtype === 98 || qStruct.qtype === 99) {
                const qa = qStruct as qDict;
                return Parse.table(qa, dataView);
            } else if (qStruct.qtype === 101) {
                const qa = qStruct as qAtom;
                return new Unary(1, qa.offset, dataView);
            }

            // get vector class
            const colFn = Parse.VECTOR_COLUMN_CLASSES[Math.abs(qStruct.qtype)];
            if (colFn) {
                if (qStruct.qtype < 0) {
                    const qa = qStruct as qAtom;
                    return new colFn(1, qa.offset, dataView);
                } else {
                    // other
                    const bl = qStruct as qVector;
                    return new colFn(bl.length, bl.offset, dataView);
                }
            }

            return null;
        }

        public static dict(dict: qDict, dataView: DataView): Dict {
            // parse dictionary
            const keys = Parse.col(dict.keys as qVector | qSymList | qGenericList, dataView);
            const values = Parse.col(dict.values as qVector | qSymList | qGenericList, dataView);
            return new Dict(keys.length, keys, values, dict.qtype);
        }

        public static flipDict(data: qDict, dv: DataView): Table {
            const keys = Parse.col(data.keys, dv) as TypeBase;
            const values = Parse.col(data.values, dv) as TypeBase;
            return new Table(
                _.times(keys.length, i => keys.toLegacy(i)),
                _.times(values.length, i => values.getValue(i)),
                dv,
            );
        }

        public static reshape(qObj: qTyped, buffer: ArrayBuffer): Table {
            const t = qObj.qtype;
            const dv = new DataView(buffer);

            if (
                qObj["class"] ===
                    "com.fd.business.querymanager.subscriptions.StreamingSubscription" ||
                qObj["class"] === "210" ||
                qObj["class"] === "207"
            ) {
                return (qObj as unknown) as Table;
            }

            // get tables out first
            const ret = Parse.table(qObj, dv);
            if (ret && ret.qtype === 98) {
                return ret as Table;
            }

            const table = new Table();

            // wrap atomic values in a table
            if (-20 < t && t < 0) {
                const colName = Parse.TYPE_NAMES[-t] || "unknown";
                table.addColumn(
                    colName,
                    t === -11
                        ? new qSymList(true, [(qObj as qAtom).offset])
                        : new qVector(-t, true, 1, (qObj as qAtom).offset),
                    dv,
                );
            } else if (t === 99) {
                // dictionary to table
                const dict = qObj as qDict;
                // cast is safe since getRootTable above handles dictionaries
                table.addColumn("Property", dict.keys as qVector | qSymList | qGenericList, dv);
                table.addColumn("Value", dict.values as qVector | qSymList | qGenericList, dv);
            } else if (t === 10) {
                // string to table
                const bl = qObj as qVector;
                table.addColumn(
                    "string",
                    new qGenericList(true, [bl], bl.offset + bl.length * getTypeSize(bl.qtype)),
                    dv,
                );
            } else if (t === 0) {
                // generic list to table
                const gl = qObj as qGenericList;
                gl.values.forEach((li, i) => {
                    if (li === null) return;
                    const col =
                        (li.qtype === 10
                            ? "string"
                            : Parse.TYPE_NAMES[Math.abs(li.qtype)] || "unknown") + i;

                    // vectorize item
                    if (li.qtype < 0) {
                        table.addColumn(
                            col,
                            li.qtype === -11
                                ? new qSymList(true, [(li as qAtom).offset])
                                : new qVector(-li.qtype, true, 1, (li as qAtom).offset),
                            dv,
                        );
                    } else
                        table.addColumn(
                            col,
                            li.qtype !== 10 && li.qtype <= 19
                                ? (li as qVector)
                                : new qGenericList(
                                      true,
                                      [li],
                                      li.qtype === 10
                                          ? (li as qVector).offset +
                                            getTypeSize(li.qtype) * (li as qVector).length
                                          : (li as qAtom).offset,
                                  ),
                            dv,
                        );
                });
            } else if (t === 11) {
                // sym list to table
                const sl = qObj as qSymList;
                sl.offsets.forEach((offset, i) => {
                    table.addColumn(
                        (Parse.TYPE_NAMES[sl.qtype] || "unknown") + i,
                        new qSymList(true, [offset]),
                        dv,
                    );
                });
            } else if (0 <= t && t < 20) {
                // basic list to table
                const bl = qObj as qVector;
                const sz = getTypeSize(bl.qtype);
                for (let i = 0; i < bl.length; i++) {
                    table.addColumn(
                        (Parse.TYPE_NAMES[bl.qtype] || "unknown") + i,
                        new qVector(bl.qtype, true, 1, bl.offset + sz * i),
                        dv,
                    );
                }
            } else if (t === 101) {
                return table;
            } else {
                throw "Unsupported Root Object";
            }

            return table;
        }

        public static table(qObj: qTyped, dv: DataView): Table | Dict {
            // TODO: better not call flipDict with Dict, keys or values
            if (qObj.qtype === 98) {
                return Parse.flipDict(qObj as qDict, dv);
            } else if (qObj.qtype === 99) {
                const dict = qObj as qDict;
                if (dict.values.qtype === 98) {
                    if (dict.keys.qtype === 98) {
                        const a = Parse.flipDict(dict.keys as qDict, dv);
                        const b = Parse.flipDict(dict.values as qDict, dv);
                        return a.merge(b);
                    } else if (dict.keys.qtype === 11 && (dict.values as qDict).keys.qtype === 11) {
                        const keys = Parse.col((dict.values as qDict).keys as qSymList, dv);
                        const names = ["key"].concat(_.times(keys.length, i => keys.toLegacy(i)));
                        const cols = Parse.col(
                            (dict.values as qDict).values as qVector | qSymList | qGenericList,
                            dv,
                        );
                        return new Table(
                            names,
                            [dict.keys as qSymList].concat(
                                _.times(cols.length, i => cols.getValue(i)),
                            ),
                            dv,
                        );
                    } else {
                        throw "Unsupported nested table/dict";
                    }
                } else return Parse.dict(qObj, dv);
            }
        }
    }
}
