// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Table implements DCDS {
        public qtype: number;
        public "class" = "203";
        public "columns": Array<string> = [];
        public "meta": { [index: string]: number } = {};
        public "cols": { [index: string]: ipc.TypeBase } = {};

        constructor(
            names?: Array<string>,
            columns?: Array<qVector | qSymList | qGenericList>,
            dv?: DataView,
        ) {
            if (names !== undefined) {
                this.columns = names;
                names.forEach((c, i) => {
                    this.meta[c] = columns[i].qtype;
                    this.cols[c] = ipc.Parse.col(columns[i], dv) as Vector | ipc.Symbol;
                });
            }
        }

        static toLegacy(
            meta: { [index: string]: number },
            columns: Array<string>,
            cols: { [index: string]: TypeBase },
        ): DCDS {
            const count: number = columns.length > 0 ? cols[columns[0]].length : 0;
            const rows = new Array(count);

            for (let i = 0; i < count; i++) {
                const obj = new Object();
                columns.forEach(c => (obj[c] = cols[c].toLegacy(i)));
                rows[i] = obj;
            }

            return {
                class: "203",
                columns: columns.slice(0),
                meta: _.clone(meta),
                rows: rows,
            };
        }

        public addColumn(name: string, col: qVector | qSymList | qGenericList, dv: DataView): void {
            this.columns.push(name);
            this.meta[name] = col.qtype;
            this.cols[name] = ipc.Parse.col(col, dv) as Vector | ipc.Symbol | List;
        }

        public addParsedColumn(name: string, col: TypeBase): void {
            this.columns.push(name);
            this.meta[name] = col.qtype;
            this.cols[name] = col;
        }

        public merge(table: Table): Table {
            this.columns = this.columns.concat(table.columns);
            table.columns.forEach(c => {
                this.meta[c] = table.meta[c];
                this.cols[c] = table.cols[c];
            });

            return this;
        }

        public toLegacy(): DCDS {
            return Table.toLegacy(this.meta, this.columns, this.cols);
        }

        public toViewState(): { _type: "table"; value: DCDS } {
            // TODO: not supporting tables as view states
            return {
                _type: "table",
                value: this.toLegacy(),
            };
        }
    }

    Table.prototype.qtype = 98;
}
