/*global _,$,console */
/// <reference path="../classes/log.ts"/>
/// <reference path="../classes/PerformanceMetrics.ts"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export interface Subscriber {
        onData(meta, data, error, performanceMetrics?: PerformanceMetrics);
    }

    export class Agent {
        private legacySubscribers: { [index: string]: Subscriber } = {};
        private rawSubscribers: { [index: string]: Subscriber } = {};

        public notifyAllLegacyChange(
            meta: any,
            data: any,
            error?: any,
            performanceMetrics?: PerformanceMetrics,
        ): void {
            _.each(this.legacySubscribers, s =>
                this.notifyChange(s, meta, data, error, performanceMetrics),
            );
        }

        public notifyAllRawChange(
            meta: any,
            data: any,
            error?: any,
            performanceMetrics?: PerformanceMetrics,
        ): void {
            _.each(this.rawSubscribers, s =>
                this.notifyChange(s, meta, data, error, performanceMetrics),
            );
        }

        public notifyChange(
            subscriber: Subscriber,
            meta,
            data,
            error?,
            performanceMetrics?: PerformanceMetrics,
        ): void {
            this.callSubscriber(
                subscriber,
                subscriber.onData,
                meta,
                data,
                error,
                performanceMetrics,
            );
        }

        public getSubscribers(): string[] {
            return _.union(_.keys(this.legacySubscribers), _.keys(this.rawSubscribers)) || [];
        }

        public subscribe(key: string, subscriber: Subscriber, raw: boolean): boolean {
            const subscribers = raw ? this.rawSubscribers : this.legacySubscribers;
            if (!subscribers[key]) {
                subscribers[key] = subscriber;
                return true;
            }

            return false;
        }

        public subscriberCount(): number {
            return this.subscriberLegacyCount() + this.subscriberRawCount();
        }

        public subscriberLegacyCount(): number {
            return _.keys(this.legacySubscribers).length;
        }

        public subscriberRawCount(): number {
            return _.keys(this.rawSubscribers).length;
        }

        public unsubscribe(key: string): boolean {
            if (this.legacySubscribers[key]) {
                delete this.legacySubscribers[key];
                return true;
            }

            if (this.rawSubscribers[key]) {
                delete this.rawSubscribers[key];
                return true;
            }

            return false;
        }

        public unsubscribeAll(): boolean {
            this.legacySubscribers = {};
            this.rawSubscribers = {};
            return true;
        }

        private callSubscriber(
            subscriber: Subscriber,
            func: Function,
            meta: any,
            data: any,
            error: any,
            performanceMetrics: PerformanceMetrics,
        ): void {
            if (func) {
                try {
                    func.apply(subscriber, Array.prototype.slice.call(arguments, 2));
                } catch (exception) {
                    this.onSubscriberError(subscriber, exception);
                }
            }
        }

        private onSubscriberError(subscriber: Subscriber, exception: any): void {
            Log.Error("ERROR: Subscriber error!", exception);
        }
    }
}
