                options.query = source ? source.get("_updateQueryString") : "";
                options.connection = source ? source.get("_connection") : "";
                options.updateType = source ? source.get("_updateType") : "";
                options.updateAnalytic = source ? source.get("_updateAnalytic") : "";

                options.updateParams = source.bindViewStateToKdbParams(
                    source.get("_updateQueryParams"),
                );

