/// <reference types="backbone" />
/// <reference path="ICommandable.ts" />
/// <reference path="widget.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Popup extends CommandoModel {
        idAttribute = "id";

        constructor(attributes, options, parent: Commandable) {
            super(attributes, options, parent);

            if (!this.get("id")) {
                this.set(
                    {
                        id: Helpers.getGUID(),
                    },
                    {
                        silent: true,
                    },
                );
            }

            this.transformToWidgetCollection();

            this.listenTo(this, "change", this.onMaterialChange.bind(this));
            this.listenTo(
                this.get("widgets"),
                "add remove materialChange",
                this.onMaterialChange.bind(this),
            );
        }

        convertPathsToModels(vsModel): void {
            _.each(this.attributes, (value, key) => {
                if (value instanceof Array) {
                    _.each(value, (itm, i) => {
                        _.each(itm, (v, k) => {
                            if (v._dashboardsType === "viewstate") {
                                value[i][k] = vsModel.getByPath(v.value);
                            }
                        });
                    });
                }
                if (value._dashboardsType === "viewstate") {
                    this.set(key, vsModel.getByPath(value.value), { silent: true });
                }
            });
        }

        defaults(): object {
            return {
                visible: false,
                dialogTitle: "",
                dialogHeight: 0,
                dialogWidth: 0,
                modal: false,
                resizable: false,
                widgets: new WidgetCollection([], {}, this),
                id: Helpers.getGUID(),
            };
        }

        doCommand(path: Array<string>, todos: Array<Todo>): void {
            if (path.length > 0) {
                const key = path.pop();
                if (key === "widgets") {
                    return this.get(key).doCommand(path, todos);
                } else {
                    throw "Unsupported command key";
                }
            } else {
                todos.forEach(t => {
                    let payload;

                    if (_.isArray(t.payload)) {
                        payload = _.find(t.payload, {
                            id: this.id,
                        });
                    } else {
                        payload = t.payload;
                    }

                    if (payload) {
                        this.set(payload);
                    }
                });
            }
        }

        getComponents(): Array<Component> {
            return this.get("widgets").getComponents();
        }

        postCommand(sender: object, command: Command): void {
            if (sender instanceof WidgetCollection) {
                command.path.push("widgets");
            }

            command.path.push(this.id);
            return this.parent.postCommand(this, command);
        }

        onMaterialChange(): void {
            this.trigger("materialChange");
        }

        transformToWidgetCollection(): void {
            let widgets = this.get("widgets");

            if (!(widgets instanceof WidgetCollection)) {
                widgets = new WidgetCollection(widgets ? widgets[0] : [], {}, this);
                this.set("widgets", widgets, { silent: true });
            }
        }
    }

    export class PopupCollection extends ComandoCollection<Popup> {
        descriptor = "Popup";

        commandSet(models): void {
            let added = [];
            let changed = [];
            const oldJSON = this.toJSON();
            let removed = [];

            added = _.differenceBy(models, oldJSON, item => item.id);
            changed = _.filter(models, item => {
                const model = this.get(item.id);

                if (model) {
                    return model.changedAttributes(item);
                }

                return false;
            });
            removed = _.differenceBy(oldJSON, models, item => item.id);

            if (added.length) {
                this.commandAdd(added.map(obj => new Popup(obj, {}, this)));
            }

            _.each(changed, popup => {
                this.postCommand(
                    this,
                    new Command(
                        this.getCommandDescription(CommandOp.Set, popup),
                        [popup.id],
                        [new Todo(CommandOp.Set, "", popup)],
                        [new Todo(CommandOp.Set, "", oldJSON)],
                    ),
                );
            });

            if (removed.length) {
                this.commandRemove(removed.map(m => this.get(m.id) || new Popup(m, {}, this)));
            }
        }

        postInitialize(vsModel): void {
            this.each(popup => {
                popup.convertPathsToModels(vsModel);
            });

            this.trigger("viewStatesParsed");

            this.listenTo(this, "add remove reset", function() {
                this.trigger("materialChange");
            });
        }
    }

    PopupCollection.prototype.model = Popup;
}
