/*g"obals Backbone,_,$,console,Tools,moment */ // eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Helpers {
        public static convertRollingToDate(val, type) {
            let m = Tools.convertValueToMoment(null, type), // get current time in user's timezone
                modifier,
                regexResult,
                time;

            if (!val) {
                val = "NOW";
            }

            if (Tools.getTypeCategory(type) === "duration") {
                m = Tools.convertValueToDuration(m.formatNano("HH:mm:ss.SSS"), type);
            }

            if (val === "T") {
                if (_.includes(["date", "datetime", "timestamp"], type)) {
                    (m as moment.Moment).set({
                        hour: 0,
                        minute: 0,
                        second: 0,
                        millisecond: 0,
                    });
                }
            } else if (val.toString().match(RTTI.REGEX_ROLLING)) {
                // handle T+x, T-x first
                regexResult = val.toString().match(/^T([\+|\-]\d+)/);
                if (regexResult) {
                    modifier = parseInt(regexResult[1], 10);
                    if (modifier) {
                        switch (type) {
                            case "date":
                            case "datetime":
                            case "timestamp":
                                (m as moment.Moment).set({
                                    hour: 0,
                                    minute: 0,
                                    second: 0,
                                    millisecond: 0,
                                });
                                m.add(modifier, "days");
                                break;
                            case "time":
                            case "minute":
                                m.add(modifier, "minutes");
                                break;
                            case "second":
                                m.add(modifier, "seconds");
                                break;
                        }
                    }
                }

                // pass anything after @ to moment's parser
                regexResult = val.toString().match(/(?:@)(.+)$/);
                if (regexResult) {
                    // T+x@hh:mm:ss.SSS etc
                    time = Tools.convertUserInputToMoment(regexResult[1], "HH:mm:ss.SSS");
                    if (time.isValid() && !moment.isDuration(m)) {
                        m.set({
                            hour: time.hour(),
                            minute: time.minute(),
                            second: time.second(),
                            millisecond: time.millisecond(),
                        });

                        if (time.nanosecond()) {
                            m.nanosecond(time.nanosecond());
                        }
                    }
                }
            }

            return m.toDashString();
        }

        public static convertViewSchemaToBackbone(root) {
            const self = this,
                defaultProps = {};

            _.each(_.keys(root), function(key) {
                if (_.isObject(root[key])) {
                    defaultProps[key] = self.convertViewSchemaToBackbone(root[key]);
                } else {
                    defaultProps[key] = root[key];
                }
            });

            return Backbone.Model.extend({
                defaults: function() {
                    const defaultObj = {};
                    _.each(_.keys(defaultProps), function(key) {
                        if (typeof defaultProps[key] === "function") {
                            defaultObj[key] = new defaultProps[key]();
                        } else {
                            defaultObj[key] = defaultProps[key];
                        }
                    });

                    return defaultObj;
                },
            });
        }

        public static getGUID() {
            const S4 = function() {
                return Math.floor((1 + Math.random()) * 0x10000 /* 65536 */)
                    .toString(16)
                    .substring(1);
            };

            return S4() + S4() + "-" + S4() + "-" + S4() + "-" + S4() + "-" + S4() + S4() + S4();
        }

        public static preventBackspaceBack() {
            $(document).on("keydown", function(e) {
                let el,
                    prevent = false;

                if (e.which === 8) {
                    el = e["srcElement"] || e.target;
                    if (
                        (el.tagName.toUpperCase() === "INPUT" &&
                            !_.includes(["RADIO", "CHECKBOX"], el.type.toUpperCase())) ||
                        el.tagName.toUpperCase() === "TEXTAREA" ||
                        $(el).hasClass("nicEdit-main")
                    ) {
                        prevent = el.readOnly || el.disabled;
                    } else {
                        prevent = true;
                    }
                }

                if (prevent) {
                    e.preventDefault();
                }
            });
        }

        public static validateGuid(string) {
            if (_.isString(string)) {
                return string.match(
                    /^[0-9a-f]{8}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{4}-[0-9a-f]{12}$/i,
                );
            }

            return false;
        }
    }
}
