// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Short extends Vector {
        static readonly TYPE_NUM = 5;
        static readonly SHORT_NULL = -32768;

        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Short, dataView, 2);
        }

        static listToIPC(values: Array<number>): Uint8Array {
            const size = values.length * 2 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(Short.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Short.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        static toIPC(value: number): Uint8Array {
            const buffer = Util.createBuffer(3);
            buffer.wb(256 - Short.TYPE_NUM); // type
            Short.writeValue(value, buffer.wb); // value
            return buffer.data;
        }

        static writeValue(value: number, wb: (b: number) => void): void {
            if (value !== null) {
                const ib = new Int16Array(1);
                ib[0] = value;
                _.each(new Uint8Array(ib.buffer), i => wb(i));
            } else {
                _.each([0, 128], i => wb(i));
            }
        }

        public getValue(i: number): number {
            const val = this.getScalar(i);
            return val === -32768
                ? null
                : val === -32767
                ? -Infinity
                : val === 32767
                ? Infinity
                : val;
        }

        public calcRange(): number[] {
            let xMax = Number.NEGATIVE_INFINITY;
            let xMin = Number.POSITIVE_INFINITY;

            for (let i = 0; i < this.length; i += 1) {
                const x = this.getScalar(i);
                if (xMax === Short.SHORT_NULL || (x > xMax && x !== Short.SHORT_NULL)) {
                    xMax = x;
                }
                if (xMin === Short.SHORT_NULL || (x < xMin && x !== Short.SHORT_NULL)) {
                    xMin = x;
                }
            }

            return [xMin, xMax];
        }

        public getScalar(i): number {
            return this.dataView.getInt16(this.getByteLocation(i), true);
        }

        public hash(i: number): number {
            return this.dataView.getInt16(this.getHashLocation(i));
        }
    }
}
