/// <reference path="./GraphNode.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    export class FilterNode extends GraphNode {
        static operatorMap: { [index: string]: string } = {
            AND: " and ",
            OR: " or ",
            equal: "=",
            in: " in ",
            like: " like ",
            // eslint-disable-next-line @typescript-eslint/camelcase
            greater_or_equal: ">=",
            less: "<",
            // eslint-disable-next-line @typescript-eslint/camelcase
            less_or_equal: "<=",
            greater: ">",
            // eslint-disable-next-line @typescript-eslint/camelcase
            is_blank: " is blank",
            // eslint-disable-next-line @typescript-eslint/camelcase
            not_equal: "!=",
            // eslint-disable-next-line @typescript-eslint/camelcase
            not_in: " not in ",
            // eslint-disable-next-line @typescript-eslint/camelcase
            not_like: " not like ",
            // eslint-disable-next-line @typescript-eslint/camelcase
            is_not_blank: " is not blank",
        };

        static dataToIPC(data: any): Uint8Array {
            data = FilterNode.processBindings(data);
            return QuickBase.ipc.Dict.toIPC(
                ["select", "where", "distinct"],
                QuickBase.ipc.List.toIPC([
                    QuickBase.ipc.Dict.toIPC(
                        _.keys(data.select),
                        QuickBase.ipc.Symbol.listToIPC(_.values(data.select)),
                    ),
                    QuickBase.ipc.Util.fromHexString(
                        DataFilter.App.generateIpcHexMessage(data.where).slice(16),
                    ),
                    QuickBase.ipc.Boolean.toIPC(data.distinct),
                ]),
            );
        }

        static getActiveBindingKeys(data: any): Array<string> {
            let keys: Array<string> = [];
            data = data as Data;
            _.each(
                data.where.children,
                c => (keys = FilterNode.getBindingKeysFromArray(keys, _.keys(c))),
            );
            return FilterNode.getBindingKeysFromArray(keys, _.keys(data.select));
        }

        static processBindings(data: any): Data {
            data = _.cloneDeep(data) as Data;

            this.replaceAllBindings(data.where.children);
            this.replaceKeys(data.select);

            return data;
        }
    }
}
