// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class DashClientLib {
        public static applyAnalytic(): void {
            const self = (this as unknown) as QuickBase.DocumentDataModel,
                idAttribute = self.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX,
                successFn = function(data, buffer): void {
                    self.onExecuteSubscriptionSuccess(data, self.get("_forceReset"), buffer);
                },
                errorFn = function(error, ...args): void {
                    self.onError(self, self.attributes, error, "Error", args);
                };

            // set up data set to receive data
            self.dataSet = new DataSet({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: self.agent,
                maxRows: self.get("_maxRows"),
                primaryKey: idAttribute,
            });

            // set up data set to receive data
            self.dataSet2 = new DataSet2({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: self.agent,
                maxRows: self.get("_maxRows"),
                primaryKey: idAttribute,
            });

            self.unset("error");
            self.unset("queryStatus");
            self.updateParameters(true);

            if (self.get("_subscriptionType") === "streaming") {
                self.execute = self.wrapWithErrorHandler(
                    function() {
                        self.onExecuteBegin();
                        self.runWithConnectionCheck(errorFn, function() {
                            self.updateBuilder(function() {
                                self.subscriptionId = DashClient.client.subscribe(
                                    self.get("_maxRows"),
                                    self.get("_selectedAnalytic"),
                                    self.get("_connection"),
                                    successFn,
                                    errorFn,
                                    self.bindViewStateToKdbParams(self.get("_analyticParams")),
                                );
                                Log.Log("Subbd: " + self.subscriptionId);
                                //console.trace();
                            }, errorFn);
                        });
                    },
                    self,
                    self.attributes,
                );
            } else {
                self.execute = self.wrapWithErrorHandler(
                    function() {
                        self.onExecuteBegin();
                        self.runWithConnectionCheck(errorFn, function() {
                            self.updateBuilder(function() {
                                DashClient.runAnalytic2(
                                    self.get("_selectedAnalytic"),
                                    self.get("_connection"),
                                    self.bindViewStateToKdbParams(self.get("_analyticParams")),
                                    successFn,
                                    errorFn,
                                    self.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS,
                                    null,
                                    self.performance,
                                );
                            }, errorFn);
                        });
                    },
                    self,
                    self.attributes,
                );
            }
        }

        public static applyQuery() {
            const self = (this as unknown) as DocumentDataModel,
                idAttribute = self.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX,
                successFn = function(data, buffer) {
                    self.onExecuteSuccess(data, self.get("_forceReset"), buffer);
                    const fn = x => new Date(x);
                    // _.defer(()=>{
                    //     _.each(self.performance.performance, (v,k)=> {
                    //         console.log(k + ": " + moment(fn(v)).format('HH:mm:ss.SSS'));
                    //     });
                    // });
                },
                errorFn = function(error) {
                    self.onError(self, self.attributes, error, "Error", arguments);
                },
                viewStateOptions = self.applyViewStateAttributes();

            // set up data set to receive data
            self.dataSet = new DataSet({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: self.agent,
                maxRows: self.get("_maxRows"),
                primaryKey: idAttribute,
            });

            // set up data set to receive data
            self.dataSet2 = new DataSet2({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: self.agent,
                maxRows: self.get("_maxRows"),
                primaryKey: idAttribute,
            });

            // set defaults
            self.unset("error");
            self.unset("queryStatus");
            self.updateParameters();

            if (self.get("_subscriptionType") === "streaming") {
                self.execute = self.wrapWithErrorHandler(
                    function() {
                        self.onExecuteBegin();

                        let query = self.get("_queryString");
                        const params = self.bindViewStateToKdbParams(self.get("_queryParams"));
                        query = DashClientLib.replaceParameters(query, params);
                        self.subscriptionId = DashClient.client.subscribe(
                            self.get("_maxRows"),
                            query,
                            self.get("_connection"),
                            successFn,
                            errorFn,
                        );
                        Log.Log("Subbd: " + self.subscriptionId);
                        //console.trace();
                    },
                    self,
                    self.attributes,
                );
            } else {
                self.execute = self.wrapWithErrorHandler(
                    function() {
                        self.onExecuteBegin();

                        const query = self.get("_queryString");
                        const params = self.bindViewStateToKdbParams(self.get("_queryParams"));
                        //query = DashClientLib.replaceParameters(query, params);
                        if (query.trim() === "") return;
                        if (self.get("_dataType") === "sql") {
                            DashClient.runAnalytic2(
                                ".s.e",
                                self.get("_connection"),
                                [{ type: "string", value: self.toSQLString(query, params) }],
                                successFn,
                                errorFn,
                                self.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS,
                                undefined,
                                self.performance,
                            );
                        } else {
                            DashClient.getQueryData2(
                                "q",
                                self.get("_connection"),
                                query,
                                params,
                                successFn,
                                errorFn,
                                self.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS,
                                undefined,
                                self.performance,
                            );
                        }
                    },
                    self,
                    self.attributes,
                );
            }
        }

        public static bindViewStateToKdbParams(queryParams) {
            let self = (this as unknown) as QuickBase.DocumentDataModel,
                typeName,
                typeDef,
                paramPath,
                newParams = [];

            //Copy params array so we don't trigger an update event on the model
            $.extend(true, newParams, queryParams);

            //Replace viewstate parameters with values on relevent kdb query parameters
            // gK: wtf does relevent mean!!
            if (newParams && newParams.length > 0) {
                _.each(newParams, function(queryParam) {
                    if (typeof queryParam.value === "string") {
                        paramPath = queryParam.value.match(DocumentDataModel.PARAMETER_RX);
                        if (paramPath) {
                            const viewModel = self
                                .getViewState()
                                .getByPath(
                                    $.trim(paramPath[0].substring(2, paramPath[0].length - 2)),
                                );

                            if (viewModel) {
                                const val = _.isFunction(viewModel.get)
                                    ? viewModel.get("value")
                                    : viewModel["value"];
                                if (val === undefined) {
                                    queryParam.value = null;
                                }

                                queryParam.value =
                                    _.isObject(val) && !_.isArray(val)
                                        ? $.extend(true, {}, val)
                                        : val;
                            }
                        }
                    }

                    // dict types - replace viewstate path with viewstate value
                    if (queryParam.type === "dict" && _.isObject(queryParam.value)) {
                        _.each(_.keys(queryParam.value), function(key) {
                            if (queryParam.value[key] instanceof Backbone.Model) {
                                queryParam.value[key] = {
                                    type: queryParam.value[key].get("_type"),
                                    IsKdbParam: true,
                                    isViewState: true,
                                    name: queryParam.value[key].path,
                                    value: queryParam.value[key].get("value"),
                                };
                            } else if (typeof queryParam.value[key] === "object") {
                                //when no viewstates present (e.g. in ActionTracker, DDM is created without viewstates),
                                // no conversion is required for dictionary values
                                if (_.has(queryParam.value[key], "_isKdbReady")) return;

                                // Update value

                                //var viewModel = self.getViewState().getByPath($.trim(paramPath[0].substring(2, paramPath[0].length - 2))),
                                let viewModel = self
                                    .getViewState()
                                    .getByPath(queryParam.value[key].name);
                                if (viewModel) {
                                    queryParam.value[key].type = viewModel.get("_type");
                                    queryParam.value[key].value = viewModel.get("value");
                                } else if (
                                    typeof queryParam.value[key].value === "string" &&
                                    queryParam.value[key].value.indexOf("<%") === 0
                                ) {
                                    // path to view state is in the value of the object
                                    const queryPath = $.trim(
                                        queryParam.value[key].value.substring(
                                            2,
                                            queryParam.value[key].value.length - 2,
                                        ),
                                    );
                                    viewModel = self.getViewState().getByPath(queryPath);
                                    if (viewModel) {
                                        queryParam.value[key].value = viewModel.get("value");
                                    }
                                }
                            } else if (typeof queryParam.value[key] === "string") {
                                // Generate parameter
                                if (queryParam.value[key].indexOf("<%") === 0) {
                                    const queryPath = $.trim(
                                        queryParam.value[key].substring(
                                            2,
                                            queryParam.value[key].length - 2,
                                        ),
                                    );

                                    const viewModel = self.getViewState().getByPath(queryPath);

                                    if (viewModel) {
                                        queryParam.value[key] = {
                                            type: viewModel.get("_type"),
                                            IsKdbParam: true,
                                            isViewState: true,
                                            name: queryParam.value[key],
                                            value: viewModel.get("value"),
                                        };
                                    }
                                }
                            }
                        });
                    } else {
                        typeName = queryParam.type;
                        typeDef = RTTI.getByName(typeName);
                        if (typeDef) {
                            if (typeDef.name === "dict") {
                                // items dilimeter
                                const dictItems = queryParam.value.split("\u0002");
                                const paramValue = {};

                                _.each(dictItems, function(item) {
                                    // item properies dilimeter
                                    const parts = item.split("\u0001");

                                    if (parts.length === 6) {
                                        // key is name
                                        paramValue[parts[0]] = {
                                            type: parts[1],
                                            IsKdbParam: true,
                                            isViewState: true,
                                            name: parts[0],
                                            value: parts[2],
                                        };
                                    }
                                });

                                queryParam.value = paramValue;
                            }
                        }
                    }
                });
            }

            return newParams;
        }

        public static generatePivotModel(idAttribute, childId, modelType, options) {
            let topSelf = (this as unknown) as DocumentDataModel,
                model;

            if (options.breakdownCols instanceof Backbone.Model) {
                options.breakdownCols = options.breakdownCols.get("value");
            }

            // init empty meta set for wheres transform
            topSelf.meta = {};

            model = Backbone.Model.extend(
                {
                    defaults: {
                        _dataType: topSelf.get("_dataType"),
                        _pivotType: topSelf.get("_pivotType"),
                        _subscriptionType: topSelf.get("_subscriptionType"),
                        _subscriptionInterval: topSelf.get("_subscriptionInterval"),
                    },
                    idAttribute: idAttribute,
                    childId: childId,
                    childModelType: modelType,

                    initialize: function() {
                        this.isDirty = true;
                        this.isPending = false;
                        this.legacyUpdates = [];
                        this.agent = new Agent();
                    },

                    apply() {
                        this.dataSet = new DataSet({
                            idAttribute: childId,
                            collectionFunc: Backbone.Collection.extend({
                                model: modelType,
                            }),
                            agent: this.agent,
                            maxRows: options.maxRows,
                            primaryKey: childId,
                        });
                    },

                    getRoot: function() {
                        return topSelf;
                    },

                    getViewState: function() {
                        return topSelf.getViewState();
                    },

                    execute: function(args) {
                        let self = (this as unknown) as DocumentDataModel,
                            clientExecutePending =
                                !topSelf.clientPivotData ||
                                topSelf.isDirty ||
                                (args && args.intervalQuery),
                            errorFn,
                            executeFn = function(data?, buffer?) {
                                self.onExecuteSuccess(
                                    topSelf.executeClientPivotQuery(topSelf.clientPivotData, {
                                        wheres: options.wheres,
                                    }) as DCDS,
                                    topSelf.get("_forceReset"),
                                    undefined,
                                );
                            };

                        if (!this.wheres) {
                            this.wheres = options.wheres;
                        }

                        self.onExecuteBegin();
                        try {
                            options.topX = null;
                            options.ordering = null;
                            options.queryParams = topSelf.bindViewStateToKdbParams(
                                topSelf.get("_queryParams"),
                            );
                            options.maxRows =
                                topSelf.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS;
                            options.queryString = options.queryString;

                            // options only contain wheres from level 1 on
                            _.each(options.wheres, function(where, i) {
                                const whereKey =
                                    typeof where === "string" ? where : where.columnName;
                                const columnType = topSelf.meta[whereKey];
                                // transform where to object with type information from top meta set
                                options.wheres[i] = {
                                    columnName: whereKey,
                                    columnType: columnType,
                                    columnOperator: "=",
                                    columnValue: self.get(whereKey),
                                };
                            });

                            errorFn = function(error) {
                                topSelf.onError(model, options, error, "Error", arguments);
                            };

                            if (self.get("_pivotType") === "client") {
                                if (clientExecutePending) {
                                    topSelf.runWithConnectionCheck(errorFn, function() {
                                        switch (topSelf.get("_dataType")) {
                                            case "query":
                                                DashClient.getQueryData(
                                                    "kdb",
                                                    topSelf.get("_connection"),
                                                    options.queryString,
                                                    options.queryParams,
                                                    function(data) {
                                                        if (data) {
                                                            topSelf.clientPivotData = data;
                                                            executeFn();
                                                        } else {
                                                            executeFn(data);
                                                        }
                                                    },
                                                    errorFn,
                                                    null,
                                                    null,
                                                    topSelf.performance,
                                                );
                                                break;
                                            case "virtual":
                                                topSelf.executeVirtualQuery(
                                                    topSelf.get("_virtualQueryString"),
                                                    topSelf.get("_virtualParams"),
                                                    function(data) {
                                                        topSelf.clientPivotData = data;
                                                        executeFn();
                                                    },
                                                );
                                                break;
                                        }
                                    });
                                } else {
                                    executeFn();
                                }
                            } else {
                                topSelf.runWithConnectionCheck(errorFn, function() {
                                    DashClientLib.getPivotData(
                                        options,
                                        function(data, buffer) {
                                            //data = ipc.Parse.reshape(data, buffer);
                                            // add to top meta set for where transform above - set from level 0 on
                                            _.extend(topSelf.meta, data["meta"]);

                                            // on success
                                            self.onExecuteSuccess(
                                                data,
                                                topSelf.get("_forceReset"),
                                                buffer,
                                                topSelf.meta,
                                            );
                                        },
                                        errorFn,
                                        topSelf.performance,
                                    );
                                });
                            }
                        } catch (e) {
                            topSelf.onError(model, options, e, "Error", arguments);
                        }
                    },

                    exportPivotData: function(callback, errorCallback) {
                        errorCallback("Pivot export not supported");
                    },

                    /* eslint-disable @typescript-eslint/unbound-method */
                    clearVirtualSubscriptions: _.noop,
                    clearVirtualSubscriptionsAll: _.noop,
                    executeClientPivotQuery: topSelf.executeClientPivotQuery,
                    formatValue: topSelf.formatValue,
                    getViewStateAttribute: topSelf.getViewStateAttribute,
                    isPagingData: topSelf.isPagingData,
                    legacyFlush: topSelf.legacyFlush,
                    legacyUpdate: topSelf.legacyUpdate,
                    logPrefix: topSelf.logPrefix,
                    onComplete: topSelf.onComplete,
                    onExecuteBegin: topSelf.onExecuteBegin,
                    onExecuteSuccess: topSelf.onExecuteSuccess,
                    reset: topSelf.reset,
                    start: topSelf.start,
                    stop: topSelf.stop,
                    subscribe: topSelf.subscribe,
                    triggerCallbacks: _.noop,
                    updateVirtualSubscriptions: _.noop,
                    unsubscribe: topSelf.unsubscribe,
                    /* eslint-enable @typescript-eslint/unbound-method */
                },
                {
                    staticIdAttribute: idAttribute,
                },
            );

            return model;
        }

        public static connectionCollectionSync(method: string, collection: any, options: any) {
            switch (method) {
                case "read":
                    DashClient.runAPI(
                        ".api.conList",
                        "gw",
                        [],
                        data =>
                            options.success(
                                _.map(data.rows, r => {
                                    r.type = r.dbtype;
                                    delete r.type;
                                    r.password = r.pass;
                                    delete r.pass;
                                    return r;
                                }),
                            ),
                        e => options.error(e),
                    );
                    break;
                default:
                    throw "data operation not supported for DocumentCollection";
            }
        }

        public static connectionSync(method: string, collection: any, options: any) {
            let connection,
                model = (this as unknown) as Backbone.Model;

            switch (method) {
                case "update":
                    connection = {};
                    connection.originalName = model.get("originalName");
                case "create":
                    connection = _.extend(connection || {}, {
                        name: model.id,
                        dbtype: model.get("type"),
                        tls: model.get("tls"),
                        host: model.get("host"),
                        port: model.get("port"),
                        user: model.get("user"),
                        pass: model.get("password"),
                        database: "",
                    });

                    // call updateConnections for create & update
                    DashClient.runAPI(
                        ".api.conUpsert",
                        "gw",
                        [
                            { type: "symbol", value: model.id },
                            { type: "string", value: JSON.stringify(connection) },
                        ],
                        data => options.success(data),
                        e => options.error(e),
                    );
                    break;
                case "delete":
                    DashClient.runAPI(
                        ".api.conDelete",
                        "gw",
                        [{ type: "symbol", value: model.id }],
                        () => options.success(),
                        e => options.error(e),
                    );
                    break;
                default:
                    throw "data operation not supported for Document";
            }
        }

        public static documentCollectionSync(method: string, collection: any, options: any) {
            switch (method) {
                case "read":
                    DashClient.runAPI(
                        ".api.dashList",
                        "gw",
                        [],
                        function(data) {
                            const dashboards = [];
                            data.rows.forEach(r => {
                                try {
                                    dashboards.push(
                                        new QuickBase.Document(JSON.parse(r.description)),
                                    );
                                } catch (e) {
                                    // nothin here
                                    Log.Error("Error parsing dashboard description", r);
                                }
                            });

                            if (options.success) {
                                // HAVE TO RETURN MODELS IN ORDER TO WORK!!!
                                options.success(dashboards);
                            }
                        },
                        function(data) {
                            alert("Retrieve Dashboards FAILED");
                            if (options.error) {
                                options.error(data);
                            }
                        },
                    );
                    break;
                default:
                    throw "data operation not supported for DashCollection";
            }
        }

        public static documentSync(method: string, collection: any, options: any) {
            let dashboard,
                model = (this as unknown) as Backbone.Model;

            switch (method) {
                case "read":
                    DashClient.runAPI(
                        ".api.dashRead",
                        "gw",
                        [{ type: "symbol", value: model.id }],
                        function(data) {
                            try {
                                dashboard = JSON.parse(data.rows[0].string);
                            } catch (e) {
                                options.error && options.error(e);
                            }

                            options.success && options.success(dashboard);
                        },
                        function(e) {
                            options.error && options.error(e);
                        },
                    );
                    break;
                case "update":
                    DashClient.runAPI(
                        ".api.dashUpsert",
                        "gw",
                        [
                            { type: "symbol", value: model.id },
                            {
                                type: "string",
                                value: JSON.stringify(
                                    _.omit(model.attributes, ["permissionEntity"]),
                                    null,
                                    2,
                                ),
                            },
                        ],
                        function() {
                            options.success && options.success(model.attributes);
                        },
                        function(e) {
                            options.error && options.error(e);
                        },
                    );

                    break;
                case "delete":
                    DashClient.runAPI(
                        ".api.dashDelete",
                        "gw",
                        [{ type: "symbol", value: model.id }],
                        function() {
                            options.success && options.success();
                        },
                        function(e) {
                            options.error && options.error(e);
                        },
                    );
                    break;
                default:
                    throw "data operation not supported for Document";
            }
        }

        public static replaceParameters(str, paramObjs) {
            let fnRx = /^\{(\[([^\]]+)\])?(.*)\}$/,
                inlineRx = /<%[^<>%]+%>/g,
                paramValueRx,
                matches,
                params,
                paramCount = 0;

            if (typeof str === "string") {
                str = this.strip(str);
                matches = fnRx.exec(str);
                if (matches && matches.length === 4) {
                    if (matches[2]) {
                        params = matches[2].split(";");
                        paramCount += params.length;
                        return (
                            str +
                            "[" +
                            _.map(params, function(p, i) {
                                /*
                            TODO dict
                            var val = paramObjs[i].value;
                            if (val && _.isObject(val)) {
                                var typeDef = RTTI.getByName('dict');
                                return typeDef.toKDBInput(val);
                            }*/

                                const val = RTTI.getByName(paramObjs[i].type).toKDBInput(
                                    paramObjs[i].value,
                                );

                                return val;
                            }).join(";") +
                            "]"
                        );
                    } else {
                        return str + "[]";
                    }
                }

                // replace inline params
                str = str.replace(inlineRx, function(m) {
                    const i = paramCount++;
                    return RTTI.getByName(paramObjs[i].type).toKDBInput(paramObjs[i].value);
                });
            }

            return str;
        }

        public static stop() {
            const self = (this as unknown) as DocumentDataModel;

            if (self.pollingEvent) {
                // destroy polling event
                clearInterval(self.pollingEvent);
                self.pollingEvent = null;
            }

            if (self.subscriptionId) {
                DashClient.client.unsubscribe(self.subscriptionId);
                self.subscriptionId = null;
                self.isPending = false;
            }
        }

        public static getPivotData(options, callback, errorCallback, performance?) {
            DashClient.getQueryData(
                "q",
                options.connection,
                Pivot.constructPivotQuery(options),
                options.queryParams,
                function(data, buffer) {
                    callback(data, buffer);
                },
                errorCallback,
                options.maxRows,
                undefined,
                performance,
            );
        }

        public static parameterRx = new RegExp("<%([^<>%]+?)%>", "g");
        public static DEFAULT_NULL_VALL = -9223372036854776000; //if a value of 0N is returned this comes back from delta client

        public static strip(query) {
            return query && query.length > 0
                ? query.replace(/((^|\s+)\/.*$)|^\s+|\s*\n/gm, " ").trim()
                : query;
        }
    }
}
