/// <reference path="./GraphNode.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    export class DataNode extends GraphNode {
        static partitionParameterToIPC(value: any, ipcType: any): Uint8Array {
            return value instanceof Array
                ? QuickBase.ipc.List.toIPC(
                      value.map((val: any) => ipcType.toIPC(ipc.fromString(ipcType, "" + val))),
                  )
                : ipcType.toIPC(ipc.fromString(ipcType, "" + value));
        }

        static dataToIPC(data: any): Uint8Array {
            data = DataNode.processBindings(data);
            if (data.name && data.params) {
                return QuickBase.ipc.List.toIPC(
                    [QuickBase.ipc.Symbol.toIPC(data.name)].concat(
                        DataNode.recursiveToIPC(data.params),
                    ),
                );
            } else if (data.name && data.partition) {
                const typeNum = QuickBase.RTTI.getByName(data.partition.type).num;
                const ipcType = QuickBase.ipc.Util.getType("-" + typeNum);
                const ipcOperator =
                    data.partition.operator === "equals"
                        ? 8
                        : data.partition.operator === "in"
                        ? 23
                        : 24;
                return QuickBase.ipc.Dict.toIPC(
                    ["table", "operator", "type", "values"],
                    QuickBase.ipc.List.toIPC([
                        QuickBase.ipc.Symbol.toIPC(data.partition.name),
                        new Uint8Array([102, ipcOperator]),
                        QuickBase.ipc.Symbol.toIPC(
                            data.partition.type === "long" ? "year" : data.partition.type,
                        ),
                        data.partition.parameters.length > 1
                            ? QuickBase.ipc.List.toIPC(
                                  data.partition.parameters.map(p =>
                                      DataNode.partitionParameterToIPC(p.value, ipcType),
                                  ),
                              )
                            : DataNode.partitionParameterToIPC(
                                  data.partition.parameters[0].value,
                                  ipcType,
                              ),
                    ]),
                );
            } else {
                return QuickBase.ipc.QString.toIPC(data as string);
            }
        }

        static processBindings(data: any): Data {
            data = _.cloneDeep(data) as Data;

            if (data && data.params) {
                DataNode.replaceAllBindings(data.params);
            } else if (data && data.partition) {
                DataNode.replaceAllBindings(data.partition.parameters);
            }

            return data;
        }

        static getActiveBindingKeys(data: any): Array<string> {
            if ((data as Data).params) {
                return (data as Data).params
                    .filter((p: DataParam) => {
                        return _.isString(p.value) ? GraphNode.VSRx.exec(p.value) : false;
                    })
                    .map((p: DataParam) => (p.value as string).substring(2, p.value.length - 2));
            } else {
                return [];
            }
        }

        static recursiveToIPC(params: any): any {
            return _.flatten(
                _.map(params, (p: DataParam) => {
                    let type = QuickBase.RTTI.getByName(p.type === "list" ? "List" : p.type);
                    const typeNum = type ? type.num : 11; // set undefined or null types to 11 (symbol)
                    if (typeNum === 98) {
                        return DataNode.recursiveToIPC(p.value);
                    } else if (typeNum === 7 && p.value === "") {
                        p.value = "0";
                    }
                    type =
                        QuickBase.ipc.Util.getType(typeNum > 0 ? "-" + typeNum : "" + typeNum) ||
                        QuickBase.ipc.Util.getType("-11");

                    if (typeNum === 0 && p.value instanceof Array) {
                        return type.toIPC(p.value.map(v => QuickBase.ipc.Symbol.toIPC(v)));
                    } else {
                        return type.toIPC(ipc.fromString(type, p.value));
                    }
                }),
            );
        }
    }
}
