// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Util {
        private static typeDict: { [index: string]: any } = {
            "10": QString,
            "0": List,
            "-1": Boolean,
            "-2": Symbol,
            "-4": Byte,
            "-5": Short,
            "-6": Int,
            "-7": QLong,
            "-8": Float,
            "-9": Double,
            "-10": Char,
            "-11": Symbol,
            "-12": Timestamp,
            "-13": Month,
            "-14": QDate,
            "-15": DateTime,
            "-16": Timespan,
            "-17": Minute,
            "-18": Second,
            "-19": Time,
        };

        static getType(typeNum: number | string): any {
            return Util.typeDict[typeNum];
        }

        static mergeTypedArraysUnsafe(a: any, b: any) {
            const c = new a.constructor(a.length + b.length);
            c.set(a);
            c.set(b, a.length);

            return c;
        }

        static fromHexString(hexString: string): Uint8Array {
            return new Uint8Array(
                ((hexString || "").match(/.{1,2}/g) || []).map(byte => parseInt(byte, 16)),
            );
        }

        static toHexString(bytes: Array<number>) {
            let result = "";
            for (let i = 0; i < bytes.length; i++) {
                const str = bytes[i].toString(16);
                result += str.length === 1 ? "0" + str : str;
            }

            return result;
        }

        static pack(bytes: Array<number>) {
            const chars = [];
            for (let i = 0, n = bytes.length; i < n; ) {
                chars.push(((bytes[i++] & 0xff) << 8) | (bytes[i++] & 0xff));
            }
            return String.fromCharCode.apply(null, chars);
        }

        static base64(bytes: Array<number>): string {
            return btoa(String.fromCharCode.apply(null, bytes));
        }

        static toMessage(arr: ArrayLike<number>) {
            const header = Util.createBuffer(8);
            header.wi(1);
            header.wi(arr.length + 8);
            return Util.mergeTypedArraysUnsafe(header.data, arr);
        }

        static createBuffer(size: number) {
            const buffer = {
                data: new Uint8Array(size),
                i: 0,
                wb: (b: number) => {
                    buffer.data[buffer.i++] = b;
                },
                wi: (i: number) => {
                    _.times(4, () => {
                        buffer.wb(i & 255);
                        i = i >> 8;
                    });
                },
            };

            return buffer;
        }
    }
}
