/*global _,$,Backbone,console */
/// <reference path="../templates/combined.ts" />
/// <reference path="ExportTools.ts" />
/// <reference path="../views/ErrorDialog.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class FileExporterView extends Backbone.View<Backbone.Model> {
        DATA_SOURCE_PROPERTY = "Basics.Data";
        EXPORT_PROPERTY_OBJECT = "FileExport";
        EXPORT_PROPERTY_KEYS = [
            "ShowExportCsvButton",
            "ShowExportExcelButton",
            "ShowFullExportButton",
            "ShowClipboard",
            "ShowScreenshot",
        ];

        api: any;
        appViewModel: Backbone.Model;

        isIE: boolean;

        $lnkCopyScreenshot: JQuery;
        $lnkCsvExport: JQuery;
        $lnkDownloadScreenshot: JQuery;
        $lnkExcelExport: JQuery;
        $lnksExport: JQuery;
        $lnkFullExport: JQuery;
        $lnksScreenshot: JQuery;
        $root: JQuery;

        fileExport: (f: any) => void;
        getExportConfig: () => any;
        getSvgNode: () => any;

        SETTINGS: Array<string>;

        constructor(options) {
            super(options);
            // this is used by
            _.extend(this, _.pick(options, this.SETTINGS));

            this.isIE =
                HTMLCanvasElement &&
                HTMLCanvasElement.prototype &&
                (HTMLCanvasElement as any).prototype.msToBlob;

            this.$el.html(T.fileExporter({}));

            this.$root = this.$el.find(">.pnlButtons");
            this.$lnksExport = this.$el.find(".lnksExport");
            this.$lnkCsvExport = this.$el.find("a.lnkCsvExport");
            this.$lnkExcelExport = this.$el.find("a.lnkExcelExport");
            this.$lnkFullExport = this.$el.find("a.lnkFullExport");
            this.$lnksScreenshot = this.$el.find(".lnksScreenshot");
            this.$lnkCopyScreenshot = this.$el.find("a.lnkCopyScreenshot");
            this.$lnkDownloadScreenshot = this.$el.find("a.lnkDownloadScreenshot");

            this.initializeEvents();
        }

        initializeEvents() {
            this.listenTo(
                this.appViewModel,
                "change:" +
                    this.EXPORT_PROPERTY_OBJECT +
                    this.EXPORT_PROPERTY_KEYS.concat(" change:" + this.EXPORT_PROPERTY_OBJECT),
                this.displayChoiceLinkButtons,
            );

            //important arugment is file extension string
            this.$lnkCsvExport.click(_.bind(this.fileExport, this, ".csv"));
            this.$lnkExcelExport.click(_.bind(this.fileExport, this, ".xlsx"));
            this.$lnkFullExport.click(_.bind(this.apiExport, this));
            this.$lnkDownloadScreenshot.click(_.bind(this.downloadImage, this));
        }

        apiExport() {
            let self = this,
                $spinner,
                exportConfig = this.getExportConfig(),
                exportFileName = ExportTools.getExportFileName(
                    exportConfig.fileName,
                    exportConfig.altFileName,
                    "FullExport",
                );

            $spinner = $("<span />")
                .addClass("export-spinner fa fa-spinner fa-pulse")
                .attr("title", t("Download in progress"))
                .appendTo(this.$lnkFullExport);
            self.$lnkFullExport.addClass("export-in-progress");

            self.api.exportData(
                this.appViewModel.get(self.DATA_SOURCE_PROPERTY),
                function() {
                    $spinner.remove();
                    self.$lnkFullExport.removeClass("export-in-progress");
                },
                { exportName: exportFileName },
            );
        }

        displayChoiceLinkButtons() {
            let changedFileExport = false,
                redrawChart = false;

            const settings = this.appViewModel.get(this.EXPORT_PROPERTY_OBJECT);

            if (!settings) {
                return;
            }

            if (settings.changed) {
                changedFileExport = true;
            }

            if (
                settings.ShowExportCsvButton ||
                settings.ShowExportExcelButton ||
                settings.ShowFullExportButton ||
                (settings.ShowScreenshot && !this.isIE)
            ) {
                //redraw chart if links panel was not visible
                if (this.$lnksExport.hasClass("link-disabled") && changedFileExport) {
                    redrawChart = true;
                }

                this.$lnksExport.removeClass("link-disabled");
                this.$lnkCsvExport.toggleClass("link-disabled", !settings.ShowExportCsvButton);
                this.$lnkExcelExport.toggleClass("link-disabled", !settings.ShowExportExcelButton);
                this.$lnkFullExport.toggleClass("link-disabled", !settings.ShowFullExportButton);
                this.$lnkDownloadScreenshot.toggleClass(
                    "link-disabled",
                    !(settings.ShowScreenshot && !this.isIE),
                );
            } else {
                this.$lnksExport.addClass("link-disabled");

                if (changedFileExport) {
                    redrawChart = true;
                }
            }

            if (
                settings.ShowExportCsvButton ||
                settings.ShowExportExcelButton ||
                settings.ShowFullExportButton ||
                (settings.ShowScreenshot && !this.isIE)
            ) {
                this.trigger("enable-links");
            } else {
                this.trigger("disable-links");
            }

            if (redrawChart) {
                this.trigger("redraw-chart");
            }
        }

        downloadImage() {
            let fileName = this.getFileName();

            fileName = _.isString(fileName) && fileName.length ? fileName : "Screenshot";

            fileName += ".png";

            this.generateImage(function(blob) {
                Tools.fileSave(fileName, blob);
            });
        }

        generateImage(callback) {
            let boundingRect,
                canvas = document.createElement("canvas"),
                context = canvas.getContext("2d"),
                height,
                image = new Image(),
                width,
                svgBlob,
                svgBlobUrl,
                svgNode = this.getSvgNode(),
                svgString;

            boundingRect = svgNode.getBoundingClientRect();
            height = Math.round(boundingRect.height);
            width = Math.round(boundingRect.width);

            canvas.width = width;
            canvas.height = height;
            canvas.style.display = "block";

            svgNode.style.width = width;
            svgNode.style.height = height;
            svgString = getSVGString(svgNode);

            if (this.isIE) {
                alert("This feature is not supported in your browser");
                return;
            }

            context.clearRect(0, 0, width, height);
            context.fillStyle = getBackroundColor(svgNode);
            context.fillRect(0, 0, width, height); // copy background colour

            svgBlob = new Blob([svgString], { type: "image/svg+xml;charset=utf-8" });
            svgBlobUrl = URL.createObjectURL(svgBlob);

            image.onload = function() {
                URL.revokeObjectURL(svgBlobUrl);
                context.drawImage(image, 0, 0);
                canvas.toBlob(callback);
            };

            image.src = svgBlobUrl;

            function getSVGString(svgNode) {
                const svgNodeClone = cloneWithStyle(svgNode);

                const serializer = new XMLSerializer();
                let svgString = serializer.serializeToString(svgNodeClone);
                svgString = svgString.replace(
                    /<svg/,
                    '<svg width="' + width + '" height="' + height + '"',
                );
                svgString = svgString.replace(/(\w+)?:?xlink=/g, "xmlns:xlink="); // Fix root xlink without namespace
                svgString = svgString.replace(/NS\d+:href/g, "xlink:href"); // Safari NS namespace fix
                // see https://stackoverflow.com/questions/12625876/how-to-detect-chrome-and-safari-browser-webkit#answer-12625944
                const isChrome =
                    /Chrome/.test(navigator.userAgent) && /Google Inc/.test(navigator.vendor);
                if (isChrome) {
                    // for once it's chrome being annoying
                    let rex;
                    while ((rex = /(<rect[^>]*) style="[^>]*"([^>]*>)/.exec(svgString))) {
                        svgString = svgString.replace(rex[0], rex[1] + rex[2]);
                    }
                }
                return svgString;

                function cloneWithStyle(node) {
                    const source = node,
                        target = node.cloneNode(true);
                    target.style.cssText = getComputedStyle(source);
                    const sourceElements = source.getElementsByTagName("*");
                    const targetElements = target.getElementsByTagName("*");
                    for (let i = sourceElements.length; i--; ) {
                        const sourceElement = sourceElements[i];
                        const targetElement = targetElements[i];
                        targetElement.style.cssText = getComputedStyle(sourceElement);
                    }
                    return target;
                }
            }

            function getComputedStyle(node) {
                // because FF computedStyle.cssText is lazy
                const vanillaComputedStyle = document.defaultView.getComputedStyle(node, "");
                try {
                    return (
                        vanillaComputedStyle.cssText ||
                        Array.from(vanillaComputedStyle)
                            .reduce(function(a, b) {
                                return a + b + ": " + vanillaComputedStyle[b] + "; ";
                            }, "")
                            .trim()
                    );
                } catch (e) {
                    // IE will choke on Array.from but who cares
                    return "";
                }
            }

            function getBackroundColor(svgNode) {
                const color = document.defaultView.getComputedStyle(svgNode)["background-color"];
                return /^rgb\(/.test(color) ? color : getBackroundColor(svgNode.parentNode);
            }
        }

        getButtonSettings() {}

        getFileName() {
            let config = this.getExportConfig(),
                fileName = "";
            if (
                _.isObject(config) &&
                ((_.isArray(config.fileName) && config.fileName.length) || config.altFileName)
            ) {
                fileName = ExportTools.getExportFileName(config.fileName, config.altFileName, "");
            }

            return fileName;
        }

        remove() {
            this.$lnkCsvExport.off();
            this.$lnkExcelExport.off();
            this.$lnkFullExport.off();
            this.$lnkCopyScreenshot.off();
            this.$lnkDownloadScreenshot.off();

            return Backbone.View.prototype.remove.apply(this, arguments);
        }

        //Show error if chart is pivot chart
        showExportError(textMsg) {
            var textMsg = textMsg || "Export of .csv or .xlsx for pivot charts is not supported.";
            ErrorDialog.ShowError(textMsg);
        }
    }

    // setting need to be set on prototype
    FileExporterView.prototype.SETTINGS = ["api", "appViewModel"];
}
