/*global $, Backbone*/
/// <reference path="../templates/combined.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export interface DialogBaseOptions extends Backbone.ViewOptions<Backbone.Model> {
        container?: HTMLElement;
        callback?: (
            o?: any,
            success?: () => void,
            error?: (model: Backbone.Model, msg: string) => void,
        ) => void;
        cancelBtnCallback?: (o?: any) => void;
        dialogTitle: string;
        iconClass: string;
        minHeight?: number;
        userClass?: string;
        renderTemplate?: any;
        requireSuccess?: boolean;
        width?: number;
        height?: number;
        onClose?: () => void;
        onOpen?: () => void;
    }

    export class DialogBase extends Backbone.View<Backbone.Model> {
        protected $dialog: JQuery;
        protected isCancel = false;
        $dialogTitlebar: JQuery;
        $dialogButtonpane: JQuery;
        $dialogRoot: JQuery;

        protected options: DialogBaseOptions;
        private resizeListener;

        constructor(options: DialogBaseOptions) {
            super(options);
            this.options = options;
            this.preRender();
            this.render();
            this.postRender();

            // attach resize listener
            this.resizeListener = this.onResize.bind(this);
            $(window).on("resize", this.resizeListener);
        }

        public dialogClass() {
            return "dialog-base " + (this.options.userClass || "");
        }

        public dialogButtons() {
            return [
                {
                    text: "Cancel",
                    click: () => this.dialogClose(),
                    icons: {
                        primary: "fa fa-times",
                    },
                },
            ];
        }

        public dialogClose() {
            const close = () => {
                if (this.$dialog) {
                    this.$dialog.dialog("close");
                }
            };

            if (this.isCancel) {
                if (this.options.cancelBtnCallback) {
                    this.options.cancelBtnCallback();
                }

                close();
            } else {
                if (this.options.requireSuccess) {
                    if (this.options.callback) {
                        this.options.callback(this.model, close, (model, errorMsg) => {
                            ErrorDialog.ShowError(errorMsg || "Unspecified Error");
                        });
                    }
                } else {
                    if (this.options.callback) {
                        this.options.callback(this.model);
                    }

                    close();
                }
            }
        }

        dialogOpen() {}

        onResize() {
            let appH = $(window).height(),
                maxHeight;

            // posision dialog in middle center of the app div
            this.$dialogRoot.position({
                of: $(window),
            });

            // resize the content div
            maxHeight =
                appH * 0.95 -
                (this.$dialogTitlebar.outerHeight(true) + this.$dialogButtonpane.outerHeight(true));

            this.$dialog.css("max-height", maxHeight);
        }

        preRender() {}

        postRender() {}

        remove() {
            if (this.$dialog) {
                this.$dialog.dialog("destroy");
            }

            this.options = null;
            this.$dialog = null;
            this.$dialogTitlebar = null;
            this.$dialogButtonpane = null;
            this.$dialogRoot = null;

            // remove resize listener
            $(window).off("resize", this.resizeListener);

            // remove from DOM
            return Backbone.View.prototype.remove.apply(this);
        }

        openDialog() {
            this.$el.dialog("open");
        }

        render() {
            const dialogOptions = {};

            if (this.options.container) {
                dialogOptions["appendTo"] = this.options.container;
            }

            this.preRender();
            this.$el.html(
                this.options.renderTemplate ? this.options.renderTemplate() : this.renderTemplate(),
            );

            this.$dialog = this.$el.dialog(
                _.extend(
                    {
                        dialogClass: this.dialogClass(),
                        title: this.options.dialogTitle,
                        resizable: false,
                        minHeight: this.options.minHeight || 300,
                        //'maxWidth': this.maxWidth,
                        width: this.options.width || "57em",
                        height: this.options.height || "auto",
                        modal: true,
                        autoOpen: true,
                        draggable: false,
                        closeOnEscape: true,
                        buttons: this.dialogButtons(),
                        position: { my: "center", at: "center", of: $(window) },
                        open: () => {
                            this.$el
                                .parent()
                                .find(".titleIcon")
                                .remove();
                            // add a dialog icon
                            $(
                                '<i class="titleIcon fa ' + this.options.iconClass + '" />',
                            ).insertBefore(this.$el.parent().find(".ui-dialog-title"));

                            this.$dialogRoot = $(".ui-dialog.dialog-base");
                            //this.$dialogRoot.css("max-height", "95%");
                            this.$dialogTitlebar = this.$dialogRoot.find(".ui-dialog-titlebar");
                            this.$dialogButtonpane = this.$dialogRoot.find(".ui-dialog-buttonpane");

                            this.dialogOpen();
                            //this.onResize();

                            // focus on first button
                            // .ui-dialog-buttonpane > div > button:nth-child(1)
                            this.$dialogButtonpane
                                .find(".ui-dialog-buttonpane > div > button:nth-child(1)")
                                .focus();

                            if (this.options.onOpen) {
                                this.options.onOpen();
                            }
                        },
                        close: () => {
                            this.trigger("closing");

                            if (this.options.onClose) {
                                this.options.onClose();
                            }

                            this.remove();
                        },
                        resize: $.proxy(this.onResize, this),
                    },
                    dialogOptions,
                ),
            );

            return this;
        }

        renderTemplate() {
            return "";
        }
    }
}
