/*global Backbone*/ // eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class AppViewModel extends Backbone.Model implements Commandable {
        constructor(attributes, options) {
            super(attributes, options);
            Document.parent = this;

            this.listenTo(this, "change:selectedDocumentId", this.resetCommands.bind(this));
        }

        defaults(): object {
            return {
                commands: [],
                commandIndex: -1,
                buildMode: true,
                isInspecting: false,
                selectedDocumentId: null,
                selectedScreenId: null,
                selectedWidgetId: null,
                selectedSubComponent: null,
                userConfigurable: false,
                componentsPanel: new Backbone.Model({
                    search: null,
                }),
            };
        }

        goToCommandIndex(index: number): void {
            const commandIndex = this.get("commandIndex");
            const commands = this.get("commands");
            const doc = DocumentCollection.singleton.get(this.get("selectedDocumentId"));

            // TODO NF - hack for long click undo
            if (index === commandIndex) {
                index = index - 1;
            }

            if (index > commandIndex) {
                // for cycle forward
                for (let i = commandIndex + 1; i <= index; i++) {
                    if (i >= 0 && i < commands.length) {
                        doc.doCommand(_.clone(commands[i].path), commands[i].todo);
                    }
                }
            } else if (index < commandIndex) {
                // for cycle back
                for (let i = commandIndex; i > index; i--) {
                    if (i >= 0 && i < commands.length) {
                        // path get altered by doCommand hence clone
                        doc.doCommand(_.clone(commands[i].path), commands[i].undo);
                    }
                }
            }

            this.set("commandIndex", index);
        }

        doCommand(path: Array<string>, todos: Array<Todo>): void {
            // not really doing anything here
        }

        postCommand(sender: object, command: Command): void {
            const commands = this.get("commands");
            const index = this.get("commandIndex");

            // remove any elements in the redo queue
            if (index < commands.length - 1) {
                commands.splice(index + 1, commands.length - index - 1);
            }

            commands.push(command);
            Log.Info("AppViewModel::onCommand", this.get("commands"));
            this.goToCommandIndex(index + 1);
        }

        redo(): void {
            this.goToCommandIndex(this.get("commandIndex") + 1);
        }

        resetCommands(): void {
            this.set("commands", [], { silent: true });
            this.set("commandIndex", -1, { silent: true });
            this.trigger("change:commandIndex");
        }

        undo(): void {
            this.goToCommandIndex(this.get("commandIndex") - 1);
        }
    }
}
