/*global _, $, Backbone, console, Conv, T, Tools */
/// <reference path="../templates/combined.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class HeuristicHelpers {
        static getStringColumns(dataSource): Array<string> {
            const data = dataSource.source ? dataSource.source : dataSource;
            const stringColumns = _.filter(data.dataSet.columns.models, m => {
                const t = Number(m.get("kdbType"));
                return t === 11;
            }).map(c => c.id as string);

            return stringColumns;
        }

        static getScalarColumns(dataSource): Array<string> {
            const data = dataSource.source ? dataSource.source : dataSource;
            const scalarColumns = _.filter(data.dataSet.columns.models, m => {
                const t = Number(m.get("kdbType"));
                return (3 < t && t < 11) || (11 < t && t < 20);
            }).map(c => c.id as string);

            return scalarColumns;
        }

        static getNumericColumns(dataSource): Array<string> {
            const data = dataSource.source ? dataSource.source : dataSource;
            const numericColumns = _.filter(data.dataSet.columns.models, m => {
                const t = Number(m.get("kdbType"));
                return 3 < t && t < 11;
            }).map(c => c.id as string);

            return numericColumns;
        }

        static getTimeColumns(dataSource): Array<string> {
            const data = dataSource.source ? dataSource.source : dataSource;
            const timeColumns = _.filter(data.dataSet.columns.models, m => {
                const t = Number(m.get("kdbType"));
                return 11 < t && t < 20;
            }).map(c => c.id as string);

            return timeColumns;
        }

        static getListColumns(dataSource): Array<string> {
            const data = dataSource.source ? dataSource.source : dataSource;
            const listColumns = _.filter(data.dataSet.columns.models, m => {
                const t = Number(m.get("kdbType"));
                return t === 0;
            }).map(c => c.id as string);

            return listColumns;
        }
    }
}
