/*global DashboardListView*/
/// <reference path="../templates/combined.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class AboutDialog extends Backbone.View<Backbone.Model> {
        $dialog: JQuery = null;
        $nameInput: JQuery = null;
        container: JQuery = null;

        appModel = null;
        viewModel: DocumentViewModel = null;

        constructor(options) {
            super(options);

            this.appModel = options.appModel;
            this.container = options.container;
            this.viewModel = options.viewModel;

            this.el.className = "quick-base window dashboard-name-prompt";
            this.$el.html(
                T.AboutDialog(
                    _.extend(
                        {
                            DeltaClientHost: location.hostname,
                            DeltaClientPort: location.port
                                ? location.port
                                : location.protocol == "https:"
                                ? 443
                                : 80,
                        },
                        this.appModel.toJSON(),
                    ),
                ),
            );

            this.$nameInput = this.$el.find(".name-input");

            this.$dialog = this.$el["dialog"](
                _.extend(
                    {
                        appendTo: this.container,
                        dialogClass: "quick-base about-dialog",
                        title: t("About"),
                        resizable: true,
                        height: 329,
                        width: 470,
                        modal: true,
                        autoOpen: true,
                        draggable: true,
                        open: () => {
                            // add a dialog icon
                            $('<i class="fa fa-info-circle" />').insertBefore(
                                this.$el.parent().find(".ui-dialog-title"),
                            );
                        },
                        close: () => {
                            this.remove();
                        },
                    },
                    options,
                ),
            );
        }

        remove() {
            // remove event handlers
            this.stopListening();
            this.$el.off();

            // remove from dom
            return Backbone.View.prototype.remove.call(this);
        }
    }
}
