// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export abstract class I32 extends Vector {
        protected static readonly NULL = -2147483648;
        protected static readonly INFINITY_NEGATIVE = -2147483647;
        protected static readonly INFINITY_POSITIVE = 2147483647;

        constructor(length: number, offset: number, qtype: number, dataView: DataView) {
            super(length, offset, qtype, dataView, 4);
        }

        public calcRange(): number[] {
            let xMax = Number.NEGATIVE_INFINITY;
            let xMin = Number.POSITIVE_INFINITY;

            for (let i = 0; i < this.length; i += 1) {
                const x = this.getScalar(i);
                if (xMax === I32.NULL || (x > xMax && x !== I32.NULL)) {
                    xMax = x;
                }
                if (xMin === I32.NULL || (x < xMin && x !== I32.NULL)) {
                    xMin = x;
                }
            }

            return [xMin, xMax];
        }

        public getScalar(i: number): number {
            return this.dataView.getInt32(this.getByteLocation(i), true);
        }

        public hash(i: number): number {
            return this.dataView.getInt32(this.getHashLocation(i));
        }
    }
}
