// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    export interface AggData {
        [key: string]: (string | boolean)[];
    }

    export interface XBar {
        num: string;
        col: string;
        cast: string;
    }
    export interface SelectData {
        [key: string]: string | XBar;
    }

    export interface Data {
        name?: string;
        params?: DataParam[];
        select?: SelectData;
        where?: FilterGroup;
        distinct?: boolean;
        update?: UpdateField[];
        filter?: FilterGroup;
        groupBy?: SelectData;
        agg?: AggData;
        fn?: string;
        swap?: boolean;
        left?: string[];
        right?: string[];
        partition?: Partition;
    }

    export interface DataParam {
        isViewState: boolean;
        IsKdbParam: boolean;
        name: string;
        type: string;
        value: string | string[];
    }
    export interface FilterGroup {
        operator: string;
        children: (FilterRule | FilterGroup)[];
    }

    export interface FilterRule {
        active: boolean;
        operator: string;
        propertyName: string;
        propertyType: number;
        type: string;
        value: string;
        value2: string;
    }

    export interface Partition {
        name: string;
        type: string;
        parameters: any[];
        operator: string;
    }

    export interface UpdateField {
        column: string;
        propertyType: number;
        val: string;
    }
}
