/// <reference types="backbone" />
/// <reference path="ICommandable.ts" />
/// <reference path="widget.ts" />
/// <reference path="documentDataModel.ts" />
/// <reference path="documentViewModel.ts" />
/// <reference path="screen.ts" />
/// <reference path="popup.ts" />
/// <reference path="../classes/log.ts"/>

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Document extends Backbone.Model implements Commandable {
        static upgrades = [
            {
                version: "4.3.0",
                fn: function(doc) {
                    _.each(doc.get("viewState").getViewStateList(), function(viewState) {
                        const convertDictItemToModel = function(item) {
                            let path;

                            if (item instanceof DocumentViewModel) {
                                path = item.path;
                            } else if (_.isObject(item)) {
                                path = item.name;
                            } else {
                                path = item;
                            }

                            return doc.get("viewState").getByPath(path);
                        };

                        if (viewState.model.get("_type") === "dict") {
                            // convert paths to models
                            viewState.model.set(
                                "_default",
                                _.mapValues(
                                    viewState.model.get("_default"),
                                    convertDictItemToModel,
                                ),
                            );
                            viewState.model.set(
                                "value",
                                _.mapValues(viewState.model.get("value"), convertDictItemToModel),
                            );
                        }
                    });
                },
            },
            {
                version: "4.4.0",
                fn: function(doc) {
                    Document.reorganizePopupsAndMappers(doc, false);
                },
            },
            {
                version: "4.4.0p7",
                fn: function(doc) {
                    //KXAX-17830: fix conversion of querymappers within popup
                    Document.reorganizePopupsAndMappers(doc, true);
                },
            },
            {
                version: "4.6.0",
                fn: function(doc) {
                    doc.set(
                        "saveViewerState",
                        doc.get("enableSaveViewState") === false ? "disabled" : "enabled",
                    );
                },
            },
            {
                version: "4.6.0P1",
                fn: function(doc) {
                    // KXAX-22061: Reset cookies with new naming conventions
                    Document.renameCookie(doc);
                },
            },
            // make sure to update the version returned by defaults() each release
        ];

        IMMATERIAL_ATTRIBUTES = ["data", "viewState", "wasChanged"];

        sampleData = null;
        SAVE_DELAY = 30000; // 30 sec
        // "disable" autosave
        //SAVE_DELAY = 1000 * 1000;
        saveTimeout = null;
        viewStateInit = false;
        idAttribute: "id";

        // document is the top of the tree
        // and there is only one, hence static
        public static parent: Commandable;

        constructor(attributes: any, options?: any) {
            super(attributes, options);
            const self = this;

            //console.log("Dashboard name init: " + this.get('name'), this.attributes);

            if (!this.get("id")) {
                // generate id
                //console.log("generated id! for dash");
                this.set(
                    {
                        id: Helpers.getGUID(),
                    },
                    {
                        silent: true,
                    },
                );
            }

            this.silentCast("viewState", DocumentViewModel, { root: true });
            this.silentCast("popups", PopupCollection);
            this.silentCast("screens", ScreenCollection);

            if (!(this.get("data") instanceof DocumentDataModel)) {
                this.set(
                    "data",
                    new DocumentDataModel(this.get("data"), null, "", this.get("viewState"), true),
                    { silent: true },
                );
            }

            ////Save any changes to data object of document
            //this.get('data').on('change', function (e) {
            //    this.saveDelayed();
            //});

            this.listenTo(this, "view-state-init", this.onViewStateInit);

            this.listenTo(this, "change", this.determineMaterialChange);

            this.listenTo(this.get("screens"), "change add remove", function(e) {
                self.trigger("change", arguments);
            });

            if (!this.has("showLoadingIndicators")) {
                this.set("showLoadingIndicators", true, { silent: true });
            }
        }

        static reorganizePopupsAndMappers(doc: Document, popupMode: boolean) {
            const popupSettingsMap = {
                "Basics.DialogHeight": "dialogHeight",
                "Basics.DialogTitle": "dialogTitle",
                "Basics.DialogWidth": "dialogWidth",
                "Basics.Modal": "modal",
                "Basics.Resizable": "resizable",
                "Basics.Visible": "visible",
            };

            const processPopup = function(component) {
                // copy popup to document
                const componentSettings = component.get("options"),
                    newSettings = {};

                _.each(popupSettingsMap, function(newKey, oldKey) {
                    const setting = componentSettings.get(oldKey);

                    if (_.isObject(setting) && setting._dashboardsType === "viewstate") {
                        newSettings[newKey] = doc.get("viewState").getByPath(setting.value);
                    } else {
                        newSettings[newKey] = setting;
                    }
                });

                newSettings["widgets"] = component.get("widgets");

                doc.get("popups").add(newSettings);

                //KXAX-17830: fix conversion of querymappers within popup
                if (popupMode) {
                    processWidgets(newSettings["widgets"]);
                }
            };

            const processQueryMapper = function(component) {
                // copy mappings to data source
                const componentMapping = component.get("options").get("Basics.Mapping");
                let dataModel;
                const dataSourceSettings = component.get("options").get("Basics.Data");
                let dataSourceMapping;
                const mapping = {
                    key: undefined,
                    value: undefined,
                    mappings: undefined,
                };
                const subscriptionKey = _.uniqueId("queryMapperUpgrade_");

                if (
                    _.isObject(dataSourceSettings) &&
                    dataSourceSettings._dashboardsType === "data"
                ) {
                    dataModel = doc.get("data").getByPath(dataSourceSettings.value);
                }

                if (dataModel && componentMapping && componentMapping.length) {
                    dataSourceMapping = dataModel.get("_mappings") || {};

                    // subscribe to get column info
                    dataModel.subscribe(
                        subscriptionKey,
                        {
                            onData: function(meta, data) {
                                var meta = meta.columns.collection.map("id");
                                dataModel.unsubscribe(subscriptionKey);

                                mapping.key = meta[0] || "";
                                mapping.value = meta[1] || "";
                                mapping.mappings = dataSourceMapping.mappings || {};

                                // merge component mapping with existing data source mapping
                                _.each(componentMapping, function(m) {
                                    if (m.Viewstate instanceof Backbone.Model) {
                                        mapping.mappings[m.Key] = {
                                            _dashboardsType: "viewstate",
                                            value: m.Viewstate.path,
                                        };
                                    } else {
                                        // m.Viewstate can be undefined in some dashboards so will be removed from object
                                        // setting to empty string ensures the key is retained
                                        mapping.mappings[m.Key] = m.Viewstate || "";
                                    }
                                });

                                dataModel.set("_mappings", mapping);
                                dataModel.apply();
                            },
                        },
                        false,
                    );
                }
            };

            const processWidgets = function(widgetCollection) {
                const widgetsToRemove = [];

                const processComponent = function(component) {
                    const subComponents = component.get("components");
                    const subWidgets = component.get("widgets");

                    if (component && component.get("key") === "Popup") {
                        processPopup(component);
                    } else if (component && component.get("key") === "QueryMapper") {
                        processQueryMapper(component);
                    } else if (subComponents && !subComponents.isEmpty()) {
                        // tabs only - check child components
                        subComponents.each(function(subComponent) {
                            if (
                                component &&
                                _.includes(["Popup", "QueryMapper"], component.get("key"))
                            ) {
                                subComponents.remove(subComponent);
                            }

                            processComponent(subComponent);
                        });
                    } else if (subWidgets && !subWidgets.isEmpty()) {
                        // check child widgets
                        processWidgets(subWidgets);
                    }
                };

                const processWidget = function(widget) {
                    const component = widget.get("component");

                    if (component) {
                        if (_.includes(["Popup", "QueryMapper"], component.get("key"))) {
                            widgetsToRemove.push(widget);
                        }

                        processComponent(component);
                    }
                };

                widgetCollection.each(function(widget) {
                    processWidget(widget);
                });

                // remove query mapper widgets
                widgetCollection.remove(widgetsToRemove);
            };

            doc.get(popupMode ? "popups" : "screens").each(function(x) {
                processWidgets(x.get("widgets"));
            });
        }

        static renameCookie(doc: Document) {
            const cookies = {
                // eslint-disable-next-line prettier/prettier
                "viewStateDialogSize": "dashboard-viewStateDialog-size",
                // eslint-disable-next-line prettier/prettier
                "viewStateDialogPosition": "dashboard-viewStateDialog-position",
                // eslint-disable-next-line prettier/prettier
                "viewStateDialogWeights": "dashboard-viewStateDialog-weights",
                "kx-datadialog-settings": "dashboard-datadialog-settings",
            };

            _.each(cookies, function(newKey, oldKey) {
                const cValue = $.cookie(oldKey);

                $.removeCookie(oldKey, { path: "/" });
                $.cookie(newKey, cValue);
            });
        }

        commandSet(json) {
            const oldJSON = _.mapValues(json, (value, key) => this.previous(key));

            const c = new Command(
                "Set Options",
                [],
                [new Todo(CommandOp.Set, "", json)],
                [new Todo(CommandOp.Set, "", oldJSON)],
            );

            this.postCommand(this, c);
        }

        defaults(): object {
            const viewState = new DocumentViewModel(
                {
                    selected: {
                        _viewType: true,
                        _default: "0",
                        value: "",
                    },
                    ".settings": new Backbone.Model(),
                },
                { root: true },
                this,
            );

            // create screens collection and populate with screens
            const screensCollection = new ScreenCollection([], {}, this);
            screensCollection.add(new Screen({}, {}, screensCollection));

            return {
                id: null,
                name: "My New Dashboard",
                creationDate: null,
                lastUpdateDate: null,
                thumb: null,
                screenDetails: [],
                screens: screensCollection,
                popups: new PopupCollection([], {}, this),
                tags: [],
                // convert kx-darkroom -> dark, kx-light -> light
                dashboardTheme: _.capitalize(
                    ($.cookie("dashboard-theme") || "dark").replace(/(kx-|room)/g, ""),
                ),
                themeSwitchable: true,

                saveTimestamp: null,

                // layout params, equal to screen model
                // undefined properties will be populated when screen is loaded
                //  check DocumentView.js passScreenPropertiesToDocumentModel function
                relativeHeight: undefined,
                rowCount: undefined,
                rowHeight: undefined,
                colCount: 36,
                floatable: true,

                viewState: viewState,
                data: new DocumentDataModel({}, null, "", viewState, true),
                permissionEntity: null,

                worksheetPadding: 10,
                widgetsSpacing: 0,
                borderColor: "000000",
                borderBackground: "000000",
                transparentBackground: true, //--
                borderWidth: 0,
                borderRounding: 0,
                borderSpacing: 0,
                borderShadow: false,
                saveViewerState: "enabled",
                enableShareDashboard: false,
                notifications: null,
                showLoadingIndicators: true,
                unsavedViewerPrompt: false,
                advancedCss: "",
                cssClasses: "",
                version: "4.7.0",
            };
        }

        doCommand(path: Array<string>, todos: Array<Todo>) {
            if (path.length > 0) {
                const key = path.pop();
                if (["screens", "popups", "data", "viewState"].indexOf(key) != -1) {
                    return this.get(key).doCommand(path, todos);
                } else {
                    throw "Unsupported command key";
                }
            } else {
                todos.forEach(t => {
                    this.set(t.payload);
                });
            }
        }

        determineMaterialChange() {
            if (this.changed && !_.isEmpty(_.omit(this.changed, this.IMMATERIAL_ATTRIBUTES))) {
                this.trigger("materialChange");
            }
        }

        getComponents(): Array<Component> {
            return this.get("screens")
                .getComponents()
                .concat(this.get("popups").getComponents());
        }

        onViewStateInit() {
            this.get("viewState").postInitialize();

            const cssClasses = this.get("cssClasses");
            if (cssClasses && cssClasses._dashboardsType === "viewstate") {
                this.set("cssClasses", this.get("viewState").getByPath(cssClasses.value), {
                    silent: true,
                });
            }

            this.get("popups").postInitialize(this.get("viewState"));

            this.viewStateInit = true;
        }

        saveDelayed(errorHandler) {
            const screenDetails = [];

            // clear delayed save
            if (this.saveTimeout) {
                clearTimeout(this.saveTimeout);
            }

            this.saveTimeout = setTimeout(() => this.saveNow(errorHandler), this.SAVE_DELAY);
        }

        saveNow(errorHandler, successHandler?) {
            const self = this,
                screenDetails = [];

            if (_.isEmpty(this.get("screens").models)) {
                errorHandler("Document is empty, skipping save.");
                Log.Warn("Document is empty, skipping save.");
                return;
            }

            // clear delayed save
            if (this.saveTimeout) {
                clearTimeout(this.saveTimeout);
            }

            Log.Info("SAVE", this.id);

            // set updated date
            this.set(
                {
                    lastUpdateDate: new Date().toUTCString(),
                },
                { silent: true },
            );

            _.each(this.get("screens").models, function(m) {
                screenDetails.push({ label: m.get("name"), value: m.id });
            });
            this.set({ screenDetails: screenDetails });

            this.set(
                {
                    thumb:
                        this.get("screens").models.length > 0
                            ? this.get("screens").models[0].get("thumb")
                            : null,
                },
                { silent: true },
            );

            // update document view state
            const attributes = $.extend(true, {}, this.attributes);

            // Convert any docDataModels to their path for notifications
            this.setDataProperties(attributes.notifications);
            this.setDataProperties(attributes.shortcuts);

            attributes.popups.each(popup => {
                // attempting to recursively set widgets causes stack overflow
                popup.set(self.setDataProperties(_.omit(popup.attributes, "widgets")), {
                    silent: true,
                });
            });

            attributes.cssClasses = this.setViewStateProperty(attributes.cssClasses);

            this.save(attributes, {
                error: errorHandler,
                success: function(model) {
                    Log.Info("success", arguments);
                    self.trigger("documentSaved");
                    // mark as not changed
                    self.set("wasChanged", false);
                    if (successHandler) {
                        successHandler(model);
                    }
                },
            });
        }

        silentCast(key, ObjType, options?) {
            if (!(this.get(key) instanceof ObjType)) {
                this.set(key, new ObjType(this.get(key), options, this), { silent: true });
            }
        }

        setDataProperties(root) {
            if (root) {
                for (const key in root) {
                    if (root[key] && root[key].path) {
                        if (root[key] instanceof DocumentDataModel) {
                            root[key] = {
                                _dashboardsType: "data",
                                value: root[key].path,
                            };
                        } else if (root[key] instanceof DocumentViewModel) {
                            root[key] = {
                                _dashboardsType: "viewstate",
                                value: root[key].path,
                            };
                        }
                    } else if (typeof root[key] === "object") {
                        root[key] = this.setDataProperties(root[key]);
                    }
                }
                return root;
            }
        }

        setViewStateProperty(viewstate) {
            if (viewstate instanceof DocumentViewModel) {
                viewstate = {
                    _dashboardsType: "viewstate",
                    value: viewstate.path,
                };
            }

            return viewstate;
        }

        toJSON() {
            // Clone Attributes
            const attributes = $.extend(true, {}, this.attributes);

            // Convert any docDataModels to their path
            this.setDataProperties(attributes.notifications);
            this.setDataProperties(attributes.shortcuts);

            attributes.popups.each(popup => {
                // attempting to recursively set widgets causes stack overflow
                popup.set(this.setDataProperties(_.omit(popup.attributes, "widgets")));
            });

            attributes.cssClasses = this.setViewStateProperty(attributes.cssClasses);
            return attributes;
        }

        toJSONClone() {
            // Clone Attributes
            const attributes = _.cloneDeep(this.attributes);

            // Convert any docDataModels to their path
            this.setDataProperties(attributes.notifications);
            this.setDataProperties(attributes.shortcuts);

            attributes.popups.each(popup => {
                // attempting to recursively set widgets causes stack overflow
                // popup.set(this.setDataProperties(_.omit(popup.attributes, 'widgets')));
                popup.attributes = _.extend(
                    this.setDataProperties(_.omit(popup.attributes, "widgets")),
                    { widgets: popup.get("widgets") },
                );
            });

            attributes.cssClasses = this.setViewStateProperty(attributes.cssClasses);
            return attributes;
        }

        parseCastAndListen(data, key, ObjType, events: string, createFn?: Function) {
            const self = this;

            // if data and data[key] present but not of ObjType
            if (data && data[key] && !(data[key] instanceof ObjType)) {
                // cast
                data[key] = createFn ? createFn(data[key]) : new ObjType(data[key], {}, this);
            }
        }

        postCommand(sender: object, command: Command) {
            if (sender instanceof ScreenCollection) {
                command.path.push("screens");
            } else if (sender instanceof PopupCollection) {
                command.path.push("popups");
            } else if (sender instanceof DocumentDataModel) {
                command.path.push("data");
            } else if (sender instanceof DocumentViewModel) {
                command.path.push("viewState");
            }

            // TODO gk maybe this should be here and document collection should be singleton
            // command.path.push(this.id);

            this.trigger("postCommand");
            this.set("wasChanged", true);

            return Document.parent && Document.parent.postCommand(this, command);
        }

        parse(data) {
            const cssClasses = this.get("cssClasses");
            if (cssClasses && cssClasses._dashboardsType === "viewstate") {
                this.set("cssClasses", this.get("viewState").getByPath(cssClasses.value));
            }

            // cast and listen to models
            this.parseCastAndListen(data, "viewState", DocumentViewModel, "change", function(d) {
                return new DocumentViewModel(
                    d,
                    {
                        root: true,
                    },
                    this,
                );
            });
            this.parseCastAndListen(data, "data", DocumentDataModel, "change", function(d) {
                return new DocumentDataModel(d, null, "", data["viewState"], true);
            });
            this.parseCastAndListen(data, "screens", ScreenCollection, "change add remove");

            this.parseCastAndListen(data, "popups", PopupCollection, "change add remove");

            if (this.viewStateInit) this.get("popups").postInitialize(this.get("viewState"));

            // convert shortcut data sources to models
            _.each(data.shortcuts, shortcut => {
                const source = shortcut.Data;

                if (source && source._dashboardsType === "data") {
                    shortcut.Data = data.data.getByPath(source.value);
                }
            });

            return data;
        }

        upgrade() {
            let i, upgrade;

            if (_.keys(this.attributes).length > 0) {
                // find next available upgrade
                i =
                    QuickBase.Document.upgrades.indexOf(
                        _.find(QuickBase.Document.upgrades, {
                            version: this.get("version"),
                        }),
                    ) + 1;

                for (i; i < QuickBase.Document.upgrades.length; i += 1) {
                    upgrade = QuickBase.Document.upgrades[i];
                    upgrade.fn(this);
                    this.set("version", upgrade.version);
                }
            }
        }
    }

    export class DocumentCollection extends Backbone.Collection<QuickBase.Document> {
        static singleton: DocumentCollection;

        constructor(models, options) {
            super(models, options);
            if (DocumentCollection.singleton) {
                throw "Singleton Error! DocumentCollection() invoked 2x.";
            }
            DocumentCollection.singleton = this;
        }

        getSortedDocuments() {
            // sort by defaultdash, starred, name
            return this.sortBy(function(m) {
                const isDefault = _.includes(m.get("tags"), "defaultdash") ? 1 : 0,
                    isStarred = _.includes(m.get("tags"), "starred") ? 1 : 0;
                return 3 - (isDefault * 2 + isStarred) + m.get("name");
            });
        }

        static requiredAccessLevel = null;
    }

    DocumentCollection.prototype.model = Document;
}
