/// <reference path="TypeBase.ts" />
/// <reference path="../spark-md5.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class List extends TypeBase {
        keyIndex?: { [index: string]: number };
        values: Array<qTyped>;
        end: number;
        indexOffset = 0;

        constructor(values: Array<qTyped>, end: number, dataView: DataView) {
            super(values.length, undefined, 0, dataView);
            this.end = end;
            this.values = values;
        }

        static toIPC(values: Array<Uint8Array>): Uint8Array {
            const size = _.sumBy(values, v => v.length) + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(0); // type(00 - list)
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => {
                _.each(v, vb => buffer.wb(vb));
            });

            return buffer.data;
        }

        // if you are qTyped you are one of these
        // qAtom qVector qGenericList qSymList qDict
        getFirstOffset(i: number): number {
            let qStruct = this.values[i];

            if (qStruct.qtype === 98 || qStruct.qtype === 99) {
                const d = qStruct as qDict;
                qStruct =
                    d.keys.qtype === 98 || d.keys.qtype === 99 ? (d.keys as qDict).keys : d.keys;
            }

            if (qStruct.qtype === 0) {
                // is a generic list
                const l = qStruct as qGenericList;
                return new List(l.values, l.end, this.dataView).getFirstOffset(0);
            } else if (qStruct.qtype === 11) {
                // is a sym list
                const sl = qStruct as qSymList;
                return sl.offsets[0];
            } else if (qStruct.qtype >= 0 && qStruct.qtype <= 19) {
                // other
                const bl = qStruct as qVector;
                return bl.offset;
            }

            const qa = qStruct as qAtom;
            return qa.offset;
        }

        // if you are qTyped you are one of these
        // qAtom qVector qGenericList qSymList qDict
        getEndOffset(i: number): number {
            let qStruct = this.values[i];

            if (qStruct.qtype === 98 || qStruct.qtype === 99) {
                const d = qStruct as qDict;
                qStruct =
                    d.values.qtype === 98 || d.values.qtype === 99
                        ? (d.values as qDict).values
                        : d.values;
            }

            if (qStruct.qtype === 0) {
                // is a generic list
                const l = qStruct as qGenericList;
                return l.end;
            } else if (qStruct.qtype === 11) {
                // is a sym list
                const sl = qStruct as qSymList;
                let end = sl.offsets[sl.offsets.length - 1];
                while (this.dataView.getUint8(end++) !== 0);
                return end;
            } else if (qStruct.qtype === -11) {
                const qa = qStruct as qAtom;
                let end = qa.offset;
                while (this.dataView.getUint8(end++) !== 0);
                return end;
            } else if (qStruct.qtype >= 0 && qStruct.qtype <= 19) {
                // other
                const bl = qStruct as qVector;
                return bl.offset + getTypeSize(bl.qtype) * bl.length;
            }

            const qa = qStruct as qAtom;
            return qa.offset + getTypeSize(Math.abs(qa.qtype));
        }

        //getEndOffset(i: number): number {
        //    return (i < (this.values.length - 1)) ?
        //        this.getFirstOffset(i+1) : this.end;
        //}

        getValue(i: number): any {
            const qCol = this.values[i];
            //return a dictionary when qCol is a qDict
            if (qCol.qtype === 98 || qCol.qtype === 99) {
                return Parse.table(qCol as qDict, this.dataView);
            } else {
                //otherwise return a column
                return Parse.col(qCol as qVector, this.dataView);
            }
        }

        isolate(i: number): string | number {
            let col;
            const start = this.getFirstOffset(i),
                end = this.getEndOffset(i),
                sz = end - start,
                buffer = new ArrayBuffer(sz);

            // copying value from original buffer into new buffer
            const uBuffer = new Uint8Array(buffer, 0, sz);
            uBuffer.set(new Uint8Array(this.dataView.buffer, start, sz));

            this.offsetItem(i, -start);

            if (this.values[i].qtype === 98 || this.values[i].qtype === 99) {
                this.values[i] = Parse.table(this.values[i] as qDict, new DataView(buffer));

                // hash buffer
                return SparkMD5.hash(uBuffer);
            } else {
                this.values[i] = col = Parse.col(this.values[i] as qVector, new DataView(buffer));

                // string - TODO do better
                if (col.qtype === 10) {
                    return this.toLegacy(i);
                }

                // hash single vector values and symbols
                if (
                    col.length === 1 &&
                    col.qtype > -20 &&
                    col.qtype < 20 &&
                    col.qtype !== 0 &&
                    col.qtype !== 11 &&
                    col.qtype !== -11
                ) {
                    debugger;
                    const vec0 = (col as Vector).hash(0);
                    return Long.isLong(vec0) ? vec0.toString() : (vec0 as number);
                }

                // hash buffer
                return SparkMD5.hash(uBuffer);
            }
        }

        offsetItem(i: number, offset: number): void {
            let qStruct = this.values[i];
            let dValues;

            // TODO: this will be more complicated
            if (qStruct.qtype === 98 || qStruct.qtype === 99) {
                const d = qStruct as qDict;
                qStruct =
                    d.keys.qtype === 98 || d.keys.qtype === 99 ? (d.keys as qDict).keys : d.keys;
                dValues =
                    d.values.qtype === 98 || d.values.qtype === 99
                        ? (d.values as qDict).values
                        : d.values;

                this.offsetQTyped(i, offset, dValues);
            }

            this.offsetQTyped(i, offset, qStruct);
        }

        offsetQTyped(i: number, offset: number, qStruct: qTyped): void {
            if (qStruct.qtype === 0) {
                // is a generic list
                const l = qStruct as qGenericList;
                const cl = new List(l.values, l.end, this.dataView);
                for (let i = 0; i < cl.length; i++) cl.offsetItem(i, offset);
            } else if (qStruct.qtype === 11) {
                // is a sym list
                const sl = qStruct as qSymList;
                for (let i = 0; i < sl.offsets.length; i++) sl.offsets[i] += offset;
            } else {
                // could be atom or vector
                (qStruct as qAtom).offset += offset;
            }
        }

        public mergeIndexed(arg: List): void {
            if (arg.length >= this.length) throw "Invalid Merge Size";

            if (arg.length === 0) return;

            // calculate end of arg data
            const argOffsetStart = arg.getFirstOffset(0);
            const argOffsetEnd = arg.end;

            // calculate arg size
            const argSize = argOffsetEnd - argOffsetStart;

            // calculate new buffer size
            const thisFirstOriginal = this.getFirstOffset(arg.values.length);

            // calculate original size
            const thisOriginalSize = this.end - thisFirstOriginal;

            // calculate new totalSize
            const totalSize = argSize + thisOriginalSize;

            // TODO: new buffer creation isn't always necessary
            // could track buffer size and copy only when necessary.
            // store thisOffsetEnd - offset in tmpBufferSize

            // create a new buffer
            const buffer = new ArrayBuffer(totalSize);

            // copy in arg data
            new Uint8Array(buffer, 0, argSize).set(
                new Uint8Array(arg.dataView.buffer, argOffsetStart, argSize),
            );

            // copy in original data
            new Uint8Array(buffer, argSize, thisOriginalSize).set(
                new Uint8Array(this.dataView.buffer, thisFirstOriginal, thisOriginalSize),
            );

            // update offset
            for (let i = 0; i < arg.length; i++) {
                // update each qTyped offset by -argOffsetStart to align to 0
                this.values[i] = arg.values[i];
                this.offsetItem(i, -argOffsetStart);
            }

            // align to zero - correcting for any move of orginal data
            const orginalMove = argSize - thisFirstOriginal;
            for (let i = arg.length; i < this.length; i++) this.offsetItem(i, orginalMove);
            this.end += orginalMove;
            //this.offset = 0;

            // use new databuffer
            this.dataView = new DataView(buffer);
        }

        public mergeKeyedPrimary(arg: List, maxRows: number, insertIndices: Array<number>): number {
            // convert to indexed
            if (this.keyIndex === undefined) {
                const keyIndex = {};
                for (let i = 0; i < this.length; i++) {
                    const hash = this.isolate(i);
                    keyIndex[hash] = i;
                }

                // assign keyIndex
                this.keyIndex = keyIndex;

                // free buffer;
                delete this.dataView;

                // override getValue
                // eslint-disable-next-line @typescript-eslint/unbound-method
                this.getValue = (i): TypeBase | qTyped | Dict =>
                    this.values[(i + this.indexOffset) % this.length];
            }

            for (let i = 0; i < arg.length; i++) {
                // get arg value and existing index
                const argHash = arg.isolate(i);
                const argValue = arg.values[i];
                const valueIndex = this.keyIndex[argHash];

                // if a new item
                if (valueIndex === undefined) {
                    // if length exceeds max
                    if (this.length === maxRows) {
                        // loop around using target index
                        const targetIndex = this.indexOffset % maxRows;
                        this.keyIndex[argHash] = targetIndex;
                        this.values[targetIndex] = argValue;
                        insertIndices.push(targetIndex);
                        this.indexOffset++;
                    } else {
                        // add value
                        this.keyIndex[argHash] = this.length;
                        insertIndices.push(this.length);
                        this.values.push(argValue);
                        this.length++;
                    }
                } else {
                    insertIndices.push(valueIndex);
                }
            }

            //console.log("+" + arg.length + ":" + JSON.stringify(_.times(arg.length, i=>arg.getValue(i))));
            //console.log("=" + this.length +"("+this.indexOffset+"):" + JSON.stringify(_.times(this.length, i=>this.getValue(i))));
            return this.indexOffset;
        }

        public mergeKeyed(
            arg: List,
            indices: Array<number>,
            indexOffset: number,
            maxRows: number,
        ): void {
            // convert to indexed
            if (this.values === undefined) {
                const values = [];
                for (let i = 0; i < this.length; i++) {
                    const str = this.getValue(i);
                    values.push(str);
                }

                // assign keyIndex and values
                this.values = values;

                // free buffer and offsets;
                delete this.dataView;

                // eslint-disable-next-line @typescript-eslint/unbound-method
                this.getValue = (i): TypeBase | qTyped | Dict =>
                    this.values[(i + this.indexOffset) % this.length];
            }

            for (let i = 0; i < indices.length; i++) {
                // get arg value and existing index
                const argValue = arg.getValue(i);
                const targetIndex = indices[i];

                if (targetIndex === this.length) {
                    this.values.push(argValue);
                    this.length++;
                } else if (targetIndex > this.length) {
                    debugger;
                } else this.values[targetIndex] = argValue;
            }

            this.indexOffset = indexOffset;
        }

        toLegacy(i: number): any {
            const qCol = this.values[i];
            const dataView = this.dataView;

            if (qCol.qtype === 10) {
                // string / char list
                const bl = qCol as qVector;
                const s = [];
                for (i = 0; i < bl.length; i++) s.push(dataView.getUint8(bl.offset + i));
                return Char.u16u8(s);
            } else if (-20 < qCol.qtype && qCol.qtype < 0) {
                // atoms
                return Parse.col(qCol as qVector, dataView).toLegacy(0);
            } else if (0 <= qCol.qtype && qCol.qtype < 20) {
                // lists
                const col = Parse.col(qCol as qVector, dataView) as Vector;
                return _.times(col.length).map(i => col.toLegacy(i));
            } else if (qCol.qtype === 98 || qCol.qtype === 99) {
                // table
                return Parse.table(qCol as qDict, dataView).toLegacy();
            } else if (qCol.qtype === 101) {
                return null;
            }
        }

        public toViewState(i) {
            const qCol = this.values[i];
            return qCol.qtype === 99 || qCol.qtype === 98
                ? Parse.table(qCol as qDict, this.dataView).toViewState()
                : {
                      _type: qCol.qtype === 10 ? "string" : Parse.TYPE_NAMES[Math.abs(qCol.qtype)],
                      value: Tools.convertKDBToViewstateValue(this.toLegacy(i)),
                  };
        }
    }
}
