/*global _,$,Backbone,console,moment*/
/// <reference path="RawDataFormatter.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class FileExportDataGenerator {
        private static EXCEL_INCOMPATIBLE_FORMATS = [
            "",
            "SmartNumber",
            "SmartNumber.0",
            "SmartNumber.00",
            "SmartNumber.000",
            "SmartNumber.0000",
            "SmartNumber.00000",
            "SmartNumber.000000",
            "SmartNumber.0000000",
            "SmartNumber.00000000",
            "SmartNumber.000000000",
            "SmartNumber.0000000000",
            "HH:mm",
            "HH:mm:ss",
            "HH:mm:ss.SSS",
            "HH:mm:ss.SSSSSS",
            "HH:mm:ss.SSSSSSSSS",
            "YYYY-MM-DD HH:mm:ss.SSS",
            "YYYY-MM-DD HH:mm:ss.SSSSSS",
            "YYYY-MM-DD HH:mm:ss.SSSSSSSSS",
            "mm:ss",
            "mm:ss.SSS",
            "Mm:ss.SSSSSS",
            "mm:ss.SSSSSSSSS",
            "ss",
            "ss.SSS",
            "ss.SSSSSS",
            "ss.SSSSSSSSS",
        ];

        /*
        * @return {obj} -
          {
            data: Array - aray of objects with cell properites 
            columnHeaders: Object - colomn meta data with fomat, 
                type, isStringType prerties for each colums  
          }  
        * Must match FileExport class expected format
        */
        public static getFormData(dataCollection, metaData, bookType) {
            const returnDataRows = [];

            //contains list of column metadata for
            const columnHeaders = [];

            //Add column headers as a first exported row
            returnDataRows.push(
                _.map(metaData, function(header, colIndex) {
                    //check if name property exists
                    const columnName = header.colName ? header.colName : header.colId;

                    // If dataCollection is empty (chartjs multidatasource with noResults returned)
                    if (!dataCollection.length) {
                        columnHeaders[colIndex] = {
                            isStringType: false,
                            format: "",
                            type: "String",
                        };
                    }

                    return {
                        formattedVal: columnName,
                        value: columnName,
                        type: "String",
                    };
                }),
            );
            //ReturnDataRows.push(metaData);

            //Get data and create table rows
            for (var i = 0; i < dataCollection.length; i++) {
                var tableRow = [];
                //Iterate throught each column header
                _.each(metaData, function(header, colIndex) {
                    let cellVal, formattedVal;
                    //get each cell from row
                    cellVal = dataCollection[i][header.colId];

                    if (cellVal !== undefined && cellVal !== null) {
                        formattedVal = FileExportDataGenerator.generateExportData(
                            header,
                            cellVal,
                            colIndex,
                            bookType,
                            columnHeaders,
                        );

                        tableRow.push(formattedVal);
                    } else {
                        //all indexes of column headers must be filled
                        columnHeaders[colIndex] = columnHeaders[colIndex] || {
                            isStringType: false,
                            format: header.dataFormat,
                            type: header.dataType,
                        };
                        // Prevent removing empty comma in regex
                        tableRow.push({
                            formattedVal: "",
                            value: "",
                            type: null,
                        });
                        console.log("Cell value is either undefined or null: " + header.colId);
                    }
                });

                returnDataRows.push(tableRow);
            }
            //get serted indexes of all columns and find
            return {
                data: returnDataRows,
                columnHeaders: columnHeaders,
            };
        }

        /* Validate and convert value it to appropriate format for XLSX exporter
         * !NOTE XLSX is not able to handle comlex formatter options for Time/DateTime
         *   export so export those as string after applying formatter
         * @param header {obj} - metaData for current col
         * @param header.dataFormat {String} - format for column
         * @param header.dataKdbType {Number} - value of kdb type
         * @param header.dataType {String} - xAxis has options to specify col type
         * @param header.hideTrailingZeroes {Boolean} - property found in Grids only
         * @param header.prefix {String} - prefix to be appended to value
         * @param header.suffix {String} - suffix to be appended to value
         * @param value {any} - cell value can be moment obj, String or Number
         * @param colIndex {Number} - index number of current row of the cell
         * @param bookType {String} - .csv exported data is treated as a
         *   string by XLSX exporter some conversion must be passed as string
         *   eg. moment formatted dates
         * @return returnCellObject {obj}
         */
        private static generateExportData(header, value, colIndex, bookType, columnHeaders) {
            let index = colIndex,
                cellFormat, //set format for individual cells used in "%"
                format = header.dataFormat,
                colKdbType = header.dataKdbType,
                dataType = header.dataType,
                hideTrailingZeroes = header.hideTrailingZeroes,
                prefix = header.prefix,
                suffix = header.suffix,
                returnCellObject = {},
                //for formats incompatible with XLSX
                isFormatXLSXCompatible = FileExportDataGenerator.checkIfFormatXLSXCompatible(
                    format,
                ),
                exportAsString =
                    dataType !== "Boolean" && (!format || !isFormatXLSXCompatible) ? true : false,
                formattedValue;

            /* columnHeaders used by XLSX to get metaData of cells, index is linked to index of the cell in the row */
            columnHeaders[index] = columnHeaders[index] || {
                isStringType: false,
                format: format,
                type: dataType,
            };

            if (value !== "") {
                /* Formats are applied based on KDB DATA TYPE of the value and only uses
                user selected dataType to distingush between Time and DateTime WHEN applying formats */
                formattedValue = RawDataFormatter.applyFormatToValue(
                    value,
                    format,
                    dataType,
                    colKdbType,
                    hideTrailingZeroes,
                );

                // Escape special characters
                if (bookType === "csv" && /^[-\+=@]/.test(formattedValue)) {
                    formattedValue = "'" + formattedValue;
                }
                //!NOTE resolve suffix conflicts in config generator
                //May need to update prefix suffix policy
                if (exportAsString) {
                    returnCellObject = {
                        formattedVal: formattedValue,
                        value: prefix + formattedValue + suffix,
                    };

                    //mark to be exported as string
                    columnHeaders[index].type = "String";
                } else if (dataType === "Boolean") {
                    //No further manipulatons required
                    returnCellObject = {
                        formattedVal: formattedValue,
                        value: formattedValue === "true" ? true : false,
                    };
                    /* Explicitly convert number to percentage if % suffix is present
                     * !NOTE to avoid this explicitly set col type to "String" in
                     * component's config generator tools See Multichart vs Datagrid*/
                } else if (dataType === "Number" && suffix === "%") {
                    //check how value will look like after converted to %
                    const pecentageVal = Number(value) * 100;

                    //use decimal places
                    const percentageStr =
                        pecentageVal - Math.floor(pecentageVal) > 0
                            ? pecentageVal.toFixed(2)
                            : pecentageVal.toFixed(0);

                    formattedValue = pecentageVal + "%";

                    if (percentageStr.indexOf(".") !== -1) {
                        format = "0.00%";
                    } else {
                        format = "0%";
                    }

                    //Prevent excell from cutting excess of 16 chars
                    if (percentageStr.length > 16) {
                        columnHeaders[index].isStringType = true;
                    }
                    returnCellObject = {
                        formattedVal: formattedValue,
                        value: Number(value), //has to be number
                        format: format,
                    };

                    //Suffix and prefix can only be applied to String type or Number
                } else if (
                    !_.includes(["Date", "Time", "DateTime", "Boolean"], dataType) &&
                    (prefix || suffix)
                ) {
                    //prefix/suffix must already contain space before and after
                    returnCellObject = {
                        formattedVal: formattedValue,
                        value: prefix + formattedValue + suffix,
                    };
                    columnHeaders[index].type = "String";
                } else {
                    returnCellObject = FileExportDataGenerator._convertToExportFormat(
                        index,
                        formattedValue,
                        value,
                        dataType,
                        format,
                        bookType,
                        hideTrailingZeroes,
                        columnHeaders,
                    );
                }

                //if value is ""
            } else {
                returnCellObject = {
                    formattedVal: "",
                    value: "",
                    type: "String", //mark only cell
                };
            }

            return returnCellObject;
        }

        /* Function checks if string is XLSX compatible */
        private static checkIfFormatXLSXCompatible(format) {
            let isCompatible = true;
            if (
                _.includes(FileExportDataGenerator.EXCEL_INCOMPATIBLE_FORMATS, format) ||
                format.indexOf(".SSS") !== -1
            ) {
                isCompatible = false;
            }
            return isCompatible;
        }
        /*
         * Convert values for XLSX to be exported as their selectd type if possible.
         * @param index {Number} - index of culumn
         * @param formattedValue {String} - value with formatter applied
         * @param value {any} - raw cell val
         * @param type {String} - column type
         * @param format {String} - format applied to raw value
         * @param bookType {String} - csv, xlsx or xls
         * @param hideTrailingZeroes {Boolean} - property found in Grids only
         * @return returnCellObject {obj}
         */
        private static _convertToExportFormat(
            index,
            formattedValue,
            value,
            type,
            format,
            bookType,
            hideTrailingZeroes,
            columnHeaders,
        ) {
            //convert to string instead of exporting using type Number or Date
            let exportValue,
                returnCellObject = {
                    format: undefined,
                    formattedVal: formattedValue,
                    value: "",
                    type: undefined,
                };

            //apply only to numeric values that exported as numbers not strings
            if (type === "Number") {
                //if numeric type has no numeric value Formatted numbers
                if (isNaN(Number(value)) || !_.isFinite(Number(value))) {
                    type = "String"; //skip applying fromatter
                    returnCellObject.type = "String";
                    /* If numeric type with length > 16 excel trims off 
                    least significant digits for num > 16 chars */
                } else if (_.isString(formattedValue) && formattedValue.length > 16) {
                    type = "String"; //skip applying fromatter
                    columnHeaders[index].isStringType = true;
                }
            }
            // in some cases skip formatting based on type
            // apply formatter if not forced to string
            switch (type) {
                case "Number":
                    //update format based on number digits not percentage
                    if (hideTrailingZeroes && !/[%]/.test(format)) {
                        //formatted values is a string with traling zeroes removed
                        const precision =
                                formattedValue.indexOf(".") !== -1
                                    ? formattedValue.split(".")[1].length
                                    : 0,
                            //generate new precission
                            precisionString = precision ? "." + Array(precision + 1).join("0") : "",
                            //get format string whole part
                            fromatWholePart =
                                format.indexOf(".") !== -1 ? format.split(".")[0] : format,
                            //combine formatter whole part with precission part
                            replaceFormat = fromatWholePart + precisionString;

                        if (replaceFormat !== format) {
                            //update format for value
                            returnCellObject.format = replaceFormat;
                        }
                    }
                    exportValue = Number(value);
                    break;
                //invalid dates tobe converted to empty strings
                /* XSLX converter for .csv exports Date types without
                applying formatters so moment formatter is not suitable */
                case "Date":
                    //utc prevents daysaving being applied
                    if (
                        bookType !== "csv" &&
                        moment.utc(formattedValue, format).isValid() &&
                        moment.utc(formattedValue, format).format() !== "Invalid date"
                    ) {
                        //ignore timezone offsets
                        exportValue = moment.utc(formattedValue, format).format();
                    } else if (bookType === "csv") {
                        exportValue = formattedValue;
                        columnHeaders[index].type = "String";
                    }
                    //invalid dates exported as string
                    else {
                        //export using formatted value
                        exportValue = formattedValue;
                        columnHeaders[index].isStringType = true;
                    }
                    break;
                case "Time":
                case "DateTime":
                    //export those as a string
                    exportValue = formattedValue;
                    columnHeaders[index].type = "String";
                    break;
                case "String":
                default:
                    exportValue = formattedValue;
                    break;
            }

            returnCellObject.value = exportValue;

            return returnCellObject;
        }
    }
}
