/// <reference types="backbone" />


class DeepModel extends Backbone.Model {

    static keyPathSeparator = '.';

    // Override constructor
    // Support having nested defaults by using _.deepExtend instead of _.extend
    constructor(attributes, options) {
        super(attributes, options);
        var defaults;
        var attrs = attributes || {};
        this.cid = _.uniqueId('c');
        this.attributes = {};
        if (options && options.collection) this.collection = options.collection;
        if (options && options.parse) attrs = this.parse(attrs, options) || {};
        if (defaults = _.result(this, 'defaults')) {
            //<custom code>
            // Replaced the call to _.defaults with _.deepExtend.
            attrs = _.merge(defaults, attrs);
            //</custom code>
        }
        this.set(attrs, options);
        this.changed = {} as any[];
        this.initialize.apply(this, arguments);
    }


    /**
     * Takes a nested object and returns a shallow object keyed with the path names
     * e.g. { "level1.level2": "value" }
     *
     * @param  {Object}      Nested object e.g. { level1: { level2: 'value' } }
     * @return {Object}      Shallow object with path names e.g. { 'level1.level2': 'value' }
     */
    private static objToPaths(obj, flattenPath?, currentPath?) {
        var flattenArrays = false,
            obj2,
            ret = {},
            separator = DeepModel.keyPathSeparator;

        for (var key in obj) {
            var val = obj[key];

            currentPath = (currentPath) ? currentPath + separator + key : key;

            // flattenPath is the path that we should return in dot syntax,
            // e.g. return { Layers.0.Visible: true } instead of
            // Layers: [
            //   {
            //     Visible: true
            //   }
            // ]
            // Used by ComponentApi.setProperty and allows a single deep property to be sent
            // to a component's onSettingsChange while maintaining array order.
            // If it is set to true then all arrays should be flattened.
            if (flattenPath === true) {
                flattenArrays = true;
            } else if (flattenPath && typeof flattenPath === 'string' && flattenPath !== currentPath && flattenPath.indexOf(currentPath) === 0) {
                flattenArrays = true;
            }

            if (val && (val.constructor === Object)) {
                if (!_.isEmpty(val)) {
                    if (_.has(val, '_dashboardsType')) {
                        // Don't flatten objects with _dashboardsType key
                        ret[key] = val;
                    } else {
                        //Recursion for embedded objects
                        obj2 = this.objToPaths(val, flattenPath, currentPath);

                        for (var key2 in obj2) {
                            var val2 = obj2[key2];

                            ret[key + separator + key2] = val2;
                        }
                    }
                } else {
                    ret[key] = val;
                }
            } else if (val && (val.constructor === Array) && flattenArrays) {
                obj2 = this.objToPaths(val, flattenPath, currentPath);

                for (var key2 in obj2) {
                    var val2 = obj2[key2];

                    ret[key + separator + key2] = val2;
                }
            } else {
                ret[key] = val;
            }
        }

        return ret;
    }

    /**
     * [getNested description]
     * @param  {object} obj           to fetch attribute from
     * @param  {string} path          path e.g. 'user.name'
     * @param  {[type]} return_exists [description]
     * @return {mixed}                [description]
     */
    private static getNested(obj, path, return_exists?) {
        var separator = DeepModel.keyPathSeparator;

        var fields = path ? path.split(separator) : [];
        var result = obj;
        return_exists || (return_exists === false);
        for (var i = 0, n = fields.length; i < n; i++) {
            if (return_exists && !_.has(result, fields[i])) {
                return false;
            }
            result = result[fields[i]];

            if (result == null && i < n - 1) {
                result = {};
            }

            if (typeof result === 'undefined') {
                if (return_exists) {
                    return true;
                }
                return result;
            }
        }
        if (return_exists) {
            return true;
        }
        return result;
    }



    /**
     * @param {Object} obj                Object to fetch attribute from
     * @param {String} path               Object path e.g. 'user.name'
     * @param {Object} [options]          Options
     * @param {Boolean} [options.unset]   Whether to delete the value
     * @param {Mixed}                     Value to set
     */
    private static setNested(obj, path, val, options?) {
        options = options || {};

        var separator = DeepModel.keyPathSeparator;

        var fields = path ? path.split(separator) : [];
        var result = obj;
        for (var i = 0, n = fields.length; i < n && result !== undefined; i++) {
            var field = fields[i];

            //If the last in the path, set the value
            if (i === n - 1) {
                options.unset ? delete result[field] : result[field] = val;
            } else {
                //Create the child object if it doesn't exist, or isn't an object
                if (typeof result[field] === 'undefined' || !_.isObject(result[field])) {
                    var nextField = fields[i + 1];

                    // create array if next field is integer, else create object
                    result[field] = /^\d+$/.test(nextField) ? [] : {};
                }

                //Move onto the next part of the path
                result = result[field];
            }
        }
    }

    private static deleteNested(obj, path) {
        this.setNested(obj, path, null, {
            unset: true
        });
    }

    // Return a copy of the model's `attributes` object.
    toJSON(options) {
        return _.merge({}, this.attributes);
    }

    // Override get
    // Supports nested attributes via the syntax 'obj.attr' e.g. 'author.user.name'
    get(attr) {
        return DeepModel.getNested(this.attributes, attr);
    }

    // Override set
    // Supports nested attributes via the syntax 'obj.attr' e.g. 'author.user.name'
    set(key: any, val: any, options?: Backbone.ModelSetOptions): Backbone.Model {
        var attr, attrs, unset, changes, silent, changing, prev, current;
        if (key == null) return this as unknown as Backbone.Model;

        // Handle both `"key", value` and `{key: value}` -style arguments.
        if (typeof key === 'object') {
            attrs = key;
            options = val || {};
        } else {
            (attrs = {})[key] = val;
        }

        options || (options = {});

        // Run validation.
        if (!this._validate(attrs, options)) return null;

        // Extract attributes and options.
        unset = options['unset'];
        silent = options.silent;
        changes = [];
        changing = this._changing;
        this._changing = true;

        if (!changing) {
            this._previousAttributes = _.merge({}, this.attributes); //<custom>: Replaced _.clone with _.deepClone
            this.changed = {} as any[]
        }
        current = this.attributes, prev = this._previousAttributes;

        // Check for changes of `id`.
        if (this.idAttribute in attrs) this.id = attrs[this.idAttribute];

        //<custom code>
        attrs = DeepModel.objToPaths(attrs);
        //</custom code>

        // For each `set` attribute, update or delete the current value.
        for (attr in attrs) {
            val = attrs[attr];

            //<custom code>: Using getNested, setNested and deleteNested
            if (!_.isEqual(DeepModel.getNested(current, attr), val)) changes.push(attr);
            if (!_.isEqual(DeepModel.getNested(prev, attr), val)) {
                DeepModel.setNested(this.changed as any, attr, val);
            } else {
                DeepModel.deleteNested(this.changed, attr);
            }
            unset ? DeepModel.deleteNested(current, attr) : DeepModel.setNested(current, attr, val);
            //</custom code>
        }

        // Remove any top-level empty objects
        _.each(this.changed, _.bind(function (v, k) {
            if (_.isEmpty(v) && _.isObject(v) && !_.isArray(v)) {
                delete this.changed[k];
            }
        }, this));

        // Trigger all relevant attribute changes.
        if (!silent) {
            if (changes.length) this._pending = true;

            //<custom code>
            var separator = DeepModel.keyPathSeparator;
            var alreadyTriggered = {}; // * @restorer

            for (var i = 0, l = changes.length; i < l; i++) {
                var key = changes[i];

                if (!alreadyTriggered.hasOwnProperty(key) || !alreadyTriggered[key]) { // * @restorer
                    alreadyTriggered[key] = true; // * @restorer
                    this.trigger('change:' + key, this, DeepModel.getNested(current, key), options);
                } // * @restorer

                var fields = key.split(separator);

                //Trigger change events for parent keys with wildcard (*) notation
                for (var n = fields.length - 1; n > 0; n--) {
                    var parentKey = fields.slice(0, n).join(separator),
                        wildcardKey = parentKey + separator + '*';

                    if (!alreadyTriggered.hasOwnProperty(wildcardKey) || !alreadyTriggered[wildcardKey]) { // * @restorer
                        alreadyTriggered[wildcardKey] = true; // * @restorer
                        this.trigger('change:' + wildcardKey, this, DeepModel.getNested(current, parentKey), options);
                    } // * @restorer

                    // + @restorer
                    if (!alreadyTriggered.hasOwnProperty(parentKey) || !alreadyTriggered[parentKey]) {
                        alreadyTriggered[parentKey] = true;
                        this.trigger('change:' + parentKey, this, DeepModel.getNested(current, parentKey), options);
                    }
                    // - @restorer
                }
                //</custom code>
            }
        }

        if (changing) return this as unknown as Backbone.Model;
        if (!silent) {
            while (this._pending) {
                this._pending = false;
                this.trigger('change', this, options);
            }
        }
        this._pending = false;
        this._changing = false;
        return this as unknown as Backbone.Model;
    }

    // Clear all attributes on the model, firing `"change"` unless you choose
    // to silence it.
    clear(options) {
        var attrs = {};
        var shallowAttributes = DeepModel.objToPaths(this.attributes);
        for (var key in shallowAttributes) attrs[key] = void 0;
        return this.set(attrs, _.extend({}, options, {
            unset: true
        }));
    }

    // Determine if the model has changed since the last `"change"` event.
    // If you specify an attribute name, determine if that attribute has changed.
    hasChanged(attr?) {
        if (attr == null) return !_.isEmpty(this.changed);
        return DeepModel.getNested(this.changed, attr) !== undefined;
    }

    // Return an object containing all the attributes that have changed, or
    // false if there are no changed attributes. Useful for determining what
    // parts of a view need to be updated and/or what attributes need to be
    // persisted to the server. Unset attributes will be set to undefined.
    // You can also pass an attributes object to diff against the model,
    // determining if there *would be* a change.
    changedAttributes(diff, propertyPath?): any {
        //<custom code>: objToPaths
        if (!diff) return this.hasChanged() ? DeepModel.objToPaths(this.changed, propertyPath) : false;
        //</custom code>

        var old = this._changing ? this._previousAttributes : this.attributes;

        //<custom code>
        var diffFlat = DeepModel.objToPaths(diff);
        var oldFlat = DeepModel.objToPaths(old);

        var changed: any = false;
        for (var attr in diffFlat) {
            if (_.includes(['array', 'object'], typeof old[attr]) && diff[attr] === undefined) {
                (changed || (changed = {}))[attr] = undefined;
            } else if (!_.isEqual(oldFlat[attr], diffFlat[attr])) {
                (changed || (changed = {}))[attr] = diffFlat[attr];
            }
        }
        //</custom code>

        return changed;
    }

    // Get the previous value of an attribute, recorded at the time the last
    // `"change"` event was fired.
    previous(attr) {
        if (attr == null || !this._previousAttributes) return null;

        //<custom code>
        return DeepModel.getNested(this._previousAttributes, attr);
        //</custom code>
    }

    // Get all of the attributes of the model at the time of the previous
    // `"change"` event.
    previousAttributes() {
        //<custom code>
        return _.merge({}, this._previousAttributes);
        //</custom code>
    }

    toFlat(flattenArrays) {
        return DeepModel.objToPaths(this.attributes, flattenArrays);
    }
}

Backbone.DeepModel = DeepModel;