///<reference path="../classes/RTTI.ts"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Pivot {
        private static PIVOT_FUNCTION =
            "$[{x~key x}`.pivot.pivotV3;.pivot.pivotV3;{[t;w;d;a;f;n;o] g:(); if [(type d)=99h; [g:d[`g]; d:d[`d],g;]];piv: 0!?[t;w;d!d;a!f]; if [(count g)>0; [d: (-1 _ d); a2:d,a; piv2: 0!?[piv;();g!g;a2!a2]; grpData:{[x;d;grpCol;f] labels: $[(count f)>1;(`$((string x[grpCol]) ,/: string f[;0]));x[grpCol]]; d xkey (d,labels) xcol flip x _ grpCol}; piv: 0! () uj/ each[grpData[;d;g[0];f]] piv2; ]]; if [(count o)>0;[field: o[0]; order: o[1]; piv:$[order>0;field xasc piv;field xdesc piv];]]; $[n<=0;piv;n sublist piv]}]";

        private static constructPivotWhereString(wheres) {
            const whereStrings = [];

            //Loop through wheres object array and construct wheres string
            wheres.forEach(function(where) {
                switch (where.columnType) {
                    case 1: //boolean
                    case 2: //guid
                    case 4: //byte
                    case 5: //short
                    case 6: //int
                    case 7: //bigint
                    case 8: //real
                    case 9: // float
                    case 12: //timestamp
                    case 13: //month
                    case 14: //date
                    case 15: //datetime
                    case 16: //timespan
                    case 17: //minute
                    case 18: //second
                    case 19: //time
                        whereStrings.push(
                            "(" +
                                where.columnOperator +
                                ';`$"' +
                                where.columnName +
                                '"; ' +
                                RTTI.getByQTypeNum(where.columnType).toKDBInput(where.columnValue) +
                                ")",
                        );
                        break;
                    case 10: //char
                        if (where.columnValue.length == 1) {
                            whereStrings.push(
                                "(" +
                                    where.columnOperator +
                                    ';`$"' +
                                    where.columnName +
                                    '"; "' +
                                    where.columnValue.toString() +
                                    '")',
                            );
                        } else {
                            whereStrings.push(
                                '(~\\:;`$"' +
                                    where.columnName +
                                    '"; "' +
                                    where.columnValue.toString() +
                                    '")',
                            );
                        }
                        break;
                    case 11: //symbol
                        whereStrings.push(
                            "(" +
                                where.columnOperator +
                                ';`$"' +
                                where.columnName +
                                '"; enlist `$"' +
                                where.columnValue.toString() +
                                '")',
                        );
                        break;
                    default:
                        if (_.isString(where.columnValue)) {
                            whereStrings.push(
                                '(~\\:;`$"' +
                                    where.columnName +
                                    '"; "' +
                                    where.columnValue.toString() +
                                    '")',
                            );
                        } else {
                            whereStrings.push(
                                "(" +
                                    where.columnOperator +
                                    ';`$"' +
                                    where.columnName +
                                    '"; enlist `$"' +
                                    where.columnValue.toString() +
                                    '")',
                            );
                        }
                        break;
                }
            });

            if (whereStrings.length == 1) {
                return "enlist " + whereStrings[0];
            } else {
                return "(" + whereStrings.join(";") + ")";
            }
        }

        private static _generateOrderingKdbSyntax(ordering) {
            let toReturn = "",
                column,
                direction,
                parts;

            //if (typeof ordering === 'undefined') { ordering = ''; } //optional param, needs a default
            ordering = ordering || "";
            parts = ordering.split(",");
            if (parts.length === 2) {
                direction = parts[1];

                if (direction != "null") {
                    column = parts[0];
                    toReturn = "`" + column + ";" + (direction === "asc" ? 1 : -1);
                }
            }

            return toReturn;
        }
        private static strip(query) {
            return query && query.length > 0
                ? query.replace(/((^|\s+)\/.*$)|^\s+|\s*\n/gm, " ")
                : query;
        }

        public static constructPivotQuery(options) {
            let dimsString = "",
                aggsString = "",
                whereString = "",
                funcsString = "",
                topString = "0j",
                query,
                pivotSource = options.pivotSource,
                table = options.table,
                queryString = this.strip(options.queryString),
                queryParams = options.queryParams,
                aggFn = options.aggFn,
                aggregateFns = options.aggregateFns,
                aggStr,
                ordering = options._generateOrderingKdbSyntax,
                deltaParameterRx = /^{\[([a-z0-9A-Z\;\w\/ ]+?)\]/,
                deltaParameterRgx,
                deltaParameterString,
                columnLabelString = "",
                dimsDict = "",
                stringParameterRx = /^[A-Za-z0-9]+\w*$/;

            // helper fn
            function toSymbolListString(arr) {
                if (arr.length > 1) {
                    return (
                        _.reduce(
                            arr,
                            function(acc, value, index: number, collection) {
                                let newValue =
                                    acc + (acc.substr(-1) !== ";" && index > 0 ? ";" : "");

                                if (value.match(stringParameterRx)) {
                                    newValue += "`" + value;
                                } else {
                                    newValue +=
                                        '`$"' +
                                        value +
                                        '"' +
                                        (index < collection.length - 1 ? ";" : "");
                                }

                                return newValue;
                            },
                            "(",
                        ) + ")"
                    );
                } else if (arr.length === 1) {
                    return arr[0].match(stringParameterRx)
                        ? "enlist `" + arr[0]
                        : 'enlist `$"' + arr[0] + '"';
                }
            }

            ordering = this._generateOrderingKdbSyntax(ordering);

            dimsString = toSymbolListString(options.dimensions);

            // prefer aggregate labels over columns
            aggsString = toSymbolListString(
                _.isArray(options.aggregateLabels) && !_.isEmpty(options.aggregateLabels)
                    ? _.map(options.aggregateLabels, function(label, i) {
                          return label || options.aggregateCols[i];
                      })
                    : options.aggregateCols,
            );

            whereString = this.constructPivotWhereString(options.wheres);

            if (options.aggregateCols.length === 1) {
                funcsString = "enlist ";
            }
            funcsString += "(";
            _.each(options.aggregateCols, function(c, idx) {
                if (c.length > 0) {
                    aggStr = (aggregateFns && aggregateFns[idx]) || aggFn;
                    funcsString +=
                        "(" +
                        aggStr +
                        ";" +
                        (c.match(stringParameterRx) ? "`" + c : '`$"' + c + '"') +
                        ");";
                }
            });

            funcsString = funcsString.substr(0, funcsString.length - 1) + ")";

            if (options.topX && options.topX !== "null" && options.topX !== "all") {
                topString = options.topX + "j";
            }

            if (options.columnLabel && options.columnLabel.length > 0) {
                columnLabelString = "enlist `" + options.columnLabel;
            } else {
                columnLabelString = "()";
            }

            dimsDict = "`d`g!(" + dimsString + ";" + columnLabelString + ")";

            if (pivotSource === "query") {
                queryString = $.trim(queryString);
                deltaParameterRgx = deltaParameterRx.exec(queryString);
                deltaParameterString = "";

                //Format query string to use parametersif required
                if ($.isArray(deltaParameterRgx)) {
                    deltaParameterString = deltaParameterRgx[0];
                } else if (queryString.substring(0, 3) !== "{[]") {
                    queryString = "{[] " + queryString + "}[]";
                } else {
                    queryString = queryString + "[]";
                }

                if (deltaParameterString.length > 0) {
                    if (queryParams.length === 1) {
                        queryString = queryString + "[" + queryParams[0].name + "]";
                    } else {
                        //append parameter value array to query
                        //the kdbQuery.parameters values will then be passed to this array
                        queryString = queryString + "[";
                        _.each(queryParams, function(param, idx: number) {
                            if (idx > 0) {
                                queryString = queryString + ";";
                            }

                            queryString = queryString + param.name;
                        });
                        queryString = queryString + "]";
                    }
                }

                query =
                    deltaParameterString +
                    " " +
                    this.PIVOT_FUNCTION +
                    "[" +
                    queryString +
                    ";" +
                    whereString +
                    ";" +
                    dimsDict +
                    ";" +
                    aggsString +
                    ";" +
                    funcsString +
                    ";" +
                    topString +
                    ";(" +
                    ordering +
                    ")]";

                if (deltaParameterString.length > 0) {
                    query = query + "}";
                }
            } else {
                query =
                    this.PIVOT_FUNCTION +
                    "[`" +
                    table +
                    ";" +
                    whereString +
                    ";" +
                    dimsDict +
                    ";" +
                    aggsString +
                    ";" +
                    funcsString +
                    ";" +
                    topString +
                    ";(" +
                    ordering +
                    ")]";
            }

            return query;
        }
    }
}
