/// <reference path="I64.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Timespan extends I64 {
        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Timespan, dataView);
        }

        public static fromScalar(value: number): rTimespanClass {
            // TODO: timespan loses precision due to implementation in delta client
            return value === null ? null : new rTimespanClass(value);
        }

        public static fromMoment(m: moment.Duration): rTimespanClass {
            return m === null ? null : new rTimespanClass(m.valueOfNano());
        }

        public static fromString(str: string): rTimespanClass {
            let m: moment.Duration;
            str = str.replace("1970-01-01T", "").replace("Z", "");
            const timeParts = str.match(Constants.TIME_PARTS_RX);
            if (timeParts && timeParts[1]) {
                // convert days to hours expected format 0D12:12:12.000000000
                if (str.indexOf("D") !== -1) {
                    const dayHourArr = str.substring(0, str.indexOf(":")).split("D"),
                        hours = Number(dayHourArr[1]) + Number(dayHourArr[0]) * 24;
                    if (hours) {
                        //get substring excluding hours
                        timeParts[1] = hours + timeParts[1].substring(2);
                    }
                }

                m = moment.duration(timeParts[1]);
                const nano = Number(timeParts[2] || 0);
                if (nano && !_.isNaN(nano)) m.nanoseconds(nano);
            } else {
                m = moment.duration(str);
            }

            return Timespan.fromMoment(m);
        }

        public static listToIPC(values: Array<rTimespanClass>): Uint8Array {
            const size = values.length * 8 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(TypeNum.Timespan); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Timespan.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        public static toIPC(value: rTimespanClass): Uint8Array {
            const buffer = Util.createBuffer(9);

            buffer.wb(256 - TypeNum.Timespan); // type
            Timespan.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        public static toMoment(value: rTimespanClass): moment.Duration {
            if (value === null) return null;
            const m = moment.duration((value.i > 0 ? Math.floor : Math.ceil)(value.i / 1000000));
            m.nanoseconds(value.i % 1000000);
            return m;
        }

        public static toScalar(value: rTimespanClass): number {
            // TODO: timespan loses precision due to implementation in delta client
            return value === null ? null : value.i;
        }

        public static toString(value: rTimespanClass, format?: string, options?): string {
            if (value === null) return "";
            const m = Timespan.toMoment(value);
            const str = m.format(format || "HH:mm:ss.SSS", options || { trim: false });
            return format ? str : str + _.padEnd("" + m.nanoseconds(), 6, "0");
        }

        public static writeValue(value: rTimespanClass, wb: (b: number) => void): void {
            QLong.writeValue(value ? Timespan.toScalar(value).toFixed(0) : null, wb);
        }

        public getValue(i: number): rTimespanClass {
            const s = this.getLegacyScalar(i);
            const l =
                typeof s === "bigint" || typeof s === "number"
                    ? Long.fromString((s as bigint | number).toString())
                    : s;

            return l.equals(I64.LONG_NULL) ? null : new rTimespanClass(Number(l.toString()));
        }
    }
}
