/// <reference path="../../classes/RTTI.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.DataFilter {
    export class RuleModel extends Backbone.Model {
        public static VSRx = /^<%[^<>%]*%>$/i;
        public schema: any;
        private options: any;

        constructor(options?: any) {
            super(options);

            this.options = options;

            this.setSchema();
        }

        defaults(): any {
            return {
                propertyName: null,
                propertyType: null,
                operator: null,
                value: null,
                value2: null,
                active: true,
                type: "normal",
                compareFields: false,
            };
        }

        getBindingValue(key) {
            const binding = _.find(
                this.get("appModel")
                    .get("settingsModel")
                    .get("Bindings"),
                b => b.Key === key,
            );
            return binding && binding.ViewState instanceof DocumentViewModel
                ? binding.ViewState.get("value")
                : "";
        }

        getTypeOperators(kdbType: number = null) {
            const result = [],
                operators = {
                    equal: [
                        {
                            label: "equal",
                            val: "equal",
                        },
                        {
                            label: "not equal",
                            val: "not_equal",
                        },
                    ],
                    like: [
                        {
                            label: "like",
                            val: "like",
                        },
                        {
                            label: "not like",
                            val: "not_like",
                        },
                    ],
                    blank: [
                        {
                            label: "is blank",
                            val: "is_blank",
                        },
                        {
                            label: "is not blank",
                            val: "is_not_blank",
                        },
                    ],
                    compare: [
                        {
                            label: "less",
                            val: "less",
                        },
                        {
                            label: "less or equal",
                            val: "less_or_equal",
                        },
                        {
                            label: "greater",
                            val: "greater",
                        },
                        {
                            label: "greater or equal",
                            val: "greater_or_equal",
                        },
                    ],
                    in: [
                        {
                            label: "in",
                            val: "in",
                        },
                        {
                            label: "not in",
                            val: "not_in",
                        },
                    ],
                },
                pushOperators = (...oprs): void => {
                    _.each(oprs, opr => result.push(...opr));
                };

            if (this.get("compareFields")) {
                operators.like = operators.blank = operators.in = [];
            }

            pushOperators(operators.equal);

            switch (kdbType) {
                case 0: // list
                    pushOperators(operators.like, operators.blank);
                    break;

                // numbers
                case 5: // short
                case 6: // int
                case 7: // long
                case 8: // real
                case 9: // float

                case 4: // byte
                    pushOperators(operators.compare, operators.blank, operators.in);
                    break;

                // time
                case 12: // timestamp
                case 13: // month
                case 14: // date
                case 15: // datetime
                case 16: // timespan
                case 17: // minute
                case 18: // second
                case 19: // time
                    pushOperators(operators.compare, operators.blank);
                    break;
                case 11: // symbol
                    pushOperators(operators.in, operators.like, operators.blank);
                    break;
                case 2: // guid
                    pushOperators(operators.in, operators.blank);
                    break;
                case 10: // char
                case 1: // boolean
                    pushOperators(operators.blank);
                    break;
            }

            return result;
        }

        replaceBindings(val: string): string {
            return _.isString(val) && this.get("appModel").get("Bindings")
                ? val.replace(RuleModel.VSRx, (val: string) => {
                      const key = val.substring(2, val.length - 2);
                      return this.getBindingValue(key);
                  })
                : val;
        }

        setSchema() {
            const kdbType = this.get("propertyType");

            if (this.get("type") === "histogram") {
                // filter out items with appropriate data types
                const histogramKdbTypes = [5, 6, 7, 8, 9, 12, 13, 14, 15, 16, 17, 18, 19];

                const dataItems = this.options.appModel
                    .get("data.items")
                    .filter((i, k) => {
                        return _.includes(
                            histogramKdbTypes,
                            this.options.appModel.get("data.meta.columns.models")[k].get("kdbType"),
                        );
                    })
                    .map(i => {
                        return {
                            label: i.get("propertyName"),
                            val: i.get("propertyName"),
                        };
                    });

                this.schema = {
                    active: {
                        title: "Is Active:",
                        type: "Checkbox",
                    },
                    propertyName: {
                        title: "Property:",
                        type: "Select",
                        options: _.sortBy(dataItems, "label"),
                    },
                    value: _.extend(
                        {
                            title: "From",
                        },
                        RTTI.getByQTypeNum(kdbType || 11).editor,
                    ),
                    value2: _.extend(
                        {
                            title: "To",
                        },
                        RTTI.getByQTypeNum(kdbType || 11).editor,
                    ),
                };
            } else {
                const dataItems = this.options.appModel.get("data.items").map(i => {
                    return {
                        label: i.get("propertyName"),
                        val: i.get("propertyName"),
                    };
                });

                // default schema
                this.schema = {
                    active: {
                        title: "Is Active:",
                        type: "Checkbox",
                    },
                    propertyName: {
                        title: "Property:",
                        type: "Select",
                        options: _.sortBy(dataItems, "label"),
                    },
                    operator: {
                        title: "Operator:",
                        type: "Select",
                        options: this.getTypeOperators(kdbType),
                    },
                    value: _.extend(
                        {
                            title: "Value",
                        },
                        RTTI.getByQTypeNum(kdbType || 11).editor,
                    ),
                };
            }

            if (this.get("compareFields")) {
                this.schema.value = {
                    title: "Value",
                    type: "Select",
                };
            } else if (
                this.get("operator") &&
                this.get("propertyName") &&
                _.some(
                    [
                        "in",
                        "not_in",
                        "equal",
                        "not_equal",
                        "greater",
                        "less",
                        "greater_or_equal",
                        "less_or_equal",
                    ],
                    o => {
                        return o === this.get("operator");
                    },
                )
            ) {
                const dataItem = this.options.appModel
                    .get("data.items")
                    .get(this.get("propertyName"));
                const singleSelect =
                    _.indexOf(
                        [
                            "equal",
                            "not_equal",
                            "greater",
                            "less",
                            "greater_or_equal",
                            "less_or_equal",
                        ],
                        this.get("operator"),
                    ) >= 0;

                if (dataItem) {
                    let results = null;

                    if (dataItem.get("result") && dataItem.get("result").data) {
                        // data source is set
                        const dataMappingLabel =
                            dataItem.get("DataSourceMapping.Text") || this.get("propertyName");
                        const dataMappingValue =
                            dataItem.get("DataSourceMapping.Value") || this.get("propertyName");

                        results = dataItem.get("result").data.collection.map(m => {
                            const lbl = m.get(dataMappingLabel),
                                val = m.get(dataMappingValue);
                            // convert boolean values to strings
                            return {
                                label: _.isBoolean(lbl) ? lbl + "" : lbl,
                                val: _.isBoolean(val) ? val + "" : val,
                            };
                        });

                        results = _.uniqBy(
                            _.reject(results, r => _.isNil(r.label)),
                            r => r.label,
                        );

                        if (
                            results.length <= 0 &&
                            this.get("value") &&
                            _.isArray(this.get("value")) &&
                            this.get("value").length > 0
                        ) {
                            _.each(this.get("value"), v => {
                                results.push({
                                    label: v,
                                    val: v,
                                });
                            });
                        }

                        results.splice(0, 0, {
                            label: "",
                            val: "",
                        });

                        // set
                        this.schema.value = {
                            title: "Value",
                            type: singleSelect ? "Select" : "MultiSelect",
                            options: results,
                            modelKey: "value",
                        };
                    } else if (dataItem.get("items").length > 0) {
                        results = _.map(dataItem.get("items"), (m: any) => {
                            return {
                                label: m.Text,
                                val: m.Value,
                            };
                        });

                        results.splice(0, 0, {
                            label: "",
                            val: "",
                        });
                        // set
                        this.schema.value = {
                            title: "Value",
                            type: singleSelect ? "Select" : "MultiSelect",
                            options: results,
                            modelKey: "value",
                        };
                    } else if (!singleSelect) {
                        // no items
                        this.schema.value = _.extend(
                            {
                                title: "Value",
                            },
                            RTTI.getByQTypeNum(0).editor,
                        );
                    }
                }
            }
        }

        isValueValid(value): boolean {
            if (_.includes(["is_not_blank", "is_blank"], this.get("operator"))) {
                return true;
            } else {
                return !(_.isUndefined(value) || _.isNull(value) || value === "");
            }
        }

        toJSON(): any {
            let j;

            if (this.get("type") === "histogram") {
                j = _.pick(
                    this.attributes,
                    "propertyName",
                    "propertyType",
                    "value",
                    "value2",
                    "active",
                    "type",
                );
            } else {
                j = _.pick(
                    this.attributes,
                    "propertyName",
                    "propertyType",
                    "operator",
                    "value",
                    "active",
                    "type",
                    "compareFields",
                );
            }

            if (j.propertyType === null) {
                j.propertyType = 11; // default to symbol!
            }
            return j;
        }

        toIPC(): Uint8Array {
            if (this.get("type") === "histogram") {
                return this.toIPCHistogram();
            }

            return this.toIPCNormal();
        }

        toIPCHistogram(): Uint8Array {
            const operator = new Uint8Array([102, 5]); // AND
            const children = [];
            const name = this.get("propertyName");
            const type = this.get("propertyType");
            const value1 = type === 7 ? Math.ceil(this.get("value")) : this.get("value");
            const op1 = "greater_or_equal";
            const value2 = type === 7 ? Math.floor(this.get("value2")) : this.get("value2");
            const op2 = "less_or_equal";

            if (
                this.get("active") &&
                name &&
                op1 &&
                op2 &&
                this.isValueValid(value1) &&
                this.isValueValid(value2)
            ) {
                children.push(this.toIPCNormal(name, op1, value1));
                children.push(this.toIPCNormal(name, op2, value2));
                return QuickBase.ipc.List.toIPC([operator].concat(children));
            }

            return null;
        }

        toIPCNormal(name = null, op = null, value = null): Uint8Array {
            name = name || this.get("propertyName");
            op = op || this.get("operator");
            value = this.replaceBindings(value || this.get("value"));

            // casting generic list 0 to symbol, used for strings
            const type = -this.get("propertyType") || 10;

            if (this.get("active") && name && op && this.isValueValid(value)) {
                const qtype = QuickBase.ipc.Util.getType(type);

                if (this.get("compareFields") && value === this.get("value")) {
                    value = QuickBase.ipc.Symbol.toIPC(value);
                } else if (op === "in" || op === "not_in") {
                    // make array regardless for value type
                    if (!_.isArray(value)) {
                        value = value === null ? [] : [value];
                    }

                    // put value in array or pass empty array
                    value = qtype.listToIPC(_.map(value, value => this.typeCast(value)));
                } else {
                    // Boolean types in datasource comes as a string so cast to boolean
                    if (type === -1 && _.isString(value)) {
                        value = value === "true";
                    }
                    value = qtype.toIPC(ipc.fromString(qtype, value));
                }

                return QuickBase.ipc.Dict.toIPC(
                    ["src", "comparison", "value", "dataType", "compareFields"],
                    QuickBase.ipc.List.toIPC(
                        _.map([this.get("propertyName"), op], v =>
                            QuickBase.ipc.Symbol.toIPC(v),
                        ).concat([
                            value,
                            QuickBase.ipc.Short.toIPC(type),
                            QuickBase.ipc.Boolean.toIPC(this.get("compareFields")),
                        ]),
                    ),
                );
            }

            return null;
        }

        toKDB(): string {
            let qtype, value;

            if (
                this.get("active") &&
                this.get("propertyName") &&
                this.get("operator") &&
                this.isValueValid(this.get("value"))
            ) {
                if (this.get("operator") === "in" || this.get("operator") === "not_in") {
                    qtype = RTTI.getByQTypeNum(0);
                    value = this.get("value");
                    value = qtype.toKDBInput(value);
                } else {
                    qtype = RTTI.getByQTypeNum(this.get("propertyType") || 11); // if bad data then symbol
                    value = qtype.toKDBInput(this.get("value"));
                }

                return (
                    "(`src`comparison`value!(`" +
                    this.get("propertyName") +
                    ";`" +
                    this.get("operator") +
                    ";" +
                    value +
                    "))"
                );
            }
            return null;
        }

        /**
         * Cat value if model has property "propertyType" is kdb type code
         * @param {any} value - value to cast
         */
        typeCast(value): number | string {
            switch (this.get("propertyType")) {
                case 4: // byte
                case 5: // short
                case 6: // int
                case 7: // long
                case 8: // real
                case 9: // float
                    if (!_.isNaN(Number(value))) {
                        return Number(value);
                    }
            }

            return value !== null ? "" + value : "";
        }

        validate(value1, value2): boolean {
            let v1, v2;

            if (!isNaN(value1) && !isNaN(value2)) {
                // numbers
                v1 = new Number(value1);
                v2 = new Number(value2);
            } else {
                // dates
                v1 = moment(value1);
                v2 = moment(value2);
            }

            return v1 >= v2; // return true if error
        }
    }
}
