/*global $, T, Backbone, FormSchemas, BBForm, DialogBase*/
/// <reference path="../templates/combined.ts" />
/// <reference path="DialogBase.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ConfirmDialog extends DialogBase {
        constructor(options) {
            super(
                _.extend(
                    {
                        iconClass: "fa-trash-o",
                        dialogTitle: "Confirm Delete",
                        minHeight: 110,
                        width: 275,
                    },
                    options,
                    {
                        userClass:
                            "confirm-dialog" + (options.userClass ? " " + options.userClass : ""),
                    },
                ),
            );

            if (_.isFunction(options.renderTemplate)) {
                this.renderTemplate = options.renderTemplate;
            }
        }

        dialogButtons() {
            return [
                {
                    text: "OK",
                    click: () => {
                        this.dialogClose();
                    },
                    icons: {
                        primary: "fa fa-check",
                    },
                },
                {
                    text: "Cancel",
                    click: () => {
                        this.isCancel = true;
                        this.dialogClose();
                    },
                    icons: {
                        primary: "fa fa-times",
                    },
                },
            ];
        }

        renderTemplate() {
            return t("Are you sure you want to delete this item?");
        }

        static ShowDelete(callbackFn, text) {
            new ConfirmDialog(
                _.extend(
                    {
                        iconClass: "fa-trash-o",
                        dialogTitle: "Confirm Delete",
                        callback: callbackFn,
                    },
                    text
                        ? {
                              renderTemplate() {
                                  return text;
                              },
                          }
                        : {},
                ),
            );
        }

        static ShowConfirm(props) {
            const txt = props.txt || "",
                containerOptions = props.container ? { container: props.container } : {};

            new ConfirmDialog(
                _.extend(
                    {
                        iconClass: props.iconClass || "fa-question-circle", //'fa-question', //'fa-stop',
                        dialogTitle: props.dialogTitle || "Confirmation",
                        callback: props.callback,
                        cancelBtnCallback: props.cancelBtnCallback || _.noop,
                        renderTemplate:
                            props.renderTemplate ||
                            function() {
                                return (
                                    txt + t("<br/>Are you sure you want to reload the Application?")
                                );
                            },
                    },
                    containerOptions,
                ),
            );
        }
    }
}
