        public static getPivotData(
            options: any,
            success: FnSuccess,
            error: Function,
            performance: PerformanceMetrics,
        ) {
            let query,
                connectionName = options.connection,
                pivotSource = options.pivotSource,
                queryParams = options.queryParams,
                kdbParams = [],
                formattedQuery,
                rows = options.maxRows || null;

            query = Pivot.constructPivotQuery(options);
            formattedQuery = kdb.KDBQuery.create(query, rows);

            if (pivotSource === "query") {
                this.createParamsObj(kdbParams, queryParams);
                formattedQuery.parameters = kdbParams;
            }

            //Run query using deltaClient
            performance && performance.trigger("serverStart");
            this.deltaClient.request(
                "QueryAPI.runQueryBA",
                function(arg1, bData) {
                    performance && performance.trigger("serverEnd");
                    const data = deserialize(bData);
                    if (success) {
                        success(data as qTyped, bData);
                    }
                },
                function() {
                    if (error) {
                        error(arguments);
                    }
                },
                formattedQuery,
                connectionName,
            );

            //If subscription type is subscription run managed query query
            if (options.subscriptionType === "subscription") {
                performance && performance.trigger("serverStart");
                this.deltaClient.request(
                    "SubscriptionAPI.addSubscription",
                    function(data, bData) {
                        if (success) {
                            performance && performance.trigger("serverEnd");
                            if (bData && bData.byteLength) {
                                data.dataSet = deserialize(bData);
                            }

                            success(data, bData);
                        }
                    },
                    function(data) {
                        if (error) {
                            error(data);
                        }
                    },
                    formattedQuery,
                    connectionName,
                    options.subscriptionInterval * 1000,
                );
            }
        }

        public static getPolledSubscription(runId): any | number {
            return this.deltaClient.connection.oobupdate.filter(function(upd) {
                return upd.type === "QRPolledMessage" && upd.data[0] === runId;
            });
        }

        /*
         * Legacy getQueryData return always rows
         */
        public static getQueryData(
            dataSource: string,
            connection: any,
            query: string,
            parameters: any,
            success: FnSuccessLegacy,
            error: Function,
            maxRows?: number,
            paging?: any,
            performance?: PerformanceMetrics,
        ) {
            DeltaClientLib.getQueryData2(
                dataSource,
                connection,
                query,
                parameters,
                DeltaClientLib.legacyRowCallback(success),
                error,
                maxRows,
                paging,
                performance,
            );
        }

        public static getQueryData2(
            dataSource: string,
            connection: any,
            query: string,
            parameters: any,
            success: FnSuccess,
            error: Function,
            maxRows?: number,
            paging?: any,
            performance?: PerformanceMetrics,
        ) {
            var formattedQuery,
                kdbParams = [],
                sortDict,
                runQueryArgs,
                rows = maxRows && maxRows != 0 ? Number(maxRows) : null,
                self = this;

            if (!paging || !connection || !_.includes(DeltaClientLib.PAGING_TYPES, paging.type)) {
                paging = {
                    type: "NONE",
                };
            }

            if (dataSource === "sql") {
                //run jdbc query if source set to sql
                this.getSqlQueryData(
                    connection,
                    this.strip(query),
                    parameters,
                    success,
                    error,
                    maxRows,
                );
            } else {
                if (!paging || paging.type === "NONE") {
                    formattedQuery = kdb.KDBQuery.create(this.strip(query), rows);
                } else {
                    sortDict = new kdb.QDict();

                    _.each(paging.sorting, function(value, key) {
                        sortDict.add(key, value === true, kdb.KDB.BOOLEAN);
                    });

                    formattedQuery = kdb.KDBQuery.createPaged(
                        this.strip(query),
                        paging.type,
                        paging.num,
                        paging.size,
                        sortDict,
                    );
                    formattedQuery.unlimitedRows = paging.size === -1;
                }

                if (parameters) {
                    this.createParamsObj(kdbParams, parameters);
                    formattedQuery.parameters = kdbParams;
                }

                Log.Info("QUERY: ", formattedQuery);

                var reqObj,
                    self = this;
                runQueryArgs = [
                    connection ? "QueryAPI.runQueryBA" : "QueryAPI.runQuery",
                    function(arg1, bData) {
                        performance && performance.trigger("serverEnd");
                        self.deltaClient.connection.removeResponder(reqObj.uid);
                        const data = connection ? deserialize(bData) : arg1;
                        if (success) {
                            success(data, bData);
                        }
                    },
                    function() {
                        self.deltaClient.connection.removeResponder(reqObj.id);
                        if (error) {
                            error(arguments);
                        }
                    },
                    formattedQuery,
                ];

                //enable running queries omitting conections (avoid hitting QueryRouter)
                if (connection) {
                    runQueryArgs.push(connection);
                }

                performance && performance.trigger("serverStart");
                reqObj = this.deltaClient.request.apply(this.deltaClient.request, runQueryArgs);
            }
        }

        public static getConfigAPIDataWithProfile(
            dataSource,
            connection,
            api,
            param,
            overrideName,
            profileName,
            callback,
            errorCallback,
        ) {
            let runQueryArgs;

            runQueryArgs = [
                api,
                function(data) {
                    if (data) {
                        if (callback) {
                            callback(data);
                        }
                    }
                },
                function() {
                    if (errorCallback) {
                        errorCallback(arguments);
                    }
                },
                param,
                overrideName,
                profileName,
            ];

            //enable running queries omitting conections (avoid hitting QueryRouter)
            if (connection) {
                runQueryArgs.push(connection);
            }

            this.deltaClient.request.apply(this.deltaClient.request, runQueryArgs);
        }

