        public static legacyRowCallback(callback) {
            return function(data, bData) {
                if (callback) {
                    if (bData && bData.byteLength) {
                        if (data.dataSet) {
                            data.dataSet = ipc.Parse.reshape(data.dataSet, bData).toLegacy();
                        } else {
                            data = ipc.Parse.reshape(data, bData).toLegacy();
                        }

                        callback(data);
                    } else callback(data);
                }
            };
        }

