        /*
         * Function converts export parameters for Full export to compatible object
         *  for exact logic see <dashboardsDomainname>/demoClient/deltaAPIUAT.html#/main >
         * js/controllers/queryAPIControler.js > $scope.runKDBQuery();
         */
        public static processExportParams(exportParams) {
            const uiParams = new kdb.QDict(),
                kdbTypeMap = {
                    columnLabel: "SYMBOL",
                    dateFormat: "STRING",
                };
            _.each(kdbTypeMap, function(kdbType, propKey) {
                if (exportParams[propKey] && exportParams[propKey].length) {
                    // add columnLabel ui Param
                    const columnDict = new kdb.QDict();
                    const columnDetails = exportParams[propKey].split("&&");

                    _.each(columnDetails, function(col) {
                        const nameAndLabel = col.trim().split("#");
                        columnDict.add(nameAndLabel[0], nameAndLabel[1], kdb.KDB[kdbType]);
                    });

                    uiParams.add(propKey, columnDict, kdb.KDB.DICT);
                }
            });
            return uiParams;
        }

