        public static runAnalytic2(
            analytic: string,
            connection: string,
            params: any[],
            success: FnSuccess,
            error: Function,
            maxRows?: number,
            paging?: any,
            performance?: PerformanceMetrics,
        ) {
            const kdbParams = [],
                rows = maxRows && maxRows != 0 ? Number(maxRows) : null,
                self = this;

            if (!params) {
                params = [];
            }

            if (!connection) {
                connection = "ds_rdb_fx_eval";
            }

            if (!paging || !connection || !_.includes(DeltaClientLib.PAGING_TYPES, paging.type)) {
                paging = {
                    type: "NONE",
                };
            }

            //Create paramter object for passing ot deltaclient
            this.createParamsObj(kdbParams, params);

            if (!paging || paging.type === "NONE") {
                performance && performance.trigger("serverStart");
                const reqObj = this.deltaClient.request(
                    connection ? "AnalyticAPI.runBA" : "AnalyticAPI.run",
                    function(arg1, bData) {
                        performance && performance.trigger("serverEnd");
                        self.deltaClient.connection.removeResponder(reqObj.uid);
                        const data = connection ? deserialize(bData) : arg1;
                        if (success) {
                            success(data, bData);
                        }
                    },
                    function(data) {
                        self.deltaClient.connection.removeResponder(reqObj.uid);
                        Log.Error("Analytic run failed (" + analytic + ")", data);
                        if (error) {
                            error(data);
                        }
                    },
                    analytic,
                    kdbParams,
                    connection,
                    rows,
                );
            } else {
                performance && performance.trigger("serverStart");
                const reqObj = this.deltaClient.request(
                    connection ? "AnalyticAPI.runPagedBA" : "AnalyticAPI.runPaged",
                    function(data, bData) {
                        performance && performance.trigger("serverEnd");
                        self.deltaClient.connection.removeResponder(reqObj.uid);
                        var data = connection ? deserialize(bData) : data;
                        if (success) {
                            success(data, bData);
                        }
                    },
                    function(data) {
                        self.deltaClient.connection.removeResponder(reqObj.uid);
                        Log.Error("Analytic run failed (" + analytic + ")", data);
                        if (error) {
                            error(data);
                        }
                    },
                    analytic,
                    kdbParams,
                    connection,
                    paging.type,
                    paging.num,
                    paging.size,
                    _.keys(paging.sorting)[0] || "",
                    _.values(paging.sorting)[0] || false,
                );
            }
        }

        public static runApiFunction(apiFunction, connection, params, callback, errorCallback) {
            let args, delay, onceCallback;

            // ensure the callback is only called once
            onceCallback = _.once(function(success, data) {
                if (success) {
                    if (data && callback) {
                        callback(data);
                    }
                } else {
                    Log.Error("Api Analytic run failed (" + apiFunction + ")", data);
                    if (errorCallback) {
                        errorCallback(data);
                    }
                }

                clearTimeout(this.timeout);
            });

            // call error callback if no response within timeout
            this.timeout = _.delay(function() {
                if (onceCallback) {
                    onceCallback(false, "timed out");
                }
            }, this.DEFAULT_TIMEOUT);

            args = _.flatten([
                apiFunction,
                _.partial(onceCallback, true, _),
                _.partial(onceCallback, false, _),
                params,
            ]);

            this.deltaClient.request.apply(this.deltaClient, args);
        }

