        public static startManagedAnalytic2(
            analytic: string,
            connection: string,
            params: any[],
            success: FnSuccess,
            error: Function,
            interval: number,
            maxRows: number,
            performance: PerformanceMetrics,
        ) {
            const self = this,
                kdbParams = [],
                callback = success || _.noop,
                rows = maxRows || null;

            if (!params) {
                params = [];
            }

            //Create paramter object for passing ot deltaclient
            this.createParamsObj(kdbParams, params);

            //Get initial snapshot
            performance && performance.trigger("serverStart");
            this.deltaClient.request(
                connection ? "AnalyticAPI.runBA" : "AnalyticAPI.run",
                function(data, bData) {
                    performance && performance.trigger("serverEnd");
                    data = data || {};

                    if (bData && bData.byteLength) {
                        data = deserialize(bData);
                    }

                    callback(data, bData);

                    //Start managed analytic. Update frequency depends on interval variable
                    self.deltaClient.request(
                        "AnalyticAPI.startPolled",
                        function(data, bData) {
                            // reformat new api response into subscription response format
                            if (
                                data["class"] ==
                                    "com.fd.delta.stream.queryrouter.entity.QRResponseMessage" &&
                                data["success"]
                            ) {
                                callback(
                                    ({
                                        class: "207",
                                        add: true,
                                        subId: data.runID,
                                        clientId: "polled",
                                    } as unknown) as qTyped,
                                    undefined,
                                );
                            } else {
                                error(data);
                            }

                            DeltaClientLib.getPolledSubscription(data.runID).subscribe(function(d) {
                                if (!d.data[1]) error("Analytic Failed: " + d.data);

                                performance && performance.trigger("serverEnd");

                                if (d.appMsg && d.appMsg.byteLength) {
                                    d.data.class = "209";
                                    d.data.subId = d.data[0];
                                    d.data.clientId = "polled";
                                    d.data.dataSet = deserialize(d.appMsg);
                                }

                                callback(d.data, d.appMsg);
                            });
                        },
                        function(data) {
                            Log.Error("Managed Analytic run failed.", data);
                            if (error) {
                                error(data);
                            }
                        },
                        analytic,
                        kdbParams,
                        connection,
                        interval,
                        rows,
                    );
                },
                function(data) {
                    Log.Error("Analytic run failed.", data);
                    if (error) {
                        error(data);
                    }
                },
                analytic,
                kdbParams,
                connection,
                rows,
            );
        }

