        public static startLegacyManagedQuery2(
            connection: string,
            query: string,
            params: any[],
            success: FnSuccess,
            error: Function,
            interval: number,
            maxRows: number,
            paging: any,
            performance: PerformanceMetrics,
        ) {
            let self = this,
                callback = success || _.noop,
                formattedQuery,
                kdbParams = [],
                rows = maxRows && maxRows != 0 ? Number(maxRows) : null,
                runQueryArgs,
                sortDict,
                subQueryArgs;

            if (!paging || !connection || !_.includes(DeltaClientLib.PAGING_TYPES, paging.type)) {
                paging = {
                    type: "NONE",
                };
            }

            if (!paging || paging.type === "NONE") {
                formattedQuery = kdb.KDBQuery.create(this.strip(query), rows);
            } else {
                sortDict = new kdb.QDict();

                _.each(paging.sorting, function(value, key) {
                    sortDict.add(key, value === true, kdb.KDB.BOOLEAN);
                });

                formattedQuery = kdb.KDBQuery.createPaged(
                    this.strip(query),
                    paging.type,
                    paging.num,
                    paging.size,
                    sortDict,
                );
            }

            if (params) {
                this.createParamsObj(kdbParams, params);
                formattedQuery.parameters = kdbParams;
            }

            // run a static query first to get immediate results
            runQueryArgs = [
                connection ? "QueryAPI.runQueryBA" : "QueryAPI.runQuery",
                function(data, bData) {
                    data = data || {};
                    performance && performance.trigger("serverEnd");

                    if (bData && bData.byteLength) {
                        data = deserialize(bData);
                    }

                    callback(data, bData);

                    // run subscription query
                    self.deltaClient.request.apply(self.deltaClient.request, subQueryArgs);
                },
                function() {
                    if (error) {
                        error(arguments);
                    }
                },
                formattedQuery,
                connection,
            ];

            subQueryArgs = [
                "SubscriptionAPI.addSubscription",
                function(data, bData) {
                    if (callback) {
                        if (bData && bData.byteLength) {
                            data.dataSet = deserialize(bData);
                        }

                        callback(data, bData);
                    }
                },
                function(data) {
                    if (error) {
                        error(data);
                    }
                },
                formattedQuery,
                connection,
                interval,
            ];
            performance && performance.trigger("serverStart");
            this.deltaClient.request.apply(this.deltaClient.request, runQueryArgs);
        }

