/// <reference path="../lib/delta-client-lib.ts" />
/// <reference path="../lib/dashClient.ts" />
/// <reference path="../classes/Agent.ts" />
/// <reference path="../classes/ComponentInterface.ts"/>
/// <reference path="../classes/DataMapper.ts" />
/// <reference path="../classes/DataSet.ts" />
/// <reference path="../classes/DataSet2.ts" />
/// <reference path="../classes/log.ts"/>
/// <reference path="../classes/LegacyUpdate.ts"/>
/// <reference path="../classes/PerformanceMetrics.ts"/>
/// <reference path="../classes/viewStateBinding.ts"/>
/// <reference path="connection.ts"/>
/// <reference path="../lib/ipc/Parse.ts" />
/// <reference path="../lib/qb/QueryBuilder.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class DocumentDataModel extends Backbone.Model
        implements Commandable, DataModelInterface {
        static readonly DEFAULT_MAX_ROWS = null;
        static readonly DEFAULT_PAGE_SIZE = 2000;
        static readonly DEFAULT_PAGING = "NONE";
        static readonly IMMATERIAL_ATTRIBUTES = ["error", "queryStatus"];
        static readonly NO_DATA_ERROR_TEXT = "No data returned from selected data source";
        static readonly PAGING_ATTRIBUTES = [
            "_pagingNum",
            "_pageSize",
            "_pagingType",
            "_pageSort",
            "_serverPaging",
        ];
        static readonly PARAMETER_RX = new RegExp("<%([^<>%]+?)%>", "g");
        static readonly TYPES_TO_PARSE = [
            "date",
            "datetime",
            "guid",
            "minute",
            "month",
            "second",
            "time",
            "timespan",
            "timestamp",
        ];
        static readonly VIEW_STATE_ATTRIBUTES = {
            // attributes that can be bound to view states
            analytic: {
                _subscriptionInterval: "onQuerySettingChange",
            },
            pivot: {
                _aggregateCols: "onPivotSettingChange",
                _aggregateFns: "onPivotSettingChange",
                _aggregateLabels: "onPivotSettingChange",
                _breakdownCols: "onPivotSettingChange",
            },
            query: {
                _subscriptionInterval: "onQuerySettingChange",
            },
        };
        static readonly VIRTUAL_QUERY_REGEX = /^(function(?: )?\()([a-z0-9A-Z,\w\/ ]+?)(,(?: +)?callback\)(?: ){)([\s\S]+)(\})$/;
        static readonly AGGREGATE_COLS_PROP = "_aggregateCols";
        static readonly AGGREGATE_LABELS_PROP = "_aggregateLabels";
        static readonly AUTO_EXECUTE = "_autoExecute";
        static readonly OLD_THRESHOLD = 2000;
        static readonly FAST_THRESHOLD = 10;
        agent = new Agent();
        callbacks = [];
        clientPivotData: any;
        dataMapper: DataMapper = null;
        dataSet: DataSet = null;
        dataSet2: DataSet2 = null;
        debouncedParameterChange: Function;
        execute: Function;
        forceInternalReset = false;
        forceRender?: boolean;
        group: boolean;
        isClone = false;
        isDirty = false;
        isPending = false;
        legacyFlushFrame?: number;
        legacyFlushTime = 0;
        consolidateCount = 0;
        dataExecuteSuccessCount = 0;
        onExecuteBeginCount = 0;
        durationMovingAverage = DocumentDataModel.FAST_THRESHOLD;
        deferredUpdate?: LegacyUpdate;
        meta: any;
        paramChanged = [];
        parameterModels: Array<any> = [];
        performance: PerformanceMetrics = new PerformanceMetrics();
        path: string;
        pollingEvent = null;
        root: DocumentDataModel;
        subscriptionId = null;
        subscriptionTypes = ["polling", "subscription", "streaming"];
        viewStateBindings: { [index: string]: ViewStateBinding } = {};
        viewState: any;
        virtualSubscriptions = {};

        constructor(
            attributes: Record<string, any>,
            root: DocumentDataModel | null,
            path: string,
            viewState: any,
            group?: boolean,
            virtualSubscriptions?: Record<string, any>,
        ) {
            super(attributes);

            const dataModels = [];

            this.root = root || this;
            this.path = path;
            this.viewState = viewState;
            this.group = group;
            this.virtualSubscriptions = virtualSubscriptions || {};

            // fix to avoid setting defaults on a group
            if (this.group) {
                this.unset("_pagingType", { silent: true });
                this.unset(DocumentDataModel.AUTO_EXECUTE, { silent: true });
            }

            // FIX PIVOT
            if (!this.get("_queryString") && (this.get("_pivotQuery") || this.get("_table"))) {
                this.set(
                    "_queryString",
                    this.get("_pivotQuery") || "select from " + this.get("_table"),
                );
                this.unset("_pivotQuery");
                this.unset("_table");
            }

            // convert each child object into a DocumentData Model
            _.keys(this.attributes).forEach(key => {
                const obj = this.get(key);

                if (_.isArray(obj)) {
                    // do nothing
                } else if (_.isObject(obj) && !(obj instanceof Backbone.Model)) {
                    // if dataType - it is a data source apply
                    if (obj["_dataType"]) {
                        // HACK TODO
                        if (obj["_dataType"] === "copy") {
                            obj["_dataType"] = "query";
                        }

                        if (
                            _.includes(
                                [
                                    "analytic",
                                    "streaming",
                                    "pivot",
                                    "query",
                                    "virtual",
                                    "builder",
                                    "sql",
                                ],
                                obj["_dataType"],
                            )
                        ) {
                            const model = new DocumentDataModel(
                                obj,
                                this.root,
                                this.path ? this.path + "/" + key : key,
                                this.viewState,
                            );

                            dataModels.push(model);
                            this.set(key, model, { silent: true });

                            // eslint-disable-next-line @typescript-eslint/unbound-method
                            this.listenTo(model, "materialChange", this.onMaterialChange);
                        } else {
                            Log.Error(
                                this.logPrefix() + " unsupported datatype: ",
                                obj["_dataType"],
                            );
                        }
                    } else if (obj["_dashboardsType"] === "viewstate") {
                        const model = this.viewState.getByPath(obj["value"]);
                        this.set(key, model, { silent: true });
                    } else if (
                        !_.includes(["_analyticParams", "_mappings", "_pageSort"], key) &&
                        !(obj instanceof Backbone.Model && obj.get("_viewType"))
                    ) {
                        const model = new DocumentDataModel(
                            obj,
                            this.root,
                            this.path ? this.path + "/" + key : key,
                            this.viewState,
                            true,
                        );

                        this.set(key, model, { silent: true });
                        this.listenTo(model, "change", () => this.trigger("change", this));

                        // eslint-disable-next-line @typescript-eslint/unbound-method
                        this.listenTo(model, "materialChange", this.onMaterialChange);
                    }
                }
            });

            //fix: KXAX-11979
            //reset settings, so data config popup doesn't reuse paging info from datagrid on the dashboard
            if (this.has("_pagingNum")) {
                this.set("_pagingNum", 0, { silent: true });
            }
            if (this.has("_pageSort")) {
                this.set("_pageSort", {}, { silent: true });
            }

            this.debouncedParameterChange = _.throttle(this.applyParameterChange.bind(this), 60, {
                leading: false,
            });

            // eslint-disable-next-line @typescript-eslint/unbound-method
            this.listenTo(this, "change", this.onModelChange);
        }

        static convertDeltaClientColumnSpecToMeta(columnSpec): ColumnInfo[] {
            //{"columns":["sym","midprice"],"meta":{"sym":11,"midprice":9}
            return (columnSpec["columns"] || []).map((col: string, i: number) => {
                return {
                    id: col,
                    index: i,
                    kdbType: columnSpec["meta"][col],
                };
            });
        }

        static convertMetaToDeltaClientColumnSpec(columns: ColumnInfo[]): any {
            return {
                columns: _.map(columns, c => c.id),
                meta: _.fromPairs(_.map(columns, c => [c.id, c.kdbType])),
            };
        }

        apply(): void {
            this.performance.trigger("apply");
            let isVirtual = false;
            // components listen for change:error so we need an initial value that we can unset later
            if (!this.has("error")) {
                this.set("error", null, { silent: true });
            }

            if (this.get("_dataType") === "pivot") {
                this.set(
                    {
                        _dataType: "query",
                        _pivotType: "server",
                    },
                    { silent: true },
                );
            }

            // reset model state
            this.stop();
            this.reset();

            if (this.get("_pivotType")) {
                this.applyPivot();
            } else {
                switch (this.get("_dataType")) {
                    case "query":
                        this.applyQuery();
                        break;
                    case "analytic":
                    case "builder":
                    case "streaming":
                        this.applyAnalytic();
                        break;
                    case "copy":
                        this.applyCopy();
                        break;
                    case "virtual":
                        this.applyVirtual();
                        isVirtual = true;
                        break;
                    case "sql":
                        this.applyQuery();
                        break;
                }
            }

            this.forceInternalReset = true;

            if (isVirtual && this.performance) this.performance.trigger("serverStart");
            this.updateVirtualSubscriptions();
            if (isVirtual && this.performance) this.performance.trigger("serverEnd");

            this.dataMapper = new DataMapper({
                mappings: this.get("_mappings"),
                setMappings: (mappings): void => {
                    this.set("_mappings", mappings);
                },
                subscribe: (key, listener): void => this.subscribe(key, listener, !isVirtual),
                unsubscribe: (key): void => this.unsubscribe(key),
                viewState: this.viewState,
            });

            this.dataMapper.initialize();
        }

        applyCopy(): void {
            const idAttribute = this.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX;

            // set up data set to receive data
            this.dataSet = new DataSet({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: this.agent,
                maxRows: this.get("_maxRows"),
                primaryKey: idAttribute,
            });

            // set defaults
            this.unset("error");
            this.unset("queryStatus");
            this.updateParameters();

            // copy has no params to be dirty
            this.isDirty = false;
            this.execute = _.noop.bind(this);
        }

        applyAnalytic(): void {
            const idAttribute = this.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX;
            const successFn = (data: qTyped, buffer: ArrayBuffer): void => {
                this.onExecuteSubscriptionSuccess(data, this.get("_forceReset"), buffer);
            };
            const errorFn = (error, ...args): void => {
                this.onError(this, this.attributes, error, "Error", args);
            };

            let paging;
            const viewStateOptions = this.applyViewStateAttributes();

            // set up data set to receive data
            this.dataSet = new DataSet({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: this.agent,
                maxRows: this.get("_maxRows"),
                primaryKey: idAttribute,
            });

            // set up data set to receive data
            this.dataSet2 = new DataSet2({
                idAttribute: idAttribute,
                agent: this.agent,
                maxRows: this.get("_maxRows"),
                primaryKey: idAttribute,
            });

            this.unset("error");
            this.unset("queryStatus");
            this.updateParameters(true);

            if (this.get("_subscriptionType") === "subscription") {
                const subscriptionInterval = this.formatValue(
                    viewStateOptions["_subscriptionInterval"] as number,
                    "_subscriptionInterval",
                );

                this.execute = this.wrapWithErrorHandler(
                    () => {
                        this.onExecuteBegin();
                        if (this.get("_serverPaging")) {
                            paging = {
                                type: this.getPagingType(),
                                num: this.get("_pagingNum") || 0,
                                size: this.get("_pageSize") || DocumentDataModel.DEFAULT_PAGE_SIZE,
                                sorting: this.get("_pageSort") || {},
                            };
                        }

                        this.runWithConnectionCheck(errorFn, () => {
                            this.updateBuilder(() => {
                                DeltaClientLib.startManagedAnalytic2(
                                    this.get("_selectedAnalytic"),
                                    this.get("_connection"),
                                    this.bindViewStateToKdbParams(this.get("_analyticParams")),
                                    successFn,
                                    errorFn,
                                    subscriptionInterval,
                                    this.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS,
                                    this.performance,
                                );
                            }, errorFn);
                        });
                    },
                    this,
                    this.attributes,
                );
            } else if (this.get("_subscriptionType") === "streaming") {
                this.execute = this.wrapWithErrorHandler(
                    () => {
                        this.updateBuilder(() => {
                            this.onExecuteBegin();
                            this.runWithConnectionCheck(errorFn, () => {
                                DeltaClientLib.startStreamingAnalytic2(
                                    this.get("_selectedAnalytic"),
                                    this.get("_connection"),
                                    this.bindViewStateToKdbParams(this.get("_analyticParams")),
                                    successFn,
                                    errorFn,
                                    this.performance,
                                );
                            });
                        }, errorFn);
                    },
                    this,
                    this.attributes,
                );
            } else {
                this.execute = this.wrapWithErrorHandler(
                    () => {
                        this.updateBuilder(() => {
                            this.onExecuteBegin();

                            if (this.get("_serverPaging")) {
                                paging = {
                                    type: this.getPagingType(),
                                    num: this.get("_pagingNum") || 0,
                                    size:
                                        this.get("_pageSize") ||
                                        DocumentDataModel.DEFAULT_PAGE_SIZE,
                                    sorting: this.get("_pageSort") || {},
                                };
                            }

                            this.runWithConnectionCheck(errorFn, () => {
                                DeltaClientLib.runAnalytic2(
                                    this.get("_selectedAnalytic"),
                                    this.get("_connection"),
                                    this.bindViewStateToKdbParams(this.get("_analyticParams")),
                                    successFn,
                                    errorFn,
                                    this.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS,
                                    paging,
                                    this.performance,
                                );
                            });
                        }, errorFn);
                    },
                    this,
                    this.attributes,
                );
            }
        }

        updateBuilder(success, error): void {
            // return if not builder
            if (this.get("_dataType") !== "builder") {
                success();
                return;
            }

            // set analytic name
            const isStreaming = this.get("_subscriptionType") === "streaming";
            const queryBuilderApp = new QuickBase.QueryBuilder.App();
            const qIPC = queryBuilderApp.getIPC(
                this.get("_graphJSON"),
                this.get("_selectedNode"),
                this,
            );

            if (isStreaming) {
                this.set("_selectedAnalytic", qIPC);
                this.set("_analyticParams", [], { silent: true });
            } else {
                this.set("_selectedAnalytic", ".queryBuilder.processGraph2");

                // set analytic params
                this.set(
                    "_analyticParams",
                    [
                        {
                            id: "graph",
                            name: "graph",
                            index: 0,
                            type: isStreaming ? "symbol" : "string",
                            value: qIPC,
                        },
                    ],
                    { silent: true },
                );
            }

            success();
        }

        runWithConnectionCheck(errorFn, func): void {
            const connection = this.get("_connection");
            const ignoreNullConnectionCheck = this.get("_ignoreNullConnectionCheck");

            if (ignoreNullConnectionCheck) {
                func();
            } else if (connection === undefined || connection === null || connection === "DC") {
                errorFn({
                    message: "Invalid connection specified",
                });
            } else {
                func();
            }
        }

        applyPivot(): void {
            let breakdownCols = this.get("_breakdownCols"),
                lastModel;

            const options = {
                dataSource: this.get("_dataSource"),
                connection: this.get("_connection"),
                pivotSource: "query",

                // upgrade old table value to query
                queryString: this.get("_queryString"),
                queryParams: this.get("_queryParams"),
                breakdownCols: this.get("_breakdownCols"),
                aggFn: this.get("_aggFn"),
                ordering: this.get("_ordering"),
                maxRows: this.get("_maxRows"),
                subscriptionType: this.get("_subscriptionType"),
                subscriptionInterval: this.get("_subscriptionInterval"),
                columnLabel: this.get("_columnLabel"),
            };
            const viewStateOptions = this.applyViewStateAttributes();

            _.each(viewStateOptions, (value, key) => {
                if (key === "_subscriptionInterval") {
                    viewStateOptions[key] = this.formatValue(
                        value as number,
                        "_subscriptionInterval",
                    );
                } else {
                    // trim unnecessary whitespace and try to split strings in arrays
                    if (_.isString(value)) {
                        viewStateOptions[key] = value.trim();
                        viewStateOptions[key] = value.split(",");
                    }
                }

                // DeltaClientLib expects attributes without _ prefix
                options[key.replace("_", "")] = value;
            });

            breakdownCols = options["breakdownCols"];

            lastModel = Backbone.Model.extend({
                idAttribute: _.last(breakdownCols),
                getRoot: () => this,
            });

            // loop calculating values from the level n bottom up to level 0
            _.each(breakdownCols, (breakdownEl, i: number) => {
                const localOptions = {};

                // calc ids for generate pivot model
                const childId = breakdownCols[breakdownCols.length - i - 1];
                const id =
                    i === breakdownCols.length - 1
                        ? "id"
                        : breakdownCols[breakdownCols.length - i - 2];

                // DEBUG
                //console.log("id:" + id, breakdownCols.slice(0, breakdownCols.length - i), breakdownCols.slice(0, breakdownCols.length - i - 1));

                // calc local options object for generate pivot model
                _.extend(localOptions, options, {
                    dimensions: breakdownCols.slice(0, breakdownCols.length - i),
                    wheres: breakdownCols.slice(0, breakdownCols.length - i - 1),
                    id: id,
                });

                // generate pivot model - using previous as inner child
                lastModel = this.generatePivotModel(id, childId, lastModel, localOptions);
            });

            this.unset("error");
            this.unset("queryStatus");
            this.updateParameters();

            _.extend(this, {
                dataSet: new DataSet({
                    idAttribute: lastModel.prototype.childId,
                    collectionFunc: Backbone.Collection.extend({
                        model: lastModel.prototype.childModelType || lastModel,
                    }),
                    agent: this.agent,
                    maxRows: options.maxRows,
                    primaryKey: lastModel.prototype.childId,
                }),
                dataSet2: new DataSet2({
                    idAttribute: lastModel.prototype.childId,
                    agent: this.agent,
                    maxRows: options.maxRows,
                    primaryKey: lastModel.prototype.childId,
                }),
                execute: lastModel.prototype.execute || _.noop.bind(this),
                exportPivotData: lastModel.prototype.exportPivotData,
                staticIdAttribute: lastModel.staticIdAttribute,
            });
        }

        applyQuery(): void {
            const idAttribute = this.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX;
            const successFn = (data: any, buffer: ArrayBuffer): void => {
                this.onExecuteSubscriptionSuccess(data, this.get("_forceReset"), buffer);
            };
            const errorFn = (error, ...args): void => {
                this.onError(this, this.attributes, error, "Error", args);
            };

            const viewStateOptions = this.applyViewStateAttributes();

            // set up data set to receive data
            this.dataSet = new DataSet({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: this.agent,
                lazyLoading: this.get("_lazyLoading"),
                maxRows: this.get("_maxRows"),
                primaryKey: idAttribute,
            });

            // set up data set to receive data
            this.dataSet2 = new DataSet2({
                idAttribute: idAttribute,
                agent: this.agent,
                lazyLoading: this.get("_lazyLoading"),
                maxRows: this.get("_maxRows"),
                primaryKey: idAttribute,
            });

            // set defaults
            this.unset("error");
            this.unset("queryStatus");
            this.updateParameters();

            if (this.get("_subscriptionType") === "subscription") {
                const subscriptionInterval = this.formatValue(
                    viewStateOptions["_subscriptionInterval"] as number,
                    "_subscriptionInterval",
                );

                this.execute = this.wrapWithErrorHandler(
                    () => {
                        let paging = null;

                        this.onExecuteBegin();
                        const query = this.replaceViewStateParams(this.get("_queryString"));

                        if (this.get("_serverPaging")) {
                            paging = {
                                type: this.getPagingType(),
                                num: this.get("_pagingNum") || 0,
                                size: this.get("_pageSize") || DocumentDataModel.DEFAULT_PAGE_SIZE,
                                sorting: this.get("_pageSort") || {},
                            };
                        }

                        this.runWithConnectionCheck(errorFn, () => {
                            DeltaClientLib.startManagedQuery2(
                                this.get("_connection"),
                                query,
                                this.bindViewStateToKdbParams(this.get("_queryParams")),
                                successFn,
                                errorFn,
                                subscriptionInterval,
                                this.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS,
                                paging,
                                this.performance,
                            );
                        });
                    },
                    this,
                    this.attributes,
                );
            } else {
                this.execute = this.wrapWithErrorHandler(
                    () => {
                        let paging = null;

                        this.onExecuteBegin();
                        const query = this.replaceViewStateParams(this.get("_queryString"));

                        if (this.get("_serverPaging")) {
                            paging = {
                                type: this.getPagingType(),
                                num: this.get("_pagingNum") || 0,
                                size: this.get("_pageSize") || DocumentDataModel.DEFAULT_PAGE_SIZE,
                                sorting: this.get("_pageSort") || {},
                            };
                        }

                        this.runWithConnectionCheck(errorFn, () => {
                            DeltaClientLib.getQueryData2(
                                this.get("_dataSource"),
                                this.get("_connection"),
                                query,
                                this.bindViewStateToKdbParams(this.get("_queryParams")),
                                successFn,
                                errorFn,
                                this.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS,
                                paging,
                                this.performance,
                            );
                        });
                    },
                    this,
                    this.attributes,
                );
            }
        }

        applyVirtual(): void {
            const idAttribute = this.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX;
            const successFn = (data: DCDS, buffer: ArrayBuffer): void => {
                this.onExecuteSuccess(data, this.get("_forceReset"), buffer);
            };

            // set up data set to receive data
            this.dataSet = new DataSet({
                idAttribute: idAttribute,
                collectionFunc: Backbone.Collection.extend({
                    model: Backbone.Model.extend({ idAttribute: idAttribute }),
                }),
                agent: this.agent,
                // not possible to edit number of rows on virtual source in Data Dialog
                // set to max int
                maxRows: 9007199254740991,
                primaryKey: idAttribute,
            });

            // set defaults
            this.unset("error");
            this.unset("queryStatus");
            this.updateParameters();

            this.execute = this.wrapWithErrorHandler(
                function() {
                    this.onExecuteBegin();
                    // need to update subscriptions in the event you toggle between
                    // different virtual data sources
                    this.updateVirtualSubscriptions();
                    this.executeVirtualQuery(
                        this.get("_virtualQueryString"),
                        this.get("_virtualParams"),
                        successFn,
                    );
                },
                this,
                this.attributes,
            );
        }

        applyParameterChange(): void {
            let forceExecute, isApplyRequired;

            if (this.paramChanged && this.paramChanged.length) {
                forceExecute = !!_.find(this.paramChanged, o => o === "#forced");
                isApplyRequired = !!_.find(this.paramChanged, o => o.runApply);

                Log.Info(this.logPrefix() + "apply parameter change", this.paramChanged);
                this.paramChanged = [];

                if (this.agent.subscriberCount()) {
                    if (isApplyRequired) {
                        this.apply();
                        this.start();
                    } else if (this.isPending) {
                        this.isDirty = true;
                    } else {
                        this.isDirty = this.isDirty || forceExecute;
                        this.performance.trigger("paramChange");
                        this.start();
                    }
                } else if (forceExecute) {
                    const executeCount = this.onExecuteBeginCount;

                    if (!this.dataSet) {
                        // apply() might trigger an execute(), but not always
                        this.apply();
                    }

                    // if count is the same, then manually execute()
                    if (executeCount === this.onExecuteBeginCount) {
                        this.execute();
                    }
                }
            }
        }

        applyViewStateAttributes(): { [index: string]: number | string | string[] } {
            const attributes = {};
            const viewStateAttributes =
                _.clone(DocumentDataModel.VIEW_STATE_ATTRIBUTES[this.get("_dataType")]) || {};

            let unusedBindings = _.clone(this.viewStateBindings);

            if (this.get("_pivotType")) {
                _.extend(viewStateAttributes, DocumentDataModel.VIEW_STATE_ATTRIBUTES["pivot"]);
            }

            _.each(viewStateAttributes, (callback, att) => {
                let value = this.get(att);

                const existingBinding = _.findKey(unusedBindings, function(binding, key) {
                    return key === att && binding.callback === callback && binding.model === value;
                });

                if (existingBinding) {
                    // keep existing binding
                    unusedBindings = _.omit(unusedBindings, existingBinding);
                    value = value.get("value");
                } else {
                    // if it's assigned to a view state then add a new binding
                    if (value instanceof Backbone.Model) {
                        this.listenTo(value, "change:value", this[callback]);
                        this.viewStateBindings[att] = new ViewStateBinding(
                            callback,
                            value as DocumentViewModel,
                        );
                        value = value.get("value");
                    }
                }

                attributes[att] = value;
            });

            //fix: labels length not matching columns length
            if (
                this.get("_pivotType") &&
                attributes[DocumentDataModel.AGGREGATE_COLS_PROP] &&
                attributes[DocumentDataModel.AGGREGATE_LABELS_PROP] &&
                attributes[DocumentDataModel.AGGREGATE_COLS_PROP].length !==
                    attributes[DocumentDataModel.AGGREGATE_LABELS_PROP].length
            ) {
                Log.Warn(this.logPrefix() + "invalid aggregate labels length");
                attributes[DocumentDataModel.AGGREGATE_LABELS_PROP] = [];
            }

            // stop listening to unused models
            _.each(unusedBindings, binding => {
                this.stopListening(binding.model, "change:value", binding.callback);
            });

            return attributes;
        }

        //Parse params to look for viewstate parameters
        bindViewStateToKdbParams(queryParams): any[] {
            let typeName, typeDef, paramPath;
            const newParams = [];

            //Copy params array so we don't trigger an update event on the model
            $.extend(true, newParams, queryParams);

            //Replace viewstate parameters with values on relevent kdb query parameters
            if (newParams && newParams.length > 0) {
                newParams.forEach(queryParam => {
                    if (typeof queryParam.value === "string") {
                        paramPath = queryParam.value.match(DocumentDataModel.PARAMETER_RX);
                        if (paramPath) {
                            const viewModel = this.getViewState().getByPath(
                                $.trim(paramPath[0].substring(2, paramPath[0].length - 2)),
                            );

                            if (viewModel) {
                                const val = _.isFunction(viewModel.get)
                                    ? viewModel.get("value")
                                    : viewModel["value"];
                                if (val === undefined) {
                                    queryParam.value = null;
                                }

                                queryParam.value =
                                    _.isObject(val) && !_.isArray(val)
                                        ? $.extend(true, {}, val)
                                        : val;
                            }
                        }
                    }

                    // dict types - replace viewstate path with viewstate value
                    if (queryParam.type === "dict" && _.isObject(queryParam.value)) {
                        _.keys(queryParam.value).forEach(key => {
                            if (queryParam.value[key] instanceof Backbone.Model) {
                                queryParam.value[key] = {
                                    type: queryParam.value[key].get("_type"),
                                    IsKdbParam: true,
                                    isViewState: true,
                                    name: queryParam.value[key].path,
                                    value: queryParam.value[key].get("value"),
                                };
                            } else if (typeof queryParam.value[key] === "object") {
                                //when no viewstates present (e.g. in ActionTracker, DDM is created without viewstates),
                                // no conversion is required for dictionary values
                                if (_.has(queryParam.value[key], "_isKdbReady")) return;

                                // Update value
                                if (
                                    typeof queryParam.value[key].value === "string" &&
                                    queryParam.value[key].value.indexOf("<%") === 0
                                ) {
                                    // path to view state is in the value of the object
                                    const queryPath = $.trim(
                                        queryParam.value[key].value.substring(
                                            2,
                                            queryParam.value[key].value.length - 2,
                                        ),
                                    );
                                    const viewModel = this.getViewState().getByPath(queryPath);
                                    if (viewModel) {
                                        queryParam.value[key].type = viewModel.get("_type");
                                        queryParam.value[key].value = viewModel.get("value");
                                    }
                                } else if (queryParam.value[key].type === "dict") {
                                    queryParam.value[key] = this.bindViewStateToKdbParams([
                                        queryParam.value[key],
                                    ])[0];
                                }
                            } else if (typeof queryParam.value[key] === "string") {
                                // Generate parameter
                                let queryPath: string;

                                if (queryParam.value[key].indexOf("<%") === 0) {
                                    queryPath = $.trim(
                                        queryParam.value[key].substring(
                                            2,
                                            queryParam.value[key].length - 2,
                                        ),
                                    );

                                    const viewModel = this.getViewState().getByPath(queryPath);

                                    if (viewModel) {
                                        queryParam.value[key] = {
                                            type: viewModel.get("_type"),
                                            IsKdbParam: true,
                                            isViewState: true,
                                            name: queryParam.value[key],
                                            value: viewModel.get("value"),
                                        };
                                    }
                                }
                            }

                            // apply toKDBInput
                            typeName = queryParam.value[key].type;
                            typeDef = typeName ? RTTI.getByName(typeName) : null;
                            if (typeDef && typeDef.toKDBInput) {
                                queryParam.value[key].value = _.includes(
                                    DocumentDataModel.TYPES_TO_PARSE,
                                    typeName,
                                )
                                    ? typeDef.toKDBInput(queryParam.value[key].value)
                                    : queryParam.value[key].value;
                                Log.Info(
                                    this.logPrefix() +
                                        "View State  Binding [" +
                                        queryParam.name +
                                        "]",
                                    queryParam.value[key].value,
                                );
                            }
                        });
                    } else {
                        typeName = queryParam.type;
                        typeDef = RTTI.getByName(typeName);
                        if (typeDef) {
                            if (typeDef.name !== "dict" && typeDef.toKDBInput) {
                                queryParam.value = _.includes(
                                    DocumentDataModel.TYPES_TO_PARSE,
                                    typeName,
                                )
                                    ? typeDef.toKDBInput(queryParam.value)
                                    : queryParam.value;
                                Log.Info(
                                    this.logPrefix() +
                                        "View State  Binding [" +
                                        queryParam.name +
                                        "]",
                                    queryParam.value,
                                );
                            } else if (typeDef.name === "dict") {
                                // items dilimeter
                                const dictItems = queryParam.value.split("\u0002");
                                const paramValue = {};

                                dictItems.forEach(item => {
                                    // item properies dilimeter
                                    const parts = item.split("\u0001");

                                    if (parts.length === 6) {
                                        // key is name
                                        paramValue[parts[0]] = {
                                            type: parts[1],
                                            IsKdbParam: true,
                                            isViewState: true,
                                            name: parts[0],
                                            value: parts[2],
                                        };
                                    }
                                });

                                queryParam.value = paramValue;
                            }
                        }
                    }
                });
            }

            return newParams;
        }

        clearVirtualSubscriptions(keys): void {
            _.each(keys, key => {
                const subscription = this.virtualSubscriptions[key];

                if (subscription) {
                    if (subscription instanceof DocumentDataModel) {
                        subscription.unsubscribe(key);
                    }

                    delete this.virtualSubscriptions[key];
                }
            });
        }

        //clears all virtual subscription
        clearVirtualSubscriptionsAll(): void {
            const keys = _.keys(this.virtualSubscriptions);
            this.clearVirtualSubscriptions(keys);
        }

        clone(): DocumentDataModel {
            const clone = new DocumentDataModel(
                this.attributes,
                this.root,
                this.path,
                this.viewState,
                this.group,
            );

            //_.each(['dataSet', 'path', 'viewState'], function (key) {
            //    clone[key] = this[key];
            //}, this);
            clone.isClone = true;

            return clone;
        }

        defaults(): any {
            return { _pagingType: "NONE", [DocumentDataModel.AUTO_EXECUTE]: true };
        }

        destroy(): void {
            // stop fetching data
            this.stop();
            this.reset();

            if (this.agent) {
                this.agent.unsubscribeAll();
            }

            Backbone.Model.prototype.destroy.call(this);
        }

        determineMaterialChange(): void {
            if (
                this.changed &&
                !_.isEmpty(_.omit(this.changed, DocumentDataModel.IMMATERIAL_ATTRIBUTES))
            ) {
                this.trigger("materialChange");
            }
        }

        doCommand(path: Array<string>, todos: Array<Todo>): void {
            if (path.length > 0) {
                const vsPath = path.reverse().join("/");
                return this.getByPath(vsPath).doCommand(path, todos);
            }

            //TODO: do something its your command
        }

        executeClientPivotQuery(base: DCDS, options) {
            const data = {
                columns: [],
                meta: {},
                rows: [],
            };

            // check no data
            if (!base) {
                return data;
            }

            const aggregateCols = this.getViewStateAttribute("_aggregateCols") || [];
            const aggregateFns = this.getViewStateAttribute("_aggregateFns") || [];
            let aggregateLabels = this.getViewStateAttribute("_aggregateLabels") || [];
            const breakdownCols: string[] = this.getViewStateAttribute("_breakdownCols") || [];
            const aggregateCompare = aggregateCols.map(c =>
                Tools.getComparator(base.meta[c] || 0, a => a),
            );

            aggregateLabels = _.map(aggregateCols, function(aggregateCol, i) {
                return aggregateLabels[i] || aggregateCol;
            });

            let wheres = options && options.wheres ? options.wheres : [];

            // only include wheres that match breakdownCols
            wheres = _.filter(wheres, function(where, i) {
                // use meta to pick comparator
                where.comparator = Tools.getComparator(base.meta[where.columnName] || 0, a => a);
                return where.columnName === breakdownCols[i];
            });

            // determine current breakdown columns
            const currentBreakdownCols = _.take(breakdownCols, wheres.length + 1);

            // get the last breakdown column to group by and compare
            const lastBreakdown: string = _.last(currentBreakdownCols);
            const groupCompare = Tools.getComparator(
                base.meta[lastBreakdown] || 0,
                b => b[lastBreakdown],
            );

            // for each row in data
            base.rows.forEach(row => {
                // filter the rows by the where clauses
                let j = 0;
                for (; j < wheres.length; j++) {
                    const w = wheres[j];
                    if (w.comparator(w["columnValue"], row[w["columnName"]]) !== 0) {
                        break;
                    }
                }

                if (j < wheres.length) return;

                // update groups
                const key = row[lastBreakdown];
                const groupIndex = Tools.binarySearch2(key, data.rows, groupCompare);
                let group;

                if (groupIndex < 0) {
                    group = _.pick(row, currentBreakdownCols);
                    data.rows.splice(-groupIndex - 1, 0, group);

                    // initialize group
                    aggregateLabels.forEach((label, i) => {
                        if (aggregateFns[i] === "count") {
                            group[label] = 0;
                        } else if (_.includes(["max", "min", "sum"], aggregateFns[i])) {
                            group[label] = null;
                        } else {
                            group[label] = [];
                        }
                    });
                } else group = data.rows[groupIndex];

                // update aggregate columns on existing group
                aggregateCols.forEach((col, i) => {
                    const fn = aggregateFns[i],
                        label = aggregateLabels[i];

                    // update our aggregate with the new value
                    switch (fn) {
                        case "count":
                            group[label] += 1;
                            break;
                        case "max":
                            if (
                                row[col] !== null &&
                                (group[label] === null ||
                                    aggregateCompare[i](group[label], row[col]) === -1)
                            ) {
                                group[label] = row[col];
                            }
                            break;
                        case "min":
                            if (
                                row[col] !== null &&
                                (group[label] === null ||
                                    aggregateCompare[i](group[label], row[col]) === 1)
                            ) {
                                group[label] = row[col];
                            }
                            break;
                        case "sum":
                            if (row[col] !== null) {
                                group[label] += Number(row[col]);
                            }
                            break;
                        default:
                            // custom function or avg
                            group[label] = group[label] || [];
                            group[label].push(row);
                            break;
                    }
                });
            });

            // post process custom fns and averages
            if (!_.every(aggregateFns, a => _.includes(["count", "max", "min", "sum"], a))) {
                data.rows.forEach(row => {
                    aggregateLabels.forEach((label, j) => {
                        let d = row[label];
                        const fn = aggregateFns[j];

                        if (_.isArray(d)) {
                            switch (fn) {
                                case "avg":
                                    // avg function requires number type
                                    d = _.map(d, v =>
                                        _.transform(v, (acc, val, key) => (acc[key] = Number(val))),
                                    );
                                    row[label] = _.meanBy(d, aggregateCols[j]);
                                    break;
                                case "first":
                                    row[label] = _.first(d)[aggregateCols[j]];
                                    break;
                                case "last":
                                    row[label] = _.last(d)[aggregateCols[j]];
                                    break;
                                default:
                                    // custom function
                                    row[label] = this.executeUserJavascript(fn, ["data"], [d]);
                                    break;
                            }
                        }
                    });
                });
            }

            // add breakdown columns and set meta
            _.each(currentBreakdownCols, function(breakdownCol) {
                data["columns"].push(breakdownCol);
                data["meta"][breakdownCol] = base.meta[breakdownCol] || 11;
            });

            // add labelled aggregate columns and set meta
            _.each(aggregateCols, function(aggregateCol, i) {
                data["columns"].push(aggregateLabels[i]);
                data["meta"][aggregateLabels[i]] = base.meta[aggregateCol] || 11;
            });

            return data;
        }

        executeUserJavascript(fnBody, paramNames, paramValues): void {
            try {
                const fn = new Function(paramNames, fnBody);
                return fn.apply({}, paramValues);
            } catch (error) {
                // try to get the line/col in the user's code that's triggering the error
                const errorParts = /<anonymous>:(\d+):(\d+)/.exec(error.stack);

                if (errorParts && errorParts.length === 3) {
                    const line = Number(errorParts[1]);
                    const col = Number(errorParts[2]);
                    throw error.message + " (" + (line - 2) + ":" + col + ")";
                }

                throw error.message;
            }
        }

        executeVirtualQuery(query, params, success): void {
            const fnParts = DocumentDataModel.VIRTUAL_QUERY_REGEX.exec(query),
                paramNames = [],
                paramValues = [];
            let shallowCloneRequired = false,
                fnBody;

            if (!fnParts) {
                // not in the required function format
                throw "Invalid function definition";
            }

            _.each(params, (param): void => {
                let key, paramSource;

                switch (param.type) {
                    case "datasource":
                        key = this.path + "_" + param.value;

                        if (this.get("isSourceDataDialog")) {
                            key += "#edit";
                        }

                        paramNames.push(param.name);
                        paramSource = this.virtualSubscriptions[key];

                        if (paramSource) {
                            const sourcePk =
                                paramSource.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX;
                            const targetPk =
                                this.get("_subscriptionKey") || DataSet.DEFAULT_ROW_INDEX;

                            shallowCloneRequired =
                                shallowCloneRequired ||
                                (targetPk === DataSet.DEFAULT_ROW_INDEX &&
                                    (sourcePk === DataSet.DEFAULT_ROW_INDEX ||
                                        paramSource.get("_dataType") === "virtual"));

                            // get columns/meta into the correct format and add rows from our data collection
                            paramValues.push(
                                _.extend(
                                    DocumentDataModel.convertMetaToDeltaClientColumnSpec(
                                        paramSource.dataSet
                                            ? _.sortBy(
                                                  paramSource.dataSet.columns.map(
                                                      c => c.attributes,
                                                  ),
                                                  "index",
                                              )
                                            : [],
                                    ),
                                    {
                                        rows: paramSource.dataSet
                                            ? paramSource.dataSet.collection.models.map(
                                                  m => m.attributes,
                                              )
                                            : [],
                                    },
                                ),
                            );
                        } else {
                            paramValues.push(null);
                        }
                        break;
                    case "text":
                        // simple text, use value as given
                        paramNames.push(param.name);
                        paramValues.push(param.value);
                        break;
                    case "viewstate":
                        paramNames.push(param.name);
                        paramSource = this.viewState.getByPath(param.value);

                        if (paramSource) {
                            // use the viewstate value
                            paramValues.push(paramSource.get("value"));
                        } else {
                            paramValues.push(null);
                        }
                        break;
                }
            });

            // define our callback function
            paramNames.push("callback");
            paramValues.push(function(result) {
                // shallow clone attributes
                // this is required where the dataset will re-index rows on _rowIndex
                if (result.rows && shallowCloneRequired) {
                    result.rows = result.rows.map(r => Object.assign({}, r));
                }
                success(result);
            });

            paramNames.push("Tools");
            paramValues.push(Tools);

            // create a function using the user's function body and call it with our parameters
            if (fnParts && fnParts.length === 6) {
                fnBody = fnParts[4];
                this.executeUserJavascript(fnBody, paramNames, paramValues);
            }
        }

        exportData(callback, errorCallback, options): void {
            let pivotModel,
                splitFocus,
                exportParams: ExportParams = {},
                exportName = "";

            if (options) {
                if (options.exportName) {
                    exportName = options.exportName;
                }
                if (options.exportParams) {
                    exportParams = options.exportParams;
                }
            }

            if (this.get("_dataType") === "analytic") {
                this.runWithConnectionCheck(
                    errorCallback,
                    function(): void {
                        DeltaClientLib.exportAnalyticData(
                            this.get("_selectedAnalytic"),
                            this.get("_connection"),
                            this.bindViewStateToKdbParams(this.get("_analyticParams")),
                            exportName,
                            exportParams,
                            callback,
                            errorCallback,
                        );
                    }.bind(this),
                );
            } else if (this.get("_dataType") === "pivot" || this.get("_pivotType")) {
                pivotModel = this;

                // find the currently focused data model
                if (options && options.focus && _.isString(options.focus)) {
                    splitFocus = options.focus.split(",");

                    _.each(splitFocus, function(id) {
                        if (pivotModel.dataSet.collection.has(id)) {
                            pivotModel = pivotModel.dataSet.collection.get(id);
                        }
                    });
                }

                pivotModel.exportPivotData(callback, errorCallback, options);
            } else if (this.get("_dataType") === "query") {
                this.runWithConnectionCheck(
                    errorCallback,
                    function(): void {
                        DeltaClientLib.exportQueryData(
                            this.get("_connection"),
                            this.replaceViewStateParams(this.get("_queryString")),
                            this.bindViewStateToKdbParams(this.get("_queryParams")),
                            exportName,
                            exportParams,
                            callback,
                            errorCallback,
                        );
                    }.bind(this),
                );
            } else {
                // export not supported for this datatype
                errorCallback("Export not currently supported for type " + this.get("_dataType"));
            }
        }

        flatten(): any {
            if (
                _.includes(
                    ["analytic", "streaming", "pivot", "query", "virtual", "builder", "sql"],
                    this.get("_dataType"),
                )
            ) {
                return [this];
            } else {
                return _.reduce(
                    _.omit(this.attributes, "error"),
                    function(memo, value) {
                        return memo.concat(value.flatten());
                    },
                    [],
                );
            }
        }

        flattenObject(ob) {
            const toReturn = {};
            if (_.keys(ob).length) {
                for (const i in ob) {
                    if (!ob.hasOwnProperty(i)) continue;

                    if (typeof ob[i] === "object") {
                        const flatObject = this.flattenObject(ob[i]);
                        for (const x in flatObject) {
                            if (!flatObject.hasOwnProperty(x)) continue;

                            toReturn[i + "." + x] = flatObject[x];
                        }
                    } else {
                        toReturn[i] = ob[i];
                    }
                }

                return toReturn;
            }

            return { 0: ob.toString() };
        }

        forceExecute(key, callback): void {
            this.paramChanged.push("#forced");
            if (callback) this.callbacks.push(callback);
            this.debouncedParameterChange();
        }

        formatValue(value: number, key: string): number {
            if (key === "_subscriptionInterval") {
                value = Math.min(3600, Math.max(0.5, Number(value || 0))) * 1000;
            }

            return value;
        }

        toSQLString(queryStr: string, params): string {
            let query = queryStr.replace(/"/g, "'");
            _.each(params, param => {
                const typeName = param.type;
                const typeDef = typeName ? RTTI.getByName(typeName) : null;
                if (typeDef && typeDef.toSQLInput) {
                    const regex = new RegExp("{{" + param.name + "}}", "g");
                    query = query.replace(regex, typeDef.toSQLInput(param.value));
                }
            });
            return query;
        }

        generatePivotModel(idAttribute, childId, modelType, options) {
            // eslint-disable-next-line @typescript-eslint/no-this-alias
            const topSelf: DocumentDataModel = this;

            if (options.breakdownCols instanceof Backbone.Model) {
                options.breakdownCols = options.breakdownCols.get("value");
            }

            // init empty meta set for wheres transform
            topSelf.meta = {};

            const model = Backbone.Model.extend(
                {
                    defaults: {
                        _dataType: topSelf.get("_dataType"),
                        _pivotType: topSelf.get("_pivotType"),
                        _subscriptionType: topSelf.get("_subscriptionType"),
                        _subscriptionInterval: topSelf.get("_subscriptionInterval"),
                    },
                    idAttribute: idAttribute,
                    childId: childId,
                    childModelType: modelType,

                    initialize() {
                        this.isDirty = true;
                        this.isPending = false;
                        this.legacyUpdates = [];
                        this.agent = new Agent();
                    },

                    apply() {
                        this.dataSet = new DataSet({
                            idAttribute: childId,
                            collectionFunc: Backbone.Collection.extend({
                                model: modelType,
                            }),
                            agent: this.agent,
                            maxRows: options.maxRows,
                            primaryKey: childId,
                        });
                    },

                    getRoot() {
                        return topSelf;
                    },

                    getViewState() {
                        return topSelf.getViewState();
                    },

                    execute(args) {
                        // eslint-disable-next-line @typescript-eslint/no-this-alias
                        const self: DocumentDataModel = this,
                            clientExecutePending =
                                !topSelf.clientPivotData ||
                                topSelf.isDirty ||
                                (args && args.intervalQuery);

                        if (!this.wheres) {
                            this.wheres = options.wheres;
                        }

                        const parentExecuteFn = self.onExecuteSubscriptionSuccess.bind(self);

                        const executeFn = function(data?): void {
                            if (data) {
                                parentExecuteFn(data);
                            } else {
                                parentExecuteFn(
                                    topSelf.executeClientPivotQuery(topSelf.clientPivotData, {
                                        wheres: options.wheres,
                                    }),
                                    topSelf.get("_forceReset"),
                                );
                            }
                        };

                        self.onExecuteBegin();

                        try {
                            options.topX = null;
                            options.ordering = null;
                            options.queryParams = topSelf.bindViewStateToKdbParams(
                                topSelf.get("_queryParams"),
                            );
                            options.maxRows =
                                topSelf.get("_maxRows") || DocumentDataModel.DEFAULT_MAX_ROWS;

                            // options only contain wheres from level 1 on
                            _.each(options.wheres, function(where, i) {
                                const whereKey =
                                    typeof where === "string" ? where : where.columnName;
                                const columnType = topSelf.meta[whereKey];
                                // transform where to object with type information from top meta set
                                options.wheres[i] = {
                                    columnName: whereKey,
                                    columnType: columnType,
                                    columnOperator: "=",
                                    columnValue: self.get(whereKey),
                                };
                            });

                            const errorFn = function(error, ...args): void {
                                topSelf.onError(model, options, error, "Error", args);
                            };

                            if (self.get("_pivotType") === "client") {
                                if (clientExecutePending) {
                                    self.stop();
                                    topSelf.runWithConnectionCheck(errorFn, function() {
                                        switch (topSelf.get("_dataType")) {
                                            case "query":
                                                const query = topSelf.replaceViewStateParams(
                                                    options.queryString,
                                                );
                                                if (
                                                    self.get("_subscriptionType") === "subscription"
                                                ) {
                                                    DeltaClientLib.startManagedQuery(
                                                        options.connection,
                                                        query,
                                                        topSelf.bindViewStateToKdbParams(
                                                            options.queryParams,
                                                        ),
                                                        function(data: any, bData) {
                                                            if (
                                                                data &&
                                                                data.dataSet &&
                                                                data.dataSet.rows
                                                            ) {
                                                                topSelf.clientPivotData =
                                                                    data.dataSet;
                                                                executeFn();
                                                            } else {
                                                                executeFn(data);
                                                            }
                                                        },
                                                        errorFn,
                                                        options.subscriptionInterval * 1000,
                                                        options.maxRows,
                                                        null, // TODO NF paging,
                                                        self.performance,
                                                    );
                                                } else {
                                                    DeltaClientLib.getQueryData(
                                                        options.dataSource,
                                                        options.connection,
                                                        query,
                                                        topSelf.bindViewStateToKdbParams(
                                                            options.queryParams,
                                                        ),
                                                        function(data) {
                                                            topSelf.clientPivotData = data;
                                                            executeFn();
                                                        },
                                                        errorFn,
                                                        options.maxRows,
                                                        null, // TODO NF paging,
                                                        self.performance,
                                                    );
                                                }
                                                break;
                                            case "analytic":
                                            case "streaming":
                                                if (
                                                    self.get("_subscriptionType") === "subscription"
                                                ) {
                                                    DeltaClientLib.startManagedAnalytic(
                                                        topSelf.get("_selectedAnalytic"),
                                                        topSelf.get("_connection"),
                                                        topSelf.bindViewStateToKdbParams(
                                                            topSelf.get("_analyticParams"),
                                                        ),
                                                        function(data: any, bData) {
                                                            if (data && data.rows) {
                                                                topSelf.clientPivotData = data;
                                                                executeFn();
                                                            } else {
                                                                executeFn(data);
                                                            }
                                                        },
                                                        errorFn,
                                                        options.subscriptionInterval * 1000,
                                                        options.maxRows,
                                                        self.performance,
                                                    );
                                                } else {
                                                    DeltaClientLib.runAnalytic(
                                                        topSelf.get("_selectedAnalytic"),
                                                        topSelf.get("_connection"),
                                                        topSelf.bindViewStateToKdbParams(
                                                            topSelf.get("_analyticParams"),
                                                        ),
                                                        function(data) {
                                                            topSelf.clientPivotData = data;
                                                            executeFn();
                                                        },
                                                        errorFn,
                                                        options.maxRows,
                                                        null, // TODO NF paging,
                                                        self.performance,
                                                    );
                                                }
                                                break;
                                            case "virtual":
                                                topSelf.executeVirtualQuery(
                                                    topSelf.get("_virtualQueryString"),
                                                    topSelf.get("_virtualParams"),
                                                    function(data) {
                                                        topSelf.clientPivotData = data;
                                                        executeFn();
                                                    },
                                                );
                                                break;
                                            case "builder":
                                                if (
                                                    self.get("_subscriptionType") === "subscription"
                                                ) {
                                                    DeltaClientLib.startManagedQuery(
                                                        options.connection,
                                                        self.get("_analyticString"),
                                                        self.get("_queryBuilderParams"),
                                                        function(data: any, bData) {
                                                            if (
                                                                data &&
                                                                data.dataSet &&
                                                                data.dataSet.rows
                                                            ) {
                                                                topSelf.clientPivotData =
                                                                    data.dataSet;
                                                                executeFn();
                                                            } else {
                                                                executeFn(data);
                                                            }
                                                        },
                                                        errorFn,
                                                        options.subscriptionInterval * 1000,
                                                        options.maxRows,
                                                        null, // TODO NF paging,
                                                        self.performance,
                                                    );
                                                } else {
                                                    DeltaClientLib.getQueryData(
                                                        options.dataSource,
                                                        options.connection,
                                                        self.get("_analyticString"),
                                                        self.get("_queryBuilderParams"),
                                                        function(data) {
                                                            topSelf.clientPivotData = data;
                                                            executeFn();
                                                        },
                                                        errorFn,
                                                        options.maxRows,
                                                        null, // TODO NF paging,
                                                        self.performance,
                                                    );
                                                }
                                                break;
                                        }
                                    });
                                } else {
                                    executeFn();
                                }
                            } else {
                                if (topSelf.get("_dataType") === "builder") {
                                    options.queryString = topSelf.get("_analyticString");
                                    options.queryParams = topSelf.get("_queryBuilderParams");
                                }
                                topSelf.runWithConnectionCheck(errorFn, function() {
                                    DeltaClientLib.getPivotData(
                                        options,
                                        function(data: any, buffer: ArrayBuffer) {
                                            // add to top meta set for where transform above - set from level 0 on
                                            _.extend(topSelf.meta, data["meta"]);

                                            // on success
                                            if (
                                                topSelf.get("_subscriptionType") === "subscription"
                                            ) {
                                                self.onExecuteSubscriptionSuccess(
                                                    data,
                                                    topSelf.get("_forceReset"),
                                                    buffer,
                                                    topSelf.meta,
                                                );
                                            } else {
                                                self.onExecuteSuccess(
                                                    data,
                                                    topSelf.get("_forceReset"),
                                                    buffer,
                                                    topSelf.meta,
                                                );
                                            }
                                        },
                                        errorFn,
                                        self.performance,
                                    );
                                });
                            }
                        } catch (e) {
                            topSelf.onError(model, options, e, "Error", args);
                        }
                    },
                    //?why options are not passed as arguments
                    exportPivotData: function(callback, errorCallback, args) {
                        topSelf.runWithConnectionCheck(errorCallback, function() {
                            DeltaClientLib.exportPivotData(
                                $.extend({}, options, args),
                                callback,
                                errorCallback,
                            );
                        });
                    },

                    /* eslint-disable @typescript-eslint/unbound-method */
                    clearVirtualSubscriptions: _.noop,
                    clearVirtualSubscriptionsAll: _.noop,
                    executeClientPivotQuery: topSelf.executeClientPivotQuery,
                    formatValue: topSelf.formatValue,
                    getViewStateAttribute: topSelf.getViewStateAttribute,
                    isPagingData: topSelf.isPagingData,
                    logPrefix: topSelf.logPrefix,
                    legacyFlush: topSelf.legacyFlush,
                    legacyUpdate: topSelf.legacyUpdate,
                    onComplete: topSelf.onComplete,
                    onExecuteSubscriptionSuccess: topSelf.onExecuteSubscriptionSuccess,
                    onExecuteBegin: topSelf.onExecuteBegin,
                    onExecuteSuccess: topSelf.onExecuteSuccess,
                    reset: topSelf.reset,
                    start: topSelf.start,
                    stop: topSelf.stop,
                    subscribe: topSelf.subscribe,
                    triggerCallbacks: _.noop,
                    updateVirtualSubscriptions: _.noop,
                    unsubscribe: topSelf.unsubscribe,
                    /* eslint-enable @typescript-eslint/unbound-method */
                },
                {
                    staticIdAttribute: idAttribute,
                },
            );

            return model;
        }

        getByPath(path: string): DocumentDataModel {
            // if path is not defined, return undefined
            if (!path) {
                return undefined;
            }

            // split path by delimeter, excluding empty entries
            const splitPath = _.filter(path.split("/"), function(key) {
                return key && key.length > 0;
            });

            // default to this (normal root)
            // eslint-disable-next-line @typescript-eslint/no-this-alias
            let val = this;

            // iterate over the nested models using the path of keys
            _.each(splitPath, function(key) {
                if (val) {
                    val = val.get(key);
                }
            });

            return val;
        }

        /**
         * Get the execution rate since apply was last called
         * @return {number} The execution rate value per minute
         */
        getExecutionRate(): number {
            if (this.performance) {
                const t = this.performance.performance["apply"];
                return t ? (60000 * this.dataExecuteSuccessCount) / (Date.now() - t) : 0;
            }

            return 0;
        }

        getMappedViewStates() {
            const mapping = this.get("_mappings"),
                viewstates = [];

            if (mapping && mapping.mappings) {
                _.each(
                    mapping.mappings,
                    function(viewstate): void {
                        viewstates.push(
                            viewstate && viewstate._dashboardsType
                                ? this.viewState.getByPath(viewstate.value)
                                : viewstate,
                        );
                    }.bind(this),
                );
            }

            return viewstates;
        }

        getMeta(callback: (meta: any) => void): void {
            const key = _.uniqueId("getMeta_");

            this.subscribe(key, {
                onData: meta => {
                    callback(meta);
                    _.defer(() => this.unsubscribe(key));
                },
            });
        }

        getRoot(): DocumentDataModel {
            return this;
        }

        getTreeList() {
            const children = _.filter(this.attributes, function(child) {
                return child instanceof DocumentDataModel;
            });
            return children.length
                ? _.chain(children)
                      .map(function(child) {
                          return child.getTreeList();
                      })
                      .flatten()
                      .value()
                : [this];
        }

        getViewState() {
            //console.log('d:' + this.cid, this.viewState, this);
            return this.viewState;
        }

        getPagingType(): string {
            let pagingType = DocumentDataModel.DEFAULT_PAGING;

            if (this.get("_serverPaging")) {
                if (this.get("_forceReset")) {
                    pagingType = "PAGENO_NOCACHE";
                } else if (this.get("_pagingType") && this.get("_pagingType") !== "NONE") {
                    pagingType = this.get("_pagingType");
                } else {
                    pagingType = "PAGENO";
                }
            }

            return pagingType;
        }

        getParameters() {
            const parameters = [];
            let parameter, paramPath, paramValue, queryParameters;

            if (this.get("_queryParams")) {
                queryParameters = this.get("_queryParams");
            } else if (this.get("_updateQueryParams")) {
                queryParameters = this.get("_updateQueryParams");
            } else if (this.get("_builderParams")) {
                queryParameters = this.get("_builderParams");
            } else if (this.get("_analyticParams")) {
                queryParameters = this.get("_analyticParams");
            } else if (this.get("_virtualParams")) {
                queryParameters = _.filter(this.get("_virtualParams"), {
                    type: "viewstate",
                }).map((p: any) => ({ value: "<%" + p.value + "%>" }));
            }

            // get parameters bound to a viewstate
            if (queryParameters) {
                _.each(queryParameters, (queryParam): void => {
                    if (typeof queryParam.value === "string") {
                        paramPath = queryParam.value.match(DocumentDataModel.PARAMETER_RX);

                        if (paramPath) {
                            parameter = {
                                path: paramPath[0].substring(2, paramPath[0].length - 2),
                            };

                            // Get static readonly ituent parameters of dict
                            paramValue = this.viewState.getByPath(
                                paramPath[0].substring(2, paramPath[0].length - 2),
                            );
                            if (paramValue && paramValue.get("_type") === "dict") {
                                parameter.dictParameters = [];
                                _.each(paramValue.get("value"), function(dictVal, dictKey) {
                                    if (dictVal instanceof Backbone.Model) {
                                        parameter.dictParameters.push({
                                            path: (dictVal as any).path,
                                            key: dictKey,
                                        });
                                    } else if (_.isObject(dictVal)) {
                                        if (dictVal.name) {
                                            parameter.dictParameters.push({
                                                path: dictVal.name,
                                                key: dictKey,
                                            });
                                        } else if (dictVal.type) {
                                            parameter.dictParameters.push({
                                                raw: true,
                                                key: dictKey,
                                                type: dictVal.type,
                                                value: dictVal.value,
                                            });
                                        }
                                    } else if (_.isString(dictVal)) {
                                        if (dictVal.match(DocumentDataModel.PARAMETER_RX)) {
                                            parameter.dictParameters.push({
                                                path: dictVal.substring(2, dictVal.length - 2),
                                            });
                                        } else {
                                            parameter.dictParameters.push({
                                                path: dictVal,
                                            });
                                        }
                                    } else if (dictVal) {
                                        parameter.dictParameters.push({
                                            path: dictVal.name ? dictVal.name : dictVal,
                                        });
                                    }
                                });
                            }

                            parameters.push(parameter);
                        }
                    }
                });
            }

            return parameters;
        }

        getViewStateAttribute(att): any {
            const value = this.get(att);
            return value instanceof Backbone.Model ? value.get("value") : value;
        }

        getViewStateDict() {
            const viewStateDict = {},
                dicts = [],
                newParams = _.union(
                    _.filter(this.get("_analyticParams"), function(param) {
                        if (param.type === "dict") {
                            dicts.push(param);
                        }
                        return param.isViewState;
                    }),
                    _.filter(this.get("_queryParams"), function(param) {
                        if (param.type === "dict") {
                            dicts.push(param);
                        }
                        return param.isViewState;
                    }),
                    _.filter(this.get("_virtualParams"), function(param) {
                        if (param.type === "dict") {
                            dicts.push(param);
                        }
                        param.isVirtual = true;
                        return param.type === "viewstate";
                    }),
                );

            // add view states from dicts params
            _.each(dicts, function(d) {
                _.each(d.value, function(p) {
                    if (p.isViewState) {
                        newParams.push(p);
                    }
                });
            });

            //Replace viewstate parameters with values on relevent kdb query parameters
            if (newParams && newParams.length > 0) {
                _.each(
                    newParams,
                    _.bind(function(queryParam) {
                        const viewState = this.parseViewState(
                            queryParam.value,
                            queryParam.isVirtual,
                        );
                        if (viewState) {
                            viewStateDict[queryParam.name] = viewState;
                        }

                        if (queryParam.type === "dict" && _.isObject(queryParam.value)) {
                            _.each(
                                queryParam.value,
                                function(value, key) {
                                    const viewState = this.parseViewState(value);
                                    if (viewState) {
                                        viewStateDict[queryParam.name + "." + key] = viewState;
                                    }
                                }.bind(this),
                            );
                        }
                    }, this),
                );
            }

            // get view state attributes
            _.each(
                _.reduce(
                    _.values(DocumentDataModel.VIEW_STATE_ATTRIBUTES),
                    function(memo, ats) {
                        return memo.concat(_.keys(ats));
                    },
                    [],
                ),
                _.bind(function(key) {
                    const value = this.get(key);
                    if (value instanceof Backbone.Model) {
                        viewStateDict[key] = value;
                    }
                }, this),
            );

            return viewStateDict;
        }

        isMapping(): boolean {
            return this.get("_mappings") && !_.isEmpty(this.get("_mappings").mappings);
        }

        isPagingData(data): boolean {
            return (
                data &&
                data.rows &&
                data.rows[0] &&
                data.rows[0]["Property"] === "startRows" &&
                data.rows[3]
            );
        }

        /*
         * apply the defferred update and flush updates
         */
        legacyFlush(): void {
            this.legacyFlushTime = Date.now();
            this.legacyFlushFrame = undefined;
            if (
                (this.deferredUpdate && this.agent.subscriberLegacyCount()) ||
                this.get("_dataType").match(/virtual|copy/)
            ) {
                const legacyTable: DCDS = this.legacyUpdate(this.deferredUpdate);

                // check this.dataSet is present as legacyUpdate callbacks above can unsubscribe
                this.dataSet && this.dataSet.flushUpdates();

                if (
                    (this.deferredUpdate &&
                        this.deferredUpdate.forceReset &&
                        legacyTable["rows"] &&
                        !legacyTable["rows"].length) || // rows present but empty
                    !this.dataSet ||
                    (this.dataSet && this.dataSet.isEmpty())
                ) {
                    // ignore status update in mixed mode (legacy getMeta call + raw subscription)
                    if (this.agent.subscriberRawCount() === 0) {
                        // missing or empty dataset
                        this.set("queryStatus", {
                            type: "NoResults",
                            options: {},
                        });
                    }
                } else this.unset("queryStatus");

                if (this.consolidateCount > 0) {
                    QuickBase.Log.Info(
                        this.logPrefix() + "consolidated " + (this.consolidateCount + 1),
                    );
                } else {
                    QuickBase.Log.Info(this.logPrefix() + "deferred");
                }
            }

            this.consolidateCount = 0;
            this.deferredUpdate = undefined;
            this.durationMovingAverage =
                (this.durationMovingAverage * 2 + (Date.now() - this.legacyFlushTime)) / 3;
        }

        legacyUpdate(u: LegacyUpdate): DCDS {
            const forceReset = u.forceReset;
            const table = u.table;
            const pivotMeta = u.pivotMeta;

            let breakdownColumns = this.get("_breakdownCols");
            if (breakdownColumns instanceof Backbone.Model) {
                breakdownColumns = breakdownColumns.get("value");
            }

            if (pivotMeta) _.extend(pivotMeta, table["meta"]);

            // generate row data from columns if not present
            const legacyTable: DCDS = !table["rows"] ? table.toLegacy() : table;

            this.dataSet.onData(
                {
                    breakdownColumns: breakdownColumns,
                    columns: DocumentDataModel.convertDeltaClientColumnSpecToMeta(legacyTable),
                    paging: _.omit(u.pagingMeta, "data"),
                },
                legacyTable.rows,
                forceReset,
                undefined,
                this.performance,
            );

            return legacyTable;
        }

        logPrefix(): string {
            return "DataModel " + this.agent.subscriberCount() + " " + this.path + " ";
        }

        objToString(obj): string {
            const returnArr = [];
            _.each(_.keys(obj), function(key) {
                returnArr.push(key + ": " + obj[key]);
            });

            return returnArr.join("\n");
        }

        onComplete(): void {
            if (this.isDirty && !this.isPending) {
                this.stop();
                this.forceInternalReset = true;
                this.start();
            }
        }

        onError(model, options, error, errorType, args): void {
            const errorObj = {
                error: _.isObject(error) ? this.objToString(this.flattenObject(error)) : error,
                type: errorType,
                options: $.extend({}, options), // gk: no deep clone!!
                args: undefined,
            };

            this.triggerCallbacks(errorObj);

            if (args) {
                errorObj.args = $.extend({}, args); // gk: no deep clone!!
            }

            //gk: note no deep clone since a recurring error can gather neted levels as it reports itself

            this.set({
                error: errorObj,
            });

            this.set({
                queryStatus: errorObj,
            });

            this.isPending = false;
            this.onComplete();

            try {
                if (this.dataSet) {
                    // use forceInternalReset but don't set it to false since no valid data yet
                    this.dataSet.onData([], [], this.forceInternalReset, errorObj);
                }

                if (this.dataSet2) {
                    // use forceInternalReset but don't set it to false since no valid data yet
                    this.dataSet2.onData([], {}, this.forceInternalReset, errorObj);
                }
            } catch (ex) {
                // do nothing
            }

            Log.Error(this.logPrefix() + "onError ", errorObj);
            if (model && model.trigger) {
                model.trigger("error", model, errorObj);
            }
        }

        onExecuteBegin(): void {
            this.isPending = true;
            this.isDirty = false;
            this.onExecuteBeginCount++;

            this.set({
                queryStatus: {
                    type: "Loading",
                },
            });

            //console.trace();
            Log.Info(this.logPrefix() + "execute begin");
        }

        public onExecuteSuccess(
            data: DCDS,
            forceReset: boolean,
            buffer?: ArrayBuffer,
            pivotMeta?: { [index: string]: number },
        ): void {
            const pagingMeta = {};
            let table: ipc.Table;
            let breakdownColumns = this.get("_breakdownCols");
            if (breakdownColumns instanceof Backbone.Model) {
                breakdownColumns = breakdownColumns.get("value");
            }

            this.dataExecuteSuccessCount++;
            this.triggerCallbacks();

            // if binary
            if (buffer && buffer.byteLength) {
                // start binary paging check
                const PAGING_ROWS = ["startRows", "numRows", "totalNumRows", "data"];
                if (
                    data &&
                    data["qtype"] === 99 &&
                    data["keys"]["qtype"] === 11 &&
                    data["keys"]["offsets"]["length"] === 4 &&
                    _.isEqual(
                        PAGING_ROWS,
                        _.times(4, i =>
                            ipc.Parse.col(data["keys"] as qSymList, new DataView(buffer)).toLegacy(
                                i,
                            ),
                        ),
                    )
                ) {
                    const vals = ipc.Parse.col(
                        data["values"] as qGenericList,
                        new DataView(buffer),
                    );
                    for (let i = 0; i < 3; i++) {
                        pagingMeta[PAGING_ROWS[i]] = vals.toLegacy(i);
                    }

                    pagingMeta["pagingType"] = this.getPagingType();
                    pagingMeta["pageSize"] = this.get("_pageSize");

                    table = ipc.Parse.reshape((vals as ipc.List).values[3], buffer);
                    forceReset = true;
                } else table = ipc.Parse.reshape((data as any).dataSet || data, buffer);
                // end paging check

                if (pivotMeta) _.extend(pivotMeta, table["meta"]);

                // apply and reset forceInternalReset
                forceReset = forceReset || this.forceInternalReset;

                if (this.agent.subscriberRawCount()) {
                    this.unset("error");
                    this.dataSet2.onData(
                        {
                            breakdownColumns: breakdownColumns,
                            columns: DocumentDataModel.convertDeltaClientColumnSpecToMeta(table),
                            paging: _.omit(pagingMeta, "data"),
                        },
                        table.cols,
                        forceReset,
                        undefined,
                        this.performance,
                    );

                    this.forceInternalReset = false;

                    if (!this.dataSet2 || (this.dataSet2 && this.dataSet2.isEmpty())) {
                        // missing or empty dataset
                        this.set("queryStatus", {
                            type: "NoResults",
                            options: {},
                        });
                    } else this.unset("queryStatus");
                }
            } else if (this.isPagingData(data)) {
                // data contains 4 key/value rows, keys are: startRows, numRows, totalNumRows, data
                _.each(data["rows"], function(el) {
                    pagingMeta[el["Property"]] = el["Value"];
                });

                pagingMeta["pagingType"] = this.getPagingType();
                pagingMeta["pageSize"] = this.get("_pageSize");

                table = pagingMeta["data"] || {};
                forceReset = true;
            } else {
                table = data as ipc.Table;
            }

            // legacy subscriptions
            if (this.agent.subscriberLegacyCount() || this.get("_dataType").match(/virtual|copy/)) {
                // legacy updates deferred to animationFrame
                forceReset = forceReset || this.forceInternalReset;
                const update = new LegacyUpdate(table, forceReset, pagingMeta, pivotMeta);
                this.forceInternalReset = false;

                // TODO: apply legacy update in place if queued
                if (this.deferredUpdate) {
                    this.dataSet.background();
                    this.consolidateCount++;
                    // apply the deferred update to the background dataSet
                    this.legacyUpdate(this.deferredUpdate);
                    this.deferredUpdate = update;
                }
                // if dataSet is not in background AND
                // generally FAST to process OR
                // not run in a long time
                else if (
                    !this.dataSet.isBackground() &&
                    (this.durationMovingAverage <= DocumentDataModel.FAST_THRESHOLD ||
                        Date.now() - this.legacyFlushTime - this.durationMovingAverage >
                            DocumentDataModel.OLD_THRESHOLD)
                ) {
                    // Express frame
                    this.deferredUpdate = update;
                    this.legacyFlush();
                    QuickBase.Log.Info(this.logPrefix() + " Express");
                } else {
                    this.deferredUpdate = update;
                }

                if (!this.legacyFlushFrame && this.deferredUpdate) {
                    this.legacyFlushFrame = window.requestAnimationFrame(
                        this.legacyFlush.bind(this),
                    );
                    this.deferredUpdate = update;
                }
            }

            this.isPending = false;
            this.unset("error");
            Log.Info(this.logPrefix() + "execute success");

            // STAC
            //console.log(_.map(this.performance.performance, (v,k) => k + ": " + moment(v).format("hh:mm:ss.SSS")).join("\n"))

            this.onComplete();
        }

        onExecuteSubscriptionSuccess(
            msg: any,
            forceReset: boolean,
            buffer: ArrayBuffer,
            pivotMeta?: { [index: string]: number },
        ): void {
            if (msg["class"] === "207") {
                // Ack Message
                this.stop();

                if (msg["add"]) {
                    // add subcription
                    this.subscriptionId = msg.clientId + ":" + msg.subId;
                    this.isPending = false;
                    Log.Info(this.logPrefix() + "sub started " + msg.subId);
                } else {
                    Log.Info(this.logPrefix() + "sub stopped " + msg.subId);
                }
                return;
            } else if (msg["class"] === "208") {
                // Nack Message remove subscription
                Log.Info(this.logPrefix() + "sub failed " + msg.subId + ": " + msg.reason);
                DeltaClientLib.removeSubscriptions(msg.clientId + ":" + msg.subId);
                return;
            } else if (msg["class"] === "209") {
                // Update Message doesn't match subscription id
                if (this.subscriptionId !== msg.clientId + ":" + msg.subId) {
                    Log.Warn(
                        this.logPrefix() +
                            "sub stray persisting: " +
                            msg.clientId +
                            ":" +
                            msg.subId,
                    );
                    DeltaClientLib.removeSubscriptions(msg.clientId + ":" + msg.subId);
                    return;
                }
            } else if (msg["class"] === "210") {
                // Status Message
                return; // do nothing
            } else if (
                msg["class"] === "com.fd.business.querymanager.subscriptions.ManagedSubscription" ||
                msg["class"] === "com.fd.business.querymanager.subscriptions.StreamingSubscription"
            ) {
                return; // do nothing
            }

            this.onExecuteSuccess(msg.dataSet || msg, forceReset, buffer, pivotMeta);
        }

        onMaterialChange(): void {
            this.trigger("materialChange");
        }

        onModelChange(model, options): void {
            const changedAttributes = _.keys(model.changed),
                changedPagingAttributes = _.intersection(
                    changedAttributes,
                    DocumentDataModel.PAGING_ATTRIBUTES,
                );

            // if any _ prefixed properties change
            if (_.find(changedAttributes, testKey => testKey.indexOf("_") === 0)) {
                // trigger model data change for save status
                this.determineMaterialChange();

                // have any _ prefixed properties except _layout havebeen trigged
                if (
                    _.some(
                        changedAttributes,
                        a => a.indexOf("_") === 0 && !_.includes(["_layout", "_mappings"], a),
                    )
                ) {
                    this.isDirty = true;
                }

                if (_.includes(changedAttributes, DocumentDataModel.AUTO_EXECUTE)) {
                    // (un) subscribe to parameter changes
                    this.updateParameters();
                }

                // if paging parameters have changed we need to execute
                if (changedPagingAttributes && changedPagingAttributes.length) {
                    this.paramChanged = this.paramChanged.concat(changedPagingAttributes);
                    this.stop();
                    this.isDirty = true;
                    this.forceInternalReset = true;
                    this.debouncedParameterChange();
                }
            }
        }

        onParameterChange(model, val, options): void {
            // always run apply for pivots so children will not be homeless
            const param = {
                runApply: !!(options && options.runApply) || !!this.get("_pivotType"),
                modelPath: model.path,
                previous: model.previous("value"),
                new: val,
            };

            this.paramChanged.push(param);
            //can be viewstate/breakdown/querySetting parameter
            Log.Info(this.logPrefix() + "parameter change", param);
            this.stop();
            this.isDirty = true;
            this.forceInternalReset = true;

            // reset to first page, check KXAX-17781
            if (
                this.get("_pagingType") !== DocumentDataModel.DEFAULT_PAGING &&
                this.get("_pagingNum") !== 0
            ) {
                this.set("_pagingNum", 0);
            }

            this.debouncedParameterChange();
        }

        onPivotSettingChange(model, val): void {
            this.onParameterChange(model, val, { runApply: true });
        }

        onQuerySettingChange(model, val, options): void {
            //append "runApply" flag to options
            options = _.extend(true, { runApply: true }, options);
            this.onParameterChange(model, val, options);
        }

        parseViewState(value, virtual): any {
            if (typeof value === "string") {
                if (virtual) {
                    return this.getViewState().getByPath(value);
                }

                const paramPath = value.match(DocumentDataModel.PARAMETER_RX);
                if (paramPath) {
                    return this.getViewState().getByPath(
                        $.trim(paramPath[0].substring(2, paramPath[0].length - 2)),
                    );
                }
            }
        }

        postCommand(sender: object, command: Command): any {
            // TODO: assuming you set the data path before you got here
            return this.root.postCommand(this, command);
        }

        //Remove subscriptions previously generated by this model if they exist
        removeSubscriptions(): void {
            this.stop();

            if (this.group) {
                this.values().forEach(v => v.removeSubscriptions());
            }
        }

        replaceViewStateParams(str): string | any {
            if (typeof str === "string") {
                return str.replace(DocumentDataModel.PARAMETER_RX, (match, path) => {
                    const viewModel = this.getViewState().getByPath(path);
                    if (viewModel) {
                        const val = viewModel.get("value");
                        if (val === undefined) {
                            return null;
                        }

                        return val;
                    }

                    Log.Error(this.logPrefix() + "replace parameter: " + path + " not found");
                    return "";
                });
            }

            return str;
        }

        reset(): void {
            if (this.dataMapper) {
                this.dataMapper.cleanUp();
                delete this.dataMapper;
            }

            if (this.dataSet) {
                this.dataSet.cleanUp();
                delete this.dataSet;
            }

            if (this.dataSet2) {
                this.dataSet2.cleanUp();
                delete this.dataSet2;
            }

            this.deferredUpdate = undefined;
            this.isDirty = true;
            this.isPending = false;
            this.dataExecuteSuccessCount = 0;
            this.onExecuteBeginCount = 0;
        }

        start(): void {
            if (this.agent.subscriberCount() === 0) {
                Log.Info(this.logPrefix() + "start: no subscriptions");
                return;
            }

            // code starts after subscriber check
            const subscriptionInterval = this.formatValue(
                this.getViewStateAttribute("_subscriptionInterval"),
                "_subscriptionInterval",
            );

            const subType = this.get("_subscriptionType");
            if (subType === "polling") {
                if (!this.pollingEvent) {
                    Log.Info(this.logPrefix() + "start: polling now...");
                    this.pollingEvent = setInterval(() => {
                        if (!this.isPending) {
                            Log.Info(this.logPrefix() + "poll");
                            this.execute({
                                intervalQuery: true,
                            });
                        }
                    }, subscriptionInterval);
                    this.execute();
                } else {
                    Log.Info(this.logPrefix() + "start: already polling!");
                }
            } else if (subType === "subscription" || subType === "streaming") {
                if (!this.subscriptionId) {
                    this.execute();
                }
            } else {
                if (this.isDirty && !this.isPending) {
                    this.execute();
                }
            }
        }

        stop(): void {
            if (this.pollingEvent) {
                // destroy polling event
                //console.log("stop polling now", this);
                clearInterval(this.pollingEvent);
                this.pollingEvent = null;
            }

            if (this.subscriptionId) {
                DeltaClientLib.removeSubscriptions(this.subscriptionId);
                Log.Info(this.logPrefix() + " unsubcribed " + this.subscriptionId);
                this.subscriptionId = null;
            }
        }

        subscribe(key: string, listener: Subscriber, raw?: boolean, pagingInfo?: any) {
            if (this.agent.subscribe(key, listener, raw)) {
                Log.Info(this.logPrefix() + "subscribed " + key);

                //update model with page info properties
                if (pagingInfo) {
                    this.set(pagingInfo, { silent: true });
                } else {
                    if (this.has("_lazyLoading")) {
                        this.unset("_lazyLoading");
                        if (this.dataSet) {
                            this.dataSet.lazyLoading = false;
                        }

                        //reset
                        this.set(
                            {
                                _pagingType: "NONE",
                                _pageNum: 0,
                            },
                            { silent: true },
                        );
                    }
                }

                // gk: onData NEEDS to be called when 'meta' or 'rows' are present
                // since these collections are sent as changesets of add, remove, changed
                // thus need an initial state if any is present
                if (this.dataSet) {
                    if (
                        !raw &&
                        (!this.dataSet.collection.isEmpty() || !this.dataSet.columns.isEmpty())
                    ) {
                        this.dataSet.onDataRefresh(listener);
                    } else if (this.dataSet2 && !this.dataSet2.columns.isEmpty()) {
                        if (raw) {
                            this.dataSet2.onDataRefresh(listener);
                        } else {
                            // no dataSet content but dataSet2 content sync up
                            const dc = ipc.Table.toLegacy(
                                _.mapValues(
                                    _.keyBy(
                                        this.dataSet2.columns.map(c => c.attributes),
                                        "id",
                                    ),
                                    "kdbType",
                                ),
                                this.dataSet2.columns.map(c => c.id),
                                this.dataSet2.cols,
                            );

                            this.dataSet.onData(
                                {
                                    breakdownColumns: this.dataSet2.breakdownColumns,
                                    columns: DocumentDataModel.convertDeltaClientColumnSpecToMeta(
                                        dc,
                                    ),
                                },
                                dc.rows,
                                true,
                                undefined,
                            );
                        }
                    } else if (raw && !this.dataSet.columns.isEmpty()) {
                        // if raw and no dataSet2 but dataSet has data then we've discarded the binary, call again.
                        this.apply();
                    }

                    // trigger queryStatus change (so new subscribers get current status)
                    this.trigger("change:queryStatus", this, this.get("queryStatus"));
                } else {
                    this.apply();
                }

                // start will run execute if required
                this.start();
            } else {
                // already subscribed
                Log.Error(this.logPrefix() + "subscribe: already subscribed", key);
            }
        }

        /*
         * toJSON override is necessary to prevent all of the data
         * contained within the data type models from being serialized.
         * - only the definition properties prefixed with '_' are
         *   saved
         */
        toJSON(): object {
            const filteredAttrs = {};

            _.keys(this.attributes).forEach((key: string) => {
                const child = this.attributes[key],
                    filteredChild = {};

                // if child is a data type, lets only store the _properties
                if (_.isObject(child) && child.has && child.has("_dataType")) {
                    _.each(
                        _.filter(_.keys(child.attributes), function(testKey) {
                            return (
                                testKey.indexOf("_") === 0 &&
                                //fix: KXAX-11979 paging info should be ignored when saving
                                !_.includes(["_pageSort", "_pagingNum"], testKey)
                            );
                        }),
                        function(filteredKey) {
                            if (child.get(filteredKey) instanceof Backbone.Model) {
                                filteredChild[filteredKey] = {
                                    _dashboardsType: "viewstate",
                                    value: child.get(filteredKey).path,
                                };
                            } else {
                                filteredChild[filteredKey] = child.get(filteredKey);
                            }
                        },
                    );
                    filteredAttrs[key] = filteredChild;
                } else if (child instanceof DocumentViewModel) {
                    // just extract path to viewstate model
                    filteredAttrs[key] = {
                        _dashboardsType: "viewstate",
                        value: child.path,
                    };
                } else if (_.isObject(child) && child.type === "Error") {
                    // error object holds reference to docDataModel - causes circular struture error
                    child.options = {};
                    filteredAttrs[key] = child;
                } else {
                    filteredAttrs[key] = child;
                }
            });

            return filteredAttrs;
        }

        triggerCallbacks(error = null) {
            this.callbacks.forEach(callback => {
                callback({
                    success: error ? false : true,
                    error,
                });
            });
            this.callbacks = [];
        }

        unsubscribe(key): void {
            if (this.agent.unsubscribe(key)) {
                Log.Info(this.logPrefix() + "unsubscribed " + key);
                if (this.agent.subscriberCount() === 0) {
                    this.stop();
                    this.reset();
                    this.clearVirtualSubscriptionsAll();
                    Log.Info(this.logPrefix() + "0 subscribers, data source reset");
                }
            } else {
                // not subscribed
                Log.Warn("unsubscribe not found", key, this);
            }
        }

        updateParameterPath(type, prevPath, newPath) {
            const regex = new RegExp("^<%" + prevPath);

            if (type === "vs") {
                _.each(
                    [
                        this.get("_analyticParams"),
                        this.get("_queryParams"),
                        this.get("_updateQueryParams"),
                    ],
                    params => {
                        _.each(params, param => {
                            if (param.isViewState === "viewstate") {
                                param.value = param.value.replace(regex, "<%" + newPath);
                            }
                        });
                    },
                );

                _.each(this.get("_virtualParams"), param => {
                    if (param.type === "viewstate") {
                        param.value = param.value.replace(regex, "<%" + newPath);
                    }
                });
            } else if (type === "ds") {
                _.each(this.get("_virtualParams"), param => {
                    if (param.type === "datasource") {
                        param.value = param.value.replace(regex, "<%" + newPath);
                    }
                });
            }
        }

        updateParameters(isAnalytic?: boolean) {
            let self = this,
                paramPath,
                dictViewStateString,
                paramValue;

            // clear parameters
            const parameters = [];

            // find all parameters
            _.each(
                _.filter(_.keys(self.attributes), function(testKey) {
                    return testKey.indexOf("_") === 0;
                }),
                function(filteredKey) {
                    if (typeof self.get(filteredKey) === "string") {
                        self.get(filteredKey).replace(DocumentDataModel.PARAMETER_RX, function(
                            match,
                            path,
                        ) {
                            parameters.push(path);
                            return "";
                        });
                    }
                },
            );

            // find all kdb query params with binding to viewstate
            if (self.get("_queryParams") && self.get("_queryParams").length > 0) {
                _.each(self.get("_queryParams"), function(queryParam) {
                    if (typeof queryParam.value === "string") {
                        paramPath = queryParam.value.match(DocumentDataModel.PARAMETER_RX);

                        if (paramPath) {
                            parameters.push(paramPath[0].substring(2, paramPath[0].length - 2));
                            paramValue = self.viewState.getByPath(
                                paramPath[0].substring(2, paramPath[0].length - 2),
                            );
                        }

                        //Viewstate param is of type dict, get static readonly ituent parameters
                        if (paramValue && paramValue.get("_type") === "dict") {
                            _.each(paramValue.get("value"), function(dictVal, dictKey) {
                                if (dictVal instanceof Backbone.Model) {
                                    parameters.push((dictVal as any).path);
                                } else if (_.isObject(dictVal) && dictVal.value) {
                                    const val = dictVal.value;

                                    if (
                                        _.isString(val) &&
                                        val.match(DocumentDataModel.PARAMETER_RX)
                                    ) {
                                        parameters.push(val.substring(2, val.length - 2));
                                    }
                                } else if (_.isString(dictVal)) {
                                    if (dictVal.match(DocumentDataModel.PARAMETER_RX)) {
                                        parameters.push(dictVal.substring(2, dictVal.length - 2));
                                    }
                                }
                            });
                        }
                    }
                });
            } else if (
                self.get("_updateQueryParams") &&
                self.get("_updateQueryParams").length > 0
            ) {
                _.each(self.get("_updateQueryParams"), function(queryParam) {
                    if (typeof queryParam.value === "string") {
                        paramPath = queryParam.value.match(DocumentDataModel.PARAMETER_RX);

                        if (paramPath) {
                            parameters.push(paramPath[0].substring(2, paramPath[0].length - 2));
                            paramValue = self.viewState.getByPath(
                                paramPath[0].substring(2, paramPath[0].length - 2),
                            );
                        }

                        //Viewstate param is of type dict, get static readonly ituent parameters
                        if (paramValue && paramValue.get("_type") === "dict") {
                            _.each(paramValue.get("value"), function(dictVal, dictKey) {
                                if (dictVal instanceof Backbone.Model) {
                                    parameters.push((dictVal as any).path);
                                } else if (_.isObject(dictVal) && dictVal.value) {
                                    const val = dictVal.value;

                                    if (
                                        _.isString(val) &&
                                        val.match(DocumentDataModel.PARAMETER_RX)
                                    ) {
                                        parameters.push(val.substring(2, val.length - 2));
                                    }
                                } else if (_.isString(dictVal)) {
                                    if (dictVal.match(DocumentDataModel.PARAMETER_RX)) {
                                        parameters.push(dictVal.substring(2, dictVal.length - 2));
                                    }
                                }
                            });
                        }
                    }
                });
            } else if (self.get("_analyticParams") && self.get("_analyticParams").length > 0) {
                _.each(self.get("_analyticParams"), function(analyticParam) {
                    if (typeof analyticParam.value === "string") {
                        paramPath = analyticParam.value.match(DocumentDataModel.PARAMETER_RX);

                        if (paramPath) {
                            parameters.push(paramPath[0].substring(2, paramPath[0].length - 2));
                            paramValue = self.viewState.getByPath(
                                paramPath[0].substring(2, paramPath[0].length - 2),
                            );
                        }

                        //Viewstate param is of type dict, get static readonly ituent parameters
                        if (paramValue && paramValue.get("_type") === "dict") {
                            _.each(paramValue.get("value"), function(dictVal, dictKey) {
                                if (dictVal instanceof Backbone.Model) {
                                    parameters.push((dictVal as any).path);
                                } else if (_.isObject(dictVal) && dictVal.value) {
                                    const val = dictVal.value;

                                    if (
                                        _.isString(val) &&
                                        val.match(DocumentDataModel.PARAMETER_RX)
                                    ) {
                                        parameters.push(val.substring(2, val.length - 2));
                                    }
                                } else if (_.isString(dictVal)) {
                                    if (dictVal.match(DocumentDataModel.PARAMETER_RX)) {
                                        parameters.push(dictVal.substring(2, dictVal.length - 2));
                                    }
                                }
                            });
                        }
                    } else if (typeof analyticParam.value === "object") {
                        //Dictionary parameters have to be bound to viewstate
                        if (analyticParam.type === "dict") {
                            _.each(analyticParam.value, function(dictValue) {
                                if (_.has(dictValue, "_isKdbReady")) return;

                                dictViewStateString =
                                    typeof dictValue === "object"
                                        ? dictValue.value
                                        : "<%" + dictValue + "%>";
                                paramPath =
                                    typeof dictViewStateString === "string" &&
                                    dictViewStateString.match(DocumentDataModel.PARAMETER_RX);

                                if (paramPath) {
                                    parameters.push(
                                        paramPath[0].substring(2, paramPath[0].length - 2),
                                    );
                                }
                            });
                        }
                    }
                });
            } else if (self.get("_virtualParams") && self.get("_virtualParams").length > 0) {
                _.each(self.get("_virtualParams"), function(queryParam) {
                    paramPath = queryParam.value;

                    if (paramPath && queryParam.type === "viewstate") {
                        parameters.push(paramPath);
                        paramValue = self.viewState.getByPath(paramPath);
                    }

                    //Viewstate param is of type dict, get static readonly ituent parameters
                    if (paramValue && paramValue.get("_type") === "dict") {
                        _.each(paramValue.get("value"), function(dictVal, dictKey) {
                            if (dictVal instanceof Backbone.Model) {
                                parameters.push((dictVal as any).path);
                            } else if (_.isObject(dictVal) && dictVal.value) {
                                const val = dictVal.value;

                                if (_.isString(val) && val.match(DocumentDataModel.PARAMETER_RX)) {
                                    parameters.push(val.substring(2, val.length - 2));
                                }
                            } else if (_.isString(dictVal)) {
                                if (dictVal.match(DocumentDataModel.PARAMETER_RX)) {
                                    parameters.push(dictVal.substring(2, dictVal.length - 2));
                                }
                            }
                        });
                    }
                });
            }

            // stop listening to old parameter models
            if (self.parameterModels) {
                _.each(self.parameterModels, function(m) {
                    if (m) {
                        //Log.Info(self.logPrefix() + 'unbind ' + m.path);

                        // eslint-disable-next-line @typescript-eslint/unbound-method
                        self.stopListening(m, "change:value", self.onParameterChange);
                    }
                });
            }

            // clear parameterModels
            self.parameterModels = [];

            if (self.get(DocumentDataModel.AUTO_EXECUTE)) {
                // start listening to new parameter models
                _.each(parameters, function(path) {
                    const m = self.getViewState().getByPath(path);

                    if (m) {
                        // eslint-disable-next-line @typescript-eslint/unbound-method
                        self.listenTo(m, "change:value", self.onParameterChange);
                        self.parameterModels.push(m);
                    }
                });
            }
        }

        updateVirtualSubscriptions(): void {
            const sources = [];

            if (this.get("_dataType") !== "virtual") {
                this.clearVirtualSubscriptionsAll();
                return;
            }

            // get list of sources
            _.each(this.get("_virtualParams"), function(queryParam) {
                const sourcePath = queryParam.value;

                if (sourcePath && queryParam.type === "datasource") {
                    sources.push(sourcePath);
                }
            });

            // determine unused sources
            let unusedSubscriptions = _.keys(this.virtualSubscriptions);

            _.each(sources, (path): void => {
                let key = this.path + "_" + path,
                    model;

                if (this.get("isSourceDataDialog")) {
                    key += "#edit";
                }

                if (_.includes(unusedSubscriptions, key)) {
                    unusedSubscriptions = _.without(unusedSubscriptions, key);
                    return;
                }

                if (this.root) {
                    model = this.root.getByPath(path);
                }

                if (model && !this.virtualSubscriptions[key]) {
                    this.virtualSubscriptions[key] = model;
                    model.unsubscribe(key);
                    model.subscribe(key, {
                        key: key,
                        onData: (meta, data, error) => {
                            this.onParameterChange(
                                { path: "#virtual", previous: _.noop.bind(this) },
                                Math.random(),
                                {},
                            );
                        },
                        source: model,
                    });
                }
            });

            // unsubscribe from unused sources
            this.clearVirtualSubscriptions(unusedSubscriptions);
        }

        wrapWithErrorHandler(func, model, options) {
            const self = this;
            return function() {
                try {
                    func.apply(self, arguments);
                } catch (e) {
                    self.onError(
                        model,
                        $.extend(true, {}, options),
                        e,
                        "Error",
                        Array.prototype.slice.call(3, arguments),
                    );
                }
            };
        }
    }
}
