var deepDiff = function () {
    return {
        VALUE_CREATED: 'created',
        VALUE_UPDATED: 'updated',
        VALUE_DELETED: 'deleted',
        VALUE_UNCHANGED: 'unchanged',

        /*
         *  Get the difference between 2 json objects
         *  Remove any of the unchanged properties
         */
        getDiff : function(obj1, obj2){
           return this.removeUnchanged(this.map(obj1,obj2));
        },
        map: function (obj1, obj2) {
            if (this.isFunction(obj1) || this.isFunction(obj2)) {
                throw 'Invalid argument. Function given, object expected.';
            }
            if (this.isValue(obj1) || this.isValue(obj2)) {
                var o  =_.clone((obj1 === undefined) ? obj2 : obj1);
                return o;
            }

            var diff = {};
            for (var key in obj1) {
                if (this.isFunction(obj1[key])) {
                    continue;
                }
                var value2 = undefined;
                if ('undefined' != typeof (obj2[key])) {
                    value2 = obj2[key];
                }
                //always copy in Hidden properties
                if (key === '_Hidden') {
                    diff[key] = obj1[key];
                } else if (this.compareValues(obj1[key], value2) !== 'unchanged') {
                    diff[key] = this.map(obj1[key], value2);
                } else {
                    diff[key] = null;
                }
            }
            for (var key in obj2) {
                if (this.isFunction(obj2[key]) || ('undefined' != typeof (diff[key]))) {
                    continue;
                }
                diff[key] = this.map(undefined, obj2[key]);
            }
            return diff;

        },
        compareValues: function (value1, value2) {
            if (value1 === value2) {
                return this.VALUE_UNCHANGED;
            }
            if (this.isDate(value1) && this.isDate(value2) && value1.getTime() === value2.getTime()) {
                return this.VALUE_UNCHANGED;
            }
            if ('undefined' == typeof (value1)) {
                return this.VALUE_CREATED;
            }
            if ('undefined' == typeof (value2)) {
                return this.VALUE_DELETED;
            }

            return this.VALUE_UPDATED;
        },
        isFunction: function (obj) {
            return {}.toString.apply(obj) === '[object Function]';
        },
        isArray: function (obj) {
            return {}.toString.apply(obj) === '[object Array]';
        },
        isDate: function (obj) {
            return {}.toString.apply(obj) === '[object Date]';
        },
        isObject: function (obj) {
            return {}.toString.apply(obj) === '[object Object]';
        },
        isValue: function (obj) {
            return !this.isObject(obj) && !this.isArray(obj);
        },
        removeUnchangedJSON: function (cleanObj) {
            _.each(cleanObj, _.bind(function (val, key) {
                if (_.isEqual(val, {}) || _.isEqual([], val) || val === null) {
                    delete cleanObj[key];
                } else if (! _.isArray(val) && typeof(val) !== "string") {                
                    this.removeUnchangedJSON(cleanObj[key]);
                }
            }, this));
        },
        removeUsedKeys: function (app){
            for(var key in app){
                if(app[key] !== null && typeof(app[key]) === 'object'){
                    this.removeUsedKeys(app[key])

                    if(_.isEqual(app[key], {})) {
                        delete app[key]
                    }
                } 
                if(app[key] === null){
                    delete app[key]
                }
            }
        },
        removeUnchanged: function (obj) {
            var cleanObj = {};
            var primitiveTypes = ['string', 'number', 'boolean'];
            _.each(obj, _.bind(function (val, key) {
                if (_.indexOf(primitiveTypes, typeof(val)) >= 0) {
                    cleanObj[key] = val;
                } else if (val !== null || !_.isArray(val)) {                  
                    cleanObj[key] = _.pickBy(val, function (v, k) {
                        return v !== null;
                    });
                } else {                   
                    cleanObj[key] = val;
                }
            }, this));           
          
            this.removeUsedKeys(cleanObj);
            return cleanObj;
        }
    }
}();
