/*global _, $, Backbone, console, Conv, T, Tools */
/// <reference path="../templates/combined.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class AccordionView extends Backbone.View<Backbone.Model> {
        HEADING_HEIGHT = 28;
        onUpdate: (sections: Array<any>) => void;
        debouncedRemoveAnimate: () => void;
        sections: Array<any>;

        constructor(options) {
            super();

            this.onUpdate = options.onUpdate;

            // deep clone options.sections
            this.sections = options.sections || [];

            this.$el = options.el;
            this.$el.addClass("dash-accordion accordion");

            this.sections.forEach((section, i) => {
                if (section.$el) {
                    section.$heading = section.$el.find(section.$heading);
                    section.$content = section.$el.find(section.$content);

                    section.$el.addClass("section");

                    section.$heading.click(() => {
                        this.onClick(section);
                    });
                }
            });

            this.debouncedRemoveAnimate = _.debounce(() => {
                this.$el.removeClass("animate");
                if (this.onUpdate) {
                    this.onUpdate(this.sections);
                }
            }, 300);

            this.updateLayout(false);
        }

        adjustWeights(sections: Array<any>, heights: Array<number>) {
            const totalWeight = this.sum(
                    _.map(sections, function(s) {
                        return s.weight;
                    }),
                ),
                totalHeight = this.sum(heights);

            _.each(sections, function(s, i) {
                s.weight = totalWeight * (heights[i] / totalHeight);
            });
        }

        onClick(section: any) {
            section.isExpanded = !section.isExpanded;
            this.updateLayout(true);
        }

        onSectionResize(caller, event, ui) {
            let self = this,
                i,
                progress = 0,
                section,
                callerHeight = caller.$el.height(),
                delta = callerHeight - ui["originalSize"]["height"],
                height,
                sections = [caller],
                heights = [callerHeight];

            //console.log(callerHeight, delta);

            // start with the sections after the caller - balance space from siblings below
            for (i = self.sections.indexOf(caller) + 1; i < self.sections.length; i += 1) {
                section = self.sections[i];

                // skip non expanded sections
                if (!section.isExpanded || section.fixed) {
                    continue;
                }

                // can't shrink more than minimum
                height = Math.max(self.HEADING_HEIGHT, section.startHeight - (delta - progress));

                // update progress for next if any delta left
                progress += Math.min(section.startHeight - self.HEADING_HEIGHT, delta - progress);

                //console.log(section.$header.text());
                section.$el.height(height + "px");

                // store altered section and heights
                sections.push(section);
                heights.push(height);

                // delta complete
                if (progress === delta) {
                    break;
                }
            }

            // redraw
            self.adjustWeights(sections, heights);
        }

        resize() {
            this.updateLayout(false);
        }

        setExpanded(index, isExpanded, animate) {
            animate = animate || true;
            this.sections[index].isExpanded = isExpanded;
            this.updateLayout(animate);
        }

        sum(list) {
            return _.reduce(
                list,
                function(memo, num) {
                    return memo + num;
                },
                0,
            );
        }

        updateLayout(animate?: boolean) {
            let self = this,
                height = self.$el.height(),
                visibleWeight,
                ratio,
                visibleSections,
                availableHeight = height;

            self.$el.toggleClass("animate", animate);
            visibleSections = _.filter(self.sections, { isExpanded: true });

            // subtract minimum heights
            availableHeight -=
                (self.sections.length - visibleSections.length) * self.HEADING_HEIGHT;

            // subtract expanded fixed sections
            availableHeight -= this.sum(
                _.map(
                    _.filter(visibleSections, function(section) {
                        return section.isExpanded && section.fixed;
                    }),
                    function(section) {
                        return section.fixed;
                    },
                ),
            );

            visibleWeight = this.sum(
                _.map(visibleSections, function(section) {
                    return section.weight;
                }),
            );

            ratio = availableHeight / visibleWeight;

            _.each(self.sections, function(section, i) {
                // toggle class
                section.$el.toggleClass("expanded", section.isExpanded);

                // set height
                if (section.isExpanded) {
                    section.$el.height((section.fixed || section.weight * ratio) + "px");
                } else {
                    section.$el.height(0);
                }

                if (section.resize) {
                    section.resize();
                }

                // configure resize for sections with neighbor expanded after
                if (
                    section.isResizable &&
                    section.isExpanded &&
                    !section.fixed &&
                    i < self.sections.length - 1 &&
                    _.some(self.sections.slice(i + 1), function(s) {
                        return s.isExpanded && !s.fixed;
                    })
                ) {
                    // init
                    section.resizeable = section.$el["resizable"]({
                        handles: "s",
                        resize: $.proxy(self.onSectionResize, self, section),
                        start: function() {
                            _.each(self.sections, function(s) {
                                s.startHeight = s.$el.height();
                            });

                            section.$el["resizable"](
                                "option",
                                "maxHeight",
                                self.$el.height() -
                                    (self.sections.length - 1) * self.HEADING_HEIGHT,
                            );
                        },
                        stop: function() {
                            self.updateLayout();
                        },
                    });
                } else if (section.resizeable) {
                    // destroy
                    section.$el["resizable"]("destroy");
                    delete section.resizeable;
                }
            });

            if (animate) {
                this.debouncedRemoveAnimate();
            } else {
                if (self.onUpdate) {
                    self.onUpdate(self.sections);
                }
            }
        }
    }
}
