/// <reference path="I32.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Time extends I32 {
        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Time, dataView);
        }

        public static fromMoment(m: moment.Duration): rTimeClass {
            return m === null ? null : new rTimeClass(m.asMilliseconds());
        }

        public static fromScalar(value: number): rTimeClass {
            return value === null ? null : new rTimeClass(value);
        }

        public static fromString(str: string): rTimeClass {
            str = str.replace("1970-01-01T", "").replace("Z", "");
            const timeParts = str.match(Constants.TIME_PARTS_RX);
            const m = moment.duration(timeParts && timeParts[1] ? timeParts[1] : str);
            return Time.fromMoment(m);
        }

        public static listToIPC(values: Array<rTimeClass>): Uint8Array {
            const size = values.length * 4 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(TypeNum.Time); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Time.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        public static toIPC(value: rTimeClass): Uint8Array {
            const buffer = Util.createBuffer(5);

            buffer.wb(256 - TypeNum.Time); // type
            Time.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        public static toMoment(value: rTimeClass): moment.Duration {
            return value === null ? null : moment.duration(value.i);
        }

        public static toScalar(value: rTimeClass): number {
            return value === null ? null : value.i;
        }

        public static toString(
            value: rTimeClass,
            format = "HH:mm:ss.SSS",
            options = { trim: false },
        ): string {
            return value === null ? "" : Time.toMoment(value).format(format, options);
        }

        public static writeValue(value: rTimeClass, wb: (b: number) => void): void {
            Int.writeValue(Time.toScalar(value), wb);
        }

        getValue(i: number): rTimeClass {
            const s = super.getScalar(i);
            return s === I32.NULL ? null : new rTimeClass(s);
        }
    }
}
