// Extend cookies or set defaults
const cookies = {
    language: $.cookie("dashboard-language") || "en",
    logLevel: $.cookie("dashboard-log-level") || 1,
    theme: $.cookie("dashboard-theme") || "kx-darkroom",
    viewerTheme: $.cookie("dashboard-viewer-theme") || "kx-darkroom",
};

// this is set on login now in delta-client-base.js
$.cookie("dashboard-language", cookies.language, { expires: 30, path: "/" });
$.cookie("dashboard-log-level", cookies.logLevel, { expires: 30, path: "/" });
$.cookie("dashboard-theme", cookies.theme, { expires: 30, path: "/" });
$.cookie("dashboard-viewer-theme", cookies.viewerTheme, { expires: 30, path: "/" });

//FIX: KXAX-7082 overwrite default Backbone's decodeFragment() which uses decodeURI
_.extend(Backbone.History.prototype, {
    decodeFragment: function(fragment) {
        return fragment;
    },
});
