namespace QuickBase {
    interface INotificationObj {
        title: string;
        message: string;
        icon: string;
    }

    interface ITriggers {
        template: string;
        source: DocumentDataModel | undefined;
        icon: string;
        sound: string;
    }

    interface ISoundConfig {
        sound: string;
    }

    export class DatasourceNotifier {
        private sendNotification: (id: string, obj: INotificationObj, sound: ISoundConfig) => void;
        private datasources: { [index: string]: any } = {};
        private templates: { [index: string]: any } = {};
        private triggers: ITriggers[];

        constructor(show: (id: string, obj: INotificationObj, sound: ISoundConfig) => void) {
            this.triggers = [];
            this.sendNotification = show;
        }

        public onTriggerChange(triggers) {
            this.triggers = triggers;
            const cids = triggers.map((x) => {
                if (x.source) {
                    return x.source.cid;
                }
            });

            // Ensure we are subscribed to each datasource
            triggers.forEach((trigger) => {
                const { template, source, icon, sound } = trigger;
                // For Each trigger ensure we are subscribed
                if (source && !this.datasources[source.cid]) {
                    this.datasources[source.cid] = new NotificationListener(source, template, icon, sound, this.sendNotification);
                } else if (source) {
                    this.datasources[source.cid].updateTemplate(template, icon, sound);
                }
            })

            const unsubscriptions = Object.keys(this.datasources).filter((cid) => {
                return cids.indexOf(cid) === -1;
            });

            unsubscriptions.forEach(cid => {
                this.datasources[cid].unsubscribe();
                this.datasources[cid] = undefined;
                delete this.datasources[cid];
            })

        }
    }

    class NotificationListener {
        private template: HandlebarsTemplateDelegate<any>;
        private sendNotification: (id: string, obj: INotificationObj, sound: ISoundConfig) => void;
        private source: DocumentDataModel;
        private icon: string;
        private sound: string;

        constructor(
            source: DocumentDataModel,
            template: string,
            icon: string,
            sound: string,
            sendNotification: (id: string, obj: INotificationObj, sound: ISoundConfig) => void
        ) {
            this.source = source;
            this.sound = sound;
            this.icon = icon;
            this.template = Handlebars.compile(template);
            this.sendNotification = sendNotification;
            this.subscribe();
        }

        public updateTemplate(template, icon, sound) {
            this.template = Handlebars.compile(template);
            this.icon = icon;
            this.sound = sound;
        }

        private subscribe() {
            this.source.subscribe('notifications', this);
            this.source.dataSet.noBackground = true;
        }

        public unsubscribe() {
            this.source.unsubscribe('notifications')
        }

        public onData(collection, changeset) {
            // Do We Notify on Reset, Add or Both?
            if (changeset.add && changeset.add.length) {
                changeset.add.forEach((row) => {
                    // Omit Row Index (we don't want to display this field in notifications)
                    row = _.omit(row, ['_rowIndex']);
                    const message = this.template(row);
                    const title = this.source.path;
                    const sound = this.sound;
                    const icon = this.icon;

                    this.sendNotification('datasource-notifications', { title, message, icon }, { sound });
                })
            }
        }
    }
}