/// <reference path="./GraphNode.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    export class GroupByNode extends GraphNode {
        static dataToIPC(data: any): Uint8Array {
            data = GroupByNode.processBindings(data);
            return QuickBase.ipc.Dict.toIPC(
                ["groupBy", "agg"],
                QuickBase.ipc.List.toIPC([
                    QuickBase.ipc.Dict.toIPC(
                        _.keys(data.groupBy),
                        QuickBase.ipc.List.toIPC(
                            _.values(data.groupBy).map(v => {
                                return _.isString(v)
                                    ? QuickBase.ipc.Symbol.toIPC(v)
                                    : QuickBase.ipc.Dict.toIPC(
                                          _.keys(v),
                                          QuickBase.ipc.Symbol.listToIPC(_.values(v)),
                                      );
                            }),
                        ),
                    ),
                    _.isEmpty(data.agg)
                        ? QuickBase.ipc.List.toIPC([])
                        : QuickBase.ipc.Dict.toIPC(
                              _.keys(data.agg),
                              QuickBase.ipc.List.toIPC(
                                  _.map(_.values(data.agg), a =>
                                      QuickBase.ipc.Symbol.listToIPC(a.map(v => String(v))),
                                  ),
                              ),
                          ),
                ]),
            );
        }

        static getActiveBindingKeys(data: any): Array<string> {
            data = data as Data;
            return GroupByNode.getBindingKeysFromArray(
                [],
                _.union(_.keys(data.agg), _.keys(data.groupBy)),
            );
        }

        static processBindings(data: any): Data {
            data = _.cloneDeep(data) as Data;

            GraphNode.replaceKeys(data.agg);
            GraphNode.replaceKeys(data.groupBy);

            return data;
        }
    }
}
