/// <reference path="TypeBase.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Symbol extends TypeBase {
        offsets: Array<number>;

        // keyed data
        keyIndex?: { [index: string]: number };
        values?: Array<string>;
        indexOffset = 0;

        constructor(length: number, offsets: Array<number>, dataView: DataView) {
            super(length, length ? offsets[0] : undefined, TypeNum.Symbol, dataView);
            this.offsets = offsets;
        }

        static listToIPC(values: Array<string>): Uint8Array {
            const size = _.sumBy(values, v => v.length + 1) + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(11); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Symbol.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        static toIPC(value?: string): Uint8Array {
            const size = value ? value.length + 2 : 2;
            const buffer = Util.createBuffer(size);

            buffer.wb(256 - 11); // type
            if (value !== null || value !== undefined) {
                Symbol.writeValue(value, buffer.wb); // value
            }

            return buffer.data;
        }

        static writeValue(value: string, wb: (b: number) => void): void {
            if (value !== null && value !== undefined) {
                _.each("" + value, v => wb(v.charCodeAt(0)));
            }

            wb(0); // null terminated
        }

        getValue(i: number): string {
            if (this.values) {
                return this.values[(i + this.indexOffset) % this.length];
            } else {
                let c,
                    pos = this.offsets[(i + this.indexOffset) % this.length];
                const s = [];
                while ((c = this.dataView.getUint8(pos++)) !== 0) s.push(c);
                return u16u8(s);
            }
        }

        hash(i: number): string {
            return this.getValue(i);
        }

        public mergeIndexed(arg: ipc.Symbol): void {
            if (arg.length >= this.length) throw "Invalid Merge Size";

            // todo potentially transfer

            if (arg.offset === null) return;

            // calculate end of arg data
            let argOffsetEnd = _.last(arg.offsets);
            while (arg.dataView.getUint8(argOffsetEnd++) !== 0);

            // calculate arg size
            const argSize = argOffsetEnd - arg.offset;

            // calculate new buffer size
            const thisFirstOriginal = this.offsets[arg.length];

            // calculate end of this data
            let thisOffsetEnd = _.last(this.offsets);
            while (this.dataView.getUint8(thisOffsetEnd++) !== 0);

            // calculate original size
            const thisOriginalSize = thisOffsetEnd - thisFirstOriginal;

            // calculate new totalSize
            const totalSize = argSize + thisOriginalSize;

            // TODO: new buffer creation isn't always necessary
            // could track buffer size and copy only when necessary.
            // store thisOffsetEnd - offset in tmpBufferSize

            // create a new buffer
            const buffer = new ArrayBuffer(totalSize);

            // copy in arg data
            new Uint8Array(buffer, 0, argSize).set(
                new Uint8Array(arg.dataView.buffer, arg.offset, argSize),
            );

            // copy in original data
            new Uint8Array(buffer, argSize, thisOriginalSize).set(
                new Uint8Array(this.dataView.buffer, thisFirstOriginal, thisOriginalSize),
            );

            // update offset
            for (let i = 0; i < arg.length; i++) this.offsets[i] = arg.offsets[i] - arg.offset; // align to zero

            // align to zero - correcting for any move of orginal data
            const orginalMove = argSize - thisFirstOriginal;
            for (let i = arg.length; i < this.length; i++)
                this.offsets[i] = this.offsets[i] + orginalMove;

            this.offset = 0;

            // use new databuffer
            this.dataView = new DataView(buffer);
        }

        public mergeKeyedPrimary(
            arg: ipc.Symbol,
            maxRows: number,
            insertIndices: Array<number>,
        ): number {
            // convert to indexed
            if (this.keyIndex === undefined) {
                const keyIndex = {};
                const values = [];
                for (let i = 0; i < this.length; i++) {
                    const value = this.getValue(i);
                    keyIndex[value] = i;
                    values.push(value);
                }

                // assign keyIndex and values
                this.keyIndex = keyIndex;
                this.values = values;

                // free buffer and offsets;
                delete this.offsets;
                delete this.dataView;
            }

            for (let i = 0; i < arg.length; i++) {
                // get arg value and existing index
                const argValue = arg.getValue(i);
                const valueIndex = this.keyIndex[argValue];

                // if a new item
                if (valueIndex === undefined) {
                    // if length exceeds max
                    if (this.length === maxRows) {
                        // loop around using target index
                        const targetIndex = this.indexOffset % maxRows;
                        this.keyIndex[argValue] = targetIndex;
                        this.values[targetIndex] = argValue;
                        insertIndices.push(targetIndex);
                        this.indexOffset++;
                    } else {
                        // add value
                        this.keyIndex[argValue] = this.length;
                        insertIndices.push(this.length);
                        this.values.push(argValue);
                        this.length++;
                    }
                } else {
                    insertIndices.push(valueIndex);
                }
            }

            //console.log("+" + arg.length + ":" + JSON.stringify(_.times(arg.length, i=>arg.getValue(i))));
            //console.log("=" + this.length +"("+this.indexOffset+"):" + JSON.stringify(_.times(this.length, i=>this.getValue(i))));
            return this.indexOffset;
        }

        public mergeKeyed(
            arg: ipc.Symbol,
            indices: Array<number>,
            indexOffset: number,
            maxRows: number,
        ): void {
            // convert to indexed
            if (this.values === undefined) {
                const values = [];
                for (let i = 0; i < this.length; i++) {
                    const str = this.getValue(i);
                    values.push(str);
                }

                // assign keyIndex and values
                this.values = values;

                // free buffer and offsets;
                delete this.offsets;
                delete this.dataView;
            }

            for (let i = 0; i < indices.length; i++) {
                // get arg value and existing index
                const argValue = arg.getValue(i);
                const targetIndex = indices[i];

                if (targetIndex === this.length) {
                    this.values.push(argValue);
                    this.length++;
                } else if (targetIndex > this.length) {
                    debugger;
                } else this.values[targetIndex] = argValue;
            }

            this.indexOffset = indexOffset;
        }
    }
}
