/// <reference path="F64.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Double extends F64 {
        static TYPE_NUM = 9;

        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Double, dataView);
        }

        static listToIPC(values: Array<number>): Uint8Array {
            const size = values.length * 8 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(Double.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Double.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        static toIPC(value: number): Uint8Array {
            const buffer = Util.createBuffer(9);
            buffer.wb(256 - Double.TYPE_NUM); // type
            Double.writeValue(value, buffer.wb); // value
            return buffer.data;
        }

        static writeValue(value: number | null, wb: (b: number) => void): void {
            if (value !== null) {
                const fb = new Float64Array(1);
                fb[0] = value;
                _.each(new Uint8Array(fb.buffer), f => wb(f));
            } else {
                _.each([0, 0, 0, 0, 0, 0, 248, 255], l => wb(l));
            }
        }

        getValue(i: number): number {
            const val = this.getScalar(i);
            return isNaN(val) ? null : val;
        }
    }
}
