/*global $, T, Backbone, FormSchemas, BBForm, DialogBase*/
/// <reference path="../templates/combined.ts" />
/// <reference path="DialogBase.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ErrorDialog extends DialogBase {
        constructor(options) {
            super(
                _.extend(
                    {
                        iconClass: "fa-exclamation-triangle",
                        dialogTitle: "Error",
                        minHeight: 110,
                        width: 275,
                    },
                    options,
                    {
                        userClass:
                            "error-dialog" + (options.userClass ? " " + options.userClass : ""),
                    },
                ),
            );
        }

        dialogButtons() {
            return [
                {
                    text: "OK",
                    click: () => {
                        this.dialogClose();
                    },
                    icons: {
                        primary: "fa fa-check",
                    },
                },
            ];
        }

        renderTemplate() {
            return t("Are you sure you want to delete this item?");
        }

        static ShowError(text, callbackFn?: () => void) {
            new ErrorDialog(
                _.extend(
                    {
                        callback: _.isFunction(callbackFn) ? callbackFn : _.noop,
                    },
                    text
                        ? {
                              renderTemplate: () => {
                                  return text;
                              },
                          }
                        : {},
                ),
            );
        }
    }
}
