/*global $,_,Backbone,T*/
/// <reference path="../templates/combined.ts" />
/// <reference path="../classes/log.ts"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class DocumentSelector extends Backbone.View<Backbone.Model> {
        appModel = null;
        availableHeight = 0;
        debouncedRender: Function;
        documentsList = null;
        documentTypeMap = null;
        select2: any;
        showManage = false;
        viewModel: Backbone.Model = null;
        RECENT_DOCUMENT_COUNT = 3;

        constructor(options) {
            super(options);
            this.appModel = options.appModel;
            this.viewModel = options.viewModel;
            this.showManage = options.showManage;
            this.debouncedRender = _.debounce(this.render, 100);
            this.initializeEvents();
        }

        initializeEvents() {
            const docs = this.appModel.get("documents");
            this.listenTo(docs, "reset", this.onDocumentsReset);
            this.listenTo(docs, "add", this.onDocumentsAdd);
            this.listenTo(docs, "remove", this.onDocumentsRemove);
            this.listenTo(
                this.viewModel,
                "change:selectedDocumentId",
                this.onViewModelSelectedChanged,
            );
        }

        blurSelect() {
            // remove focus so keyboard shortcuts aren't triggered on this element
            this.select2
                .data("select2")
                .$container.find(".select2-selection")
                .blur();
        }

        getDocuments() {
            let other = [],
                recent = [],
                starred = [];

            this.documentTypeMap = {};

            const sortedByDate = _.sortBy(
                this.appModel.get("documents").map(d => d.attributes),
                d => -Date.parse(d["lastUpdateDate"]),
            );

            // Split documents by starred/recent/other
            sortedByDate.forEach(d => {
                // Do not show Draft dashboards in the Viewer
                if (!this.appModel.get("quickview") || d.name.indexOf("Draft:") !== 0) {
                    if (_.includes(d["tags"], "starred")) {
                        starred.push(d);
                        this.documentTypeMap[d.id] = "starred";
                    } else if (recent.length < this.RECENT_DOCUMENT_COUNT) {
                        recent.push(d);
                        this.documentTypeMap[d.id] = "recent";
                    } else {
                        other.push(d);
                        this.documentTypeMap[d.id] = "other";
                    }
                }
            });

            // Sort starred and other alphabetically
            starred = _.sortBy(starred, d => d["name"].toLowerCase());
            other = _.sortBy(other, d => d["name"].toLowerCase());

            this.documentsList = recent.concat(starred, other);
        }

        onDocumentsAdd(model) {
            this.listenTo(model, "change:name", this.render);
            this.$el.find("select").select2("close");
            this.render();
        }

        onDocumentsRemove(model) {
            this.stopListening(model);
            this.render();
        }

        onDocumentsReset(collection, options) {
            const self = this;

            // stop listening old
            _.each(options.previousModels, function(model) {
                self.stopListening(model);
            });

            // start listening new
            collection.each(function(model) {
                self.listenTo(model, "change:name", self.render);
            });

            this.render();
        }

        onResize() {
            this.availableHeight = $("body").height() * 0.4;
        }

        onViewModelSelectedChanged(model, value) {
            if (value !== this.$el.find("select").val()) {
                this.selectDocument(value);
            }
        }

        render() {
            const self = this;

            this.getDocuments();

            this.$el.html(T.DocumentSelector(this.documentsList));

            // create select menu
            this.select2 = this.$el.find("select").select2({
                language: {
                    noResults: function() {
                        return "No Dashboards found";
                    },
                },
                templateResult(doc, container) {
                    let $result;

                    switch (self.documentTypeMap[doc.id]) {
                        case "starred":
                            $result = $(
                                '<span><i class="dashboard-selector-icon fa fa-star" title="Starred"></i>' +
                                    _.escape(doc.text) +
                                    "</span>",
                            );
                            $(container).addClass("starred");
                            break;
                        case "recent":
                            $result = $(
                                '<span><i class="dashboard-selector-icon fa fa-clock-o" title="Recent"></i>' +
                                    _.escape(doc.text) +
                                    "</span>",
                            );
                            $(container).addClass("recent");
                            break;
                        default:
                            $result = $("<span>" + _.escape(doc.text) + "</span>");
                    }

                    return $result;
                },
                //'templateResult': function (doc) {
                //    var $icon,
                //        $preview,
                //        selectedDocument,
                //        thumb;

                //    selectedDocument = self.appModel.get('documents').get(doc.id);
                //    if (selectedDocument) {
                //        thumb = selectedDocument.get('thumb');

                //        if (thumb) {
                //            $icon = $('<span class="dashboard-selector-icon" style="background-image: url(' + thumb + ');"></span><span class="dashboard-selector-text">' + doc.text + '</span>');
                //        } else {
                //            $icon = $('<span class="dashboard-selector-icon";"></span><span class="dashboard-selector-text">' + doc.text + '</span>');
                //        }
                //    }

                //    return $icon;
                //},
                theme: "document-selector",
                width: "225px",
            });
            this.onResize();

            this.select2.on("select2:open", () => {
                const $dropdown = $(".select2-container--document-selector .select2-dropdown");

                // if show manage and link not already added
                if (this.showManage && $dropdown.find(".manage").length == 0) {
                    const $manage = $(
                        '<a class="manage" title="' +
                            t("Manage Dashboards") +
                            '">' +
                            t("Manage Dashboards") +
                            "</a>",
                    );
                    $manage
                        .button({
                            icons: {
                                secondary: "fa fa-share-square-o",
                            },
                        })
                        .click(() => {
                            $manage.removeClass("ui-state-hover");
                            this.select2.select2("close");
                            this.trigger("manage");
                        });

                    $dropdown.append("<hr>");
                    $dropdown.append($manage);
                }

                // set height
                $dropdown
                    .find(".select2-results > ul")
                    .css("max-height", this.availableHeight + "px");

                //placeholder attribute prevents IE from invoking blur() if no value is -
                //present in search field (known IE bug:https://github.com/select2/select2/issues/3300)
                if (!Tools.isIE()) {
                    $(".select2-search__field").attr("placeholder", "Search...");
                }
            });

            this.select2.on("select2:close", function() {
                _.defer(function() {
                    self.blurSelect();
                });
            });

            this.select2.on("select2:select", function() {
                const value = self.select2.val();

                if (value !== self.viewModel.get("selectedDocumentId")) {
                    try {
                        // can't pass callback to navigate so setting it here instead
                        self.appModel.get("router").onNavCancel = function() {
                            // return to initial selection
                            self.select2
                                .val(self.viewModel.get("selectedDocumentId"))
                                .trigger("change");
                        };
                        self.appModel.get("router").navigate(value, { trigger: true });
                    } catch (ex) {
                        Log.Error("Select Document Error:", ex);
                    }
                }

                self.blurSelect();
            });

            this.selectDocument(this.viewModel.get("selectedDocumentId"));

            //this.$el.find("input").attr("readonly", true);

            return this;
        }

        selectDocument(value) {
            const $select = this.$el.find("select"),
                selectedDocument = this.appModel.get("documents").get(value);

            $select.val(value).trigger("change");

            if (selectedDocument) {
                this.$el.find("input").val(selectedDocument.get("name"));
            }
        }
    }
}
