/// <reference path="I32.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Int extends I32 {
        static readonly TYPE_NUM = 6;

        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Int, dataView);
        }

        static listToIPC(values: Array<number>): Uint8Array {
            const size = values.length * 4 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(Int.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => Int.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        static toIPC(value: number | null): Uint8Array {
            const buffer = Util.createBuffer(5);
            buffer.wb(256 - Int.TYPE_NUM); // type
            Int.writeValue(value, buffer.wb); // value
            return buffer.data;
        }

        static writeValue(value: number | null, wb: (b: number) => void): void {
            if (value !== null) {
                const ib = new Int32Array(1);
                ib[0] = value;
                _.each(new Uint8Array(ib.buffer), i => wb(i));
            } else {
                _.each([0, 0, 0, 128], i => wb(i));
            }
        }

        getValue(i: number): number {
            const val = this.getScalar(i);
            return val === -2147483648
                ? null
                : val === -2147483647
                ? -Infinity
                : val === 2147483647
                ? Infinity
                : val;
        }
    }
}
