/// <reference path="I64.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class QLong extends I64 {
        static readonly TYPE_NUM = 7;

        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Long, dataView);
        }

        static listToIPC(values: Array<string>): Uint8Array {
            const size = values.length * 8 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(QLong.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => QLong.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        static toIPC(value: string): Uint8Array {
            const buffer = Util.createBuffer(9);
            buffer.wb(256 - QLong.TYPE_NUM); // type
            QLong.writeValue(value, buffer.wb); // value
            return buffer.data;
        }

        static writeValue(value: string, wb: (b: number) => void): void {
            if (value !== null) {
                const long = Long.fromString("" + value);
                _.each(long.toBytesLE(), l => wb(l));
            } else {
                _.each([0, 0, 0, 0, 0, 0, 0, 128], l => wb(l));
            }
        }

        getValue(i: number): string | number {
            const l = this.getLegacyScalar(i);
            if (l.equals(I64.LONG_INFINITY_NEGATIVE)) {
                return -Infinity;
            } else if (l.equals(I64.LONG_INFINITY_POSITIVE)) {
                return Infinity;
            }
            return l.equals(I64.LONG_NULL) ? null : l.toString();
        }
    }
}
