/*globals Backbone,_,$,console */
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ErrorTooltip {
        public static clearAll(fade?: boolean) {
            if (fade) {
                $("body .field-error").fadeOut();
            } else {
                $("body .field-error").remove();
            }
        }

        public static clear($errorTooltip: JQuery, fade?: boolean) {
            if ($errorTooltip) {
                if (fade) {
                    $errorTooltip.fadeOut();
                } else {
                    $errorTooltip.remove();
                }
            }
        }

        public static show(message, element, onLeft) {
            let $errorTooltip;

            if (!message || !element) {
                return;
            }

            $errorTooltip = $("<div/>", {
                text: message,
            })
                .addClass("field-error")
                .appendTo("body");

            $errorTooltip.position({
                of: $(element),
                my: onLeft ? "right center" : "left center",
                at: onLeft ? "left center" : "right center",
                collision: "flip fit",
                using: function(obj, info) {
                    if (info["horizontal"] === "right") {
                        $(this).addClass("error-left-side");
                    } else {
                        $(this).removeClass("error-left-side");
                    }

                    $(this).css({
                        left: obj["left"] + "px",
                        top: obj["top"] + "px",
                    });
                },
            });

            return $errorTooltip;
        }
    }
}
