/*global*/
/// <reference path="../templates/combined.ts" />
/// <reference path="ComponentDropView.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class PopupView extends Backbone.View<Backbone.Model> {
        $dialog: JQuery;

        appModel: Backbone.Model;
        viewModel: DocumentViewModel;
        viewStateListeners = {};
        quickView: boolean;
        isEditing: boolean;
        visibleListener: DocumentViewModel;
        widgetId: string;

        $contentPanel: JQuery;
        $dialogContainer: JQuery;

        onDialogAttributeChange: (() => void) & _.Cancelable;

        componentDropView: ComponentDropView;

        constructor(options) {
            super(options);
            this.appModel = options.appModel;
            this.viewModel = options.viewModel;
            this.quickView = options.quickView;
            this.isEditing = options.isEditing;

            this.$el.html(T.popup({}));
            this.$dialog = this.$el.find(".dialog");
            this.$contentPanel = this.$dialog.find(".content-panel");

            if (options.container) {
                this.$dialogContainer = options.container;
            } else {
                this.$dialogContainer = $(".quick-dash > .content-panel > .document-view");
            }

            this.onDialogAttributeChange = _.debounce(this.initializeDialog, 100);

            this.initializeDialog();
            this.initializeEvents();
            this.initializeVisible();
        }

        initializeEvents() {
            this.listenTo(this.viewModel, "change:buildMode", this.onBuildModeChange);
            this.listenTo(
                this.model,
                "change:dialogTitle change:dialogWidth change:dialogHeight" +
                    " change:modal change:resizable",
                this.onDialogAttributeChange,
            );
            this.listenTo(this.model, "change:visible", this.initializeVisible);
        }

        destroyDialog() {
            if (this.$dialog.dialog("instance")) {
                this.$dialog.dialog("destroy");
            }
        }

        initializeComponentDrop() {
            let $componentDrop;

            if (!this.componentDropView) {
                //recreate $componentDrop;
                // when popup is closed, $componentDrop is removed from DOM
                $componentDrop = $("<div></div>", { class: "component-drop" });
                this.$contentPanel.append($componentDrop);

                this.componentDropView = new ComponentDropView({
                    el: $componentDrop,
                    dashboardAppModel: this.appModel,
                    dashboardViewModel: this.viewModel,
                    DeltaClientLib: DeltaClientLib,
                    quickView: this.quickView,
                    widgets: this.model.get("widgets"),
                });
            }
        }

        popupPosition() {
            // Set the popup position
            // 1. if a cookie is defined then take the positions from here
            // 2. else if take the position from the prop panel if defined
            // 3. else default to center
            let top = this.model.get("dialogTop");
            let left = this.model.get("dialogLeft");
            const offset = $(".main-toolbar").height() || 34;

            if ((top || top === 0) && (left || left === 0)) {
                let currPosition = localStorage.getItem("popupDialog-" + this.model.get("id"));
                if (currPosition) {
                    currPosition = JSON.parse(currPosition);
                    top = currPosition.top;
                    left = currPosition.left;
                }
                top = parseFloat(top) - offset;
                left = parseFloat(left);
            }
            return {
                top: top,
                left: left,
            };
        }

        initializeDialog() {
            let reopenDialog = false;
            const settings = this.processSettings();
            let position = this.popupPosition();

            const dialogPosition = { my: "center", at: "center", of: this.$dialogContainer };

            const options = {
                appendTo: this.$dialogContainer,
                autoOpen: false,
                close: (): void => {
                    const visible = this.model.get("visible");
                    if (
                        !this.isEditing &&
                        visible instanceof Backbone.Model &&
                        visible.get("value") !== false
                    ) {
                        visible.set("value", false);
                    }

                    this.doActions(this.model.get("Actions"));

                    if (this.isEditing) {
                        this.remove();
                    } else {
                        this.removeComponentDropView();
                    }
                },
                dialogClass: "popup-dialog",
                drag: (event, ui): void => {
                    localStorage.setItem("popupDialog-" + this.model.get("id"), JSON.stringify(ui.offset));
                },
                open: (): void => {
                    if (this.componentDropView) {
                        this.componentDropView.onResize();
                    }

                    // re-centre
                    this.$dialog.dialog("option", {
                        position: dialogPosition,
                    });
                    const parent = this.$dialog.parent();
                    parent.attr("data-widgetid", this.widgetId);
                    position = this.popupPosition();

                    if (position.top && position.left) {
                        parent.css({ top: position.top, left: position.left });
                    }

                    if (this.isEditing) {
                        // apply mask to container
                        $("<div />")
                            .addClass("popup-mask ui-widget-overlay")
                            .appendTo(this.$dialogContainer)
                            .click(() => {
                                this.$dialog.dialog("close");
                            });
                    }
                },
                position: dialogPosition,
                resizeStop: (): void => {
                    if (this.componentDropView) {
                        this.componentDropView.onResize();
                    }
                },
                title: settings.dialogTitle,
            };

            _.assign(options, this.getAdjustedSize(settings));

            // set booleans
            if (this.isEditing) {
                options["modal"] = false;
                options["resizable"] = false;
            } else {
                options["modal"] = settings.modal === true ? true : false;
                options["resizable"] = settings.resizable === true ? true : false;
            }

            // if no title is given then hide it (required for correct jQuery UI formatting)
            //options['text'] = (options['title']) ? true : false;

            // apply options if dialog has already been initialized, or initialize new dialog
            if (this.$dialog.dialog("instance")) {
                if (this.$dialog.dialog("option", "modal") !== options["modal"]) {
                    // changing modal option requires closing/reopening the dialog
                    reopenDialog = this.$dialog.dialog("isOpen");
                }

                this.$dialog.dialog("option", options);

                if (reopenDialog) {
                    this.$dialog.dialog("close");
                    this.$dialog.dialog("open");
                }
            } else {
                this.$dialog.dialog(options);
            }
        }

        initializeVisible() {
            const visible = this.model.get("visible");

            if (this.visibleListener) {
                this.stopListening(this.visibleListener);
            }

            if (visible instanceof DocumentViewModel) {
                this.listenTo(visible, "change:value", this.onVisibleChange);
                this.visibleListener = visible;
            }

            this.onVisibleChange();
        }

        onBuildModeChange() {
            if (this.isEditing) {
                this.remove();
            } else {
                if (this.viewModel.get("buildMode")) {
                    this.$dialog.dialog("close");
                } else {
                    this.initializeDialog();
                    this.onVisibleChange();
                }
            }
        }

        getAdjustedSize(settings) {
            // check width/height are within window range, or set to 80% of container if no value given
            return {
                height:
                    Math.min(settings.dialogHeight, this.$dialogContainer.height()) ||
                    this.$dialogContainer.height() * 0.8,
                width:
                    Math.min(settings.dialogWidth, this.$dialogContainer.width()) ||
                    this.$dialogContainer.width() * 0.8,
            };
        }

        onVisibleChange() {
            let showDialog = this.model.get("visible");

            if (this.viewModel.get("buildMode")) {
                return;
            }

            if (showDialog instanceof DocumentViewModel) {
                showDialog = showDialog.get("value");
            }

            if (this.$dialog.dialog("instance")) {
                // viewstates has to complete propagation and finish unsubscribe if closing
                _.defer(() => {
                    this.toggleDialog(showDialog);
                });
            }
        }

        pasteWidget(widgetModel) {
            const componentId = widgetModel.get("component").get("definitionId");
            const existingWidget = this.componentDropView.widgetsCollection.first();

            if (existingWidget) {
                //specific id required for advanced css to work properly
                widgetModel = widgetModel.clone(existingWidget.get("id"));
                widgetModel.setParent(this.componentDropView.widgetsCollection);

                existingWidget.commandSet({
                    component: widgetModel.get("component"),
                });
            } else {
                widgetModel.setParent(this.componentDropView.widgetsCollection);
                this.componentDropView.addNewWidget(widgetModel, componentId, true);
            }
        }

        // returns an object of settings mapped to value or viewstate value
        // also handles listeners for viewstates
        processSettings(): any {
            const self = this,
                attributes = _.omit(this.model.attributes, "visible"),
                settings = {};

            _.each(this.viewStateListeners, function(viewstate) {
                self.stopListening(viewstate, "change:value");
            });

            this.viewStateListeners = {};

            _.each(attributes, function(value, key) {
                if (value instanceof DocumentViewModel) {
                    settings[key] = value.get("value");

                    if (!self.viewStateListeners[value.path]) {
                        self.listenTo(value, "change:value", () => {
                            //if the dialog is opened and changing property just set the prop
                            if (
                                self.$dialog.dialog("instance") &&
                                self.$dialog.dialog("isOpen") &&
                                (value.path === "height" || value.path === "width")
                            ) {
                                self.$dialog.dialog(self.getAdjustedSize(self.processSettings()));
                            } else {
                                self.initializeDialog();
                            }
                        });
                        self.viewStateListeners[value.path] = value;
                    }
                } else {
                    settings[key] = value;
                }
            });

            return settings;
        }

        remove() {
            if (this.isEditing) {
                this.$dialogContainer.find(".popup-mask").remove();
            }

            this.onDialogAttributeChange.cancel();
            this.destroyDialog();
            this.removeComponentDropView();
            this.viewStateListeners = {};

            return Backbone.View.prototype.remove.apply(this);
        }

        removeComponentDropView() {
            if (this.componentDropView) {
                this.componentDropView.remove();
                this.componentDropView = null;
            }
        }

        stringToBoolean(val) {
            if (typeof val === "boolean") {
                return val;
            } else if (val) {
                const string = val.toString();
                switch (string.toLowerCase().trim()) {
                    case "true":
                    case "yes":
                    case "1":
                        return true;
                    case "false":
                    case "no":
                    case "0":
                    case null:
                        return false;
                    default:
                        return Boolean(string);
                }
            } else {
                return false;
            }
        }

        toggleDialog(forceState) {
            let setDialogOpen;

            // forceState is true to open, false to close
            if (!_.isUndefined(forceState)) {
                setDialogOpen = forceState;
            } else {
                setDialogOpen = !this.$dialog.dialog("isOpen");
            }

            // check if dialog has been initialized
            if (this.$dialog.dialog("instance")) {
                if (this.stringToBoolean(setDialogOpen)) {
                    // initialize child component when dialog is first opened
                    this.initializeComponentDrop();
                    const size = _.pick(this.$dialog.dialog("option"), ["width", "height"]);
                    if (Number(size.width) === 0 || Number(size.height) === 0) {
                        this.$dialog.dialog("option", this.getAdjustedSize(this.processSettings()));
                    }
                    this.$dialog.dialog("open");
                    //popup is not a widget -> dash properties shown in prop inspector
                    if (this.viewModel.get("buildMode")) {
                        this.viewModel.set("selectedWidget", this.model);
                        this.viewModel.set("selectedWidgetView", this, { silent: true });
                    }
                } else {
                    this.$dialog.dialog("close");
                }
            }
        }

        private doActions(actions: any[]): void {
            _.each(actions, (action, i) => {
                const current = this.getPropertyMeta(i, "Current"),
                    target = this.getPropertyMeta(i, "Target");

                if (current) {
                    let valueToSet =
                        current.type === "viewstate" ? current.value : action["Current"];

                    if (Tools.isKDBTemporal(valueToSet)) {
                        valueToSet = Tools.convertKDBTemporalToMoment(valueToSet).toDashString();
                    }

                    if (target && target.type === "viewstate" && target.path) {
                        // setting a viewstate in the current dashboard
                        const property = this.model.get("Actions")[i].Target;
                        property.set("value", valueToSet);
                    }
                }
            });
        }

        private getPropertyMeta(index, propName): any {
            let defaultValue, viewstateType, value;
            const property = this.model.get("Actions")[index][propName];

            // if undefined or null
            if (_.isNil(property)) return null;

            const type = this.getSourceType(property);

            switch (type) {
                case "viewstate":
                    value = property.get("value");
                    defaultValue = property.get("_default");
                    viewstateType = property.get("_type");
                    break;
                default:
                    value = property;
            }

            return {
                path: property.path || null,
                type: type,
                value: value,
                viewstateType: viewstateType || null,
                defaultValue: defaultValue || null,
            };
        }

        private getSourceType(source): string | null {
            if (
                source &&
                (source instanceof DocumentViewModel ||
                    (source.get && source.get("_viewType")) ||
                    (source.has && source.has("_default") && source.has("value")))
            ) {
                return "viewstate";
            } else {
                return null;
            }
        }
    }
}
