/// <reference types="backbone" />
/// <reference path="ICommandable.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class Screen extends CommandoModel {
        readonly IMMATERIAL_ATTRIBUTES = ["isDefault", "thumb", "widgets"];
        idAttribute: "id";

        constructor(attributes, options, parent: Commandable) {
            super(attributes, options, parent);

            if (!this.get("id")) {
                // generate id
                //console.log("generated id! for screen");
                this.set(
                    {
                        id: Helpers.getGUID(),
                    },
                    {
                        silent: true,
                    },
                );
            }

            if (this.get("widgets")) {
                this.silentCast("widgets", WidgetCollection);

                this.listenTo(this.get("widgets"), "change add remove", function(e) {
                    this.trigger("change");
                });
            }

            this.listenTo(this, "change", this.determineMaterialChange.bind(this));
            this.listenTo(this.get("widgets"), "materialChange", this.onMaterialChange.bind(this));
        }

        clone(): Screen {
            const attributes = this.toJSON();

            // new id will be generated
            delete attributes.id;
            attributes.widgets = attributes.widgets.clone();

            const clone = new Screen(attributes, {}, this.parent);

            // change collection's parent to new screen
            attributes.widgets.setParent(clone);

            return clone;
        }

        defaults(): object {
            return {
                id: null,
                name: "Screen 1",
                thumb: null,
                widgets: new WidgetCollection([], {}, this),

                // layout params, equal to document model
                // undefined properties will be populated when screen is loaded (check DocumentView)
                relativeHeight: undefined,
                rowCount: undefined,
                rowHeight: undefined,
                colCount: 36,
                floatable: true,
            };
        }

        commandSet(json): void {
            const oldJSON = _.mapValues(json, (value, key) => this.previous(key));

            this.postCommand(
                this,
                new Command(
                    "Set Screen",
                    [],
                    [new Todo(CommandOp.Set, "", json)],
                    [new Todo(CommandOp.Set, "", oldJSON)],
                ),
            );
        }

        determineMaterialChange(): void {
            if (this.changed && !_.isEmpty(_.omit(this.changed, this.IMMATERIAL_ATTRIBUTES))) {
                this.trigger("materialChange");
            }
        }

        doCommand(path: Array<string>, todos: Array<Todo>): void {
            if (path.length > 0) {
                const key = path.pop();
                if (key === "widgets") {
                    return this.get(key).doCommand(path, todos);
                } else {
                    throw "Unsupported command key";
                }
            } else {
                todos.forEach(t => {
                    this.set(t.payload);
                });
            }
        }

        getComponents(): Array<Component> {
            return this.get("widgets").getComponents();
        }

        onMaterialChange(): void {
            this.trigger("materialChange");
        }

        parseCastAndListen(data, key, ObjType, events: string): void {
            // if data and data[key] present but not of ObjType
            if (data && data[key] && !(data[key] instanceof ObjType)) {
                // cast
                data[key] = new ObjType(data[key], {}, this);

                // listen to events

                this.listenTo(data[key], events, function() {
                    this.trigger("change", this);
                });
            }
        }

        parse(data) {
            // cast and listen to models
            this.parseCastAndListen(data, "widgets", WidgetCollection, "change add remove");

            return data;
        }

        postCommand(sender: object, command: Command): void {
            if (sender instanceof WidgetCollection) {
                command.path.push("widgets");
            }

            command.path.push(this.id);
            return this.parent && this.parent.postCommand(this, command);
        }

        /*preinitialize(models: any, options: any) {
            this.parent = options.parent;
        }*/

        silentCast(key, ObjType): void {
            if (!(this.get(key) instanceof ObjType)) {
                this.set(key, new ObjType(this.get(key), {}, this), { silent: true });
            }
        }
    }

    export class ScreenCollection extends ComandoCollection<Screen> {
        descriptor = "Screen";
    }

    ScreenCollection.prototype.model = Screen;
}
