/// <reference path="./RuleModel.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.DataFilter {
    export class GroupModel extends Backbone.Model {
        constructor(options?: any) {
            super(options);

            const children = this.get("children");

            if (!children || _.isArray(children)) {
                // new children collection
                // children should be an array here

                // convert array to collection
                const collection = new Backbone.Collection<RuleModel | GroupModel>();

                // one by one make RuleModel or GroupModel
                _.each(children || [], i => {
                    let itemModel;

                    // add app model
                    _.extend(i, {
                        appModel: options.appModel,
                    });

                    if (i.hasOwnProperty("children")) {
                        itemModel = new GroupModel(i);
                    } else {
                        itemModel = new RuleModel(i);
                    }
                    collection.add(itemModel);
                });

                this.set("children", collection);
            }

            // Set Group as Active if Children
            if (this.get("children").length > 0) {
                this.set("active", true, { silent: true });
            }

            this.listenTo(this.get("children"), "add remove reset change", () => {
                const items = this.get("children").filter(c => c.get("active"));

                if (items.length > 0) {
                    this.set("active", true);
                } else {
                    this.set("active", false);
                }
                this.trigger("change");
            });
        }

        defaults(): any {
            return {
                children: new Backbone.Collection<RuleModel | GroupModel>(),
                operator: "AND", // AND / OR
                active: false,
            };
        }

        // returns true if all the group's active rules are null
        isNull(): boolean {
            return _.every(
                this.get("children").filter(c => c.get("active")),
                c => {
                    return c instanceof GroupModel ? c.isNull() : _.isNull(c.toIPC());
                },
            );
        }

        toJSON(): any {
            return {
                operator: this.get("operator"),
                children: this.get("children").toJSON(),
            };
        }

        toIPC(): Uint8Array {
            let temp;
            const children = new Array<Uint8Array>(),
                // filter out active items
                items = this.get("children").filter(c => c.get("active"));

            if (items.length > 0) {
                _.each(items, child => {
                    if (child instanceof GroupModel && child.isNull()) {
                        // group models with all-null rules are excluded from ipc
                        temp = null;
                    } else {
                        temp = child.toIPC();
                    }
                    if (temp) {
                        children.push(temp);
                    }
                });
            }

            // set operator to AND or OR IPC
            const operator = new Uint8Array([102, this.get("operator") === "AND" ? 5 : 6]);

            return QuickBase.ipc.List.toIPC([operator].concat(children));
        }

        toKDB(): string {
            let kdb = "(" + this.get("operator") + ";",
                temp;
            const children = [];

            if (this.get("children").length > 0) {
                this.get("children").each(function(child) {
                    temp = child.toKDB();
                    if (temp) {
                        children.push(temp);
                    }
                });

                if (children.length <= 0) {
                    return null;
                }

                kdb += children.join(";");
                return kdb + ")";
            }

            return null;
        }
    }
}
