/// <reference path="Vector.ts" />
/// <reference path="../long.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export abstract class I64 extends Vector {
        static LONG_NULL = new QuickBase.Long(0x0, 0x80000000);
        static LONG_INFINITY_NEGATIVE = new QuickBase.Long(0x00000001, 0x80000000);
        static LONG_INFINITY_POSITIVE = QuickBase.Long.MAX_VALUE;

        constructor(length: number, offset: number, qtype: number, dataView: DataView) {
            super(length, offset, qtype, dataView, 8);
        }

        calcRange(): bigint[] | Long[] {
            // @ts-ignore TS2304
            if (typeof BigInt !== "undefined") {
                let xMax = this.getScalar(0);
                let xMin = xMax;
                // @ts-ignore TS2304
                const LONG_NULL = BigInt("-9223372036854775808");
                for (let i = 1; i < this.length; i++) {
                    const x = this.getScalar(i);
                    // @ts-ignore TS2304
                    if (xMax === LONG_NULL || (x > xMax && x !== LONG_NULL)) {
                        xMax = x;
                    }
                    // @ts-ignore TS2304
                    if (xMin === LONG_NULL || (x < xMin && x !== LONG_NULL)) {
                        xMin = x;
                    }
                }
                return [xMin, xMax];
            }
            let xMax = this.getLegacyScalar(0);
            let xMin = xMax;
            const nullVal = I64.LONG_NULL;
            for (let i = 1; i < this.length; i++) {
                const x = this.getLegacyScalar(i);
                if (xMax.eq(nullVal) || (x.gt(xMax) && !x.eq(nullVal))) {
                    xMax = x;
                }
                if (xMin.eq(nullVal) || (x.lt(xMin) && !x.eq(nullVal))) {
                    xMin = x;
                }
            }
            return [xMin, xMax];
        }

        getLegacyScalar(i: number): Long {
            const bb = new Uint8Array(this.dataView.buffer, this.getByteLocation(i), this.SIZE);
            return Long.fromBytesLE(bb);
        }

        getScalar(i: number): bigint {
            return this.dataView.getBigInt64(this.getByteLocation(i), true);
        }

        public hash(i: number): bigint | Long {
            return this.dataView.getBigInt64(this.getHashLocation(i), true);
        }
    }

    // IE11 specific function
    if (Tools.isIE11) {
        // eslint-disable-next-line @typescript-eslint/unbound-method
        I64.prototype.hash = function(i: number) {
            const bb = new Uint8Array(this.dataView.buffer, this.getHashLocation(i), this.SIZE);
            return Long.fromBytesLE(bb);
        };

        // eslint-disable-next-line @typescript-eslint/unbound-method
        I64.prototype.toTypedArray = function() {
            this.align();
            if (this.indexOffset === 0)
                return new Uint8Array(this.dataView.buffer, this.offset, this.length * this.SIZE);

            const typedArray = new Uint8Array(this.length * this.SIZE);

            // copy indexOffset+ to start
            typedArray.set(
                new Uint8Array(
                    this.dataView.buffer,
                    this.indexOffset * this.SIZE,
                    (this.length - this.indexOffset) * this.SIZE,
                ),
            );

            // copy indexOffset- to start
            typedArray.set(
                new Uint8Array(this.dataView.buffer, 0, this.indexOffset * this.SIZE),
                (this.length - this.indexOffset) * this.SIZE,
            );

            return typedArray;
        };
    }
}
