/*global _,$,console,Backbone */
/// <reference path="../classes/log.ts"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    var DataSet_ROW_INDEX = "_rowIndex";

    export class DataMapper {
        columns: Array<string>;
        data: object;
        mappings: Backbone.Collection<Backbone.Model>;
        model: Backbone.Model;
        setDataModelMappings: Function;
        subscribe: Function;
        subscribed: boolean;
        unsubscribe: Function;
        subscriptionKey: string;
        viewState: DocumentViewModel;

        trigger(eventName: string, ...args: any[]): any {
            return;
        }
        listenTo(object: any, events: string, callback: Function): any {
            return;
        }
        stopListening(object?: any, events?: string, callback?: Function): any {
            return;
        }

        constructor(options) {
            const obj = options.mappings || {};

            this.setDataModelMappings = options.setMappings;
            this.subscriptionKey = _.uniqueId("mapper_");
            this.subscribe = options.subscribe;
            this.subscribed = false;
            this.unsubscribe = options.unsubscribe;
            this.viewState = options.viewState;

            this.model = new Backbone.Model({
                key: obj.key || "",
                value: obj.value || "",
            });

            this.mappings = new Backbone.Collection(
                _.map(obj.mappings, (viewstate, key) => {
                    return new Backbone.Model({
                        key: key,
                        value: "",
                        viewstate:
                            viewstate && viewstate._dashboardsType
                                ? this.viewState.getByPath(obj.mappings[key].value)
                                : viewstate,
                    });
                }) || [],
            );

            _.extend(this, Backbone.Events);
        }

        public initialize() {
            this.setSubscription();
            this.initializeEvents();
        }

        private initializeEvents() {
            this.listenTo(this.mappings, "add reset remove", this.onMappingsCollectionChange);
            this.listenTo(this.mappings, "change:key", this.onMappingItemKeyChange);
            this.listenTo(this.mappings, "change:viewstate", this.onMappingItemViewStateChange);
            this.listenTo(this.model, "change:key", this.onMappingKeyChange);
            this.listenTo(this.model, "change:value", this.onMappingValueChange);
        }

        public addMapping() {
            const possibleKeys = this.getPossibleKeys();

            const unusedKeys = _.difference(
                possibleKeys,
                this.mappings.map(mapping => {
                    return mapping.get("key");
                }),
            );

            const key = unusedKeys ? unusedKeys[0] : possibleKeys[0] || "";

            // try to find a viewstate with name matching the key
            const viewState = _.isString(key) ? this.viewState.getByPath(key) : "";

            this.mappings.add({
                key: key,
                value: this.getValue(key),
                viewstate: viewState || "",
            });
        }

        public cleanUp() {
            this.stopListening();
            this.mappings.reset();
            this.setSubscription();
        }

        private generateValues() {
            this.mappings.each(
                function(model) {
                    model.set("value", this.getValue(model.get("key")));
                }.bind(this),
            );
        }

        public getColumns() {
            return this.columns || [];
        }

        public getPossibleKeys() {
            return this.data ? _.keys(this.data) : [];
        }

        public getValue(key) {
            return this.data ? this.data[key] : undefined;
        }

        onData(meta, data, buffer?) {
            let key = this.model.get("key"),
                valKey = this.model.get("value"),
                n,
                rows,
                ds = {},
                mapping = [],
                currentMapping = this.mappings.reduce((map, model) => {
                    map[model.get("key")] = model.get("viewstate");
                    return map;
                }, {});

            // get column orientated or row dataset
            if (data.cols) {
                n = data.cols[key] ? data.cols[key].length : 0;
            } else {
                rows = data.collection.models.map(m => m.attributes);
                n = rows.length;
            }

            this.columns = meta.columns.collection.map("id");

            if (this.columns.length && !_.isEqual(this.columns, ["unknown0"])) {
                // set default mapping key/value
                if (!(this.model.get("key") && _.includes(this.columns, this.model.get("key")))) {
                    this.model.set(
                        "key",
                        (_.intersection(this.columns, ["k", "Key", "key"]) || [])[0] ||
                            this.columns[0] ||
                            "",
                    );
                }

                if (
                    !(this.model.get("value") && _.includes(this.columns, this.model.get("value")))
                ) {
                    this.model.set(
                        "value",
                        (_.intersection(this.columns, ["v", "Val", "val"]) || [])[0] ||
                            this.columns[1] ||
                            "",
                    );
                }
            }

            for (let i = 0; i < n; i++) {
                const keyVal = rows ? rows[i][key] : data.cols[key].getValue(i);

                ds[keyVal] = rows
                    ? Tools.convertKDBToViewstateValue(rows[i][valKey])
                    : (data.cols[valKey] ? data.cols[valKey].toViewState(i) || {} : {}).value;

                mapping.push({
                    Key: keyVal,
                    Viewstate: currentMapping[keyVal],
                });
            }

            this.data = ds;
            this.trigger("on-data");

            _.each(
                mapping,
                function(item) {
                    const key = item["Key"],
                        value = ds[key],
                        viewStateModel = _.isString(item["Viewstate"])
                            ? this.viewState.getByPath(item["Viewstate"])
                            : item["Viewstate"] && item["Viewstate"].path
                            ? this.viewState.getByPath(item["Viewstate"].path)
                            : null;

                    if (_.isObject(value)) {
                        _.each(value, function(v: any) {
                            if (v && v._type) {
                                v.type = v._type;
                                delete v._type;
                            }
                        });
                    }

                    if (
                        viewStateModel &&
                        _.isFunction(viewStateModel.set) &&
                        !_.isUndefined(value)
                    ) {
                        viewStateModel.setModelValue(value);
                    }
                }.bind(this),
            );
        }

        private onMappingItemKeyChange(model) {
            model.set("value", this.getValue(model.get("key")));
            this.setDataModel();
        }

        private onMappingItemViewStateChange() {
            this.setDataModel();
        }

        private onMappingKeyChange() {
            this.generateValues();
            this.setDataModel();
            this.trigger("key-change");
        }

        private onMappingsCollectionChange() {
            this.setSubscription();
            this.setDataModel();
            this.trigger("mappings-change");
        }

        private onMappingValueChange() {
            this.generateValues();
            this.setDataModel();
            this.trigger("value-change");
        }

        public removeMapping(model) {
            this.mappings.remove(model);
        }

        public resetMappings() {
            this.mappings.reset(
                _.map(this.getPossibleKeys(), key => {
                    return new Backbone.Model({
                        key: key,
                        value: this.getValue(key),
                        viewstate: "",
                    });
                }),
            );

            this.onMappingsCollectionChange();
        }

        private setDataModel() {
            this.setDataModelMappings(this.toJSON());
        }

        public setSubscription(editMode?: boolean) {
            if (this.mappings.isEmpty() && !editMode) {
                if (this.subscribed) {
                    this.subscribed = false;
                    this.unsubscribe(this.subscriptionKey);
                }
            } else {
                if (!this.subscribed) {
                    this.subscribed = true;
                    this.subscribe(this.subscriptionKey, {
                        onData: this.onData.bind(this),
                    });
                }
            }
        }

        toJSON() {
            return {
                key: this.model.get("key"),
                mappings: this.mappings.reduce((map, model) => {
                    const key = model.get("key");
                    const viewstate = model.get("viewstate");

                    if (viewstate instanceof DocumentViewModel) {
                        map[key] = {
                            _dashboardsType: "viewstate",
                            value: viewstate.path,
                        };
                    } else {
                        map[key] = viewstate;
                    }

                    return map;
                }, {}),
                value: this.model.get("value"),
            };
        }
    }
}
