/*global $, T, Backbone, FormSchemas, BBForm, DialogBase*/
/// <reference path="../templates/combined.ts" />
/// <reference path="DialogBase.ts" />
/// <reference path="ErrorDialog.ts" />
namespace QuickBase {
    // eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
    export class WarningDialog extends ErrorDialog {
        constructor(options) {
            super(
                _.extend(
                    {
                        dialogTitle: "Warning",
                    },
                    options,
                    {
                        userClass:
                            "warning-dialog" + (options.userClass ? " " + options.userClass : ""),
                    },
                ),
            );
        }

        static ShowWarning(text, callbackFn?: () => void) {
            new ErrorDialog(
                _.extend(
                    {
                        dialogTitle: "Warning",
                        callback: _.isFunction(callbackFn) ? callbackFn : _.noop,
                    },
                    text
                        ? {
                              renderTemplate: () => {
                                  return t(text);
                              },
                          }
                        : {},
                ),
            );
        }
    }
}
