/// <reference path="./GraphNode.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    export class JoinNode extends GraphNode {
        constructor(data: GraphNode) {
            super(data);
            if (_.isEmpty(this.data)) {
                // set default
                this.data = { fn: "lj", swap: false, left: [], right: [] };
            }
        }

        static dataToIPC(data: any): Uint8Array {
            data = data as Data;
            if (data) {
                return QuickBase.ipc.Dict.toIPC(
                    ["fn", "swap", "left", "right"],
                    QuickBase.ipc.List.toIPC([
                        QuickBase.ipc.Symbol.toIPC(data.fn),
                        QuickBase.ipc.Boolean.toIPC(data.swap),
                        QuickBase.ipc.List.toIPC(
                            _.map(data.left, value => QuickBase.ipc.Symbol.toIPC(String(value))),
                        ),
                        QuickBase.ipc.List.toIPC(
                            _.map(data.right, value => QuickBase.ipc.Symbol.toIPC(String(value))),
                        ),
                    ]),
                );
            } else return QuickBase.ipc.Int.toIPC(null);
        }
    }
}
