// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export enum CommandOp {
        Add,
        Remove,
        Set,
    }

    export class Todo {
        op: CommandOp;
        key: string | number;
        payload;

        constructor(op, key, payload) {
            this.op = op;
            this.key = key;
            this.payload = payload;
        }
    }

    export class Command {
        readonly title: string;
        path: string[];
        readonly todo: Array<Todo>;
        readonly undo: Array<Todo>;

        constructor(title, path, todo, undo) {
            this.title = title;
            this.path = path;
            this.todo = todo;
            this.undo = undo;
        }

        static pathArrayToText(path: string[]): string {
            return path.reverse().join(".");
        }

        pathText(): string {
            return Command.pathArrayToText(this.path);
        }
    }

    export interface Commandable {
        postCommand(sender: object, command: Command): void;
        doCommand(path: Array<string>, todos: Array<Todo>): void;
    }

    export abstract class CommandoModel extends Backbone.Model implements Commandable {
        protected parent: Commandable;

        constructor(attributes, options, parent: Commandable) {
            super(attributes, options);
            this.parent = parent;
        }

        setParent(parent: Commandable): void {
            this.parent = parent;
        }

        getComponents(): Array<Component> {
            return [];
        }

        abstract postCommand(sender: object, command: Command): void;
        abstract doCommand(path: Array<string>, todos: Array<Todo>): void;
    }

    export abstract class ComandoCollection<T extends CommandoModel> extends Backbone.Collection<T>
        implements Commandable {
        descriptor: string;
        doCommandByOp;
        parent: Commandable;

        constructor(models, options, parent: Commandable) {
            super(
                models,
                _.extend(options, {
                    parent: parent,
                }),
            );

            this.doCommandByOp = {
                [CommandOp.Add]: this.doCommandAdd.bind(this),
                [CommandOp.Remove]: this.doCommandRemove.bind(this),
                [CommandOp.Set]: this.doCommandSet.bind(this),
            };
        }

        commandAdd(models, verb?: string): void {
            const modelsToAdd = _.isArray(models) ? models : [models];

            this.postCommand(
                this,
                new Command(
                    this.getCommandDescription(CommandOp.Add, models, verb || "Add"),
                    [],
                    modelsToAdd.map(
                        m =>
                            new Todo(CommandOp.Add, "", {
                                attributes: m.toJSON(),
                            }),
                    ),
                    modelsToAdd.map(m => new Todo(CommandOp.Remove, "", m.id)),
                ),
            );
        }

        commandRemove(models): void {
            const modelsToRemove = _.isArray(models) ? models : [models];

            this.postCommand(
                this,
                new Command(
                    this.getCommandDescription(CommandOp.Remove, models),
                    [],
                    modelsToRemove.map(m => new Todo(CommandOp.Remove, "", m.id)),
                    modelsToRemove.map(
                        m =>
                            new Todo(CommandOp.Add, "", {
                                attributes: m.toJSON(),
                                index: this.findIndex(m),
                            }),
                    ),
                ),
            );
        }

        commandSet(models): void {
            const oldJSON = models.map((m, i) => {
                const model = this.get(m.id) || this.at(i);

                if (model) {
                    const changedAttributes = model.changedAttributes(m);
                    const previousAttributes = _.mapValues(changedAttributes, (value, key) =>
                        model.previous(key),
                    );

                    if (model.id) {
                        previousAttributes.id = model.id;
                    }

                    return previousAttributes;
                }
            });

            this.postCommand(
                this,
                new Command(
                    this.getCommandDescription(CommandOp.Set, models),
                    [],
                    [new Todo(CommandOp.Set, "", models)],
                    [new Todo(CommandOp.Set, "", oldJSON)],
                ),
            );
        }

        doCommand(path: Array<string>, todos: Array<Todo>): void {
            const key = path.pop();

            if (key) {
                const child = this.get(key);
                child && child.doCommand(path, todos);
            } else {
                todos.forEach(t => {
                    this.doCommandByOp[t.op](t.payload);
                });
            }
        }

        doCommandAdd(payload): void {
            this.add(payload.attributes, { at: payload.index });
        }

        doCommandSet(payload): void {
            this.set(payload, { remove: false });
        }

        doCommandRemove(payload): void {
            this.remove(payload);
        }

        getCommandDescription(op: CommandOp, payload?, verb?: string): string {
            let descriptor = this.getCommandDescriptor(payload);
            verb = verb || CommandOp[op];

            if (descriptor && _.isArray(payload) && payload.length > 1) {
                descriptor = this.getPluralDescriptor(descriptor);
            }

            return verb + " " + descriptor;
        }

        getCommandDescriptor(payload): string {
            return this.descriptor;
        }

        getComponents(): Array<Component> {
            return _.flatMap(this.models, m => m.getComponents());
        }

        getPluralDescriptor(descriptor: string): string {
            return descriptor + "s";
        }

        postCommand(sender: object, command: Command): void {
            return this.parent.postCommand(this, command);
        }

        preinitialize(models, options): void {
            this.parent = options.parent;
        }

        setParent(parent: Commandable): void {
            this.parent = parent;
        }

        _prepareModel(attrs, options?): object | boolean {
            if (this._isModel(attrs)) {
                if (!attrs.collection) attrs.collection = this;
                return attrs;
            }
            options = options ? _.clone(options) : {};
            options.collection = this;

            // gk: this line is overriden to pass-through parent
            const model = new this.model(attrs, options, this);
            if (!model.validationError) return model;
            this.trigger("invalid", this, model.validationError, options);
            return false;
        }
    }
}
