/*globals Backbone,_,$,console */
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    interface TooltipPosition {
        bottom?: number;
        left?: number;
        right?: number;
        top?: number;
    }

    export interface TooltipOptions {
        anchorEl?: HTMLElement;
        cssClass?: string;
        data?: any;
        text?: string;
        position?: TooltipPosition;
    }

    export class GenericTooltip extends Backbone.View<Backbone.Model> {
        private static directions = ["left", "right", "top", "bottom"];

        public text: string;

        private cssClass: string;
        private anchorEl: HTMLElement;
        private data;
        private position: TooltipPosition;
        private templateFn?; // handlebars compiled function

        constructor(options: TooltipOptions) {
            super(options as any);
            this.cssClass = options.cssClass;
            this.anchorEl = options.anchorEl;
            this.data = options.data || {};
            this.text = options.text;
            this.position = options.position;

            this.$el.addClass("generic-tooltip " + this.cssClass);
            this.$el.appendTo("#appdiv");

            this.compileTemplate();
        }

        public remove(): Backbone.View {
            return Backbone.View.prototype.remove.call(this);
        }

        public renderTemplate(data?): void {
            try {
                const visible =
                    !this.anchorEl || (this.anchorEl.offsetWidth && this.anchorEl.offsetHeight);
                // if anchor element is not visible, don't show tooltip
                this.el.style.display = visible ? "" : "none";
                if (visible) {
                    this.el.innerHTML =
                        "<div>" + this.templateFn(_.extend({}, this.data, data)) + "</div>";
                    this.updatePosition(this.position);
                }
            } catch (e) {
                console.log(this.cssClass + " handlebars render error.", e);
            }
        }

        // adds props to the tooltip's inline CSS
        public updateCSS(props): GenericTooltip {
            this.$el.css(
                _.reduce(
                    props,
                    (res, val: string, key: string) => {
                        res[key] = val;
                        return res;
                    },
                    {},
                ),
            );
            return this;
        }

        // adds left, right, top, and/or bottom to the tooltip's inline CSS
        // the anchorEl element is used as a reference to adjust the tooltip position as necessary
        public updatePosition(props: TooltipPosition): GenericTooltip {
            let absPos;
            if (this.anchorEl) {
                absPos = this.anchorEl.getBoundingClientRect();
            }
            return this.updateCSS(
                _.mapValues(
                    _.pickBy(props, v => !_.isNil(v)),
                    (v, k) => {
                        let pos = Math.max(0, v);
                        // add in the component's global position
                        pos += absPos ? absPos[k] : 0;
                        // prevent overflow beyond window size
                        if (
                            (k === "left" || k === "right") &&
                            $(window).width() < pos + this.$el.outerWidth()
                        ) {
                            pos = $(window).width() - this.$el.outerWidth();
                        } else if (
                            (k === "top" || k === "bottom") &&
                            $(window).height() < pos + this.$el.outerHeight()
                        ) {
                            pos = $(window).height() - this.$el.outerHeight();
                        }
                        return pos + "px";
                    },
                ),
            );
        }

        public updateTooltip(props: TooltipOptions): GenericTooltip {
            _.each(props, (v, k) => {
                this[k] = v;
            });
            return this;
        }

        private compileTemplate(): void {
            try {
                this.templateFn = Handlebars.compile(this.text);
            } catch (e) {
                console.log(this.cssClass + " handlebars compile error.", e);
            }
        }
    }
}
