/*global DashboardListView*/
/// <reference path="../lib/delta-client-lib.ts" />
/// <reference path="../templates/combined.ts" />
/// <reference path="../views/AboutDialog.ts" />
/// <reference path="../views/HelpDialog.ts" />
/// <reference path="../views/ChangePasswordDialog.ts" />
/// <reference path="../views/AreYouSureDialog.ts" />
/// <reference path="../classes/log.ts"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ProfileMenu extends Backbone.View<Backbone.Model> {
        MAJOR_TIMEZONES = [
            {
                label: "UTC / Unaltered",
                value: "UTC",
            },
            {
                label: "Frankfurt",
                value: "Europe/Berlin",
            },
            {
                label: "London",
                value: "Europe/London",
            },
            {
                label: "New York",
                value: "America/New_York",
            },
            {
                label: "Sydney",
                value: "Australia/Sydney",
            },
            {
                label: "Tokyo",
                value: "Asia/Tokyo",
            },
        ];

        logLevels = [
            {
                label: "None",
                value: 0,
            },
            {
                label: "Errors",
                value: 1,
            },
            {
                label: "Handled/Warnings",
                value: 2,
            },
            {
                label: "Log & Debugs",
                value: 3,
            },
            {
                label: "All",
                value: 4,
            },
        ];

        $breadcrumbs: JQuery;
        $buttonset: JQuery;
        $changePasswordButton: JQuery;
        $selectLanguage: JQuery;
        $selectLogLevel: JQuery;
        $selectTheme: JQuery;
        $selectViewerTheme: JQuery;
        $selectTimezone: JQuery;

        dialogContainer = null;
        themes;
        isQuickView = false;
        timezones: any;
        appModel;
        viewModel;

        constructor(options) {
            super(options);
            this.el.className = "account-menu ui-widget ui-widget-content";
            this.appModel = options.appModel;
            this.dialogContainer = options.dialogContainer;
            this.themes = options.themes;
            this.viewModel = options.viewModel;
            this.isQuickView = options.isQuickView ? true : false;
            // Prevent menu from closing when clicked
            this.$el.click(function(e) {
                const target = $(e.target);
                if (!target.is("li")) {
                    e.stopPropagation();
                }
            });

            this.listenTo(this.viewModel, "change:themeSwitchable", this.onThemeSwitchableChange);
            this.getTimezones();
            this.render();
        }

        getTimezones() {
            this.timezones = _.map(moment.tz.names(), function(timezone) {
                return {
                    group: timezone.substr(0, timezone.indexOf("/")),
                    label: timezone.substr(timezone.indexOf("/") + 1),
                    value: timezone,
                };
            });
            this.timezones = _.groupBy(this.timezones, "group");
        }

        onAbout() {
            const dialog = new AboutDialog({
                appModel: this.appModel,
                container: this.dialogContainer,
                viewModel: this.viewModel,
            });
        }

        onHelp() {
            const dialog = new HelpDialog({
                appModel: this.appModel,
                container: this.dialogContainer,
                viewModel: this.viewModel,
            });
        }

        onChangeLanguage() {
            const value = this.$selectLanguage.val();
            if (value && value !== $.cookie("dashboard-language")) {
                $.cookie("dashboard-language", value, { expires: 90, path: "/" });
                window.location.reload(false);
            }
        }

        onChangeLogLevel() {
            const value = this.$selectLogLevel.val();
            if (value && value !== $.cookie("dashboard-log-level")) {
                $.cookie("dashboard-log-level", value, { expires: 90, path: "/" });
            }
            Log.trace(value.toString());
        }

        onChangePassword() {
            const dialog = new ChangePasswordDialog({
                appModel: this.appModel,
                container: this.dialogContainer,
            });
        }

        onChangeTheme() {
            const value = this.$selectTheme.val();
            if (value && value !== $.cookie("dashboard-theme")) {
                $.cookie("dashboard-theme", value, { expires: 90, path: "/" });
                this.trigger("themeChange", value);
            }
        }

        onChangeViewerTheme() {
            const value = this.$selectViewerTheme.val();
            if (value && value !== $.cookie("dashboard-viewer-theme")) {
                $.cookie("dashboard-viewer-theme", value, { expires: 90, path: "/" });
                this.trigger("themeChange", value);
            }
        }

        onChangeTimezone() {
            const value = this.$selectTimezone.val();
            if (value && value !== $.cookie("dashboard-timezone")) {
                $.cookie("dashboard-timezone", value, { expires: 90, path: "/" });
                window.location.reload(false);
            }
        }

        onLogout() {
            const self = this,
                currentDashId = this.viewModel.get("selectedDocumentId"),
                currentDash = this.appModel.get("documents").find(function(model) {
                    return model.id === currentDashId;
                }),
                isChanged = this.isQuickView
                    ? currentDash && currentDash.get("viewState")["wasChanged"]
                    : currentDash && currentDash.get("wasChanged");

            if (isChanged && (!this.isQuickView || currentDash.get("unsavedViewerPrompt"))) {
                const x = new AreYouSureDialog({
                    userClass: this.isQuickView ? "quick-view" : "quick-dash",
                    container: this.dialogContainer,
                    // don't save & exit
                    noSaveCb: _.bind(DeltaClientLib.logout, DeltaClientLib),
                    saveCb() {
                        if (self.isQuickView) {
                            // save viewstate & exit
                            const viewState = currentDash.get("viewState");
                            // KXAX-22809 required for child viewstates inside a parent group
                            viewState.withAllProperties = true;

                            currentDash.get("viewState").save(
                                { id: currentDashId },
                                {
                                    success: _.bind(DeltaClientLib.logout, DeltaClientLib),
                                },
                            );
                            viewState.withAllProperties = false;
                        } else {
                            // save & exit
                            currentDash.saveNow(
                                $.noop, // error
                                _.bind(DeltaClientLib.logout, DeltaClientLib), // success
                            );
                        }
                    },
                });
            } else {
                DeltaClientLib.logout();
            }
        }

        onThemeSwitchableChange() {
            if (this.viewModel.get("themeSwitchable")) {
                this.$selectViewerTheme.prop("disabled", false);
            } else {
                this.$selectViewerTheme.prop("disabled", true);
            }
        }

        render() {
            let self = this,
                dropdowns;

            this.$el.html(
                T.ProfileMenu({
                    majorTimezones: this.MAJOR_TIMEZONES,
                    saml: this.appModel.get("saml"),
                    isLite: this.appModel.get("isLite"),
                    logLevel: this.logLevels,
                    themes: this.themes,
                    timezones: this.timezones,
                    editor: !this.isQuickView,
                    showBreadcrumbs: this.appModel.get("showBreadcrumbs"),
                }),
            );

            this.$buttonset = this.$el.find(".buttonset");
            this.$selectLanguage = this.$el.find(".select-language");
            this.$selectLogLevel = this.$el.find(".select-log-level");
            this.$selectTheme = this.$el.find(".select-theme");
            this.$selectTimezone = this.$el.find(".select-timezone");
            this.$selectViewerTheme = this.$el.find(".select-viewer-theme");
            this.$breadcrumbs = this.$el.find(".breadcrumbs");

            this.$buttonset.buttonset();
            this.$buttonset.menu({
                select: function(event) {
                    const option = $(event.currentTarget).attr("data-options");

                    switch (option) {
                        case "change-password":
                            self.onChangePassword();
                            break;
                        case "about":
                            self.onAbout();
                            break;
                        case "helpguide":
                            self.onHelp();
                            break;
                        case "logout":
                            self.onLogout();
                            break;
                        case "login-history":
                            self.trigger("login-history");
                            break;
                        case "logout-local":
                            self.ssoLogout("local");
                            break;
                        case "logout-global":
                            self.ssoLogout("global");
                        case "open-editor":
                            window.open("./edit#" + Tools.getHash(), "_blank");
                            break;
                        case "open-viewer":
                            window.open("../#" + Tools.getHash(), "_blank");
                            break;
                        case "document-breadcrumbs":
                            self.toggleBreadcrumbs();
                    }
                },
            });

            dropdowns = [
                {
                    $el: this.$selectLanguage,
                    value: $.cookie("dashboard-language") || "en",
                    options: {},
                    callback: this.onChangeLanguage,
                },
                {
                    $el: this.$selectLogLevel,
                    value: $.cookie("dashboard-log-level") || "4",
                    options: { minimumResultsForSearch: 10 },
                    callback: this.onChangeLogLevel,
                },
                {
                    $el: this.$selectTheme,
                    value: $.cookie("dashboard-theme") || "kx-darkroom",
                    options: { minimumResultsForSearch: 3 },
                    callback: this.onChangeTheme,
                },
                {
                    $el: this.$selectViewerTheme,
                    value: $.cookie("dashboard-viewer-theme") || "kx-darkroom",
                    options: { minimumResultsForSearch: 3 },
                    callback: this.onChangeViewerTheme,
                },
                {
                    $el: this.$selectTimezone,
                    value: Tools.timezone,
                    options: {},
                    callback: this.onChangeTimezone,
                },
            ];
            _.each(dropdowns, function(d) {
                d.$el.val(d.value);
                d.$el.select2(_.extend({ theme: "dashboards" }, d.options));
                d.$el.on("select2:select", _.bind(d.callback, self));
                d.$el.on("select2:open", function() {
                    $("body > .select2-container--dashboards.select2-container--open").addClass(
                        "select2--environment",
                    );
                    $(".select2-search__field").on("click", function(e) {
                        e.stopPropagation();
                    });
                });
                d.$el.on("select2:close", function() {
                    $(".select2-search__field").off();
                });
            });

            if (this.isQuickView) {
                this.$el.find(".row.theme").css("display", "none");
            } else {
                this.$el.find(".row.viewer-theme").css("display", "none");
            }

            return this;
        }

        ssoLogout(type: string) {
            DeltaClientLib.ssoLogout(type);
        }

        toggleBreadcrumbs() {
            // toggle it on the model
            this.appModel.set("showBreadcrumbs", !this.appModel.get("showBreadcrumbs"));

            // update view
            this.appModel.get("showBreadcrumbs")
                ? this.$breadcrumbs.addClass("checked")
                : this.$breadcrumbs.removeClass("checked");
        }
    }
}
