// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Dict {
        public length: number;
        public qtype: number;
        protected keys: TypeBase;
        protected values: TypeBase;

        constructor(length: number, keys: TypeBase, values: TypeBase, qtype?: number) {
            this.length = length;
            this.keys = keys;
            this.values = values;
            // qtype can be table(98) or dict(99)
            this.qtype = qtype || TypeNum.Dict;
        }

        public static toIPC(keys: Array<string>, valueData: Uint8Array): Uint8Array {
            const size = _.sumBy(keys, k => k.length + 1) + 7 + valueData.length;
            const buffer = Util.createBuffer(size);

            buffer.wb(99); // type(99 - dict)
            buffer.wb(11); // type(11 - symbol)
            buffer.wb(0); // attributes
            buffer.wi(keys.length); // vector size
            _.each(keys, k => Symbol.writeValue(k, buffer.wb)); // values
            _.each(valueData, d => buffer.wb(d));
            return buffer.data;
        }

        public getKey(i: number): any {
            return this.keys.getValue(i);
        }

        public getValue(i: number): any {
            return this.values.getValue(i);
        }

        public toObject(): object {
            const obj = {};
            for (let i = 0; i < this.length; i++) {
                obj[this.getKey(i)] = this.getValue(i);
            }
            return obj;
        }

        public toLegacy(): DCDS {
            const t = new Table();
            if (this.qtype === 99) {
                t.addParsedColumn("Property", this.keys);
                t.addParsedColumn("Value", this.values);
            } else {
                _.times(this.keys.length, i => {
                    t.addParsedColumn(this.keys.toLegacy(i), this.values.getValue(i));
                });
            }
            return t.toLegacy();
        }

        public toViewState() {
            const ret = {};
            for (let i = 0; i < this.length; i++) {
                ret[this.getKey(i)] = this.values.toViewState(i);
            }

            return {
                _type: "dict",
                value: ret,
            };
        }
    }

    Dict.prototype.qtype = 99;
}
