/*globals Backbone,_,$,console, win.Client, win.AckMessage, NackMessage, setTimeout, clearTimeout, win, kdb*/
/// <reference path="../classes/log.ts"/>
/// <reference path="delta.d.ts"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    type ManagedQueryFn = (
        connection: string,
        query: string,
        params: any[],
        success: FnSuccess,
        error: Function,
        interval: number,
        maxRows: number,
        paging: any,
        performance: PerformanceMetrics,
    ) => void;

    export class DCBase {
        static host;
        static port;
        static secure;
        static fromUrl;
        static username;
        static successCallback;
        static errorCallback;
        static token;
        static appid;
        static saml;
        static forceLogin;
        static clientLoggedIn = false;
        static eCode: number | null;

        static changingPassword = false;
        static changingPasswordFailed = false;
        static changingPasswordOld: string;
        static changingPasswordNew: string;
        static disconnected = false;

        static properties;
        static DEFAULT_TIMEOUT = 60000;

        static deltaClient: win.Client;

        static showLastLoginAttempts;

        // yuck - these functions need to be set
        static changingPasswordFail: (msg: string) => void;
        static changingPasswordSuccess: () => void;

        // used for legacy switching
        static startManagedQuery: ManagedQueryFn;
        static startManagedQuery2: ManagedQueryFn;
        static startLegacyManagedQuery: ManagedQueryFn;
        static startLegacyManagedQuery2: ManagedQueryFn;

        static initializeDeltaClient(
            host,
            port,
            secure,
            fromUrl,
            username,
            password,
            successCallback,
            errorCallback,
            token,
            appid,
            useBinary,
            saml,
            forceLogin,
        ) {
            this.host = host;
            this.port = port;
            this.secure = secure;
            this.fromUrl = fromUrl;
            this.username = username;
            this.successCallback = successCallback;
            this.errorCallback = errorCallback;
            this.token = token;
            this.appid = appid;
            this.saml = saml;
            this.forceLogin = forceLogin;
            this.properties = {};
            this.clientLoggedIn = false;
            this.errorCallback = errorCallback;

            if (!this.deltaClient) {
                this.deltaClient = new win.Client(
                    {
                        host: this.host,
                        port: this.port,
                        secure: this.secure,
                        fromURL: this.fromUrl,
                    },
                    useBinary,
                    useBinary,
                );

                this.initDeltaClient(password);
            } else {
                this.saml
                    ? this.deltaClient.register("dashboards")
                    : this.deltaClient.login(
                          this.username,
                          password,
                          this.appid,
                          [],
                          this.forceLogin,
                      );
            }
        }

        static initializeDeltaClientCopy(
            client,
            host,
            port,
            secure,
            fromUrl,
            username,
            password,
            successCallback,
            errorCallback,
            appid,
            saml,
        ) {
            this.host = host;
            this.port = port;
            this.secure = secure;
            this.fromUrl = fromUrl;
            this.username = username;
            this.successCallback = successCallback;
            this.errorCallback = errorCallback;
            this.appid = appid;
            this.saml = saml;
            this.properties = {};
            this.clientLoggedIn = true;

            this.deltaClient = client;
            this.deltaClient.DeltaClientLogin = true;

            this.initDeltaClient(password);
        }

        static initDeltaClient(password) {
            let wasConnected = false,
                connectionTimeoutDelay = null;

            this.deltaClient.status._subscribe({
                next: data => {
                    //console.log(data.type);
                    switch (data.type) {
                        case win.Client.status.CONNECTING:
                            if (wasConnected) {
                                //Disconnect not firing so if has been connected before fire disconnect event
                                $(this).trigger("DeltaClientDisconnect");
                                this.disconnected = true;
                            }

                            this.eCode = data.eCode;
                            if (!connectionTimeoutDelay) {
                                connectionTimeoutDelay = _.delay(() => {
                                    this.sendError(
                                        this.eCode === 1006
                                            ? "WebSocket connection error, please check proxy settings"
                                            : "Server connectivity issues, please try again.",
                                    );
                                }, this.DEFAULT_TIMEOUT);
                            }
                            break;
                        case win.Client.status.CONNECTED:
                            try {
                                //this.changeUserPassword.call(this);
                                wasConnected = true;

                                const sysConfig = this.deltaClient.systemConfig();
                                if (sysConfig) {
                                    this.showLastLoginAttempts = sysConfig.showLastLoginAttempts;
                                }

                                if (this.clientLoggedIn) {
                                    $(this).trigger("DeltaClientConnect");
                                } else this.onFirstLogin();

                                clearTimeout(connectionTimeoutDelay);
                                $.cookie("deltaToken", this.deltaClient.tokenKey(), {
                                    expires: 30,
                                    path: "/",
                                });
                                connectionTimeoutDelay = null;
                            } catch (e) {
                                this.sendError(
                                    "There was a problem connecting to the server: " + e,
                                );
                            }
                            break;
                        /*case win.Client.status.DISCONNECTED:
                            $(this).trigger('DeltaClientDisconnect');
                            this.disconnected = true;
                            break;*/
                        case win.Client.status.LOGGED_IN:
                            // called in host / auto scenario
                            clearTimeout(connectionTimeoutDelay);
                            connectionTimeoutDelay = null;

                            this.username = this.deltaClient.user();

                            // set deltaToken to be the current tokenKey()
                            // $.cookie('deltaToken', this.deltaClient.tokenKey(), { expires: 30, path: '/' });

                            break;
                        case win.Client.status.LOGGED_OUT:
                            //console.log("user logged out");
                            if (
                                this.changingPassword &&
                                _.isString(data.data) &&
                                data.data.indexOf("Client action") !== -1
                            ) {
                                // delay password change otherwise it will not work
                                setTimeout(function() {
                                    this.changingPasswordFailed = false;
                                    // this will change password and login the user
                                    this.deltaClient.resetPassword(
                                        this.username,
                                        this.changingPasswordOld,
                                        this.changingPasswordNew,
                                    );
                                }, 500);
                            } else if (
                                _.isString(data.data) &&
                                data.data.indexOf("no connection available") === -1
                            ) {
                                // user initiated logout or session timeout
                                $.removeCookie("deltaToken", { path: "/" });

                                if (this.saml) {
                                    this.ssoLogout("local");
                                } else {
                                    if (data.data.indexOf("timeout") !== -1) {
                                        $.cookie("deltaLoginError", "Your session timed out", {
                                            expires: 30,
                                            path: "/",
                                        });
                                    } else if (data.data.indexOf("force logout") !== -1) {
                                        $.cookie("deltaLoginError", "Forced logout", {
                                            expires: 30,
                                            path: "/",
                                        });
                                    } else if (data.data.indexOf("session limit") !== -1) {
                                        $.cookie("deltaLoginError", data.data.split("|")[2], {
                                            expires: 30,
                                            path: "/",
                                        });
                                    } else {
                                        $.removeCookie("deltaLoginError", { path: "/" });
                                    }

                                    // at this stage there is no point handling before unload as user is logged out
                                    window.onbeforeunload = function(e) {}; // override Dashboards save notification
                                    location.reload();
                                }
                            } else if (!data.data && this.disconnected) {
                                // data is not defined on appServer bounce
                                this.errorCallback("loginRequired");
                            } else if (wasConnected) {
                                // server was brought down and back up
                                $.removeCookie("deltaToken", { path: "/" });
                                location.reload();
                            }
                            break;
                        case win.Client.status.EXPIRED:
                            clearTimeout(connectionTimeoutDelay);
                            connectionTimeoutDelay = null;
                            Log.Log("Expired", data);
                            this.sendError("Your password has expired");
                            break;
                        case win.Client.status.RESET:
                            clearTimeout(connectionTimeoutDelay);
                            connectionTimeoutDelay = null;
                            $(this).trigger("Reset");
                            this.sendError("Please reset password");
                            Log.Log("Reset", data);
                            break;
                        case win.Client.status.RESET_FAIL:
                            clearTimeout(connectionTimeoutDelay);
                            connectionTimeoutDelay = null;
                            this.changingPasswordFail && this.changingPasswordFail(data["data"]);
                            this.changingPasswordFailed = true;
                            Log.Log("Reset Fail", data);
                            break;
                        case win.Client.status.RESET_SUCCESS:
                            if (this.changingPassword) {
                                if (this.changingPasswordFailed) {
                                    this.changingPassword = false;
                                } else {
                                    // changing password
                                    // call password changed callback
                                    this.changingPasswordSuccess && this.changingPasswordSuccess();
                                    // reset
                                    this.changingPassword = false;
                                    this.changingPasswordFail = null;
                                    this.changingPasswordSuccess = null;
                                    this.changingPasswordOld = null;
                                    this.changingPasswordNew = null;
                                    this.successCallback && this.successCallback();
                                    // The deltaToken in use is no longer valid so prompt user to log in again on next refresh
                                    $.cookie(
                                        "deltaLoginError",
                                        "Password changed. Please login again.",
                                        { expires: 30, path: "/" },
                                    );
                                    $.removeCookie("deltaToken", { path: "/" });
                                }
                            }
                            break;
                        case win.Client.status.FAULT:
                            Log.Log("Fault", data);
                            // wrong user / password or token
                            clearTimeout(connectionTimeoutDelay);
                            connectionTimeoutDelay = null;

                            if (data["data"] !== "userSessionLimit") {
                                $.removeCookie("deltaToken", { path: "/" });
                            }

                            this.sendError(data["data"]);
                            break;
                    }
                },
                error: function() {
                    Log.Error("onError", arguments);
                },
                complete: function() {
                    Log.Info("onCompleted", arguments);
                },
            });

            // prefer token if we have one
            this.token = this.token || this.deltaClient.tokenKey();

            if (!this.deltaClient.DeltaClientLogin) {
                if (this.saml) {
                    this.deltaClient.register("dashboards");
                } else if (this.token) {
                    this.deltaClient.loginWithTokenKey(this.token, this.appid);
                } else {
                    this.deltaClient.login(
                        this.username,
                        password,
                        this.appid,
                        [],
                        this.forceLogin,
                    );
                    password = null;
                }
            } else {
                this.onFirstLogin();
            }
        }

        static getDashboardProperty(key) {
            return this.properties[key];
        }

        static changeUserPassword(
            oldPass: string,
            newPass: string,
            success: () => void,
            error: (e: any) => void,
        ) {
            // can't change password when user logged in
            if (!this.changingPassword) {
                this.changingPassword = true;
                this.changingPasswordOld = oldPass;
                this.changingPasswordNew = newPass;
                this.changingPasswordSuccess = success;
                this.changingPasswordFail = error;
                this.changingPasswordFailed = false;

                // this will change password and login the user
                this.deltaClient.resetPassword(
                    this.username,
                    this.changingPasswordOld,
                    this.changingPasswordNew,
                );
            }
        }

        static onFirstLogin() {
            // get settings timezone, etc then trigger login
            this.deltaClient.request(
                "ConfigAPI.getConfigParameter",
                data => {
                    data.rows.forEach(kvp => {
                        this.properties[kvp.Property.elementValue] = kvp.Value.elementValue;
                    });

                    // configure legacy managed
                    if (this.properties["legacy_managed"] == "true") {
                        this.startManagedQuery = this.startLegacyManagedQuery;
                        this.startManagedQuery2 = this.startLegacyManagedQuery2;
                    }

                    Tools.timezone =
                        $.cookie("dashboard-timezone") ||
                        this.properties["timezone"] ||
                        moment.tz.guess();

                    this.clientLoggedIn = true;
                    if (this.deltaClient.clientLoggingEnabled()) {
                        if (this.properties.hasOwnProperty("log-level")) {
                            // set server side log level
                            Log.ServerLogLevel = Number(this.properties["log-level"]);
                        }

                        Log.setClientLogging();
                    }

                    $(this).trigger("DeltaClientLogin");
                    this.deltaClient.DeltaClientLogin = true;
                    this.successCallback && this.successCallback();
                    $(this).trigger("DeltaClientConnect");
                },
                _.noop,
                "DASHproperties",
                "DEFAULT",
                "delta_install_default",
            );
        }

        static sendError(message, detail?) {
            if (this.errorCallback) {
                this.errorCallback(message);
            }
        }

        static ssoLogout(logoutType) {
            if (this.deltaClient) {
                this.deltaClient.ssoLogout(logoutType);
            }
            $.removeCookie("deltaToken", {
                path: "/",
            });
            _.delay(function() {
                location.reload();
            }, 500);
        }
    }
}
