/// <reference types="sharedworker" >
/// <reference path="c.ts" />
/// <reference path="ipc/Parse.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    type FnDashError = (msg?: string) => void;
    type FnDeltaSuccess = (ds: DCDS, buffer?: ArrayBuffer) => void;
    type FnDashSuccess = (data: qTyped, buffer: ArrayBuffer, perf?: Array<number>) => void;
    export class DCRequest {
        op: Op;
        connectionId?: string;
        query?: string | any[];
        maxResults = 2147483647;
        timestamp: number = new Date().getTime();
        success?: FnDashSuccess;
        error?: FnDashError;

        constructor(
            op: Op,
            connectionId?: string,
            query?: string | any[],
            maxResults?: number,
            success?: FnDashSuccess,
            error?: FnDashError,
        ) {
            this.op = op;
            this.connectionId = connectionId;
            this.query = query;
            if (maxResults) this.maxResults = maxResults;
            this.success = success;
            this.error = error;
        }
    }

    export class DashClient {
        public static client: DashClient;
        private static readonly SOCK = "Socket ";
        private static readonly WORK = "Worker ";
        private static readonly deltaNameMap: { [index: string]: any } = {
            boolean: ipc.Boolean,
            guid: ipc.Symbol,
            byte: ipc.Byte,
            short: ipc.Short,
            int: ipc.Int,
            long: ipc.QLong,
            real: ipc.Float,
            float: ipc.Double,
            char: ipc.Char,
            symbol: ipc.Symbol,
            timestamp: ipc.Timestamp,
            month: ipc.Month,
            date: ipc.QDate,
            datetime: ipc.DateTime,
            timespan: ipc.Timespan,
            minute: ipc.Minute,
            second: ipc.Second,
            time: ipc.Time,
            string: ipc.QString,
            /*"list":
            "dateRolling": 
            "datetimeRolling":
            "timestampRolling": 
            "timeRolling":
            "minuteRolling": 
            "secondRolling": 
            "dict": 
            "email": */
        };

        private requests: { [index: string]: DCRequest } = {};
        private ws: WebSocket;
        private requestId = 1;
        // query: string;
        private worker: SharedWorker.SharedWorker | Worker;
        private httpOn = false;
        private insightsAPI = false;
        private debouncedHeartbeat: Function;

        constructor(options) {
            DashClient.client = this;
            this.insightsAPI = options.insightsAPI || false;

            // http only means no streams
            this.debouncedHeartbeat = options.httpOnly
                ? _.noop.bind(this)
                : _.debounce(this.heartbeat.bind(this), 15000);

            // ws only - override http requests
            if (options.httpOn || options.httpOnly || options.insightsAPI) {
                this.httpOn = true;
            }

            // set local httpOnly is insightsAPI
            const httpOnly = options.httpOnly || options.insightsAPI;

            if (!options.noWorker && !httpOnly) {
                const workerUrl = requirejs.toUrl("worker") + ".js";
                Log.Log(DashClient.WORK + workerUrl);
                try {
                    if (window["SharedWorker"] && !("FSBL" in window)) {
                        this.worker = new SharedWorker(workerUrl, options);
                        this.worker.port.onmessage = (e): void => this.onWsMessage(e);
                        this.worker.port.onmessageerror = (e): void => this.onWorkerError(e);
                        this.worker.port.start();
                        this.worker.onerror = (e): void => this.onWorkerError(e);
                    } else {
                        this.worker = new Worker(workerUrl, options);
                        this.worker.onmessage = (e): void => this.onWsMessage(e);
                        this.worker.onerror = (e): void => this.onWorkerError(e);
                    }
                } catch (e) {
                    Log.Log(DashClient.WORK + "create err", e);
                    throw e;
                }
            } else if (httpOnly) {
                // TODO: yuck get rid of DeltaConnect event
                _.defer(() => this.onOpen(undefined));
            } else this.init();
        }

        public static createLog(log: string, success: () => void, fail: () => void): void {
            // do nothing
        }

        public static getQueryData(
            dataSource: string,
            connection: any,
            query: string,
            parameters: any,
            success: FnDashSuccess,
            error: FnDashError,
            maxResults?: number,
            paging?: any,
            performance?: PerformanceMetrics,
        ): void {
            DashClient.getQueryData2(
                dataSource,
                connection,
                query,
                parameters,
                DashClient.legacyRowCallback(success),
                error,
                maxResults,
                paging,
                performance,
            );
        }

        public static getQueryData2(
            dataSource: string,
            connection: any,
            query: string,
            parameters: any,
            success: FnDashSuccess,
            error: FnDashError,
            maxResults?: number,
            paging?: any,
            performance?: PerformanceMetrics,
        ): void {
            if (paging) {
                Log.Error("Paging not supported in lite");
            }

            if (dataSource === "sql") {
                Log.Error("SQL not supported in lite");
            }

            const paramStr = this.parametersToString(parameters);
            if (paramStr) {
                query = query + "[" + paramStr + "]";
            }

            performance && performance.trigger("serverStart");
            DashClient.client.send(
                Op.Query,
                maxResults,
                connection,
                query,
                (data, buffer, perf) => {
                    if (perf && performance) {
                        performance.set("serverStart", perf[0]);
                        performance.set("serverEnd", perf[1]);
                    }

                    success(data, buffer);
                },
                error,
            );
        }

        public static parametersToString(parameters: any): string {
            let paramStr = "";
            if (!_.isEmpty(parameters)) {
                const vals = [];
                _.each(parameters, (v, k) => {
                    vals.push(RTTI.getByName(v.type).toKDBInput(v.value));
                });

                //paramStr = "(`"+_.keys(parameters).join(";`")+")!(" + vals.join(";")+")";
                paramStr = vals.join(";");
            }

            return paramStr;
        }

        public static parametersToIPC(parameters: any): Uint8Array {
            const params: Array<Uint8Array> = [],
                paramToIPC = p => {
                    const converter = DashClient.deltaNameMap[p.type];
                    if (!converter) throw "Unsupported param type: " + p.type;
                    return converter.toIPC(ipc.fromString(converter, p.value));
                };

            if (!_.isEmpty(parameters)) {
                _.each(parameters, (v, k) => {
                    if (v.type === "dict") {
                        params.push(
                            ipc.Dict.toIPC(
                                _.map(v.value, (val, key) => key),
                                ipc.List.toIPC(_.map(v.value, val => paramToIPC(val))),
                            ),
                        );
                    } else if (v.type === "list") {
                        params.push(
                            ipc.Symbol.listToIPC(
                                v.value.map(v => (_.isNil(v) ? "" : v.toString())),
                            ),
                        );
                    } else {
                        params.push(paramToIPC(v));
                    }
                });
            }

            return ipc.List.toIPC(params);
        }

        public static runAnalytic(
            analytic: string,
            connection: string,
            parameters: any,
            callback: FnDeltaSuccess,
            errorCallback: FnDashError,
            maxResults?: number,
            paging?: any,
            perf?: any,
        ): void {
            DashClient.runAnalytic2(
                analytic,
                connection,
                parameters,
                (data, buffer) => {
                    const ds = ipc.Parse.reshape(data, buffer);
                    const legacyDs = ds.toLegacy();
                    delete ds.cols;
                    buffer = null;
                    return callback(legacyDs);
                },
                errorCallback,
                maxResults,
                null,
                perf,
            );
        }

        public static runAnalytic2(
            analytic: string,
            connection: string,
            parameters: any,
            success: FnDashSuccess,
            errorCallback: FnDashError,
            maxResults?: number,
            paging?: any,
            performance?: PerformanceMetrics,
        ): void {
            if (!parameters || !_.values(parameters).length) {
                parameters = [{ type: "symbol", value: null }];
            }

            DashClient.client.send(
                Op.Query,
                maxResults,
                connection,
                [{ type: "symbol", value: analytic }].concat(_.values(parameters)),
                (data, buffer, perf) => {
                    if (perf && performance) {
                        performance.set("serverStart", perf[0]);
                        performance.set("serverEnd", perf[1]);
                    }

                    success(data, buffer);
                },
                errorCallback,
            );
        }

        public static runAPI(
            analytic: string,
            connection: string,
            parameters: any,
            callback: FnDeltaSuccess,
            errorCallback: FnDashError,
            maxResults?: number,
            paging?: any,
            perf?: any,
        ): void {
            if (!parameters || !_.values(parameters).length) {
                parameters = [{ type: "symbol", value: null }];
            }

            DashClient.client.send(
                Op.API,
                maxResults,
                connection,
                [{ type: "symbol", value: analytic }].concat(_.values(parameters)),
                (data: qTyped, buffer: ArrayBuffer) => {
                    const ds = ipc.Parse.reshape(data, buffer);
                    const legacyDs = ds.toLegacy();
                    delete ds.cols;
                    buffer = null;
                    return callback(legacyDs);
                },
                errorCallback,
            );
        }

        public static getDeltaAnalytics(
            callback: (analytics: any) => void,
            connection: string,
        ): void {
            DashClient.runAnalytic(
                ".api.getFunctions",
                connection,
                [],
                (data: any) => {
                    return callback(
                        _.map(data.rows, (a: any) => {
                            a.parameters = a.parameters.rows;
                            a.name = ".api." + a.name;
                            return a;
                        }),
                    );
                },
                () => {
                    /* do nothing */
                },
            );
        }

        public static legacyRowCallback(callback) {
            return function(data, bData): void {
                if (callback) {
                    if (bData && bData.byteLength) {
                        if (data.dataSet) {
                            data.dataSet = ipc.Parse.reshape(data.dataSet, bData).toLegacy();
                        } else {
                            data = ipc.Parse.reshape(data, bData).toLegacy();
                        }

                        callback(data);
                    } else callback(data);
                }
            };
        }

        //call update query passing in column based dictionaries for updates, called directly from datagrid curently
        // options: {
        //  idCol: '',
        //  addedDict: {},  <-- column based dictionary 'columnName: [LISTOFROWUPDATES]'
        //  updateDict: {},  <-- column based dictionary 'columnName: [LISTOFROWUPDATES]'
        //  deletedDict: {},  <-- column based dictionary 'columnName: [LISTOFROWUPDATES]'
        //  query: '',
        //  connection: ''
        //}
        public static updateTable(options, successCallback, errorCallback): void {
            const localUpdateParam = [];

            for (let i = 0; i < 3; i += 1) {
                localUpdateParam[i] = {
                    type: "dict",
                    value: {},
                    index: i,
                };
            }

            //populate localUpdateParam array with added, updated, and deleted dictionaries
            _.each(localUpdateParam, function(updateParam, idx) {
                updateParam.index = idx;
            });

            //added
            _.each(options.addedDict.items, function(dictItem, dictKey) {
                localUpdateParam[0].value[dictKey] = {
                    type: DashClient.updateTableGetListType(options.typeConfig[dictKey], dictItem),
                    value: dictItem,
                };
            });

            //updated
            _.each(options.updatedDict.items, function(dictItem, dictKey) {
                localUpdateParam[1].value[dictKey] = {
                    type: DashClient.updateTableGetListType(options.typeConfig[dictKey], dictItem),
                    value: dictItem,
                };
            });

            //deleted
            _.each(options.deletedDict.items, function(dictItem, dictKey) {
                localUpdateParam[2].value[dictKey] = {
                    type: DashClient.updateTableGetListType(options.typeConfig[dictKey], dictItem),
                    value: dictItem,
                };
            });

            //localUpdateParam.push({
            //    index: 3,
            //    type: 'symbol',
            //    value: options.keyColumn
            //});

            if (options.updateType === "analytic") {
                if (options.updateAnalytic) {
                    DashClient.runAnalytic(
                        options.updateAnalytic,
                        options.connection,
                        localUpdateParam,
                        successCallback,
                        errorCallback,
                    );
                }
            } else {
                DashClient.getQueryData(
                    "kdb",
                    options.connection,
                    options.query,
                    localUpdateParam,
                    successCallback,
                    errorCallback,
                );
            }
            // run stored table update query
        }

        public static updateTableGetListType(kdbType, item): string {
            let charReturnType;
            //set list types for table update
            switch (kdbType) {
                case 0:
                    if (_.isArray(item)) {
                        // list of lists
                        if (_.isBoolean(item[0])) {
                            charReturnType = "listlistboolean";
                        } else if (_.isString(item[0])) {
                            charReturnType = "listliststring";
                        } else {
                            charReturnType = "listlistsymbol";
                        }
                    } else if (_.isString(item)) {
                        // list of characters is a string
                        charReturnType = "string[]";
                    } else {
                        // default
                        charReturnType = "symbol[]";
                    }

                    return charReturnType;
                case 1:
                    return "boolean[]";
                case 2:
                    //TODO test if this should be uuid
                    return "guid[]";
                case 4:
                    return "byte[]";
                case 5:
                    return "short[]";
                case 6:
                    return "int[]";
                case 7:
                    return "long[]";
                case 8:
                    return "real[]";
                case 9:
                    return "float[]";
                case 10:
                    //    return 'char[]';
                    charReturnType = "char[]";

                    _.each(item, function(i) {
                        if (i.length > 1) {
                            charReturnType = "string[]";
                        }
                    });
                    return charReturnType;
                case 11:
                    return "symbol[]";
                case 12:
                    return "timestamp[]";
                case 13:
                    return "month[]";
                case 14:
                    return "date[]";
                case 15:
                    return "datetime[]";
                case 16:
                    return "timespan[]";
                case 17:
                    return "minute[]";
                case 18:
                    return "second[]";
                case 19:
                    return "time[]";
                default:
                    return "symbol[]";
            }
        }

        public destroy(): void {
            if (this.ws) {
                try {
                    this.ws.onopen = _.noop.bind(this);
                    this.ws.onclose = _.noop.bind(this);
                    this.ws.onmessage = _.noop.bind(this);
                    this.ws.close();
                    this.ws = null;
                } catch (ex) {
                    // do nothing
                    this.ws = null;
                }
            }
        }

        public send(
            op: Op.API | Op.Query | Op.Subscribe,
            maxResults: number,
            connectionId: string,
            query: string | any[],
            success: FnDashSuccess,
            error: FnDashError,
        ): number {
            return this.sendRequest(
                new DCRequest(op, connectionId, query, maxResults, success, error),
            );
        }

        public subscribe(
            maxResults: number,
            query: string,
            connectionId: string,
            success: FnDashSuccess,
            error: FnDashError,
            parameters?: any[],
        ): number {
            if (parameters === undefined) {
                let symbols;
                const rxA = /^\((`\$"([^"]+)")(;`\$"([^"]+)")*\)$/gm;
                const rxB = /^(`[a-zA-Z]\w+)+$/gm;
                const symA = /`\$"([^"]+)"/g;

                // if match extract symbol list or error
                if (query.match(rxA)) {
                    symbols = query.match(symA).map(s => s.substring(3, s.length - 1));
                } else if (query.match(rxB)) {
                    symbols = query.match(rxB).map(s => s.substring(1, s.length));
                } else {
                    error('Enter stream query as a symbol list in the form: (`$"symA";`$"symB")');
                    return;
                }

                parameters = (symbols.length > 1 ? symbols.slice(1) : []).map(s => {
                    return { type: "symbol", value: s };
                });
            }

            // if no sym filter specified add null
            if (!_.first(parameters)) {
                parameters.push({ type: "symbol", value: null });
            }

            // insert query component
            parameters.splice(0, 0, { type: "symbol", value: query });

            // use negative maxResults for subscription to get last rows from snapshot
            return this.send(Op.Subscribe, -maxResults, connectionId, parameters, success, error);
        }

        public unsubscribe(requestId: number): void {
            delete this.requests[requestId];
            this.sendRequest(
                new DCRequest(Op.Unsubscribe, null, null, _.noop.bind(this), _.noop.bind(this)),
                requestId,
            );
        }

        private heartbeat(): void {
            if (this.ws) {
                this.sendRequest(new DCRequest(Op.Heartbeat));
            } else {
                this.init();
            }
        }

        private init(): void {
            try {
                const wsUrl =
                    location.protocol === "file:"
                        ? "ws://localhost:10001/"
                        : ("https:" === location.protocol ? "wss://" : "ws://") +
                          location.host +
                          "/api/ws";

                Log.Log(DashClient.SOCK + wsUrl);
                this.ws = new WebSocket(wsUrl);
                this.ws.binaryType = "arraybuffer";
                this.ws.onopen = this.onWsOpen.bind(this);
                this.ws.onclose = this.onWsClose.bind(this);
                this.ws.onerror = this.onWsError.bind(this);
                this.ws.onmessage = this.onWsMessage.bind(this);
            } catch (e) {
                Log.Log(DashClient.SOCK + "Create Err", e);
            }

            this.debouncedHeartbeat();
        }

        private sendRequest(request: DCRequest, requestId?: number): number {
            requestId = requestId || this.requestId++;
            this.requests[requestId] = request;

            // default params
            const dparams = [ipc.Int.toIPC(this.httpOn ? 0 : requestId), ipc.Int.toIPC(request.op)];

            // how to make ipc params
            const ipcParams = (r: DCRequest, params: any[]) => {
                params.push(ipc.Symbol.toIPC(r.connectionId));
                params.push(
                    ipc.List.toIPC([
                        ipc.Int.toIPC(r.maxResults),
                        typeof r.query === "string"
                            ? ipc.Char.listToIPC(r.query.split(""))
                            : r.query instanceof Array
                            ? DashClient.parametersToIPC(r.query)
                            : r.query,
                    ]),
                );

                return params;
            };

            switch (request.op) {
                case Op.Subscribe:
                    // subscribe is web socket only
                    if (this.insightsAPI || !request.query || request.query.length !== 2) {
                        throw new Error("Client: Unsupported Operation");
                    }
                case Op.API:
                case Op.Query:
                    if (this.worker && !this.httpOn) {
                        const params = ipcParams(request, [requestId, request.op]);
                        if (window["SharedWorker"] && !("FSBL" in window)) {
                            (this.worker as SharedWorker.SharedWorker).port.postMessage(params);
                        } else {
                            (this.worker as Worker).postMessage(params);
                        }
                    } else {
                        const xhr: XMLHttpRequest = new XMLHttpRequest();

                        // progress handler
                        xhr.onprogress = (oEvent): void => {
                            if (oEvent.lengthComputable) {
                                const percentComplete = (oEvent.loaded / oEvent.total) * 100;
                                Log.Log("DashClient download: " + percentComplete + "%");
                            } else {
                                // Unable to compute progress information since the total size is unknown
                            }
                        };

                        // onload handler
                        xhr.onload = (): void => {
                            delete this.requests[requestId];
                            if (xhr.status === 500) {
                                request.error(
                                    u16u8(
                                        new Uint8Array(
                                            xhr.response,
                                            Math.min(xhr.response.byteLength, 14),
                                        ),
                                    ),
                                );
                            } else if (
                                xhr.response &&
                                (xhr.response as ArrayBuffer).byteLength >= 10
                            ) {
                                request.success(deserialize(xhr.response) as qTyped, xhr.response, [
                                    request.timestamp,
                                    Date.now(),
                                ]);
                            } else {
                                request.error("Invalid response");
                            }
                        };

                        xhr.onerror = (ev): void => {
                            delete this.requests[requestId];
                            request.error("Network failure");
                        };

                        let urlBase = location.protocol + "//" + location.host + "/";
                        const isSQL =
                            "object" === typeof request.query && request.query[0]?.value === ".s.e";
                        if (
                            this.insightsAPI &&
                            request.op === Op.Query &&
                            (typeof request.query === "string" || isSQL)
                        ) {
                            // insights http API
                            urlBase +=
                                "servicegateway/sandbox/" +
                                encodeURIComponent(request.connectionId) +
                                (isSQL ? "/sql" : "/qsql");
                            const msg = JSON.stringify({
                                query: isSQL ? request.query[1].value : (request.query as string),
                                maxResults: Number(request.maxResults),
                            });
                            xhr.open("POST", urlBase, true);
                            xhr.responseType = "arraybuffer";
                            xhr.send(msg);
                        } else {
                            // dashboards direct http API
                            const msg = ipc.Util.toMessage(
                                ipc.List.toIPC(ipcParams(request, dparams)),
                            );

                            if (msg.byteLength < 2048) {
                                xhr.open(
                                    "GET",
                                    urlBase +
                                        "api/" +
                                        btoa(
                                            msg.reduce(
                                                (data, byte) => data + String.fromCharCode(byte),
                                                "",
                                            ),
                                        ).replace(/\//g, "+"),
                                    true,
                                );
                                xhr.responseType = "arraybuffer";
                                xhr.send();
                            } else {
                                xhr.open("POST", urlBase, true);
                                xhr.responseType = "arraybuffer";
                                xhr.send(msg);
                            }
                        }
                    }
                    break;

                case Op.Heartbeat:
                case Op.Unsubscribe:
                    // heartbeat and unsubscribe are websocket only best effort
                    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
                        this.ws.send(
                            ipc.Util.toMessage(ipc.List.toIPC(ipcParams(request, dparams))),
                        );
                        this.debouncedHeartbeat();
                    }
                    break;
                default:
                    // why here?
                    throw new Error("Client: Unsupported Operation");
            }

            return requestId;
        }

        private onDisconnect(e): void {
            Log.Log("DashClient Disconnect", e);
            $(this).trigger("DeltaClientDisconnect", e);
        }

        private onOpen(e): void {
            Log.Log("DashClient Connect");
            $(this).trigger("DeltaClientConnect");
            Log.Log("DashClient Login");
            $(this).trigger("DeltaClientLogin");
        }

        private onWorkerError(e): void {
            Log.Log(DashClient.WORK + "error", e);
            this.onDisconnect(e);
        }

        private onWsClose(e: CloseEvent): void {
            Log.Log(DashClient.SOCK + "Closed", e);
            //this.onStateChangeFn(this.ws ? this.ws.readyState : WebSocket.CLOSED, e);
            this.destroy();
        }

        private onWsError(e: Event): void {
            Log.Log(DashClient.SOCK + "Error", e);
            this.onDisconnect(e);
            this.destroy();
        }

        private onWsMessage(e: MessageEvent): void {
            // pick up state and connection errors from worker
            if (e.data instanceof Array) {
                if (e.data[0] === Op.State) {
                    if (e.data[1] === WebSocket.OPEN) {
                        this.onOpen(null);
                    } else this.onWorkerError(e.data[2] || "ws not open");
                    return;
                } else if (e.data[0] === Op.Error) {
                    return this.onWorkerError(e.data[1]);
                }
            }

            const response = deserialize(e.data, 8, true) as Array<any>;
            //console.log(x);
            // kdb Q: for some strange reason the 2nd query always return error type - so destroy to start a new
            //this.destroy();
            const key = response[0];
            const mtype = response[1];
            const request = this.requests[key];

            // remove request unless subscription
            if (request) {
                if (request.op !== Op.Subscribe) {
                    delete this.requests[key];
                }

                if (
                    request.op === Op.Query ||
                    request.op === Op.API ||
                    request.op === Op.Subscribe
                ) {
                    if (mtype === Op.Error) {
                        const errData: qVector = response[2];
                        debugger;
                        request.error(
                            u16u8(new Uint8Array(e.data, errData.offset, errData.length)),
                        );
                    } else {
                        // if worker use timestamp from bytelocation 0
                        const endTime =
                            this.httpOn && request.op !== Op.Subscribe
                                ? Date.now()
                                : new Float64Array(e.data, 0, 1)[0];
                        request.success(response[2] || {}, e.data, [request.timestamp, endTime]);
                    }
                }
            } else {
                Log.Warn('Stray Response "' + key + '"');
            }
        }

        private onWsOpen(e: Event): void {
            console.log("websocket open", e);
            if (this.ws.readyState === WebSocket.OPEN) {
                Log.Log(DashClient.SOCK + "Open", e);
                this.onOpen(e);
            } else {
                Log.Log(DashClient.SOCK + "Open Error", e);
                this.onDisconnect(e);
            }
        }
    }
}
