/// <reference path="./GraphNode.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.QueryBuilder {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    export class UpdateNode extends GraphNode {
        static dataToIPC(data: any): Uint8Array {
            data = UpdateNode.processBindings(data);
            // `type`data!(`update;`filter`update!(filterNode`data;(`side`qty)!(enlist `P;0)));
            return QuickBase.ipc.Dict.toIPC(
                ["filter", "update"],
                QuickBase.ipc.List.toIPC([
                    // filter value
                    QuickBase.ipc.Util.fromHexString(
                        DataFilter.App.generateIpcHexMessage(data.filter).slice(16),
                    ),
                    // update value
                    QuickBase.ipc.Dict.toIPC(
                        _.map(data.update, "column"),
                        QuickBase.ipc.List.toIPC(
                            _.map(data.update, u => {
                                const qtype = QuickBase.ipc.Util.getType(u.propertyType);
                                const val = qtype.toIPC(ipc.fromString(qtype, u.val));
                                return u.propertyType === -11
                                    ? QuickBase.ipc.List.toIPC([val])
                                    : val;
                            }),
                        ),
                    ),
                ]),
            );
        }

        static getActiveBindingKeys(data: any): Array<string> {
            data = data as Data;
            let keys: Array<string> = [];
            _.each(
                data.filter.children,
                c => (keys = UpdateNode.getBindingKeysFromArray(keys, _.keys(c))),
            );
            _.each(
                data.update,
                c => (keys = UpdateNode.getBindingKeysFromArray(keys, _.values(c) as string[])),
            );
            return keys;
        }

        static processBindings(data: any): Data {
            data = _.cloneDeep(data) as Data;

            GraphNode.replaceAllBindings(data.filter.children);
            GraphNode.replaceAllBindings(data.update, "val");

            return data;
        }
    }
}
