/*global*/
/// <reference path="../templates/combined.ts" />
/// <reference path="../lib/i18n.d.ts" />
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export var HelpDialog = Backbone.View.extend({
        $dialog: null,

        appModel: null,
        className: "quick-base window",
        viewModel: null,

        initialize: function(options) {
            this.appModel = options.appModel;
            this.container = options.container;
            this.viewModel = options.viewModel;
            this.tag = options.tag;
            this.render();
        },

        remove: function() {
            //completely remove dialog from DOM
            this.$el["dialog"]("destroy").remove();
            // remove event handlers
            this.stopListening();
            this.$el.off();
            // remove from dom
            this.$el.remove();
        },

        render: function() {
            let self = this,
                doc = $(document),
                docsUrl = this.appModel.get("DocsUrl"),
                webUrl = this.appModel.get("websiteUrl").replace("/edit", ""),
                url = docsUrl !== undefined ? docsUrl : webUrl + "docs/",
                isTabbed = this.appModel.get("isHelpDialogTabbed") || false;

            url = this.tag !== undefined ? url + "/" + this.tag : url;

            if (isTabbed) {
                window.open(url, "_blank");
            } else {
                this.$el.html($("<iframe />").attr("src", url));

                this.$dialog = this.$el["dialog"](
                    _.extend(
                        {
                            appendTo: this.container,
                            dialogClass: "quick-base help-dialog",
                            title: t("Help"),
                            resizable: true,
                            height: doc.height() * 0.8,
                            width: doc.width() * 0.8,
                            modal: true,
                            autoOpen: true,
                            draggable: true,
                            open: function() {
                                // add a dialog icon
                                $('<i class="fa fa-question-circle" />').insertBefore(
                                    self.$el.parent().find(".ui-dialog-title"),
                                );
                            },
                            close: function() {
                                self.remove();
                            },
                        },
                        this.options,
                    ),
                );
            }
        },
    });
}
