// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ChangeSet {
        constructor(collection) {
            this.collection = collection;
        }

        private collection: Backbone.Collection<Backbone.Model>;
        public add: Array<object>;
        public change: Array<object>;
        public reset: Array<object>;
        public remove: Array<object>;
        public cols: { [index: string]: ipc.TypeBase };

        public assign(collection: Backbone.Collection) {
            this.collection = collection;
        }

        public items() {
            if (this.reset) return this.reset;
            else return _.map(this.collection.models, m => m.attributes);
        }
    }
}
