// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Constants {
        public static readonly MSDAY = 86400000;
        public static readonly QEPOCH = Constants.MSDAY * 10957;
        public static readonly LONG_NULL = new QuickBase.Long(0x0, 0x80000000);
        public static readonly LONG_INFINITY_NEGATIVE = new QuickBase.Long(0x00000001, 0x80000000);
        public static readonly LONG_INFINITY_POSITIVE = QuickBase.Long.MAX_VALUE;
        public static readonly JS_DATE_MIN = -8640000000000000;
        public static readonly TIME_PARTS_RX = /(-?\d{1,7}:\d\d:\d\d.\d\d\d)(\d\d\d\d\d\d)/;
        public static readonly BQEPOCH = new jsbn.BigInteger(
            (Constants.QEPOCH * 1000000).toString(),
        );
    }
}
