/*global Backbone */

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class ComponentDefinition extends Backbone.Model {
        defaults(): object {
            return {
                listViewThumb: null,
                ghostViewThumb: null,
                buildViewThumb: null,

                appKey: null,
                appArgs: null,

                name: null,
                description: null,
            };
        }
    }

    export class ComponentDefinitionCollection extends Backbone.Collection<ComponentDefinition> {
        model = ComponentDefinition;
    }
}
