/// <reference path="U8.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Boolean extends U8 {
        static readonly TYPE_NUM = 1;

        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Bool, dataView);
        }

        static listToIPC(values: Array<boolean>): Uint8Array {
            const size = values.length + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(Boolean.TYPE_NUM); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => buffer.wb(v ? 1 : 0)); // values

            return buffer.data;
        }

        static toIPC(value: boolean): Uint8Array {
            const buffer = Util.createBuffer(2);
            buffer.wb(256 - Boolean.TYPE_NUM); // type
            buffer.wb(value ? 1 : 0); // value
            return buffer.data;
        }

        getValue(i: number): boolean {
            return this.getScalar(i) === 0x1;
        }
    }
}
