/*global _,$,console,Backbone */

class CopyPasteHelpers {
    appModel: Backbone.DeepModel;
    componentModel: any;
    cssDialog: any;
    dialogContainer: HTMLElement;
    dlgBulkColumnEditor: any;
    docDataModel: QuickBase.DocumentDataModel;
    docViewModel: QuickBase.DocumentViewModel;
    iconDialogLoading: boolean;
    htmlDialog: any;
    viewModel: QuickBase.DocumentViewModel;
    getCurrentDocument: any;

    constructor(options) {
        //this.getCurrentDocument = getCurrentDocument;
    }

    public static getComponentViewStates(component, getDeep = true) {
        const viewStatePaths = [];

        const extractVS = function(vs) {
            if (!vs) {
                return;
            } else if (vs instanceof QuickBase["Component"]) {
                if (getDeep) {
                    vs.get("components").each(function(component) {
                        extractVS(component);
                    });

                    vs.get("widgets").each(function(widget) {
                        extractVS(widget.get("component"));
                    });
                }

                extractVS(vs.get("options"));
            } else if (_.isArray(vs)) {
                _.each(vs, function(i) {
                    extractVS(i);
                });
            } else if (vs.attributes) {
                // backbone model with attributes
                if (vs.path) {
                    if (!vs.get("_dataSource")) {
                        // we have it (make sure not added already)
                        if (!_.includes(viewStatePaths, vs.path)) {
                            viewStatePaths.push(vs.path);

                            if (vs.get("_type") === "dict") {
                                _.each(vs.get("_default"), function(dictValue) {
                                    if (dictValue instanceof QuickBase.DocumentViewModel) {
                                        viewStatePaths.push(dictValue.path);
                                    }
                                });
                            }
                        }
                    }
                    // this will ommit data sources
                } else {
                    // recursive
                    extractVS(vs.attributes);
                }
                // object, function or value (string, number)
            } else if (_.isObject(vs) && !(vs instanceof Backbone.View)) {
                // viewstates on unopened tabs are not populated
                if (vs["_dashboardsType"] === "viewstate") {
                    if (!_.includes(viewStatePaths, vs.value)) {
                        viewStatePaths.push(vs.value);
                    }
                } else {
                    // object
                    _.each(_.keys(vs), function(key) {
                        extractVS(vs[key]);
                    });
                }
            }
        };

        // execute
        extractVS(component);
        return viewStatePaths;
    }

    public static mergeViewStates(viewState, doc) {
        let newPath;

        if (!viewState || !doc) {
            return;
        }

        // view state by view state merge the trees
        _.each(viewState, function(attributes, vsPath) {
            // check if exists
            //if (!doc.attributes.viewState.getByPath(vsPath)) {
            if (!doc.get("viewState").getByPath(vsPath)) {
                // create view state with the whole path
                const pathArray = vsPath.split("/");
                const newVS = null;
                let currentVS = null;
                let endVs;

                if (pathArray.length <= 1) {
                    // vs
                    newPath = pathArray[0];
                    currentVS = new QuickBase["DocumentViewModel"](attributes, null);
                } else {
                    // get root
                    currentVS = doc.get("viewState");

                    // middle groups
                    for (let i = 0; i < pathArray.length - 1; i += 1) {
                        currentVS.set(
                            pathArray[i],
                            currentVS.get(pathArray[i]) ||
                                new QuickBase["DocumentViewModel"](null, null),
                        );
                        currentVS = currentVS.get(pathArray[i]);

                        //if not root group build path string for nested grups
                        newPath = i === 0 ? pathArray[i] : pathArray.slice(0, i).join("/");
                        currentVS.path = newPath;
                    }

                    // vs
                    newPath = pathArray.join("/");
                    // create end viewstate
                    endVs = new QuickBase["DocumentViewModel"](attributes, null);
                    currentVS.set(pathArray[pathArray.length - 1], endVs);
                    // set end full path in to path property
                    endVs.path = newPath;
                }

                // add to main VS
                if (!doc.get("viewState").getByPath(newPath)) {
                    currentVS.path = newPath;
                    doc.get("viewState").set(newPath, currentVS);
                }
            }
        });

        // convert dictionary children to viewstates
        doc.get("viewState").postInitialize();
    }

    public static getMappingDataSources(viewStates, doc) {
        const dataSources = [];
        const self = this;

        doc.get("data")
            .getTreeList()
            .forEach(function(dataSource) {
                if (dataSource.get("_mappings")) {
                    _.each(dataSource.get("_mappings").mappings, function(mapping) {
                        if (_.includes(viewStates, mapping.value)) {
                            dataSources[dataSource.path] = dataSource.toJSON();
                        }
                    });
                }
            });

        return dataSources;
    }

    public static mergeDataSources(dataSources, doc) {
        let rootSrc;
        const self = this;
        var doc = doc;

        if (!dataSources) {
            return;
        }

        // merge data sources
        rootSrc = doc.get("data");

        _.each(dataSources, function(attributes, path) {
            // recreate data sources graph

            let pathParts = _.filter(path.split("/"), function(key) {
                    return key && key.length > 0;
                }),
                currentRoot = rootSrc,
                currentPath = "",
                dataSource;

            // build path tree
            for (let i = 0; i < pathParts.length; i += 1) {
                // update current path
                currentPath += i === 0 ? "" : "/";
                currentPath += pathParts[i];

                dataSource = new QuickBase["DocumentDataModel"](
                    i !== pathParts.length - 1 ? {} : attributes,
                    rootSrc,
                    currentPath,
                    doc.get("viewState"),
                    i !== pathParts.length - 1,
                );

                // before creating check if exists
                if (!doc.get("data").getByPath(currentPath)) {
                    // create data source
                    currentRoot.set(pathParts[i], dataSource);
                    dataSource.viewState = doc.attributes.viewState;
                    //dataSource.apply();
                    currentRoot = dataSource;
                } else {
                    currentRoot = doc.get("data").getByPath(currentPath);
                }
            }
        });
    }

    public static getComponentDataSources(component, viewStates, dataSources, getDeep = true) {
        let result = {},
            checked = {},
            self = this,
            extractDS,
            getKey,
            tempKey;

        getKey = function(ds) {
            // this function gives objects and arrays in the data sources graph an id
            // this id is required as data sources graph is a cycle graph and we need to identify visited nodes
            // this property will not be serialized or send to db
            if (!ds["_cpKey"]) {
                if (_.isObject(ds) || _.isArray(ds)) {
                    Object.defineProperty(ds, "_cpKey", {
                        enumerable: false,
                        writable: true,
                    });
                    ds["_cpKey"] = _.uniqueId("_cpKey_");
                }
            }
            return ds["_cpKey"] || null;
        };

        extractDS = function(ds) {
            if (!ds) {
                return;
            }

            // inject/get key
            tempKey = getKey(ds);

            if (!tempKey || !checked[tempKey]) {
                // not yet visited

                if (tempKey) {
                    checked[tempKey] = true;
                }

                if (
                    !getDeep &&
                    (ds instanceof QuickBase.WidgetCollection ||
                        ds instanceof QuickBase.ComponentCollection)
                ) {
                    return;
                }

                if (_.isArray(ds)) {
                    // process array

                    _.each(ds, function(i) {
                        extractDS(i);
                    });
                } else if (ds.attributes) {
                    // backbone model with attributes

                    if (
                        ds.attributes["_dataType"] &&
                        _.includes(
                            ["analytic", "pivot", "query", "virtual", "builder"],
                            ds.attributes["_dataType"],
                        )
                    ) {
                        // it's a data source

                        // get the view states
                        _.each(ds.getViewStateDict(), function(vs) {
                            if (!_.includes(viewStates, vs.path)) {
                                viewStates.push(vs.path);

                                if (vs.get("_type") === "dict") {
                                    _.each(vs.get("_default"), function(dictValue) {
                                        if (dictValue instanceof QuickBase.DocumentViewModel) {
                                            viewStates.push(dictValue.path);
                                        }
                                    });
                                }
                            }
                        });

                        // get data sources from virtual parameters
                        if (ds.attributes["_dataType"] === "virtual") {
                            // might have data sources in params
                            _.each(ds.get("_virtualParams"), function(param) {
                                let ds2;

                                if (param.type === "datasource") {
                                    // get it

                                    ds2 = dataSources.getByPath(param.value);

                                    if (ds2) {
                                        // get data source view states
                                        _.each(ds2.getViewStateDict(), function(vs) {
                                            if (!_.includes(viewStates, vs.path)) {
                                                viewStates.push(vs.path);
                                            }
                                        });
                                        result[param.value] = ds2.toJSON();
                                    }
                                }
                            });
                        }

                        result[ds.path] = ds.toJSON();
                        extractDS(ds.attributes);
                    } else {
                        extractDS(ds.attributes);
                    }
                } else if (_.isObject(ds) && !(ds instanceof Backbone.View)) {
                    // object, function or value (string, number)
                    // it's an object

                    // viewstates on unopened tabs are not populated
                    if (ds["_dashboardsType"] === "data") {
                        if (!result[ds.value]) {
                            // use datasource, extract viewstates
                            //      stray datasources ignored (e.g. "Demo Visualize" dashboard)
                            extractDS(dataSources.getByPath(ds.value));
                        }
                    } else {
                        _.each(_.keys(ds), function(key) {
                            extractDS(ds[key]);
                        });
                    }
                }
            }
        };

        extractDS(component);
        return result;
    }
}
