/// <reference path="F64.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class DateTime extends F64 {
        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.DateTime, dataView);
        }

        static fromMoment(m: moment.Moment): rDateTimeClass {
            return m === null ? null : new rDateTimeClass(m.valueOf());
        }

        static fromScalar(scalar: number): rDateTimeClass {
            // F64's getScalar returns NaN when DateTime is null
            return isNaN(scalar) || scalar === null
                ? null
                : new rDateTimeClass(Constants.QEPOCH + scalar * Constants.MSDAY);
        }

        static fromString(str: string): rDateTimeClass {
            if (str === null) return null;
            const dateMatch = str.match(/(.*)(D|T)(.*?)Z?$/);
            if (!dateMatch) return null;
            str = dateMatch[1].replace(/\./g, "-") + "T" + dateMatch[3] + "Z";
            const m = moment(str);
            return DateTime.fromMoment(m);
        }

        static listToIPC(values: Array<rDateTimeClass>): Uint8Array {
            const size = values.length * 8 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(TypeNum.DateTime); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => DateTime.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        static toIPC(value: rDateTimeClass): Uint8Array {
            const buffer = Util.createBuffer(9);

            buffer.wb(256 - TypeNum.DateTime); // type
            DateTime.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        static toMoment(value: rDateTimeClass): moment.Moment {
            if (value === null) return null;
            const m = moment.tz(value.i, "UTC");
            if (Tools.timezone && m.isValid()) m.tz(Tools.timezone);
            return m;
        }

        static toScalar(value: rDateTimeClass): number {
            return value === null ? null : (value.i - Constants.QEPOCH) / Constants.MSDAY;
        }

        static toString(value: rDateTimeClass, format?: string): string {
            if (value === null) return "";
            const m = DateTime.toMoment(value);
            return format !== undefined ? m.format(format) : m.toISOString();
        }

        static writeValue(value: rDateTimeClass, wb: (b: number) => void): void {
            Double.writeValue(DateTime.toScalar(value), wb);
        }

        getValue(i: number): rDateTimeClass | number {
            return DateTime.fromScalar(this.getScalar(i));
        }
    }
}
