// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class PerformanceMetrics {
        private perf: { [index: string]: number } = {};

        public trigger(trigger: string): void {
            this.perf[trigger] = Date.now();
        }

        public set(key: string, val: number): void {
            this.perf[key] = val;
        }

        get performance(): { [index: string]: number } {
            return this.perf;
        }
    }
}
