/// <reference types="backbone" />
/// <reference path="ICommandable.ts" />
/// <reference path="../classes/ComponentInterface.ts"/>
/// <reference path="../lib/helpers.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export class DocumentViewModel extends CommandoModel implements ViewModelInterface {
        MATERIAL_ATTRIBUTES: string[] = ["_type", "_default"];
        path: string;
        root: DocumentViewModel;
        schema: any;
        withAllProperties = false;
        parent: Commandable;

        // gk: why?
        viewStates: any[];

        constructor(attributes: any, options: any, parent?: Commandable) {
            super(attributes, options, parent);

            // set path
            if (options) {
                if (options.path) {
                    this.path = options.path;
                }

                // set root model
                this.root = options.root === true ? this : options.root;
            }

            // convert each child object into a DocumentView Model
            _.each(
                _.keys(this.attributes),
                function (key) {
                    let val = this.get(key),
                        model;

                    //Check if val is object but ensure it is not _default or value properties as these may be arrays
                    if (_.isObject(val) && ["_default", "value", ".settings"].indexOf(key) === -1) {
                        model = new DocumentViewModel(
                            val,
                            {
                                path: this.path ? this.path + "/" + key : key,
                                root: this.root,
                            },
                            this,
                        );

                        this.set(key, model, { silent: true });
                        this.listenTo(model, "change", this.onChildModelChange);
                        this.listenTo(model, "materialChange", this.onMaterialChange);
                    } else if (key === ".settings") {
                        if (!(val instanceof Backbone.Model)) {
                            // not a model means that it's previous setting being passed
                            // make all component_id props backbone models
                            for (const key2 in val) {
                                if (val.hasOwnProperty(key2)) {
                                    val[key2] = new Backbone.Model(val[key2]);
                                }
                            }
                            // make the whole settings a backbone model
                            this.set(key, new Backbone.Model(val));
                        }
                    } else {
                        this.set(key, val);

                        // apply default to value
                        if (key === "_default") {
                            if (this.get("_rolling")) {
                                this.set(
                                    "value",
                                    Helpers.convertRollingToDate(val, this.get("_type")),
                                );
                            } else {
                                this.set("value", val);
                            }
                        }
                    }
                }.bind(this),
            );

            // if is view state item not group
            if (_.has(this.attributes, "_default") && _.has(this.attributes, "value")) {
                // init type & schema
                this.attributes["_type"] = this.attributes["_type"] || "symbol";
                this.schema = {
                    _type: {
                        type: "Select",
                        options: [
                            "boolean",
                            "byte",
                            "char",
                            "date",
                            "datetime",
                            "dict",
                            "double",
                            "float",
                            "guid",
                            "int",
                            "list",
                            "long",
                            "minute",
                            "month",
                            "second",
                            "short",
                            "string",
                            "symbol",
                            "time",
                            "timespan",
                            "timestamp",
                        ],
                    },
                };

                this.updateSchema();
            }

            this.listenTo(this, "change", this.onChange);
        }

        postInitialize() {
            const self = this;

            // convert view states in dictionaries to models
            // needs to happen after view state has been set (including user view state)
            _.each(self.getViewStateList(), function (viewState) {
                if (viewState.model.get("_type") === "dict") {
                    viewState.model.set(
                        "_default",
                        _.mapValues(viewState.model.get("_default"), function (value, key) {
                            let model;

                            if (_.isString(value) && value.indexOf("<%") === 0) {
                                model = self.getByPath(value.substring(2, value.length - 2));
                            } else if (_.isObject(value)) {
                                if (value._viewType) {
                                    value = value.value;
                                } else if (value.isViewState) {
                                    model = self.getByPath(value.name);
                                }
                            }

                            return model ? model : value;
                        }),
                        { silent: true },
                    ); //otherwise causes unsaved changes on dash load

                    viewState.model.set("value", viewState.model.get("_default"));
                }
            });
        }

        destroyOnServer(options) {
            options = options ? _.clone(options) : {};
            const model = this;
            const success = options.success;
            const wait = options.wait;

            const destroy = function () {
                // destroyOnServer is the same as destroy except it skips stopListening
                //model.stopListening();
                model.trigger("destroy", model, model.collection, options);
            };

            const wrapError = function (model, options) {
                const error = options.error;
                options.error = function (resp) {
                    if (error) error.call(options.context, model, resp, options);
                    model.trigger("error", model, resp, options);
                };
            };

            options.success = function (resp) {
                if (wait) destroy();
                if (success) success.call(options.context, model, resp, options);
                if (!model.isNew()) model.trigger("sync", model, resp, options);
            };

            let xhr: boolean | JQueryXHR = false;
            if (this.isNew()) {
                _.defer(options.success);
            } else {
                wrapError(this, options);
                xhr = this.sync("delete", this, options);
            }
            if (!wait) destroy();
            return xhr;
        }

        doCommand(path: Array<string>, todos: Array<Todo>) {
            if (path.length > 0) {
                const vsPath = path.reverse().join("/");
                return this.getByPath(vsPath).doCommand(path, todos);
            }

            //TODO: do something its your command
        }

        getByPath(path) {
            let splitPath, val;

            // if path is not defined, return undefined
            if (!path) {
                return undefined;
            }

            if (_.isObject(path)) {
                console.warn("getByPath is deprecated, please update component.");
                return path;
            }

            // split path by delimeter, excluding empty entries
            splitPath = _.filter(path.split("/"), function (key) {
                return key && key.length > 0;
            });

            // default to this (normall root)
            val = this;

            // iterate over the nested models using the path of keys
            _.each(splitPath, function (key) {
                if (val && val.get) {
                    val = val.get(key);
                }
            });

            return val;
        }

        getViewStateExport() {
            let processNode,
                viewStateExport = {};

            processNode = function (root, path) {
                let settings, currentNode, nodePath;

                if (!(root && root.attributes)) {
                    return;
                }

                _.each(_.keys(root.attributes), function (key) {
                    nodePath = path ? path + "/" + key : key;
                    currentNode = root.get(key);

                    if (_.isObject(currentNode) && _.isFunction(currentNode.get)) {
                        if (currentNode.get("_viewType")) {
                            // DocumentViewModel
                            if (!_.isEqual(currentNode.get("value"), currentNode.get("_default"))) {
                                viewStateExport[nodePath] = currentNode.get("value");
                            }
                        } else if (nodePath === ".settings") {
                            // settings object
                            settings = _.mapValues(currentNode.attributes, function (value) {
                                return value.omit("version");
                            });

                            if (!_.isEmpty(settings)) {
                                viewStateExport[".csettings"] =
                                    QuickBase.Tools.encodeToBase64(
                                        QuickBase.Tools.compress(JSON.stringify(settings))
                                    );
                            }
                        } else {
                            // folder
                            processNode(currentNode, nodePath);
                        }
                    }
                });

                return viewStateExport;
            };

            return processNode(this);
        }

        getViewStateList(includeSettings?: boolean) {
            this.viewStates = [];
            this.getViewStates(this);
            return this.viewStates;
        }

        getViewStates(root, path?: string, includeSettings?: boolean) {
            let self = this,
                currentNode,
                nodePath;

            if (!root.attributes) {
                return;
            }

            _.each(_.keys(root.attributes), function (key) {
                nodePath = path ? path + "/" + key : key;
                currentNode = root.get(key);

                if (includeSettings || key !== ".settings") {
                    if (
                        _.isObject(currentNode) &&
                        _.isFunction(currentNode.get) &&
                        currentNode.get("_viewType") &&
                        key.substr(0, 1) !== "."
                    ) {
                        self.viewStates.push({
                            model: currentNode,
                            path: nodePath,
                        });
                    } else {
                        self.getViewStates(currentNode, nodePath, includeSettings);
                    }
                }
            });
        }

        onChange() {
            // listen and stop listening to children
            _.each(this.changedAttributes(), (val, key) => {
                if (this.previous(key) === undefined && val instanceof DocumentViewModel) {
                    // listen to new view state added
                    this.stopListening(val, "change materialChange");
                    this.listenTo(val, "change", this.onChildModelChange);
                    this.listenTo(val, "materialChange", this.onMaterialChange);
                } else if (val === undefined && this.previous(key) instanceof DocumentViewModel) {
                    // stop listening to view state removed
                    this.stopListening(this.previous(key), "change");
                    this.stopListening(this.previous(key), "materialChange");
                }
            });

            // determine material change
            if (this.changed && _.some(this.MATERIAL_ATTRIBUTES, a => this.hasChanged(a))) {
                this.trigger("materialChange");
            }
        }

        onChildModelChange(model, value) {
            this.trigger("change", model, value);
        }

        postCommand(sender: object, command: Command) {
            command.path.push(this.id);
            return this.parent.postCommand(this, command);
        }

        onMaterialChange() {
            this.trigger("materialChange");
        }

        setModelValue(value): void {
            let forceChangeEvent = false;

            if (this.get("_type") === "dict") {
                // before we set the value, check if any sub-properties are linked to view states
                _.each(
                    // pick all properties that correspond to view states
                    _.pickBy(
                        value,
                        (v, k) => this.get("value")[k] instanceof QuickBase.DocumentViewModel,
                    ),
                    // loop over these properties and recursively set their values
                    (val, key) => {
                        val = _.includes(_.keys(val), "value") ? val.value : val;
                        const model = this.get("value")[key];
                        if (model.get("value") !== val) {
                            // informs the parent dictionary that a change event is necessary
                            forceChangeEvent = true;
                        }
                        model.setModelValue(val);
                        value[key] = model;
                    },
                );
            }

            this.set("value", value);
            if (forceChangeEvent && _.isEmpty(this.changed)) {
                // we must force a change event because the model can't see changes to its own deep properties
                // (since the object references are still the same)
                this.trigger("change:value", this, value);
            }
        }

        /*
         * toJSON override is necessary to prevent all of the data
         * contained within the data type models from being serialized.
         * - only the definition properties prefixed with '_' are
         *   saved
         */
        toJSON(withAllProperties) {
            let self = this,
                filteredAttrs = {},
                keys = _.keys(self.attributes);

            withAllProperties =
                withAllProperties === true || (this.root && this.root.withAllProperties);

            // if value is the same as the rolling default then don't save it
            // so it will be re-generated on load
            if (
                self.get("_rolling") &&
                Helpers.convertRollingToDate(self.get("_default"), self.get("_type")) ===
                self.get("value")
            ) {
                keys = _.without(keys, "value");
            }

            _.each(keys, function (key) {
                let child = self.attributes[key],
                    filteredChild = {},
                    key2;

                if (key === ".settings") {
                    //// dunno why but I have to cast all backbone models to json
                    //_.each(child.attributes, function (value, key) {
                    //    child.set(key, value.toJSON());
                    //});
                    // now the whole thing to json
                    filteredAttrs[key] = child.toJSON();
                    for (key2 in filteredAttrs[key]) {
                        if (filteredAttrs[key].hasOwnProperty(key2)) {
                            filteredAttrs[key][key2] = filteredAttrs[key][key2].toJSON();
                        }
                    }
                }
                // if child is a data type, lets only store the _properties
                else if (_.isObject(child) && child.has && child.has("_viewType")) {
                    _.each(
                        _.filter(_.keys(child.attributes), function (testKey) {
                            return withAllProperties ? true : testKey.indexOf("_") === 0;
                        }),
                        function (filteredKey) {
                            filteredChild[filteredKey] = child.get(filteredKey);
                        },
                    );

                    if (filteredChild["_type"] === "dict") {
                        // convert child models to paths
                        filteredChild["_default"] = _.mapValues(child.get("_default"), function (
                            value,
                            key,
                        ) {
                            return value instanceof DocumentViewModel
                                ? "<%" + value.path + "%>"
                                : value;
                        });

                        // quickview will save 'value' so should convert that too
                        filteredChild["value"] = _.mapValues(child.get("value"), function (
                            value,
                            key,
                        ) {
                            return value instanceof DocumentViewModel
                                ? "<%" + value.path + "%>"
                                : value;
                        });
                    }

                    filteredAttrs[key] = filteredChild;
                } else {
                    filteredAttrs[key] = child;
                }
            });

            return filteredAttrs;
        }

        parse(data) {
            let val;

            if (data && data[".settings"]) {
                val = data[".settings"];

                if (!(val instanceof Backbone.Model)) {
                    // not a model means that it's previous setting being passed
                    // make all component_id props backbone models
                    for (const key in val) {
                        if (val.hasOwnProperty(key)) {
                            val[key] = new Backbone.Model(val[key]);
                        }
                    }
                    // make the whole settings a backbone model
                    data[".settings"] = new Backbone.Model(val);
                }

                //_.each(data[".settings"], function (value, key) {
                //    data[key] = new Backbone.Model(value);
                //});
            }
            return data;
        }

        updateBinding(path, componentView, componentViewModel, viewProp, bindingObjs) {
            const appViewModelItem = _.isString(path) ? this.getByPath(path) : path;

            componentView.stopListening(componentViewModel, "change:" + viewProp);
            if (path && appViewModelItem) {
                bindingObjs.push(appViewModelItem);
                componentViewModel.set(viewProp, appViewModelItem.get("value"));

                componentView.listenTo(appViewModelItem, "change:value", function (model, value) {
                    componentViewModel.set(viewProp, value);
                });

                componentView.listenTo(componentViewModel, "change:" + viewProp, function (
                    model,
                    value,
                ) {
                    appViewModelItem.set("value", value);
                });
            }
        }

        updateBindingDeep(path, componentView, componentViewModel, viewProp, bindingObjs) {
            const appViewModelItem = this.get(path);

            componentView.stopListening(componentViewModel, "change:" + viewProp);
            if (path && appViewModelItem) {
                bindingObjs.push(appViewModelItem);
                componentViewModel.set(viewProp, appViewModelItem.get("value"));

                componentView.listenTo(appViewModelItem, "change:value", function (model, value) {
                    componentViewModel.set(viewProp, value);
                });

                componentView.listenTo(componentViewModel, "change:" + viewProp, function (
                    model,
                    value,
                ) {
                    appViewModelItem.set("value", value);
                });
            }
        }

        updateSchema() {
            const typeDefinition = RTTI.getByName(this.get("_type")),
                editor = typeDefinition.editor;

            this.schema._default = editor;
            this.schema.value = editor;
        }

        silentCast(key, ObjType, options) {
            if (!(this.get(key) instanceof ObjType)) {
                this.set(key, new ObjType(this.get(key), options), { silent: true });
            }
        }
    }
}
