/// <reference path="ChangeSetInterface.ts"/>
/// <reference path="DataModelInterface.ts"/>
/// <reference path="ViewModelInterface.ts"/>

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export interface ComponentInterface {
        /**
         * Remove entry for current component in .settings
         */
        destroySettings(): void;

        /**
         * Execute the actions provided by the actions editor from PropertiesPanel
         * @param {Array} actions
         */
        doActions(actions, propertyPath): void;

        /**
         * Export the full dataset from a datasource, using DeltaClient ExportApi.export
         * @param {Object} source
         * @param {Object} callback - called with url to file
         * @param {Object} options - e.g. can include focus for pivot queries
         */
        exportData(
            source: Record<string, any>,
            callback: Record<string, any>,
            options: Record<string, any>,
        ): void;

        /**
         * Returns property value for a given path: onSettingsChange will keep a component updated with changes to its properties (including changes to any underlying bound view states) so getProperty is normally not necessary, assuming the component keeps a store of the changes. However, if required, a component can retrieve the current value of a property using getProperty.
         * @param {String} path
         * The path of the property.
         * @returns {Object} Value of the property. If the property is bound to a view state the value of the view state will be returned instead.
         */
        getProperty(path: string): Record<string, any>;

        /**
     * Returns property information for a given path: If more information about a property is required, use getPropertyMeta.
     * @param {String} path
     * The path of the property.
     * @returns {Object} Property information: An object containing information about the property and, if applicable, its underlying view state. The response differs depending on the value set in the property.

    Generic object properties are:
    path - path to the property
    type - type of the property, if it contains a data source or viewstate (data, viewstate or null)

    If the property is a simple property or if it is bound to a view state the object also contains:
    value - value of the property

    If the property is bound to a view state the object also contains:
    viewstateType - type of the underlying view state (symbol, int, etc)
    defaultValue - default value of the underlying view state
     */
        getPropertyMeta(path: string): Record<string, any>;

        /**
         * Get Data Source mapping
         * @param viewStatePaths
         * @param dataSourcePaths
         * @param propertyViewStates
         */
        getDataSourceMap(viewStatePaths, dataSourcePaths, propertyViewStates);

        /**
         * Get the value of a given viewstate model
         * @param {ViewModelInterface} viewState {@link ViewModelInterface | ViewModelInterface}
         * : Viewstate Model
         * @returns {String} The value of the model
         */
        getViewState(viewState: ViewModelInterface): string;

        /**
         * Extract view states from template and pass back their values
         * @param {String} template
         */
        getTemplateViewStates(template: string): void;

        /**
         * Hides the Dashboards error message on the component
         */
        hideErrorMessage(): void;

        /**
         * Hide query status
         */
        hideQueryStatus();

        /**
         * Determines if the dashboard is in build mode
         * @returns {Boolean} True if in build mode
         */
        isBuildMode(): boolean;

        /**
         * Determines if the component is selected
         * @returns {Boolean} True if the component is selected
         */
        isSelected(): boolean;

        /**
         * Load a given app using requireJS and pass it a new API interface
         * @param {String} key
         * @param {Object} element
         * @param {Object} settings - either a Backbone model or an object containing the new app's settings
         * @param {Object} options - an object of options to pass to the app initialize function
         * @returns {Object} appView
         */
        loadApp(
            key: string,
            element: Record<string, any>,
            settings: Record<string, any>,
            options: Record<string, any>,
            callback,
            namespace,
        ): void;

        /**
         * Load a setting from the .settings viewstate
         * @param {String} key
         * @returns {String} Value
         */
        loadSetting(key: string, namespace): string;

        /**
         * Navigate to a given dashboard/screen
         * @param {String|Object} options
         * Either a dashboard name or an object containing target dashboard, screen and/or viewstate parameters to pass
         * @returns {Number}
         */
        navigate(options: string | Record<string, any>): void;

        /**
         * Page a given data source
         * @param {Object} source
         */
        page(source: Record<string, any>, options);

        /**
         * Save a setting to the .settings viewstate
         * @param {String} key
         * @param {String} value
         */
        saveSetting(key: string, value: string, namespace);

        /**
     * Set the property at path to the given value: Most properties can optionally be bound to a view state by the user. If this is done, onSettingsChange will pass the value of the view state to the component, i.e. the component does not need to check if a property has been bound to a view state or not, it simply works off the value given by onSettingsChange. If the view state value is updated elsewhere in Dashboards, the new value will be passed to the component via onSettingsChange.

    To set the value of these view states, use setProperty.
     * @param {String} path
     * The path of the property to set. If that property has been bound to a view state by the user, then the value of that view state will be set instead.
     * @param {*} value
     * The value to set the property to.
     */
        setProperty(path: string, value: any, options?: any): void;

        /**
         * Set the value of the given viewstate model
         * @param {ViewModelInterface} viewState {@link ViewModelInterface | ViewModelInterface}
         * : Viewstate Model
         * @param {*} value
         * @returns {Boolean}
         */
        setViewState(viewState: ViewModelInterface, value): boolean;

        /**
         * Show a Dashboards error message on the component
         * @param errorObj
         * An object containing the error information: Error (description of the error) & Type (One of 'Error', 'Warning', 'Info')
         */
        showErrorMessage(errorObj: Record<string, any>);

        /**
         * Show notification
         * @param visualConfig
         * @param soundConfig
         */
        showNotification(visualConfig?: any, soundConfig?: any);

        /**
         * Show Query Status
         * @param errorObj
         */
        showQueryStatus(errorObj);

        /**
         * Subscribe to a given source
         * @param {DataModelInterface} source
         * The data source to subscribe to. A data source can be retrieved from onSettingsChange as follows (this example assumes it is a data property called Data in a category called Basics)
         * @param {ComponentApi~sourceCallback} callback
         * callback is a function that will be called when there is a data update.
         */
        subscribe(
            source: DataModelInterface,
            callback: (
                meta: ChangeSetInterface,
                data: ChangeSetInterface,
                error: ErrorInterface,
            ) => void,
            raw?: boolean,
            pagingInfo?: PagingInfoInterface,
        ): void;

        /**
         * Extract view states from template and pass their values to callback
         * @param {String} template
         * @param {ComponentApi~sourceCallback} callback
         */
        subscribeTemplateViewStates(
            template: string,
            callback: (viewStates: object) => void,
        ): string;

        /**
         * Unsubscribe from a given source
         * @param {DataModelInterface} source
         * : Data source
         */
        unsubscribe(source: DataModelInterface): void;

        /**
         * @param {String} subscriptionId
         */
        unsubscribeTemplateViewStates(subscriptionId: string): void;

        /**
         * Updates the table in kdb
         * @param {String} source
         * @param {Object} options
         * @param {Function} successFunc - success callback
         * @param {Function} errorFunc - error callback
         * @returns undefined
         */
        updateTable(
            source: string,
            options: Record<string, any>,
            successFunc: Function,
            errorFunc: Function,
        ): void;
    }
}
