/// <reference path="I64.ts" />
/// <reference path="Constants.ts" />

// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase.ipc {
    export class Timestamp extends I64 {
        constructor(length: number, offset: number, dataView: DataView) {
            super(length, offset, TypeNum.Timestamp, dataView);
        }

        public static fromMoment(m: moment.Moment): rTimestampClass {
            return m === null
                ? null
                : new rTimestampClass(m.valueOf(), m.milliseconds() * 1000000 + m.nanoseconds());
        }

        public static fromScalar(value: bigint | Long | number): rTimestampClass {
            const kdbNanos =
                typeof value === "bigint" || typeof value === "number"
                    ? Long.fromString(value.toString())
                    : value;

            if (kdbNanos.equals(Constants.LONG_NULL)) {
                return null;
            }

            // todo - need the precision of jsbn
            const jsNanos2 = new jsbn.BigInteger(kdbNanos.toString()).add(Constants.BQEPOCH);

            // convert to nano
            const jsNanosStr = jsNanos2.toString();
            return new rTimestampClass(
                Number(jsNanosStr.substring(0, jsNanosStr.length - 6)),
                Number(jsNanosStr.substring(jsNanosStr.length - 9)),
            );
        }

        public static fromString(str: string): rTimestampClass {
            if (str === null) return null;
            const dateMatch = str.match(/(.*)(D|T)(.*?)Z?$/);
            if (!dateMatch) return null;
            str = dateMatch[1].replace(/\./g, "-") + "T" + dateMatch[3] + "Z";
            const m = moment(str);
            m.nanoseconds(Tools.extractNanos(str));
            return Timestamp.fromMoment(m);
        }

        public static listToIPC(values: Array<rTimestampClass>): Uint8Array {
            const size = values.length * 8 + 6;
            const buffer = Util.createBuffer(size);

            buffer.wb(TypeNum.Timestamp); // type
            buffer.wb(0); // attributes
            buffer.wi(values.length); // vector size
            _.each(values, v => DateTime.writeValue(v, buffer.wb)); // values

            return buffer.data;
        }

        public static toIPC(value: rTimestampClass): Uint8Array {
            const buffer = Util.createBuffer(9);

            buffer.wb(256 - TypeNum.Timestamp); // type
            Timestamp.writeValue(value, buffer.wb); // value

            return buffer.data;
        }

        public static toMoment(value: rTimestampClass): moment.Moment {
            if (value === null) return null;
            const m = moment.tz(value.i, "UTC");
            m.nanoseconds(value.n % 1000000);
            if (Tools.timezone && m.isValid()) m.tz(Tools.timezone);
            return m;
        }

        public static toScalar(value: rTimestampClass): bigint {
            return value === null ? null : BigInt(Timestamp.toScalarString(value));
        }

        public static toString(value: rTimestampClass, format?: string): string {
            if (value === null) return "";
            const m = Timestamp.toMoment(value);
            const str = m.format(format || "YYYY-MM-DDTHH:mm:ss.SSS");
            return format ? str : str.substr(0, 23) + _.padEnd("" + m.nanoseconds(), 6, "0") + "Z";
        }

        private static writeValue(value: rTimestampClass, wb: (b: number) => void): void {
            const str = Timestamp.toScalarString(value);
            QLong.writeValue(str, wb);
        }

        private static toScalarString(value: rTimestampClass | number): string {
            if (value === null) return null;
            if (_.isNumber(value)) {
                value = this.fromMoment(moment(value));
            }
            const nanos = _.padStart(value.n.toFixed(0), 9, "0");
            const ms = value.i.toFixed(0);

            // need 64-bit+ precision
            let bignum = new jsbn.BigInteger(ms + nanos.substring(3));
            bignum = bignum.subtract(Constants.BQEPOCH);
            return bignum.toString();
        }

        // note: need more than 64-bit precision here so use big nums for calcs.
        getValue(i: number): rTimestampClass {
            const bb = new Uint8Array(this.dataView.buffer, this.getByteLocation(i), this.SIZE);
            const kdbNanos = Long.fromBytesLE(bb);
            if (kdbNanos.equals(Timestamp.LONG_NULL)) {
                return null;
            }

            // need 64-bit+ precision of jsbn
            const jsNanos2 = new jsbn.BigInteger(kdbNanos.toString()).add(Constants.BQEPOCH);

            // convert to nano
            const jsNanosStr = jsNanos2.toString();
            return new rTimestampClass(
                Number(jsNanosStr.substring(0, jsNanosStr.length - 6)),
                Number(jsNanosStr.substring(jsNanosStr.length - 9)),
            );
        }
    }
}
