/*global $,_,Backbone,console,require,T,DocumentDataModel*/
/// <reference path="../lib/backbone-deep-model.ts" />
/// <reference path="../classes/commonDefinitions.ts" />
/// <reference path="../templates/combined.ts" />
/// <reference path="../models/component.ts" />
/// <reference path="../classes/ComponentApi.ts"/>
/// <reference path="../classes/log.ts"/>

// eslint-disable-next-line @typescript-eslint/no-namespace
namespace QuickBase {
    export class AppView extends Backbone.View<Backbone.Model> {
        static COMMON_OPTIONS = [
            "callback",
            "dashboardAppModel",
            "dashboardViewModel",
            "deltaClient",
            "docDataModel",
            "docViewModel",
            "widgetId",
            "quickView",
        ];

        // common options
        callback: Function;
        dashboardAppModel: Backbone.Model;
        dashboardViewModel: Backbone.Model;
        deltaClient: any;
        docDataModel: DocumentDataModel;
        docViewModel: DocumentViewModel;
        widgetId: string;

        $appDiv = null;
        app = null;
        appOnSettingsChange = null;

        componentApi: ComponentApi;
        settingsModel: Backbone.DeepModel;
        key: string;
        appOptions: any;
        componentId: string;
        quickView: boolean;

        constructor(options) {
            super(options);

            if (!options.el) {
                this.el.classList.add("component-build-view");
            }

            _.extend(this, _.pick(options, AppView.COMMON_OPTIONS));

            if (options.key) {
                // called by ComponentApi.loadApp - use key and settings as given
                this.componentId = options.namespace || _.uniqueId("component_");
                this.appOptions = options.appOptions;
                this.key = options.key;
                this.settingsModel =
                    options.settings instanceof Backbone.Model
                        ? options.settings
                        : new ComponentOptions(options.settings, {}, null);
            } else {
                // otherwise assume called by QuickDash/Panel and this.model represents a component
                this.componentId = this.model.id;
                this.key = this.model.get("key");
                this.settingsModel = this.model.get("options");
            }

            this.initializeApi();
            this.render();
            this.requireApp();
        }

        initializeApi() {
            // define api
            this.componentApi = new ComponentApi(
                _.extend({}, _.pick(this, AppView.COMMON_OPTIONS), {
                    advancedMethods: true,
                    appKey: this.key,
                    componentId: this.componentId,
                    deltaClient: this.deltaClient ? this.deltaClient.deltaClient : null,
                    deltaClientLib: this.deltaClient,
                    settingsModel: this.settingsModel,
                }),
            );
        }

        initalizeEvents() {
            if (this.settingsModel) {
                this.listenTo(this.settingsModel, "change", this.onSettingsModelChange);
            }
        }

        initializeOnSettingsChange() {
            let hasOnSettingsChange = false;

            if (this.app && _.isFunction(this.app.onSettingsChange)) {
                this.appOnSettingsChange = _.bind(this.app.onSettingsChange, this.app);
            }

            if (this.appOnSettingsChange && _.isFunction(this.settingsModel.toFlat)) {
                // set component initial state
                this.appOnSettingsChange(
                    this.componentApi.processSettings(
                        this.settingsModel.toFlat(),
                        this.appOnSettingsChange,
                    ),
                );

                // Set flag if component implements onSettingsChange (used by PropertiesPanel)
                if (this.key === "BasicComponents") {
                    // temporary extra check until all BasicComponents implement onSettingsChange
                    if (_.isFunction(this.app.$view.onSettingsChange)) {
                        hasOnSettingsChange = true;
                    }
                } else {
                    hasOnSettingsChange = true;
                }
            }

            if (this.model) {
                if (hasOnSettingsChange) {
                    this.model.set("hasOnSettingsChange", true, { silent: true });
                } else {
                    this.model.unset("hasOnSettingsChange");
                }
            }
        }

        onSettingsModelChange(model, options) {
            try {
                let changedAttributes, processedAttributes;

                // options.propertyPath implies that we are setting a single property using a dot syntax path,
                // in that case we pass the path to changedAttributes so it can return the same, flattening arrays as necessary (e.g. Layers.0.Visible: true)
                changedAttributes = model.changedAttributes(
                    null,
                    options && options.propertyPath ? options.propertyPath : null,
                );

                // DeepModel changedAttributes can return empty objects for properties that didn't actually change
                // we remove them here
                _.each(changedAttributes, function(v, k) {
                    let after, before;

                    function accessor(item, key) {
                        let itemKey,
                            re = /^(\w+)((?:\.\w+)+)$/.exec(key);

                        if (re) {
                            // arrays need numeric index
                            itemKey = _.isArray(item) ? Number(re[1]) : re[1];
                            return _.isObject(item)
                                ? accessor(item[itemKey], re[2].substring(1))
                                : item;
                        } else {
                            return _.isObject(item) ? item[key] : item;
                        }
                    }

                    after = accessor(model.attributes, k);
                    before = accessor(model.previousAttributes(), k);

                    if (_.isEqual(before, after)) {
                        delete changedAttributes[k];
                    }
                });

                processedAttributes = this.componentApi.processSettings(
                    changedAttributes,
                    this.appOnSettingsChange,
                );

                if (this.appOnSettingsChange && !_.isEmpty(processedAttributes)) {
                    this.appOnSettingsChange(processedAttributes);
                }
            } catch (e) {
                Log.Error("AppView Settings Error", e, this);
            }
        }

        onResize(resizePanels?) {
            if (this.app && this.app.onResize) {
                this.app.onResize(resizePanels);
            }
        }

        onResizeStart(resizePanels) {
            if (this.app && this.app.onResizeStart) {
                this.app.onResizeStart(resizePanels);
            }
        }

        onResizeStop(resizePanels) {
            if (this.app && this.app.onResizeStop) {
                this.app.onResizeStop(resizePanels);
            }
        }

        remove() {
            if (this.app && _.isFunction(this.app.remove)) {
                this.app.remove();
            }

            if (this.componentApi) {
                // remove tooltip
                this.componentApi.removeTooltip();
                // Unsubscribe from all data sources and view states
                this.componentApi.unsubscribeAll();
            }

            return Backbone.View.prototype.remove.apply(this);
        }

        render() {
            this.$appDiv = this.$el;
            return this;
        }

        requireApp() {
            const self = this;

            requirejs(
                [self.key],
                function(AppModule) {
                    try {
                        self.initializeApi();

                        self.app = new AppModule(
                            _.extend(
                                {
                                    el: self.$appDiv,
                                    viewModel: self.docViewModel,
                                    dataModel: self.docDataModel,
                                    dashboardAppModel: self.dashboardAppModel,
                                    dashboardViewModel: self.dashboardViewModel,
                                    settingsModel: self.settingsModel,
                                    websiteUrl: self.dashboardAppModel.get("websiteUrl"),
                                    DeltaClientLib: self.deltaClient,
                                    api: self.componentApi.interface,
                                    widgetId: self.widgetId,
                                },
                                self.appOptions,
                            ),
                        );

                        self.initalizeEvents();
                        self.initializeOnSettingsChange();

                        if (_.isFunction(self.callback)) {
                            self.callback(self.app);
                        }
                    } catch (error) {
                        Log.Error("loading app error, require key: " + self.key, error);
                        if (_.isFunction(self.callback)) {
                            self.callback(error);
                        }
                    }
                },
                function(err) {
                    Log.Error("ComponentView:loadApp require error", err);
                    if (_.isFunction(self.callback)) {
                        self.callback(err);
                    }
                },
            );
        }
    }
}
