/*global require,Handlebars*/
/// <reference path="../classes/log.ts"/>
/// <reference path="../lib/tools.ts"/>
/// <reference types="Handlebars"/>
// eslint-disable-next-line @typescript-eslint/no-namespace,@typescript-eslint/no-unused-vars
namespace QuickBase {
    export function InitializeQuickbase(callback) {
        let globalize;

        // Initialize Globalize
        const language = $.cookie("dashboard-language") || "en";

        // Configure require.js paths
        require.config({
            paths: {
                cldr: "./locale/cldr",
                "cldr-data": "./locale/cldr-data",
                globalize: "./locale/globalize",
                locales: "./locale/locales",
            },
        });

        require([
            //'globalize',
            // CLDR content
            //'json!cldr-data/supplemental/likelySubtags.json',
            // Translation content
            "json!locales/messages.json",
            // Extend Globalize
            //'globalize/message'
        ], function(/*Globalize, likelySubtags,*/ messages) {
            const dict = messages[language || "en"] || {};
            const translateFn = function(key) {
                return dict[key] || key || "";
            };

            /*
            Globalize.load(likelySubtags);
            Globalize.loadMessages(messages);
    
            globalize = Globalize(language || 'en');
    
            translateFn = function (key) {
                if (!key) {
                    return '';
                }
    
                try {
                    return globalize.formatMessage(key);
                } catch (e) {
                    switch (e.code) {
                        case 'E_MISSING_MESSAGE':
                            //console.log('Missing Translation for "' + e.path + '"');
                            break;
                        default:
                            Log.Error('Globalize error: "' + e.message + '"');
                            break;
                    }
                }
                return key;
            };*/

            // Attach translateFn to window and Handlebars
            window["t"] = translateFn;

            Handlebars.registerHelper("moment", function(str: string, pattern: any, options: any) {
                if (str == null && pattern) {
                    return;
                }

                // if no args are passed, return a formatted date
                if (str == null && pattern == null) {
                    moment.locale("en");
                    return moment().format("MMMM DD, YYYY");
                }

                // options may be passed in arg1 or arg2, merge args to object
                let opts: any = { lang: "en", date: new Date() };
                opts = _.extend({}, opts, str, pattern, options);
                opts = _.extend({}, opts, opts.hash);
                // set the language to use
                moment.locale(opts.lang);

                // if both args are strings, this could apply to either lib.
                // so instead of doing magic we'll just ask the user to tell
                // us if the args should be passed to date.js or moment.
                if (typeof str === "string" && typeof pattern === "string") {
                    return moment(str).format(pattern);
                }

                if (Tools.isKDBTemporal(str)) {
                    const m = Tools.convertKDBTemporalToMoment(str);
                    if (moment.isDuration(m)) {
                        return m.formatNano(pattern);
                    }
                    return m.format(pattern);
                }

                // If handlebars, expose moment methods as hash properties
                if (opts.hash) {
                    if (opts.context) {
                        _.extend(opts.hash, opts.context);
                    }
                    const res = moment(str);
                    for (const key in opts.hash) {
                        if (res[key]) {
                            return res[key](opts.hash[key]);
                        } else {
                            console.log('moment.js does not support "' + key + '"');
                        }
                    }
                }
                if (typeof str === "object") {
                    return moment(str).format(pattern);
                }
                // if only a string is passed, assume it's a date pattern ('YYYY')
                if (typeof str === "string" && !pattern) {
                    return moment().format(str);
                }

                return moment(str).format(pattern);
            });

            Handlebars.registerHelper("t", function(key) {
                return translateFn(key);
            });
            /**
             * Get the native type of the given `value`
             *  Adapted version of kind-Of module https://github.com/jonschlinkert/kind-of
             *  common type sets replaced by lodash function
             *  this is used in typeOf helper in handlebars.helpers v0.9.0
             *
             * ```handlebars
             * {{typeOf 1}}
             * //=> 'number'
             * {{typeOf "1"}}
             * //=> 'string'
             * {{typeOf "foo"}}
             * //=> 'string'
             * ```
             * @param {any} `value`
             * @return {String} Returns the type of value.
             */
            Handlebars.registerHelper("typeOf", function(val): string {
                const toStringFn = Object.prototype.toString;

                const ctorName = function(val) {
                    return val.constructor ? val.constructor.name : null;
                };

                const isGeneratorFn = function(name, val?: any) {
                    return ctorName(name) === "GeneratorFunction";
                };

                const isGeneratorObj = function(val) {
                    return (
                        typeof val.throw === "function" &&
                        typeof val.return === "function" &&
                        typeof val.next === "function"
                    );
                };

                if (val === void 0) return "undefined";
                if (val === null) return "null";

                let type = typeof val as any;
                if (type === "boolean") return "boolean";
                if (type === "string") return "string";
                if (type === "number") return "number";
                if (type === "symbol") return "symbol";
                if (type === "function") {
                    return isGeneratorFn(val) ? "generatorfunction" : "function";
                }
                // Custom kdb Date type checker
                if (Tools.isKDBTemporal(val)) {
                    return Tools.Types.kdbToDashboard[val.class];
                }

                if (_.isArray(val)) return "array";
                if (_.isBuffer(val)) return "buffer";
                if (_.isArguments(val)) return "arguments";
                if (_.isDate(val)) return "date";
                if (_.isError(val)) return "error";
                if (_.isRegExp(val)) return "regexp";

                switch (ctorName(val)) {
                    case "Symbol":
                        return "symbol";
                    case "Promise":
                        return "promise";

                    // Set, Map, WeakSet, WeakMap
                    case "WeakMap":
                        return "weakmap";
                    case "WeakSet":
                        return "weakset";
                    case "Map":
                        return "map";
                    case "Set":
                        return "set";

                    // 8-bit typed arrays
                    case "Int8Array":
                        return "int8array";
                    case "Uint8Array":
                        return "uint8array";
                    case "Uint8ClampedArray":
                        return "uint8clampedarray";

                    // 16-bit typed arrays
                    case "Int16Array":
                        return "int16array";
                    case "Uint16Array":
                        return "uint16array";

                    // 32-bit typed arrays
                    case "Int32Array":
                        return "int32array";
                    case "Uint32Array":
                        return "uint32array";
                    case "Float32Array":
                        return "float32array";
                    case "Float64Array":
                        return "float64array";
                }

                if (isGeneratorObj(val)) {
                    return "generator";
                }

                // Non-plain objects
                type = toStringFn.call(val);

                switch (type) {
                    case "[object Object]":
                        return "object";
                    // iterators
                    case "[object Map Iterator]":
                        return "mapiterator";
                    case "[object Set Iterator]":
                        return "setiterator";
                    case "[object String Iterator]":
                        return "stringiterator";
                    case "[object Array Iterator]":
                        return "arrayiterator";
                }

                // other
                return type
                    .slice(8, -1)
                    .toLowerCase()
                    .replace(/\s/g, "");
            });

            Handlebars.registerHelper("isNull", function(val, strict?: boolean): boolean {
                if (strict === true) {
                    return _.isNull(val);
                } else {
                    //for strings empty string consider as null
                    return _.isString(val) ? val.length === 0 : _.isNull(val);
                }
            });

            Handlebars.registerHelper("encodeURI", function(val): string {
                // null or undefined
                return val == null ? "" : encodeURI(val);
            });

            Handlebars.registerHelper("encodeURIComponent", function(val): string {
                // null or undefined
                return val == null ? "" : encodeURIComponent(val);
            });

            Handlebars.registerHelper("toISOString", function(val): string {
                return QuickBase.Tools.convertKDBToViewstateValue(val);
            });

            if (_.isFunction(callback)) {
                callback({
                    globalize: globalize,
                    language: language,
                });
            }
        });
    }
}
